import gSdk from "../platformsdk/platform";
import { adDataSdkBase } from "./adDataSdkBase";
import ADConfigZS from "./ADConfigZS";
import { navigateSceneType } from "./adDataSdkDef";
import ZsPlayInfo from "../../commonUI/zs/ZsPlayInfo";
import UtilTool from "../UtilTools";
import { AdsCfgBase, AdsCfgOppo, AdsCfgQQ, AdsCfgWx } from "../platformsdk/Adscfg-def";

export class adDataSdkZS extends adDataSdkBase {

    private config: ADConfigZS = null;
    private adData = [];
    private isGameFinish: boolean = false;
    private isCtrlFinish: boolean = false;

    private referrerAppId: string = "";
    private sceneValue: number = 0;
    private loginDT: number = 0;

    private navigateSuccessObj = {};//跳转成功app数据对象
    constructor() {
        super();

        this.config = new ADConfigZS();
        this.loginDT = new Date().getTime();
    }

    init(params?: any) {
        let self = this;
        this.navigateSuccessObj = {};

        //加载配置
        var zsSdk = window["zs"]["sdk"];

        zsSdk.loadCfg(function (data) {
            self.config.initAdSetting(gSdk.sdk.ver, data);
            self.initAdsCfg();
            self.isCtrlFinish = true;
        }, function (msg) {
            console.log("MyOPPOAD:zs ad config err：", JSON.stringify(msg));
        });

        //初始化/来源上报
        if (gSdk.isWX) {
            zsSdk.init(1);
            let lauchOpton = gSdk.sdk.getLaunchOptionsSync();
            if (lauchOpton) {
                let referrerInfo = lauchOpton["referrerInfo"];
                if (referrerInfo) {
                    this.referrerAppId = referrerInfo["appId"] ? referrerInfo["appId"] : "";//来源APPID
                }
                this.sceneValue = lauchOpton["scene"] || 0;
            }
        }

        //登录
        zsSdk.login(function (data) {
            //PlayerInfo.user_id = data;
            gSdk.sdk.setUserId(data);

            if (gSdk.isWX) {
                self.clickGameReq({}, 1037, 1);//进入游戏上报数据
            }
            else if (gSdk.isQQ) {
                zsSdk.init(data);
            }
        }, function (msg) {
            console.log("MyOPPOAD:login fail:" + msg);
        })

        //加载游戏
        zsSdk.loadAd(function (data) {
            self.adData = data;
            self.isGameFinish = true;
        });
    }

    /**初始化广告配置 */
    private initAdsCfg() {
        let params: AdsCfgBase = null;
        let ver = this.adConfig.electControl("zs_version");
        let switchVal = this.adConfig.electControl("zs_switch");
        if (gSdk.isWX) {
            let cfgObj = new AdsCfgWx(ver, switchVal);
            cfgObj.bgbannershow = true;
            cfgObj.bgbannershowcount = this.adConfig.electControl("zs_banner_show_number");
            cfgObj.bginterstitialshow = this.adConfig.electControl("zs_table_screen");
            cfgObj.bggridshow = false;
            cfgObj.bgexposureduration = 0;
            params = cfgObj;
        }
        else if (gSdk.isOP) {
            let cfgObj = new AdsCfgOppo(ver, switchVal);
            cfgObj.bgshowad_inonemin = this.adConfig.electControl("zs_onemin_show_ad_switch");
            //cfgObj.bgshowad_inonemin = 0; //调试代码
            cfgObj.bgnative_exposure_num = this.adConfig.electControl("zs_start_exposure_num");
            //cfgObj.bgnative_exposure_num = 1; //调试代码
            params = cfgObj;
        }
        else if (gSdk.isQQ) {
            let cfgObj = new AdsCfgQQ(ver, switchVal);
            cfgObj.bgbannerrefreshtime = this.adConfig.electControl("zs_banner_refresh_time");
            params = cfgObj;
            console.log("isQQ 广告配置》》》",params);
            
        }
        else {
            params = new AdsCfgBase(ver, switchVal);
        }

        gSdk.sdk.initAdsCfg(params);
    }

    /**
     * 是否加载完成
     */
    public get isLoadGameFinish(): boolean {
        return this.isGameFinish;
    }


    /**
    * 开关配置是否加载完成
    */
    public get isLoadCtrlFinish(): boolean {
        return this.isCtrlFinish;
    }

    public get adConfig() {
        return this.config;
    }

    /**
     * 获得广告位类型
     * @param navScene 
     */
    public getAdType(navScene: navigateSceneType = navigateSceneType.default) {
        return 'endPage';
    }

    public getAdList(adType: String = 'default', maxNum?) {
        if (typeof this.adData == 'undefined') {
            console.log("getAdList adData is undefined, return");
            return [];
        }
        let data = this.adData[adType.toString()];
        if (!data) return [];

        //换天重置isplay
        let curTime = new Date().getTime();
        let day = UtilTool.Time.dayDiff(this.loginDT, curTime);
        if (day > 0) {
            this.loginDT = curTime;
            this.navigateSuccessObj = {};
        }
        // data[0]["isplay"] = "0";
        // console.log("getAdList data[0]", data[0]);
        if (maxNum != null) {
            if (data.length < maxNum) {
                while (data.length < maxNum) {
                    data.push(data[Math.floor(Math.random() * data.length)]);
                }
            } else if (data.length > maxNum) {
                while (data.length > maxNum) {
                    data.splice(Math.floor(Math.random() * data.length), 1);
                }
            }
        }
        return data;
    }

    setGamePlayed(data) {
        if (data && data.appid)
            this.navigateSuccessObj[data.appid] = 1;
    }

    /**
    * 小游戏跳转
    * @param data 
    * @param scene 
    * @param success 
    * @param fail 
    * @param complete 
    */
    navigateToMiniProgram(data, scene: navigateSceneType, success = null, fail = null, complete = null) {
        let self = this;
        var zsSdk = window["zs"]["sdk"];
        zsSdk.navigate2Mini(data, gSdk.sdk.userId,
            function () {
                self.setGamePlayed(data);
                success && success();
                if (gSdk.isWX) {
                    self.clickGameReq({}, 1, 3);//跳转上报数据
                }
                Laya.stage.event(ZsPlayInfo.EVENT_NAVIGATE_SUCCESS);
            },
            function () {
                fail && fail();
                if (gSdk.isWX)
                    Laya.stage.event(ZsPlayInfo.EVENT_NAVIGATE_FAILED);
            },
            function () {
                complete && complete();
                Laya.stage.event(ZsPlayInfo.EVENT_NAVIGATE_COMPLETED);
            });

    }

    /**
     * 获得跳转成功数据
     */
    public getNavigateSuccessObj() {
        return this.navigateSuccessObj;
    }

    //private clickGameReqUrl = "https://ad.ali-yun.wang/api/adlog/update_log";
    private clickGameReqUrl = "https://zsad.zxmn2018.com/api/adlog/update_log";

    /**
     * 
     * @param data 
     * @param scene 场景值
     * @param type 'type': 1-进入游戏上报，2-看完视频上报，3-跳出成功上报	
     */
    public clickGameReq(data, scene: number, type: number) {
        let openid = gSdk.sdk.userId;
        let appid = gSdk.sdk.getappId();
        if (openid) {
            let xhr = new Laya.HttpRequest();
            xhr.http.timeout = 10000;//设置超时时间；
            xhr.once(Laya.Event.COMPLETE, this, (e) => {
            });
            let sendData = { appid: appid, from_appid: this.referrerAppId, user_id: openid, scene: this.sceneValue, type: type }
            xhr.send(this.clickGameReqUrl, sendData, "post", "json", ["content-type", "application/json"]);
        }
    }

}
