import TriggerMgr from "./TriggerMgr";
import GameView from "./GameView"
import Utils3D from "../scripts/mgr/Utils3D";
import GameScene from "../script/GameScene";
import Constant from "../scripts/mgr/Constant"
import EventManager from "../common/EventManager";
import AudioManager from "../common/AudioManager";
import { SoundCfg } from "../scripts/defines/ResDef";

export default class Player{
    private _gameScene: GameScene = GameScene.getInstance();
    public _scene = null;
    public levelLh = null;
    public camera: Laya.Camera;
    public cameraNode = null;
    trailLh = null;
    bodyTrail = null;
    playerPos = null;
    bodyMat = [];
    skinMat = [];
    footYetiLh = null;
    constructor(scene,lvNode){
        this._scene = scene;
        this.levelLh = lvNode;
        
        this.cameraNode = this._scene.getChildByName("CameraObject");
        var cameraAni = this.cameraNode.getChildByName("Bip_jintou")
        this.camera = cameraAni.getChildByName("Main Camera");
        //Laya.stage.on(Laya.Event.KEY_DOWN,this,this.onKeyDown);
        //Laya.stage.on(Laya.Event.KEY_UP,this,this.onKeyUp)
        //Laya.stage.once("ChangeHat",this,this.changeShipin);
        this.footYetiLh = this._scene.getChildByName("luodi").getChildAt(0);
    }

    onKeyDown(e){
        var keyCode: number = e["keyCode"];
        if(!Utils3D.bRunning){
            Utils3D.bRunning = true;
            Utils3D.playAnimations(Utils3D.curPlayer,Utils3D.sAniState);
        }
        
        if(keyCode == 38){ //前
            Utils3D.curPlayer.transform.localPositionZ -= Utils3D._speedZ;
           this.camera.transform.localPositionZ -= Utils3D._speedZ;
        }else if(keyCode == 37){
            Utils3D.curPlayer.transform.localPositionX -= this._speedX;
            this.camera.transform.localPositionX -= this._speedX/2;
        }else if(keyCode == 39){
            Utils3D.curPlayer.transform.localPositionX += this._speedX;
            this.camera.transform.localPositionX += this._speedX/2;
        }
    }

    onKeyUp(e){
        Utils3D.nChangeX = 0;
        Utils3D.bMouseMoving = false;
        Utils3D.bRunning = false;
    }

    playerWeaps:Array<Laya.Sprite3D> = []; //武器
    findPlayerBody(){
        Utils3D.AniCrossFade(Utils3D.curPlayer,"tiaowu",0.5,0);
        Utils3D.AniCrossFade(Utils3D.curPlayer,"tiaowu",0.5,1);
        Utils3D.realBodies = [];
        for(var j=0;j<6;j++){
            Utils3D.realBodies[j] = Utils3D.curPlayer.getChildAt(j) as Laya.SkinnedMeshSprite3D;
            Utils3D.realBodies[j].skinnedMeshRenderer.castShadow = true;
            if(j == 0){
                Utils3D.realBodies[j].addComponent(GameView);
            }
        }

        //武器及子弹动画节点
        //Utils3D.curWeapon = Utils3D.findNode(Utils3D.curPlayer,Constant.BodyResPath[4]) as Laya.Sprite3D;
        Utils3D.weaponAni = Utils3D.findNode(Utils3D.curPlayer,Constant.BodyResPath[6]) as Laya.Sprite3D;
        Utils3D.weaponAni.active = false;
        Utils3D.attackXuli = Utils3D.findNode(Utils3D.curPlayer,Constant.BodyResPath[7]) as Laya.Sprite3D;
        Utils3D.attackXuli.active = false;

        Utils3D.bodyOrnaments = [];
        for(var k=0;k<5;k++){
            //Utils3D.BodySkin[k] = Utils3D.curPlayer.getChildAt(k+16) as Laya.SkinnedMeshSprite3D;
            Utils3D.bodyOrnaments[k] = Utils3D.curPlayer.getChildAt(k+8);
            Utils3D.bodyOrnaments[k].skinnedMeshRenderer.castShadow = true;
        }

        this.playerWeaps = [];
        var wNode = Utils3D.findNode(Utils3D.curPlayer,Constant.BodyResPath[4]);
        for(var i=0;i<6;i++){
            this.playerWeaps[i] = wNode.getChildAt(i+3);
        }
        //this.changeBodyColor();
        this.changeShipin(); 
        this.changeWeapon();
    }

    changeBodyColor(bShopSelect = false,index=0,ballIndex=-1){
        var bodyIndex = bShopSelect == false ? Utils3D.shopSelect[1] : index;
        if(Utils3D.bTryUse){
            bodyIndex = Utils3D.nLookSelect-1;
        }
        if(ballIndex >= 0){
            bodyIndex = ballIndex;
        }else{
            Utils3D.nWaistColor = bodyIndex;
            this.skinMat = [];
            for(var j = 0;j < 5;j++){
                this.skinMat[j] = Utils3D.BodySkin[j].skinnedMeshRenderer.material as Laya.BlinnPhongMaterial;
                this.skinMat[j].albedoColor = Constant.BodyColor[bodyIndex];
            }
        }
        this.bodyMat = [];
        for(var i = 0;i < 8;i++){
            this.bodyMat[i] = Utils3D.realBodies[i].skinnedMeshRenderer.material as Laya.BlinnPhongMaterial;
            this.bodyMat[i].albedoColor = Constant.BodyColor[bodyIndex];
        }
    }

    changeColor(bodyIndex){
        var pick = this._scene.getChildByName("Pick").getChildAt(0) as Laya.SkinnedMeshSprite3D;
        let matPick = pick.skinnedMeshRenderer.material as Laya.BlinnPhongMaterial;;
        matPick.albedoColor = Constant.BodyColor[bodyIndex];

        var yeti = this._scene.getChildByName("yeti");
        var yMat = yeti.meshRenderer.material as Laya.BlinnPhongMaterial;
        yMat.albedoColor = Constant.BodyColor[bodyIndex];

        var yPati0 = yeti.getChildAt(0) as Laya.ShuriKenParticle3D;
        yPati0.particleSystem.startColorConstant = Constant.BodyColor[bodyIndex];
        var yPati1 = yeti.getChildAt(1) as Laya.ShuriKenParticle3D;
        yPati1.particleSystem.startColorConstant = Constant.BodyColor[bodyIndex];

        var ff = this._scene.getChildByName("feijian");
        var f = ff.getChildAt(0) as Laya.MeshSprite3D;
        var fMat = f.meshRenderer.material as Laya.BlinnPhongMaterial;
        fMat.albedoColor = Constant.BodyColor[bodyIndex];
    }

    changeWeapon(bShopSelect = false,index=0){
        var bodyIndex = bShopSelect == false ? Utils3D.shopSelect[1] : index;
        for(var i = 0;i < 6;i++){
            this.playerWeaps[i].active = false;
        }
        if(bodyIndex < 6){
            this.playerWeaps[bodyIndex].active = true;
        }
        if(bShopSelect == false){
            Utils3D.curWeapon = null;
            Utils3D.curWeapon = this.playerWeaps[bodyIndex];
            Utils3D.curWeapon.meshRenderer.castShadow = true;
        }
        if (Utils3D.bTryUse) {
            this._gameScene.initMainLayer();
        }
    }

    changeShipin(bShopSelect = false,index=0){ //帽子
        var bodyIndex = bShopSelect == false ? Utils3D.shopSelect[2] : index;
        for(var i = 0;i < 5;i++){
            Utils3D.bodyOrnaments[i].active = false;
        }
        if(bodyIndex > 0){
            Utils3D.bodyOrnaments[bodyIndex-1].active = true;
        }
    }

    gameBegin(){
        Laya.Tween.to(Utils3D.curPlayer.transform,{localRotationEulerY:0},1000);
        Utils3D.AniCrossFade(Utils3D.curPlayer,"daiji",0.2,1);
        Utils3D.AniCrossFade(Utils3D.curPlayer,"kaiqiang",0.2,0);
    }
    
    repairBody(missdata){
        var reIndex = 0;
        Utils3D.onShocking(30,false);
        for(var i = 0;i < missdata.length;i++){
            reIndex = missdata[i];
        }
    }

    repairBodyBox(index,node){
        Utils3D.playerBodies[index].active = true;
        Utils3D.realBodies[index].active = true;
        var nameindex = Number(node.name.substr(4, 5));
        if(index == 5){
            if(Utils3D.shopSelect[2]>0){
                Utils3D.bodyOrnaments[Utils3D.shopSelect[2]-1].active = true;
            }
        }else if(index == 0){ //修复腰
            Utils3D.realBodies[7].active = false;
            //身体部分颜色始终和腰一致
            Utils3D.nWaistColor = nameindex;
            this.changeBodyColor(false,0,nameindex);
        }

        if(index > 0){ //外层皮肤颜色随球的颜色而改变(渐变效果)
            Utils3D.BodySkin[index-1].active = true;
            var mat = Utils3D.BodySkin[index-1].skinnedMeshRenderer.material as Laya.BlinnPhongMaterial;
            mat.albedoColor = Constant.BodyColor[nameindex];
        }
    }

    waistIndex = 0;//腰部颜色
    //碰到障碍物掉落效果
    createBombsAni(bodyIndex) {
        var pos = null;
        var bombSp = this._scene.getChildByName("qiu").clone() as Laya.MeshSprite3D;
        var playerPos = Utils3D.curPlayer.transform.position;
        var bodyPos = new Laya.Vector3(playerPos.x+0.3,playerPos.y+1.2,playerPos.z);
        if(bodyIndex == 1){
            bodyPos = new Laya.Vector3(playerPos.x+0.2,playerPos.y+0.6,playerPos.z);
        }
        bombSp.transform.position = new Laya.Vector3(bodyPos.x, bodyPos.y, bodyPos.z);
        this.levelLh.addChild(bombSp);
        bombSp.active = true;
        Utils3D.playAnimations(bombSp, "Take 001");
        var py = Number(bodyPos.y.toFixed(1)); //保留1位小数
        //console.log("createBombsAni   bodyPosY ========= ",py);
        Laya.timer.once(200+40*bodyIndex, this, function () {
            var ytSp = this._scene.getChildByName("luodi").clone();
            ytSp.transform.position = new Laya.Vector3(bodyPos.x, playerPos.y, bodyPos.z);
            this.levelLh.addChild(ytSp);
            ytSp.active = true;

            var randNum = Math.floor(Math.random() * 36);
            ytSp.transform.localRotationEulerY += randNum * 10;
            var randscale = Utils3D.random(8, 16) / 10;
            ytSp.transform.localScale = new Laya.Vector3(randscale, randscale, randscale);
        })
    }

    changeIntensity(num){
        for(var i = 0;i < 5;i++){
            this.skinMat[i].albedoIntensity += num;
        }
        
        for(var j = 0;j<8;j++){
            this.bodyMat[j].albedoIntensity += num;
        }
    }

    loopIntensity(){
        this.intenCount++;
        if(this.intenCount>=0 && this.intenCount < 15){
            this.changeIntensity(0.03);
        }else if(this.intenCount>=15 && this.intenCount < 30){
            this.changeIntensity(-0.03);
        }else{
            this.intenCount = -1;
            this.bIntensiting = false;
            Laya.timer.clear(this,this.loopIntensity);
        }
    }

    _speedX = 0.04;
    frameCount = 0;
    stopCount = 1;
    startCount = 1;
    tempState = "";
    intenCount = -1;
    bIntensiting = false;
    playerZ = 0;
    updating(){ //人物行走
        if(!Utils3D.bStart) return;
        if(Utils3D.nChangeX != 0){
            this.moveLeftR();
            if(Utils3D.nDirection < 3){
                this.changeMoveAni();
            }
        }
        if(!Utils3D.bHurt){
            //if(!Utils3D.bMouseMoving) return;
            if(!Utils3D.bRunning && Utils3D.nDirection != 3){
                Utils3D.bRunning = true;
                Utils3D.nDirection = 0;
                this.tempDir = -1;
                Utils3D.AniCrossFade(Utils3D.curPlayer,"qian",0.5,1);
            }
            
            this.stopCount = 0;
            this.frameCount++;
            if(this.startCount <= 20){//加速
                Utils3D.curPlayer.transform.localPositionZ -= this.startCount*(Utils3D._speedZ/20);
                this.cameraNode.transform.localPositionZ -= this.startCount*(Utils3D._speedZ/20);
            }else{ 
                Utils3D.curPlayer.transform.localPositionZ -= Utils3D._speedZ;
                this.cameraNode.transform.localPositionZ -= Utils3D._speedZ;
            }
            
            this.startCount++;
            this.playerPos = Utils3D.curPlayer.transform.position;
            
            if(Utils3D.nDirection != 3){ //滑行没脚印
                if(this.frameCount%40==0){
                    var index = this.frameCount%80==0?1:2;
                    this.createFootprints(index);
                }
            }
        }else{
            if(this.stopCount <= 30){//减速后退
                if(this.stopCount == 0){
                    Utils3D.bRunning = false;
                    if(Utils3D.nDirection != 3){
                        Utils3D.AniCrossFade(Utils3D.curPlayer,"hou",0.2,1);
                        Utils3D.AniCrossFade(Utils3D.curPlayer,"shoushang",0.2,0);
                    }
                }
                var value = Utils3D._speedZ;
                if(this.stopCount <= 15){
                     value = Utils3D._speedZ-(this.stopCount)*(Utils3D._speedZ/20);
                }
                Utils3D.curPlayer.transform.localPositionZ += value
                this.cameraNode.transform.localPositionZ += value
                if(this.stopCount == 30){
                    Utils3D.bHurt = false;
                }
            }
            this.stopCount++;
            this.startCount = 0;
        }
    }

    tempDir = -1;
    moveLeftR(){ //左右移动
        if(Utils3D.curPlayer.transform.localPositionX <= Utils3D.nMaxLeftx
            && Utils3D.curPlayer.transform.localPositionX >= Utils3D.nMaxRightx){
            // var dx = Utils3D.nChangeX>0?-0.1:0.1;
            // Utils3D.curPlayer.transform.localPositionX += dx;
            // this.camera.transform.localPositionX += dx;
            Utils3D.curPlayer.transform.localPositionX -= Utils3D.nChangeX;
            this.camera.transform.localPositionX -= Utils3D.nChangeX;
        }else{ //左右边缘情况
            if(Utils3D.curPlayer.transform.localPositionX < 0){
                if(Utils3D.nChangeX < 0){
                    Utils3D.curPlayer.transform.localPositionX += this._speedX;
                    this.camera.transform.localPositionX += this._speedX;
                }
            }else{
                if(Utils3D.nChangeX > 0){
                    Utils3D.curPlayer.transform.localPositionX -= this._speedX;
                    this.camera.transform.localPositionX -= this._speedX;
                }
            }
        }
    }

    changeMoveAni(){
        if(Utils3D.nChangeX<0){
            Utils3D.nDirection = 1;
            if(Utils3D.nDirection != this.tempDir){
                this.tempDir = Utils3D.nDirection;
                Utils3D.AniCrossFade(Utils3D.curPlayer,"zuo",0.2,1);
                //console.log("左===========")
            }
        }else{
            Utils3D.nDirection = 2;
            if(Utils3D.nDirection != this.tempDir){
                this.tempDir = Utils3D.nDirection;
                Utils3D.AniCrossFade(Utils3D.curPlayer,"you",0.2,1);
                //console.log("右右===========")
            }
        }
    }

    createFootprints(index){ //脚印
        if(index == 2 && !Utils3D.realBodies[2].active) return; //右脚断掉不显示脚印
        if(index == 1 && !Utils3D.realBodies[1].active) return; 
        var xx = index==1?-0.2:0.2;
        var bombSp = this.footYetiLh.clone() as Laya.MeshSprite3D;
        bombSp.transform.position = new Laya.Vector3(this.playerPos.x+xx,this.playerPos.y,this.playerPos.z);
        this.levelLh.addChild(bombSp);
        bombSp.active = true;
        var randNum = Math.floor(Math.random()*36);
        bombSp.transform.localRotationEulerY += randNum*10;
        var randscale = 0.15;//Utils3D.random(3,6)/10;
        bombSp.transform.localScale = new Laya.Vector3(randscale,randscale,randscale);
    }

    hitCount = 0;
    onHitBullet(){ //玩家被敌人子弹打中
        this.hitCount++;
        var cameraAni = this.cameraNode.getChildByName("Bip_jintou");
        Utils3D.playAnimations(cameraAni,"shoushang");
        if(this.hitCount == 1){
            Utils3D.realBodies[3].active = false;
            Utils3D.AniCrossFade(Utils3D.curPlayer,"shoushang",0.2,0);
            this.createBombsAni(3);
        }else if(this.hitCount == 2){
            Utils3D.realBodies[1].active = false;
            Utils3D.AniCrossFade(Utils3D.curPlayer,"shoushang",0.2,0);
            this.createBombsAni(1);
        }else if(this.hitCount == 3){
            this.hitCount = 0;
            Utils3D.bStart = false;
            Utils3D.AniCrossFade(Utils3D.curPlayer,"si",0.2,0);
            Utils3D.AniCrossFade(Utils3D.curPlayer,"si",0.2,1);
            this._gameScene.playerDeath();
            Laya.timer.once(1000,this,function(){
                Utils3D.bGameWin = false;
                this._gameScene.GameFinish();
            })
        }
        Utils3D.onShocking(60,false);
        Utils3D.playParticle(Utils3D.curPlayer,true,7);
        
    }

    onMouseUp(){
        if(!Utils3D.bStart || Utils3D.nMoveDir > 2) return;
        var aniStri = Utils3D.sAniState+"S";
        if(Utils3D.sAniState == Constant.ManAni.Run){
            aniStri = "Ready";
        }
        //console.log("onMouseUp aniStri == ",aniStri);
        //Utils3D.AniCrossFade(Utils3D.curPlayer,aniStri,0.2);
    }

    nextGame(){
        Laya.timer.clearAll(this);
        this.startCount = 0;
        this.stopCount = 0;
        Utils3D.bRunning = false;
        Utils3D.nChangeX = 0;
        Utils3D.nMoveDir = 0;
        Utils3D.curPlayer.transform.position = new Laya.Vector3(0,0,-6);
        Utils3D.bJumping = false;
        Utils3D.nBodyScale = 1;
        this.frameCount = 0;
        this.intenCount = -1;
        this.bIntensiting = false;
        Utils3D.bHurt = false;
        Utils3D.nMaxdx = 3.2;
        Utils3D.nMaxLeftx = 3.2;
        Utils3D.nMaxRightx = -3.2;
        this.tempDir = -1;
        Utils3D.nDirection = 0;
        Utils3D._speedZ = -0.08;
        Utils3D.playParticle(Utils3D.curPlayer,false,13);
    }

    set PlayerSx(value){
        Utils3D.curPlayer.transform.localScaleX = value;
    }

    get PlayerSx(){
        return Utils3D.curPlayer.transform.localScaleX;
    }

    set PlayerSy(value){
        Utils3D.curPlayer.transform.localScaleY = value;
    }

    get PlayerSy(){
        return Utils3D.curPlayer.transform.localScaleY;
    }

    set PlayerSz(value){
        Utils3D.curPlayer.transform.localScaleZ = value;
    }

    get PlayerSz(){
        return Utils3D.curPlayer.transform.localScaleZ;
    }

    set PlayerRy(value){
        Utils3D.curPlayer.transform.localRotationEulerY = value;
    }

    get PlayerRy(){
        return Utils3D.curPlayer.transform.localRotationEulerY;
    }


}