export default class FingerTap extends Laya.Script {

    //private isPlaying: boolean;
    constructor() { super(); }

    onEnable(): void {
        //this.isPlaying = true;
        //this.start();
        Laya.timer.loop(100, this, this.start);
    }

    private start() {
        let node = this.owner as Laya.Image;
        if(node.skin == "common/fingerup.png")
            node.skin = "common/fingerdown.png";
        else if(node.skin == "common/fingerdown.png")
            node.skin = "common/fingerup.png";
    }

    onDisable(): void {
        //this.isPlaying = false;
        //Laya.Tween.clearTween(this.owner);
        Laya.timer.clearAll(this);
    }
}