import { ui } from "../../ui/layaMaxUI";
import { DialogManager } from "../../common/DialogManager";
import gSdk from "../../common/platformsdk/platform";
import { UICfg } from "../../scripts/defines/ResDef";
import { navigateSceneType } from "../../common/adDataSdk/adDataSdkDef";
import adSdk from "../../common/adDataSdk/adDataSdkFactory";
import { WxBannerScreen } from "../../common/platformsdk/plat-def";

export default class MoneyGet extends ui.commonUI.zs.MoneyGetUI{
    private showData: any[] = [];
    private showIndex = -1;
    constructor(){
        super();
        this.height = Laya.stage.height;
        this.closeEffect = null;
        this.popupEffect = null;
    }

    onEnable(): void{
        this.btn_Giveup.on(Laya.Event.CLICK, this, this.onGiveupClick);
        this.btn_Get.on(Laya.Event.CLICK, this, this.onGetClick);
        this.LoadGame();
    }

    onGiveupClick(){
        Laya.stage.once("FULL_SCREEN_AD1_CLOSE", this, function() {
            gSdk.sdk.showBannerzs();
        });
        DialogManager.Ins.sceneOpen(UICfg.fullscreenad1, false);
        DialogManager.Ins.sceneClose(this.url);
    }

    onGetClick(){
        adSdk.sdk.navigateToMiniProgram(this.showData[this.showIndex], navigateSceneType.more, () => { }, () => { }, () => { });
        DialogManager.Ins.sceneClose(this.url);
    }

    LoadGame(){
        let self = this;
        var adType = "endPage";
        var zsSdk = window["zs"]["sdk"];
        zsSdk.loadAd(function (data) {
            self.showData = [];
            for (var j: number = 0; j < data[adType].length; j++) {
                self.showData.push(data[adType][j]);
            }
            self.showIndex = Math.floor(Math.random() * self.showData.length);
            self.lbl_GameName.text = self.showData[self.showIndex].app_title;
            self.img_Game.skin = self.showData[self.showIndex].app_icon;
            self.lbl_TipGameName.text = `进入${self.showData[self.showIndex].app_title}领取红包`
        });

    }
}