import { ui } from "../ui/layaMaxUI";
import gSdk from "../common/platformsdk/platform";
import AudioManager from "../common/AudioManager";
import { AudioType } from "../common/data/AudioData";
import { UICfg } from "../scripts/defines/ResDef";

export default class extends ui.commonUI.SettingDialogUI {
    constructor() {
        super();
        this.musicBtn.on(Laya.Event.CLICK, this, this.clickHanlder, [1]);
        this.rockBtn.on(Laya.Event.CLICK, this, this.clickHanlder, [2])
        this.isPopupCenter = false;
    }

    private clickHanlder(_index: number) {
        //根据开发者具体业务修改
        if (_index == 1) {
            AudioManager.inst.update(AudioType.Mute);
        }
        else if (_index == 2) {
            AudioManager.inst.update(AudioType.Vibrator);
        }
        this.updateUI();
    }

    onEnable() {
        this.closeBtn.once(Laya.Event.CLICK, this, this.close);
        this.updateUI();
    }


    private updateUI() {
        this.labVer.text = "版本号：" + gSdk.sdk.ver;
        this.musicBtn.skin = AudioManager.inst.getState(AudioType.Mute) == 1 ? "common/other-05.png" : "common/other-06.png";  //根据开发者具体业务修改
        this.rockBtn.skin = AudioManager.inst.getState(AudioType.Vibrator) == 1 ? "common/other-05.png" : "common/other-06.png";//根据开发者具体业务修改
    }

    onDisable() {

    }
}