import gSdk from "../../common/platformsdk/platform";
import AudioManager from "../../common/AudioManager";
import { EventCfg, SoundCfg } from "../defines/ResDef";
import SceneMgr from "./SceneMgr";
import UserData from "../../common/data/UserData";
import SignData from "../../common/data/SignData";
import { AudioType } from "../../common/data/AudioData";
import adSdk from "../../common/adDataSdk/adDataSdkFactory";
import EventManager from "../../common/EventManager";
import GameScene from "../../script/GameScene";

export default class GameMgr {

    private static _ins: GameMgr;

    constructor() {

    }

    public static get Ins(): GameMgr {
        if (!this._ins) this._ins = new GameMgr();
        return this._ins;
    }

    public init() {
        console.log('MyOppoAD:GameMgr init');

        //GameScene.getInstance().onLoadedComplete(SceneMgr.Ins.gameScene);

        //加载缓存数据
        UserData.inst.getData();


        //加载（导出、开关）数据
        adSdk.init();

        //播放bgm
        AudioManager.inst.playMusic(SoundCfg.bgm,0);

        //oppo上报
        if (gSdk.isOP)
            gSdk.sdk.reportMonitor();

        EventManager.Ins.on(EventCfg.EVENT_BG_TO_GAME, this, this.Bgtogame);
        EventManager.Ins.on(EventCfg.EVENT_GAME_TO_BG, this, this.Gametobg);
    }

    Bgtogame() {
        //AudioManager.inst.playMusic(SoundCfg.bgm);
        console.log("从后台返回游戏,播放音乐");
        AudioManager.inst.playMusic(SoundCfg.bgm);
        Laya.timer.once(2000, this, () => { AudioManager.inst.playMusic(SoundCfg.bgm); });
    }
    Gametobg() {
        console.log("从游戏切换到后台,停止音乐");
        //AudioManager.inst.stopMusic();
    }
}