import { AdsType, WxBannerScreen } from "./plat-def";

export class AdsData {
    caller: any = null;
    /**广告显示回调 */
    private _cbShow: Function = null;
    /**广告关闭回调 */
    private _cbClose: Function = null;
    /**广告是否正在显示 调用微信平台show()和hide()函数时更改状态*/
    isShow: boolean = false;
    /**banner广告在屏幕内还是屏幕外
     * true:屏幕内 false:屏幕外
    */
    isbannerSee: boolean = false;
    /**banner广告显示在屏幕内的次数,就是isbannerSee为true时,次数要加1 */
    bannerShowCount: number = 0;
    /**banner广告展示时间超过bgexposureduration才算有效 */
    bannerExposureTime: number = 0;
    /**banner是否在误触业务中展示过 */
    bannerMistake: boolean = false;
    /**数据 (游戏传过来的数据)*/
    data: any = null;
    /**广告实例 */
    adInstance: any = null;
    /**广告类型 */
    adType: AdsType = AdsType.NONE;
    /**是否加载成功,调用show的时候要先判断此开关 */
    isLoaded: boolean = false;
    /**广告数据(原生广告) */
    adData: any = null;
    /**广告id */
    adsId: string = "";
    /**广告id数组索引 */
    adsIndex: number = WxBannerScreen.SCREEN_NONE;
    /** 广告关闭需不需要预加载 */
    neespreLoad: boolean = true;
    /**广告加载错误次数 */
    loadCount: number = 0;
    /**原生广告是否上报过曝光 */
    nativeShowed: boolean = false;
    /**banner style 格式 */
    style: {
        width: number,      //广告宽度
        height: number,     //广告高度
        realWidth: number,  //广告真实宽度
        realHeight: number,  //广告真实高度
        top: number,           //广告顶部
        left: number          //广告左边     
    } = { width: 600, height: 100, realWidth: 600, realHeight: 100, top: 0, left: 0 }

    /**
     * 
     * @param adsType 广告类型
     */
    constructor(adsType: AdsType) {
        this.adType = adsType;
    }

    /**
     * 设置广告
     */
    setAds(showFunc: Function, closeFunc: Function, data: any, caller?: any) {
        this._cbShow = showFunc;
        this._cbClose = closeFunc;
        this.data = data;
        this.caller = caller;
    }

    /**
     * 设置广告数据
     * @param adData 
     */
    setAdsData(adData: any) {
        this.adData = adData;
    }

    /** 广告显示回调 */
    show(state, data?: any) {
        if (!this._cbShow) return;
        if (this.caller) {
            this._cbShow.call(this.caller, state, data);
        } else {
            this._cbShow(state, data);
        }
        if (state) {
            this.isShow = true;
        } else {
            this._cbClose = null;
            this.isLoaded = false;
            this.isShow = false;
        }
        this._cbShow = null;
    }

    /**广告关闭回调 */
    close(state, data?: any) {
        this.isShow = false;
        if (!this._cbClose) return;
        if (this.caller) {
            this._cbClose.call(this.caller, state, data);
        } else {
            this._cbClose(state, data);
        }
        this.isLoaded = false;
        this._cbClose = null;
    }

    /**
     * 设置广告显示回调
     * @param showFunc 
     */
    setShowCall(showFunc: Function) {
        this._cbShow = showFunc;
    }

    /**
     * 设置广告关闭回调函数
     * @param closeFunc 
     */
    setCloseCall(closeFunc: Function) {
        this._cbClose = closeFunc;
    }

    /**
     * 广告加载结果
     * @param state 
     */
    adsLoad(state: boolean) {
        this.isLoaded = state;
    }

    /**
     * 删除广告实例
     * 返回是否消除广告实例成功
     */
    destroyAdsIns(): boolean {
        if (this.adInstance && typeof this.adInstance.destroy != 'undefined') {
            this.adInstance.destroy();
            this.adInstance = null;
            return true;
        }
        return false;
    }
}