import { SceneCfg } from "../defines/ResDef";

export default class SceneMgr {

    private static _ins: SceneMgr;

    public gameScene: Laya.Scene3D;
    private camera: Laya.Camera;

    constructor() {

    }

    public static get Ins(): SceneMgr {
        if (!this._ins) this._ins = new SceneMgr();
        return this._ins;
    }

    public init(completeMethod: Function, progressMethod: Function) {
        console.log('SceneMgr init');
        let self = this;
        Laya.loader.create(self.getResource(), Laya.Handler.create(this, function () {
            console.log('游戏场景加载完成');
            self.initScene();

            completeMethod && completeMethod();
        }), Laya.Handler.create(this, function (value) {
            progressMethod && progressMethod(value);
        }, null, false));

    }

    public initScene() {
        let scene = Laya.loader.getRes(SceneCfg.game);
        this.gameScene = scene;
        // var bgLayer = new BgLayer();
        // Laya.stage.addChildAt(bgLayer,0);

        //Laya.stage.addChild(this.gameScene);
        //Laya.stage.setChildIndex(this.gameScene, 0.5);
    }

    private getResource() {
        var res: Array<any> = [SceneCfg.game,'res/level/leveldata.json'];
        return res;
    }

    public loadRes(res, contxt, callback) {
        Laya.loader.create(res, Laya.Handler.create(contxt, callback));
    }

    public getRes(res) {
        return Laya.loader.getRes(res);
    }

}