import gSdk from "../platformsdk/platform";
import { ADConfigBase, TapStyle } from "./ADConfigBase";
import ControlData from "../data/ControlData";
import { UtilMath } from "../UtilTools";

export default class ADConfigQT extends ADConfigBase {
    private _serConfig: any = {};

    constructor() {
        super();
        this._serConfig = {
            /**
             * 两个总开关
             */
            zs_switch: 0,
            zs_version: "1.0",
            /**
             * 头条
             */
            zs_nulock_gate: 0,
            zs_result_get: 0,
            zs_tap_banner: 0,
            zs_tap_video: 0,
            zs_game_banner_ad: 0,   //游戏中是否显示banner 1:是 0:否
            zs_complete_version: 0,    //是否显示完整版本 1:是 0:否

            /**
             * 头条和QQ共用
             */
            zs_over_chest_layer: 0,         //--

            /**
             * QQ
             */

            /**
             * OPPO
             */
            zs_tryuse_close_delay: 0,       //试用界面关闭按钮是否延迟出现
            zs_native_tryuse_show: 0,       //试用界面是否显示原生广告
            zs_click_award_num: 0,          //原生广告展示（1为展示，0为不展示）
            zs_tool_video: 0,               //是否交换跳过广告和查看广告位置
            zs_showad_inonemin: 0,          //1分钟内是否显示banner和原生(1:是0:否)
            zs_click_award_city: 0,         //右上角导出开关


            zs_native_click_switch: 0,      //原生广告误触（0-跳过广告 1查看广告）
            zs_native_limit: 0,             //OPPO-原生广告展示（1为展示，0为不展示）
            zs_native_touch_switch: 0,      //控制原生广告关闭按钮误触（1：开启 0：关闭）
            zs_full_screen_ad: 0,           //结算页普通领取按钮是否显示（1：不显示
            zs_jump_time: 0,                //关闭按钮是否延迟显示 (0:否 其他数字:延迟
            zs_onemin_show_ad_switch: 0,    //1分钟内是否显示banner和原生(1:不显示0:显示)
            zs_start_exposure_num: 1,       //游戏中上报曝光次数

            /**
             * 微信
             */
            zs_banner_vertical_enable: 0,   //banner文字误触文字上移开关（0关，1开）,全屏导出误触
            zs_banner_text_time: 1000,      //banner文字误触文字延时移动时间（单位：毫秒）
            zs_banner_move_time: 1000,      //banner跳过文字上移缓动时间（单位：毫秒）
            zs_banner_banner_time: 1000,    //banner文字误触banner延时显示时间（单位：毫秒）
            zs_banner_click_show_time: 1000,//全屏点击按钮后banner延迟显示时间(单位:毫秒)
            zs_banner_click_hide_time: 1000,//全屏banner出现后,延迟消失时间(单位:毫秒)
            zs_banner_show_number: 2,       //banner展示2次后重新拉取
            zs_banner_show: 1,              //banner显示开关(1:显示 0:不显示)
            zs_banner_exposure_duration: 8000,//banner曝光时长(单位:毫秒 默认值:8000)
            zs_interstitial_show: 1,        //插屏广告显示开关(1:显示 0:不显示)
            zs_grid_show: 0,                //格子广告显示开关(1:显示 0:不显示)

            zs_history_list_jump: 0,        //假返回按钮控制开关（0关，1开） 首页为三级开关受zs_auto_witch控制,其他界面为二级开关
            zs_auto_witch: 0,          //控制首页的横向滚动导出,侧边栏按钮,更多游戏按钮是否显示
            zs_horizontal_export: 0,    //横向滚动导出开关
            zs_jump_switch: 0,              //右上角游戏导出控制开关,结算界面游戏导出控制开关（0关，1开）
            zs_before_finish_jump_switch: 0,//全屏一显示开关（0关，1开）
            zs_full_screen_jump: 0,         //全屏二显示开关（0关，1开）
            zs_auto_pop_ups_switch: 0,      //侧边栏导出自动弹出开关（0关，1开）
            zs_slide_jump_switch: 1,        //控制游戏是否更换,目前后台没设置,强制为1

            zs_gdt_full_screen_1: 0,        //广点通
            zs_gdt_full_screen_2: 0,
            zs_banner_gameend_enable: 0,    //控制游戏结束狂点

            zs_start_video_switch: 0,   //开局看视频
            zs_auto_jump_switch_1: 0,     //全屏一是否自动弹点击
            zs_auto_jump_switch_2: 0,     //全屏二是否自动弹点击
            zs_false_news_switch: 0,    //结算页是否弹好友通知
            zs_play_other_game: 0,      //多次运行某个游戏,是否运行其它游戏
            zs_tryuse_switch:0,         //是否启用试用功能

            zs_ad_jump_num: 2,
            zs_banner_adunit: "",
            zs_banner_city: 1,
            zs_banner_horizontal_enable: 1,
            zs_banner_refresh_time: 30000,
            zs_banner_rotate_id1: "",
            zs_banner_rotate_id2: "",
            zs_banner_rotate_id3: "",
            zs_banner_show_time: 1,
            zs_banner_system: "",
            zs_click_award_add: "0.1",
            zs_click_award_back: "0.05",
            zs_click_award_percent: "[0.4,0.7]",
            zs_click_award_since: "1",
            zs_click_award_system: "ios",
            zs_click_award_time: "00:00-23:59",
            zs_finish_city: 1,
            zs_finish_jump: 1,
            zs_finish_time: 1,
            zs_full_screen_city: 1,
            zs_full_screen_time: 1,
            zs_revive_click_num: 0,
            zs_revive_share_num: 0,
            zs_revive_video_num: -1,
            zs_video_adunit: "",
            zs_box_yc: "1000"

        }
    }

    /**配置表游戏版本号  ,广告后台控制数据 */
    initAdSetting(version, webResponse) {
        //this._serConfig = webResponse;      //要做一下检查操作
        //console.log('MyOPPOAD:initAdSetting', JSON.stringify(webResponse));
        if (typeof webResponse == 'undefined') {
            return;
        }
        this._serConfig = Object.assign({}, this._serConfig, webResponse);

        this._serConfig.zs_banner_text_time = this._serConfig.zs_banner_text_time ? Number(this._serConfig.zs_banner_text_time) : 1000;
        this._serConfig.zs_banner_move_time = this._serConfig.zs_banner_move_time ? Number(this._serConfig.zs_banner_move_time) : 1000;
        this._serConfig.zs_banner_banner_time = this._serConfig.zs_banner_banner_time ? Number(this._serConfig.zs_banner_banner_time) : 1000;
        this._serConfig.zs_banner_click_show_time = this._serConfig.zs_banner_click_show_time ? Number(this._serConfig.zs_banner_click_show_time) : 1000;
        this._serConfig.zs_banner_click_hide_time = this._serConfig.zs_banner_click_hide_time ? Number(this._serConfig.zs_banner_click_hide_time) : 1000;
        this._serConfig.zs_banner_show_number = this._serConfig.zs_banner_show_number ? Number(this._serConfig.zs_banner_show_number) : 2;
        this._serConfig.zs_banner_exposure_duration = this._serConfig.zs_banner_exposure_duration ? Number(this._serConfig.zs_banner_exposure_duration) : 8000;
        this._serConfig.zs_box_yc = this._serConfig.zs_box_yc ? Number(this._serConfig.zs_box_yc) : 1000;
        this._serConfig.zs_click_award_add = this._serConfig.zs_click_award_add ? Number(this._serConfig.zs_click_award_add) : 0.1;
        this._serConfig.zs_click_award_back = this._serConfig.zs_click_award_back ? Number(this._serConfig.zs_click_award_back) : 0.05;
        this._serConfig.zs_click_award_percent = this._serConfig.zs_click_award_percent || "[0.4, 0.7]";

        if (gSdk.isWX) {//微信平台是为了和指色共用electControl()函数,提前在此对开关值做了处理
            if (this.isSameVersion()) {
                this._serConfig.zs_banner_show = this._serConfig.zs_banner_show == 1 ? true : false;
            }
            else
                this._serConfig.zs_banner_show = false;

            if (this.isSameVersion() && this.isSwitchOn()) {
                this._serConfig.zs_banner_vertical_enable = this._serConfig.zs_banner_vertical_enable == 1 ? true : false;
                this._serConfig.zs_history_list_jump = this._serConfig.zs_history_list_jump == 1 ? true : false;
                this._serConfig.zs_auto_witch = this._serConfig.zs_auto_witch == 1 ? true : false;
                this._serConfig.zs_horizontal_export = this._serConfig.zs_horizontal_export == 1 ? true : false;
                this._serConfig.zs_jump_switch = this._serConfig.zs_jump_switch == 1 ? true : false;
                this._serConfig.zs_gdt_full_screen_1 = this._serConfig.zs_gdt_full_screen_1 == 1 ? true : false;
                this._serConfig.zs_gdt_full_screen_2 = this._serConfig.zs_gdt_full_screen_2 == 1 ? true : false;
                this._serConfig.zs_banner_gameend_enable = this._serConfig.zs_banner_gameend_enable == 1 ? true : false;
                this._serConfig.zs_interstitial_show = this._serConfig.zs_interstitial_show == 1 ? true : false;
                this._serConfig.zs_grid_show = this._serConfig.zs_grid_show == 1 ? true : false;
                this._serConfig.zs_auto_jump_switch_1 = this._serConfig.zs_auto_jump_switch_1 == 1 ? true : false;
                this._serConfig.zs_auto_jump_switch_2 = this._serConfig.zs_auto_jump_switch_2 == 1 ? true : false;
                this._serConfig.zs_false_news_switch = this._serConfig.zs_false_news_switch == 1 ? true : false;
                this._serConfig.zs_play_other_game = this._serConfig.zs_play_other_game == 1 ? true : false;
                if (this._serConfig.zs_auto_witch) {
                    this._serConfig.zs_auto_pop_ups_switch = this._serConfig.zs_auto_pop_ups_switch == 1 ? true : false;
                    this._serConfig.zs_start_video_switch = this._serConfig.zs_start_video_switch == 1 ? true : false;
                }
                else {
                    this._serConfig.zs_auto_pop_ups_switch = false;
                    this._serConfig.zs_start_video_switch = false;
                }
            }
            else {
                this._serConfig.zs_banner_vertical_enable = false;
                this._serConfig.zs_history_list_jump = false;
                this._serConfig.zs_auto_witch = false;
                this._serConfig.zs_horizontal_export = false;
                this._serConfig.zs_jump_switch = false;
                this._serConfig.zs_auto_pop_ups_switch = false;
                this._serConfig.zs_gdt_full_screen_1 = false;
                this._serConfig.zs_gdt_full_screen_2 = false;
                this._serConfig.zs_banner_gameend_enable = false;
                this._serConfig.zs_interstitial_show = false;
                this._serConfig.zs_grid_show = false;
                this._serConfig.zs_start_video_switch = false;
                this._serConfig.zs_auto_jump_switch_1 = false;
                this._serConfig.zs_auto_jump_switch_2 = false;
                this._serConfig.zs_false_news_switch = false;
                this._serConfig.zs_play_other_game = false;
            }
        }
        else if (gSdk.isOP) {
            this._serConfig.zs_tryuse_close_delay = webResponse.zs_tryuse_close_delay == 0 ? false : true;
            this._serConfig.zs_native_tryuse_show = webResponse.zs_native_tryuse_show == 0 ? false : true;
            this._serConfig.zs_click_award_num = webResponse.zs_click_award_num == 0 ? false : true;
            this._serConfig.zs_tool_video = webResponse.zs_tool_video == 0 ? false : true;
            this._serConfig.zs_showad_inonemin = webResponse.zs_showad_inonemin == 0 ? false : true;
            this._serConfig.zs_click_award_city = webResponse.zs_click_award_city == 0 ? false : true;

            this._serConfig.zs_start_exposure_num = webResponse.zs_start_exposure_num ? Number(this._serConfig.zs_start_exposure_num) : 1;
            if (this.isSameVersion()) {
                this._serConfig.zs_native_click_switch = webResponse.zs_native_click_switch == 0 ? false : true;
                this._serConfig.zs_native_limit = webResponse.zs_native_limit == 0 ? false : true;
                this._serConfig.zs_native_touch_switch = webResponse.zs_native_touch_switch == 0 ? false : true;
                this._serConfig.zs_full_screen_ad = webResponse.zs_full_screen_ad == 0 ? false : true;
                this._serConfig.zs_onemin_show_ad_switch = webResponse.zs_onemin_show_ad_switch == 0 ? false : true;
                this._serConfig.zs_jump_time = webResponse.zs_jump_time ? Number(this._serConfig.zs_jump_time) : 0;
            }
            else {
                this._serConfig.zs_onemin_show_ad_switch = true;
                this._serConfig.zs_native_click_switch = false;
                this._serConfig.zs_native_limit = false;
                this._serConfig.zs_native_touch_switch = false;
                this._serConfig.zs_full_screen_ad = false;
                this._serConfig.zs_jump_time = 0;
            }
        }

        //console.log('MyOPPOAD:localconfig', JSON.stringify(this._serConfig));
    }

    /**总开关 */
    isSwitchOn() {
        let on = this._serConfig['zs_switch'] || 0;
        return on == 1;
    }

    /**版本开关 */
    isSameVersion() {
        let version = "0.0";
        let curVersion = gSdk.sdk.ver;
        if (gSdk.isWX) {
            version = this._serConfig['zs_version'] || "0.0";
            return version != "0.0" && curVersion != version;   //版本号不相同打开误触
        } else if (gSdk.isQQ) {
            version = this._serConfig['zs_version'] || "0.0";
            return version != "0.0" && curVersion != version;
        } else if (gSdk.isOP) {
            version = this._serConfig['zs_version'] || "0.0";
            return version != "0.0" && curVersion == version;
        } else if (gSdk.isTT) {
            version = this._serConfig['zs_version'] || "0.0";
            return version != "0.0" && curVersion != version;
        } else if (gSdk.isVV) {         //Vivo平台,版本号相同开误触
            version = this._serConfig['zs_version'] || "0.0";
            return version != "0.0" && curVersion == version;
        }
    }

    /**导出开关是否开 */
    isElectOn() {
        let jump_switch = 0;
        if (gSdk.isWX) {
            jump_switch = this._serConfig['zs_jump_switch'] || 0    //指色说明 导出位置开关（1开 0关）
        } else if (gSdk.isQQ) {
            jump_switch = this._serConfig['zs_jump_switch'] || 0
        } else if (gSdk.isOP) {
            jump_switch = this._serConfig['zs_switch'] || 0
        }
        return this.isSameVersion() && (jump_switch == 1);
    }

    /**
     * 获得开关值
     * @param key 
     */
    electControl(key) {
        return this._serConfig[key];
    }

    /**
     * 检查开关
     * @param key 
     * @param checkSwitchOn 是否需要检查总开关
     */
    checkControl(key, checkSwitchOn = false) {
        let ret = this._serConfig[key] == 1;
        if (checkSwitchOn) {
            let zs = this.isSwitchOn(), sv = this.isSameVersion();
            return ret && zs && sv;
        }
        return ret;
    }


    //是否需要全屏导出
    isCanFullJump() {
        let jump = 0;
        if (gSdk.isWX) {
            jump = this.electControl['zs_full_screen_jump'];
        } else if (gSdk.isQQ) {
            jump = this.electControl['full_screen_jump'];
        }
        if (this.isElectOn() && jump == 1) {
            return true;
        }
        return false;
    }

    /**banner 是否需要误触 */
    isBannerMistake() {
        let bswitch = 0;
        let vertical_enable = 0;
        if (gSdk.isWX) {
            bswitch = this._serConfig['zs_switch'];                                 //指色说明 误触总开关(1-开 0-关)
            vertical_enable = this._serConfig['zs_banner_vertical_enable'] || 0;    //指色说明 banner文字误触文字上移开关（0关，1开）
        } else if (gSdk.isQQ) {
            bswitch = this._serConfig['zs_switch'];
            vertical_enable = this._serConfig['zs_banner_vertical_enable'] || 0;
        }
        return this.isSameVersion() && (bswitch == 1) && (vertical_enable == 1);
    }

    energyCanShow() {
        let switch_on = this.isSwitchOn();
        let ver = this.isSameVersion();
        if (!switch_on || !ver) {
            return false;
        }

        //显示宝箱开关
        let zs_energy_layer = parseInt(this._serConfig['zs_energy_layer']) || 0;
        if (zs_energy_layer == 0) {
            return false;
        }

        if (gSdk.isWX)
            return true;

        //每天显示次数
        if (!this.isReviveTypeEnable('zs_energy_click_num')) {
            return false;
        }
        this.updateReviveTypeInfo('zs_energy_click_num');

        //时间段判断
        let t = this._serConfig['zs_award_box_time'] || "0-23";
        let ts = t.split("-");
        let curTime = new Date();
        let h = curTime.getHours();
        if (h >= parseInt(ts[0]) && h <= parseInt(ts[1])) {
            return true;
        }
        return false;

    }

    //是否显示宝箱
    boxCanShow() {
        let switch_on = this.isSwitchOn();
        let ver = this.isSameVersion();
        if (!switch_on || !ver) {
            return false;
        }

        //显示宝箱开关
        let zs_over_chest_layer = parseInt(this._serConfig['zs_over_chest_layer']) || 0;
        if (zs_over_chest_layer == 0) {
            return false;
        }

        if (gSdk.isTT || gSdk.isWX)
            return true;

        //每天显示次数
        if (!this.isReviveTypeEnable('zs_ready_click_num')) {
            return false;
        }
        this.updateReviveTypeInfo('zs_ready_click_num');

        //时间段判断
        let t = this._serConfig['zs_award_box_time'] || "0-23";
        let ts = t.split("-");
        let curTime = new Date();
        let h = curTime.getHours();
        if (h >= parseInt(ts[0]) && h <= parseInt(ts[1])) {
            return true;
        }
        return false;
    }

    //验证当天字段值
    isReviveTypeEnable(type) {
        if (this._serConfig[type] == 0) {
            return false;
        }
        if (this._serConfig[type] == -1) {
            return true;
        }
        var numVal = ControlData.inst.getValByToday(type);
        return numVal < this._serConfig[type];
    }

    //修改当天字段值
    updateReviveTypeInfo(type) {
        ControlData.inst.setValByToday(type);
    }

    //微信开关获取接口

    /**游戏结束前 */
    isBeforeGameAccount() {
        let ret = this._serConfig["zs_before_finish_jump_switch"] == 1;
        let zs = this.isSwitchOn(), sv = this.isSameVersion();
        return ret && zs && sv;
    }

    /**游戏结束后 */
    isAfterGameAccount() {
        let ret = this._serConfig["zs_full_screen_jump"] == 1;
        let zs = this.isSwitchOn(), sv = this.isSameVersion();
        return ret && zs && sv;
    }

    isCrazyTap(lev: number, taptype: number): boolean {
        return this._serConfig["zs_banner_gameend_enable"];
    }

    GetCrazyPercent() {
        let zs_click_award_percent = this._serConfig.zs_click_award_percent;
        let sp_str = zs_click_award_percent.substr(1, zs_click_award_percent.length - 2);
        let arr = sp_str.split(",");
        let iResult = UtilMath.randRange(Number(arr[0]), Number(arr[1]));
        return iResult;
    }
}

