import { UICfg } from "../../scripts/defines/ResDef";
import adSdk from "../../common/adDataSdk/adDataSdkFactory";
import { DialogManager } from "../../common/DialogManager";

export default class FakeExitBtn extends Laya.Script {
    constructor() {
        super();
    }

    onAwake() {
        (this.owner as Laya.Button).mouseEnabled = true;
        (this.owner as Laya.Button).visible = adSdk.sdk.adConfig.electControl('zs_history_list_jump');
    }

    onClick() {
        (this.owner as Laya.Button).mouseEnabled = false;
        DialogManager.Ins.sceneOpen(UICfg.fullscreenad2, false);
        (this.owner as Laya.Button).mouseEnabled = true;
    }

}