export default class Breathe extends Laya.Script {

    private isPlaying: boolean;
    constructor() { super(); }

    onEnable(): void {
        this.isPlaying = true;
        this.start();
    }

    private start() {
        let tscale: number = 1;// = this.owner["scaleX"] == 1 ? 0.9 : 1;
        let node = this.owner as Laya.Sprite;
        if(node.scaleX == 1){
            tscale = 0.9;
        }
        else if(node.scaleX == 0.9) {
            tscale = 1;
        }
        else if(node.scaleX == 0.6) {
            tscale = 0.7;
        }
        else if(node.scaleX == 0.7) {
            tscale = 0.6;
        }
        Laya.Tween.to(this.owner, { scaleX: tscale, scaleY: tscale }, 500, null, new Laya.Handler(this, () => {
            if (this.isPlaying) this.start()
        }))
    }

    onDisable(): void {
        this.isPlaying = false;
        Laya.Tween.clearTween(this.owner);
    }
}