import DataStorage from "./DataStorage"
import { GameNameCfg } from "../../scripts/defines/ResDef";

export default class UserData extends DataStorage {
    private static _instance: UserData = null;

    constructor() {
        super();
    }

    public static get inst(): UserData {
        if (this._instance == null) {
            this._instance = new UserData();
        }
        return this._instance
    }

    getData() {
        //清理缓存
        this.clear();
        this.init(GameNameCfg);
        this.login();
        this.initDatas();
        this.save();
    }

    initDatas() {
        this._data.gold = this._data.gold || 0;
        this._data.level = this._data.level || 1;
        this._data.skins = this._data.skins || "1";
        this._data.useSkin = this._data.useSkin || "1";
        this._data.singTime = this._data.singTime || 0;
    }

    get level() {
        return this._data.level;
    }

    setLevel(id?: number) {
        if (id) {
            this._data.level = id;
        } else {
            this._data.level++;
        }
        this.save();
    }

    setGold(gold: number) {
        let sum = this._data.gold + gold;
        if (sum >= 0) {
            this._data.gold = sum;
            this.save();
        }
        return sum;
    }

    get gold() {
        return this._data.gold;
    }

    get useSkin(){
        return this._data.useSkin;
    }

    changeSkin(_id:string){
        if(this._data.useSkin!=_id){
            this._data.useSkin=_id;
            this.save();
            return true;
        }
        return false;
    }

    unlockSkin(_id:string){
        if(!this.isUnlockSkin(_id)){
           if(!this._data.skins)this._data.skins=_id;
           else this._data.skins += ("|"+_id);
           //this._data.useSkin=_id;
           this.save();
           return true;
        }
        return false;
    }

    isUnlockSkin(_id:string){
        if(this._data.skins){
            let tarr = this._data.skins.split("|");
            for(let i=0;i<tarr.length;i++){
                if(tarr[i]==_id)return true;
            }
        }
        return false;
    }

    public getUnlockSkins(){
        let tarr = [];
        for(let i=1;i<=9;i++){
            if(!this.isUnlockSkin(i+"")){
                tarr.push(i);
            }
        }
        return tarr;
    }

    get singTime() {
        return this._data.singTime;
    }

    setSingTime(id?: number){
        if (id) {
            this._data.singTime = id;
        } else {
            this._data.singTime = Date.now();//
        }
        this.save();
    }
}