import { ui } from "../ui/layaMaxUI";

import Utils3D from "../scripts/mgr/Utils3D";
import GameScene from "../script/GameScene";
import Constant from "../scripts/mgr/Constant"
import display from "../common/Display";
import gSdk from "../common/platformsdk/platform";
import adSdk from "../common/adDataSdk/adDataSdkFactory";
import AdList from "../commonUI/zs/AdList";
import { DialogManager } from "../common/DialogManager";
import { UICfg } from "../scripts/defines/ResDef";
import ZsPlayInfo from "../commonUI/zs/ZsPlayInfo";
import { navigateSceneType } from "../common/adDataSdk/adDataSdkDef";
//import { WxBannerScreen } from "../../\u9A6C\u75321-\u54B8\u9C7C\u8DF3\u4E00\u8DF3/src/common/platformsdk/plat-def";

export default class ShowOffView extends ui.gameUI.ShowOffViewUI {
    private _gameScene: GameScene = GameScene.getInstance();
    tabNum = 1;
    nCurTab = 1;
    tabAarry = [];
    nPayIndex = 0;
    constructor() {
        super();
        this.height = Laya.stage.height;
        this.bg.height = this.height;
        //this.bg.graphics.drawRect(0, 0, Laya.stage.width, Laya.stage.height, "#23a8ea");
    }

    onAwake(): void {
    }

    onMouseDown(): void {

    }

    onEnable(): void {
        //完成按钮
        this.completeBtn.on(Laya.Event.CLICK, this, this.closeEvent);
        this.shareBtn.on(Laya.Event.CLICK, this, function () {
            //分享
        })
        //立即跳过
        this.closeBtn.on(Laya.Event.CLICK, this, this.closeEvent);
        if (Utils3D.bTryUse) {
            Utils3D.taskScore = 100;
        }
        this.progressBar.width = 0;

        this.showGameUI();
        Laya.timer.loop(2, this, this.updateScore)

        if (gSdk.isWX && adSdk.sdk.adConfig.electControl("zs_banner_vertical_enable")) {
            gSdk.sdk.hideBannerzs();


            this.closeBtn.bottom = 50;

            let tbanner_show_delay: number = adSdk.sdk.adConfig.electControl("zs_banner_banner_time");
            let ttext_move_delay: number = adSdk.sdk.adConfig.electControl("zs_banner_text_time");
            let ttext_move_time: number = adSdk.sdk.adConfig.electControl("zs_banner_move_time");

            Laya.timer.once(tbanner_show_delay, this, () => { gSdk.sdk.showBannerzs() });

            Laya.Tween.to(this.closeBtn, { x: this.closeBtn.x, y: this.height - 250 - this.closeBtn.height }, ttext_move_time, null, Laya.Handler.create(this, () => {
            }), ttext_move_delay);


            this.spProgress.x = 100;
            this.spProgress.y = 147 + 80;
        } else {
        }

        if (adSdk.sdk.adConfig.electControl("zs_horizontal_export")) {
            let electList: Laya.Image = this.getChildByName("exportList1") as Laya.Image;
            electList.visible = true;
            if (!electList.getChildAt(0).getComponent(AdList)) {
                let adList: AdList = electList.getChildAt(0).addComponent(AdList);
                adList.requestAdData(navigateSceneType.scroll, true, ZsPlayInfo.SCROLL_HORIZONTAL, null, null, false, true);
            }
        }
    }

    closeEvent(e: Laya.Event) {
        if (e.type == Laya.Event.CLICK) {
            this.initUI(false);
            this.close();
            this._gameScene.GameFinish(0);
        }
    }

    _score = 0;
    updateScore() {
        if (this._score < Utils3D.taskScore) {
            this._score += 2;
            this.progressBar.width += 2.98 * 2;
            this.textBar.text = this._score.toString() + "%";
        } else {
            this.initUI(true);
            if (gSdk.isTT) {
                gSdk.sdk.stopRecording();
            }
        }
    }

    initUI(bShow) {
        Laya.timer.clear(this, this.updateScore);
        this.progressBar.width = 298 * (Utils3D.taskScore / 100);
        this.textBar.text = Utils3D.taskScore.toString() + "%";
    }

    showGameUI() {
        // let value = (Laya.stage.height-1280)/2;
        // this._gameScene._endCamera.renderTarget = new Laya.RenderTexture(Laya.stage.width,Laya.stage.height);
        // //渲染顺序
        // this._gameScene._endCamera.renderingOrder = -1;
        // // //清除标记
        // this._gameScene._endCamera.clearFlag = Laya.BaseCamera.CLEARFLAG_SKY;
        // let rr = ((this._gameScene._endCamera.renderTarget as any));
        // var rtex = new Laya.Texture(rr, Laya.Texture.DEF_UV);
        // rtex.width = 500;
        // this.text.visible = true;
        // this.text.color = "#000000";
        // var point = new Laya.Vector3(0,0,0);
        // let pos = this._gameScene.object.transform.position;
        // this._gameScene.endCamera.viewport.project(pos,this._gameScene.endCamera.projectionViewMatrix,point);
        // var cx = Laya.stage.clientScaleX; 
        // var cy = Laya.stage.clientScaleY;
        // this.text.x = (point.x+22)/cx;
        // this.text.y = (point.y-14)/cy;
        // let index = Math.floor(Math.random() * 10000) + 1;
        // this.text.text = index.toString();

        if (gSdk.isTT) {
            this.shareBtn.visible = true;
            this.completeBtn.visible = false;
        } else {
            this.shareBtn.visible = false;
            this.completeBtn.visible = true;
        }
    }
}