import { ui } from "../ui/layaMaxUI";

import Utils3D from "../scripts/mgr/Utils3D";
import { UICfg } from "../scripts/defines/ResDef";
import { DialogManager } from "../common/DialogManager";

//背景天空层
export default class BgLayer extends ui.gameUI.BgLayerUI {
    constructor() {
        super();
    }

    onEnable(){
        this.imgCry.visible = false;
        this.imgSmile.visible = false;
        //this.height = Laya.stage.height;
        Laya.timer.frameLoop(1,this,this.cloudMove);
        DialogManager.Ins.sceneClose(UICfg.LoadSceneView);
    }

    onAwake(): void {
    }

    cloudMove(){
        this.cloud1.x -= 0.1;
        this.cloud3.x -= 0.1;
        this.cloud2.x -= 0.15;
        this.cloud4.x -= 0.15;
        if(this.cloud2.x <= -1800){
            this.cloud2.x = 1080;
        }
        if(this.cloud4.x <= -1800){
            this.cloud4.x = 1080;
        }

        if(this.cloud1.x <= -1800){
            this.cloud1.x = 1080;
        }
        if(this.cloud3.x <= -1800){
            this.cloud3.x = 1080;
        }
    }

    fishDeath(bShow,x=0,y=0){ //胜利失败表情
        this.imgCry.visible = bShow;
        if(Utils3D.bGameWin){
            this.imgCry.skin = "image/smile.png";
        }else{
            this.imgCry.x = x;
            this.imgCry.y = y;
            this.imgCry.skin = "image/cry.png"
        }   
    }

    setBgScale(){
        this.scaleX -= 0.004;
        this.scaleY -= 0.004;
    }

    playSmileAct(x,y){
        var cx = Laya.stage.clientScaleX; 
        var cy = Laya.stage.clientScaleY;
        this.imgSmile.x = x/cx;
        this.imgSmile.y = y/cy;
        this.ani2.play(null,false);
    }

    tweenScale(){
        Laya.Tween.to(this,{scaleX:1,scaleY:1},700);
    }

    changeBg(){
        let rNum = Math.floor(Math.random() * 3) + 2;//2-3
    }



 


  

 










}