import gSdk from "./platformsdk/platform";
import UtilTools from "./UtilTools";
export default class UITool {
    private static _ins: UITool;

    constructor() {

    }

    public goldEffect(pt: Laya.Vector2, goldCount: number = 10, _parnet = null, callback: Function = null) {
        let tparent = _parnet ? _parnet : Laya.stage;
        let count = goldCount;
        for (let i = 0; i < goldCount; i++) {
            let sprite = new Laya.Sprite();
            sprite.loadImage("common/tips-09.png");
            let ptc = UtilTools.Math.randomCircle(10);
            sprite.x = pt.x + ptc.x;
            sprite.y = pt.y + ptc.y;
            sprite.scale(0.8, 0.8);
            tparent.addChild(sprite);
            let pos;
            if (gSdk.isWX) {
                pos = { x: Laya.stage.width - 200, y: 10 };
            }
            else {
                pos = { x: 20, y: 90 };
            }
            Laya.Tween.to(sprite, pos, 500, Laya.Ease.bounceIn, Laya.Handler.create(this, () => {
                sprite.removeSelf();
                count--;
                if (count <= 0) {
                    callback && callback();
                }
            }), 50 * i);
        }
    }


    createButton(_parnet = null, skin: string, handler: Laya.Handler) {
        let tparent = _parnet ? _parnet : Laya.stage;
        let button = new Laya.Button();
        button.clickHandler = handler;
        button.stateNum = 1;
        button.skin = skin;
        button.anchorX = 0.5;
        button.anchorY = 0.5;
        tparent.addChild(button);
        return button;
    }


    public static get Ins() {
        if (!this._ins) this._ins = new UITool();
        return this._ins;
    }
}