    loadSubpackage(resList: Array<string>, callback, progressCall) {
        console.log("LoadSubpackage Load");
        let count = resList.length;
        let loadPackageCount = count;
        let load = function () {
            loadPackageCount--;
            if (loadPackageCount <= 0) {
                callback && callback();
            }
        }
        for (let i = 0; i < count; i++) {
            var loadTask = this._bd.loadSubpackage({
                name: resList[i], // name ������ name ���� root
                success: function (res) {
                    progressCall && progressCall(i);
                    load();
                },
                fail: function (res) {
                    // �ְ�����ʧ��ͨ�� fail �ص�
                }
            });

            loadTask.onProgressUpdate(res => {

            })
        }
    }

