    initAdsCfg(params: AdsCfgBase = null) {
        this._adsCfg = params;
    }

    initAds() {
        console.log("sdk-bd.ts--initAds()");
        this._adsCfg = {ver: "0.0", switch: 0};
        return;
        let videoAds = this.getAdsData(AdsType.VIDEO);
        videoAds.neespreLoad && this.preloadVedioAds();
        this.createBanner();
    }

    createVideoAds() {
        console.log("sdk-bd.ts--createVideoAds()");
        if (typeof this._bd.createRewardedVideoAd == 'undefined') {
            return;
        }
        let ads = this.getAdsData(AdsType.VIDEO);
        if (!ads) return;
        let adsIndex = ads.adsIndex;
        let videoId = baiduCfg.VideoId[adsIndex % baiduCfg.VideoId.length];
        let videoAd = this._bd.createRewardedVideoAd({
            adUnitId: videoId
        });
        videoAd.onLoad(() => {
            ads.isLoaded = true;
        });
        videoAd.onError((err) => {
            ads.isLoaded = false;
        });

        videoAd.onClose((res) => {
            if (res && res.isEnded || res === undefined) {    //视频广告成功，获得奖励
                ads.close(true);
            } else {
                ads.close(false);
            }

            ads.neespreLoad && this.preloadVedioAds();
        });
        ads.adInstance = videoAd;
    }

    preloadVedioAds() {
        let ads = this.getAdsData(AdsType.VIDEO);
        if (!ads.adInstance) {
            this.createVideoAds();
        }
        if (!ads.isLoaded && ads.adInstance) {
            let promise = ads.adInstance.load()
            if (promise && baiduCfg.usePromise) {
                promise.then(function () {
                    ads.isLoaded = true;
                }.bind(this)).catch(function (err) {
                    ads.isLoaded = false;
                }.bind(this));
            }
        }
    }

    showVideo(ads: AdsData) {
        if (!bAdsUse) {
            SdkBase.prototype.showVideo.call(this, ads);
            return;
        }
        let adsInstance = ads.adInstance;
        if (adsInstance) {
            if (baiduCfg.usePromise) {
                let promise = adsInstance.show();
                promise.then(() => {
                    ads.show(true);
                }).catch(() => {
                    ads.show(false);
                    ads.neespreLoad && this.preloadVedioAds();
                });
                return;
            } else {
                if (ads.isLoaded) {   //加载成功
                    ads.show(true);
                    adsInstance.show();
                    return;
                } else {
                    ads.show(false);
                    ads.neespreLoad && this.preloadVedioAds();
                }
            }
        } else {
            ads.show(false);
            if (ads.neespreLoad) {
                this.preloadVedioAds();
            } else {
                this.createVideoAds();
            }
        }
    }

    createBanner(style?: any) {
        //初始化banner 广告
        let sysInfo = this.getSystemInfoSync();
        let screenWidth = sysInfo.screenWidth;
        let screenHeight = sysInfo.screenHeight;
        let ads = this.getAdsData(AdsType.BANNER);

        if (ads.adInstance) {
            ads.adInstance.destroy();
            ads.adInstance = null;
        }

        style = style || {};
        for (let key in baiduCfg.style) {
            style[key] = baiduCfg.style[key];
        }
        let bannerWidth = style.width;
        if (bannerWidth <= 1) {    //按屏幕宽度百分比计算宽度
            bannerWidth = screenWidth * bannerWidth;
        }

        let bannerHeight = style.height;
        let mp = style.mp || 1;

        let adsIndex = ads.adsIndex;
        let adId = baiduCfg.bannerIds[adsIndex % baiduCfg.bannerIds.length];

        let top = mp == 1 ? (screenHeight - bannerHeight) : (mp == 2 ? 0 : style.top);
        let left = style.isCenter ? screenWidth / 2 - bannerWidth / 2 : style.left;

        let bannerAds = this._bd.createBannerAd({
            adUnitId: adId,
            adIntervals: baiduCfg.adIntervals,
            style: {
                left: left,
                top: top,
                width: bannerWidth
            }
        });

        ads.style.height = bannerHeight;
        ads.style.width = bannerWidth;
        ads.style.realHeight = bannerHeight;
        ads.style.realWidth = bannerWidth;
        ads.style.top = top;
        ads.style.left = left;

        bannerAds.onResize((res) => {
            var bannerAdHeight = res.height;
            var bannerAdWidth = res.width;
            bannerAds.style.top = mp == 1 ? (screenHeight - bannerAdHeight) : (mp == 2 ? 0 : style.top);
            bannerAds.style.left = style.isCenter ? screenWidth / 2 - bannerAdWidth / 2 : style.left;
            ads.style.realWidth = bannerAdWidth;
            ads.style.realHeight = bannerAdHeight;
            ads.style.top = bannerAds.style.top;
            ads.style.left = bannerAds.style.left;
        });

        bannerAds.onLoad(() => {
            ads.isLoaded = true;
        });

        bannerAds.onError((err) => {
            ads.isLoaded = false;
            this.reLoadBanner();
        })

        ads.adInstance = bannerAds;
        bannerAds && bannerAds.hide();
    }

    reLoadBanner() {
        let ads = this.getAdsData(AdsType.BANNER);
        if (ads.adInstance) {
            ads.adInstance.hide();
            ads.adInstance.destroy();
            ads.adInstance = null;
        }
        if (ads.loadCount < baiduCfg.error_count) {
            ads.adsIndex++;
            this.createBanner();
            if (!this.bannerhided)
                this.showBanner();
        }
    }

    showBanner(style?: any) {
        if (!bAdsUse) return;

        this.bannerhided = false;
        let ads = this.getAdsData(AdsType.BANNER);

        if (!ads.adInstance) {
            this.createBanner(style);
        }
        if (baiduCfg.usePromise) {

            if (this.bannerhided)
                return;

            this.bannershowcount++;
            let promis = ads.adInstance.show();
            if (promis) {
                promis.then(() => {
                }).catch((err) => {
                    ads.adInstance.show();
                });
            }

        } else {
            if (this.bannerhided)
                return;
            this.bannershowcount++;
            ads.adInstance.show();
        }
    }

    hideBanner(need_pre_create: boolean = true, style?: any) {
        this.bannerhided = true;
        let ads = this.getAdsData(AdsType.BANNER);
        if (ads.adInstance) {
            ads.adInstance.hide();
            ads.adInstance.destroy();
            ads.adInstance = null;
            if (ads.neespreLoad) {
                this.createBanner(style);
            }
        }
    }

    hide_banner() {
        this.bannerhided = true;
        let ads = this.getAdsData(AdsType.BANNER);
        if (ads.adInstance) {
            ads.adInstance.hide();
        }
    }

    bannerMoveBy(top: number = 80, left: number = 0) {
        let ads = this.getAdsData(AdsType.BANNER);
        if (ads.adInstance) {
            ads.adInstance.style.top += top;
            ads.adInstance.style.left += left;
        }
    }

    bannerMoveTo(top: number = 0, left: number = 0) {
        let ads = this.getAdsData(AdsType.BANNER);
        if (ads.adInstance) {
            ads.adInstance.style.top = top;
            ads.adInstance.style.left = left;
        }
    }

