import AudioManager from "./AudioManager";
import gSdk from "./platformsdk/platform";
import { AudioType } from "./data/AudioData";
import { AdsType } from "./platformsdk/plat-def";
import TipManager from "./TipManager";
import adSdk from "./adDataSdk/adDataSdkFactory";

/**
 * 微信原生格子广告显示的界面
 */
export enum WX_NATIVE_GRID_SCENE {
    SCENE_START,
    SCENE_GAME,
    SCENE_RESULT
}
export default class AdTool {
    private static _ins: AdTool;

    constructor() {
    }

    //播放视频广告
    public playVideo(failMsgTip = "请观看完整视频！", endCallback = null, parnet = null, failCallback = null, scene: number = 1037) {
        let tparent = parnet ? parnet : Laya.stage;

        if (gSdk.isVV && AudioManager.inst.getState(AudioType.Mute) == 1)
            AudioManager.inst.stopMusic();

        gSdk.sdk.showAds(AdsType.VIDEO,
            (isShow) => {
                if (isShow)
                    console.log("激励视频广告显示成功");
                else {
                    TipManager.Ins.showMsgTip("视频正在处理中", tparent);
                    if (gSdk.isVV && AudioManager.inst.getState(AudioType.Mute) == 1)
                        AudioManager.inst.resumeMusic();

                    failCallback && failCallback();
                }
            },
            (isEnd) => {
                if (gSdk.isVV && AudioManager.inst.getState(AudioType.Mute) == 1)
                    AudioManager.inst.resumeMusic();

                if (isEnd) {
                    endCallback && endCallback();

                    if (gSdk.isWX && adSdk.isZS) {
                        adSdk.sdk.clickGameReq({}, scene, 2);
                    }
                }
                else {
                    TipManager.Ins.showMsgTip(failMsgTip, tparent);

                    failCallback && failCallback();
                }
            },
            {}
        );
    }

    //根据场景获得微信模板广告位置索引
    private getCustomAdPosIndex(scene: number) {
        var adsIndexObj = { ads1: -1, ads2: -1 }
        if (scene == WX_NATIVE_GRID_SCENE.SCENE_START) {//首页
            adsIndexObj.ads1 = 0;
            adsIndexObj.ads2 = 1;
        }
        else if (scene == WX_NATIVE_GRID_SCENE.SCENE_GAME) {//游戏页
            adsIndexObj.ads1 = 0;
            adsIndexObj.ads2 = 1;
        }
        else if (scene == WX_NATIVE_GRID_SCENE.SCENE_RESULT) {//结算页
            adsIndexObj.ads1 = 2;
            adsIndexObj.ads2 = 3;
        }
        return adsIndexObj;
    }

    private _customAdHide = {};

    /**显示微信原生模板广告 */
    public showCustomAd(scene: number) {
        var adsIndexObj = this.getCustomAdPosIndex(scene);
        this._customAdHide[scene] = false;
        //1
        if (adsIndexObj.ads1 > -1)
            gSdk.sdk.showCustomAd(adsIndexObj.ads1);

        //2
        if (adsIndexObj.ads2 > -1)
            gSdk.sdk.showCustomAd(adsIndexObj.ads2);

    }

    /**隐藏微信原生模板广告 */
    public hideCustomAd(scene: number) {
        var adsIndexObj = this.getCustomAdPosIndex(scene);
        if (this._customAdHide[scene]) {
            return;
        }
        this._customAdHide[scene] = true;
        //1
        if (adsIndexObj.ads1 > -1)
            gSdk.sdk.hide_CustomAd(adsIndexObj.ads1);

        //2
        if (adsIndexObj.ads2 > -1)
            gSdk.sdk.hide_CustomAd(adsIndexObj.ads2);
    }

    public static get Ins() {
        if (!this._ins) this._ins = new AdTool();
        return this._ins;
    }
}