import SdkBase from "./sdk-base";
import { wxCfg, AdsType, bAdsUse, ShareCfg, WxBannerScreen } from "./plat-def";
import { AdsData } from "./Ads-data";
import UtilAld from "../../ald-game/UtilAld";
import EventManager from "../EventManager";
import { EventCfg } from "../../scripts/defines/ResDef";
import { AdsCfgWx } from "./Adscfg-def";

export default class SdkWx extends SdkBase {
    private _wx: any = null;
    private _adsCfg: AdsCfgWx = null;//广告配置

    private screenWidth: number;
    private screenHeight: number;

    private showingbanner: number = WxBannerScreen.SCREEN_NONE;     //当前正在显示的banner,在hide时置为WxBannerScreen.SCREEN_NONE
    private creatingbanner: number = WxBannerScreen.SCREEN_NONE;    //记录当前正在创建哪个banner,防止同时创建出问题

    private mistakeTouch: boolean = false;  //误触生效，detectBannerPos函数内只记录时间，不做banner交换操作
    private bannerObj = {}; //在各种遍历中,记录可用banner的集合,从中随机

    //以下两个变量做容错,万一出问题,代码永远卡在一个地方了
    private createBannerCount: number = 0;  //时钟创建banner次数,目前设为3次
    private creatingCount: number = 0;

    private detectTimerStart: boolean = false;

    constructor() {
        super();
        this._wx = Laya.Browser.window.wx;
        this.version = wxCfg.version;
        this.cfg = wxCfg;
    }

    FormatDateTime(blong: boolean): any {
        var dt = new Date();
        //if (blong)
        //    console.log(`${dt.getFullYear()}` + `-` + `${dt.getMonth()}` + `-` + `${dt.getDate()}`);
        //console.log(`========sdk-wx.ts--${dt.getHours()}:${dt.getMinutes()}:${dt.getSeconds()}`);
        return `${dt.getHours()}:${dt.getMinutes()}:${dt.getSeconds()}`;
    }

    compareVersion(v1, v2) {
        v1 = v1.split('.')
        v2 = v2.split('.')
        const len = Math.max(v1.length, v2.length)

        while (v1.length < len) {
            v1.push('0')
        }
        while (v2.length < len) {
            v2.push('0')
        }

        for (let i = 0; i < len; i++) {
            const num1 = parseInt(v1[i])
            const num2 = parseInt(v2[i])

            if (num1 > num2) {
                return 1
            } else if (num1 < num2) {
                return -1
            }
        }

        return 0
    }


    init(params?: any) {
        let sysInfo = this.getSystemInfoSync();
        this.screenWidth = sysInfo.screenWidth;
        this.screenHeight = sysInfo.screenHeight;

        const version = this._wx.getSystemInfoSync().SDKVersion;
        const platform = this._wx.getSystemInfoSync().platform;

        if (this.compareVersion(version, '2.11.3') >= 0 && platform.indexOf("android") >= 0) {
            this._wx.showShareMenu({
                withShareTicket: true,
                menus: ['shareAppMessage', 'shareTimeline']
            });

            this._wx.onShareTimeline(function () {
                return {
                    title: ShareCfg.title,
                    imageUrlId: ShareCfg.imageUrlId,
                    imageUrl: ShareCfg.imageUrl
                }
            });
        }
        else {
            this._wx.showShareMenu({
                withShareTicket: true
            });
        }

        this._wx.onShareAppMessage(function () {
            return {
                title: ShareCfg.title,
                imageUrlId: ShareCfg.imageUrlId,
                imageUrl: ShareCfg.imageUrl
            }
        });


        this._wx.onShow((res) => {  //从后台返回到游戏
            // EventManager.dispatchGlobalByCmd(GameEvent.ONSHOW,{});
            EventManager.Ins.event(EventCfg.EVENT_BG_TO_GAME);
        });

        this._wx.onHide(() => {  //从游戏到后台
            // EventManager.dispatchGlobalByCmd(GameEvent.ONHIDE,{});
            EventManager.Ins.event(EventCfg.EVENT_GAME_TO_BG);
        });

        this.initAds();
    }

    getappId() {
        return this.cfg.appid;
    }

    getopenId(callback) {
        let self = this;
        this.login((state, ret) => {
            if (state && ret.code) {
                var data = {
                    code: ret.code,
                    appid: self.cfg.appid
                };
                self._wx.request({
                    method: 'POST',
                    url: self.cfg.serviceUrl,
                    data: data,
                    success: (res) => {
                        self.openId = res.data.data.openid;
                        callback && callback(true, self.openId);
                    },
                    fail: (err) => {
                        //发生错误，再次请求备用地址一次
                        self._wx.request({
                            method: 'POST',
                            url: self.cfg.serviceBackupUrl,
                            data: data,
                            success: (res) => {
                                self.openId = res.data.data.openid;
                                callback && callback(true, self.openId);
                            },
                            fail: (err) => {
                                callback && callback(false);//失败回调
                            },
                            complete: () => {
                            }
                        });
                    },
                    complete: () => {
                    }
                });
            }
            else {
                callback && callback(false);
            }
        });
    }

    //*********************************************/
    //***********    平台接口   *******************/
    //********************************************/
    share(msg?: string, cb?: Function) {
        msg = msg || ShareCfg.title;
        this._wx.shareAppMessage({
            title: msg,
            imageUrlId: ShareCfg.imageUrlId,
            imageUrl: ShareCfg.imageUrl
        })
    }

    //播放震动
    playVibrative(isShort = true) {
        if (isShort) {
            this._wx.vibrateShort();
        } else {
            this._wx.vibrateLong();
        }
    }

    /**获取右上角菜单栏信息 */
    getRightMenuRectInfo(): any {
        if (typeof this._wx.getMenuButtonBoundingClientRect == 'undefined') {
            return SdkBase.prototype.getRightMenuRectInfo.call(this);
        }
        return this._wx.getMenuButtonBoundingClientRect();
    }

    /**
     * 登录
     */
    login(cb: Function = null) {
        this._wx.login({
            force: false,
            success: (ret) => {
                cb && cb(true, ret);
            },
            fail: (err) => {
                cb && cb(false);
            },
            complete: (res) => { }
        })
    }

    /**
     * 分包加载
     * @param resList 
     * @param callback 
     * @param progressCall 
     */
    loadSubpackage(resList: Array<string>, callback, progressCall) {
        let count = resList.length;
        let loadCount = count;
        let load = function () {
            loadCount--;
            if (loadCount <= 0) {
                callback && callback();
            }
        }
        for (let i = 0; i < count; i++) {
            var loadTask = this._wx.loadSubpackage({
                name: resList[i], // name 可以填 name 或者 root
                success: function (res) {
                    progressCall && progressCall(i);
                    load();
                },
                fail: function (res) {
                    // 分包加载失败通过 fail 回调
                }
            });

            loadTask.onProgressUpdate(res => {

            })
        }
    }

    /**
     * 小游戏跳转
     * @param data 
     * @param success 
     * @param fail 
     * @param complete 
     */
    navigateToMiniProgram(data, success = null, fail = null, complete = null) {
        this._wx.navigateToMiniProgram(
            {
                appId: data.link_appid,
                path: data.link_path,
                extraData: data.extraData,
                success: function (e) {
                    success && success(e)
                },
                fail: function (e) {
                    fail && fail(e);
                },
                complete: function (e) {
                    complete && complete(e);
                }
            }
        )
    }

    //获取小游戏启动时的参数
    getLaunchOptionsSync(): any {
        return this._wx.getLaunchOptionsSync()
    }

    //获取系统信息
    getSystemInfoSync(): any {
        return this._wx.getSystemInfoSync()
    }


    /**关卡开始 */
    startLevel(level: number) {
        UtilAld.startLevel(level);
    }

    /**关卡结束 event=complete:关卡完成,fail:关卡失败*/
    endLevel(level: number, event = 'complete') {
        UtilAld.endLevel(level, event);
    }


    //*********************************************/
    //***********    广告接口   *******************/
    //********************************************/
    initAdsCfg(params: AdsCfgWx = null) {
        params.bgbannershowcount = params.bgbannershowcount || 2;

        this._adsCfg = params;
    }
    initAds() {
        this._adsCfg = {
            ver: "0.0",
            switch: 0,
            bgbannershowcount: 4,
            bgexposureduration: 8000,
            bgbannershow: true,
            bginterstitialshow: false,
            bggridshow: false
        };
        if (!bAdsUse) return;

        this.createBanner(WxBannerScreen.SCREEN_START);
        this.createBanner(WxBannerScreen.SCREEN_RESULT);
        this.createBanner(WxBannerScreen.SCREEN_FULL1);
        this.createBanner(WxBannerScreen.SCREEN_FULL2);

        let videoAds = this.getAdsData(AdsType.VIDEO);
        videoAds.neespreLoad && this.preloadVedioAds();

        let instlAds = this.getAdsData(AdsType.INTERSTITIAL);
        instlAds.neespreLoad && this.preloadInstalAds();

        //wxCfg.CustomAdId.length > 0 && this.preloadCustomAds();
    }

    /**
     * 视频广告
     */

    createVideoAds() {
        if (!bAdsUse) return;

        if (typeof this._wx.createRewardedVideoAd == 'undefined') {
            return;
        }

        let ads = this.getAdsData(AdsType.VIDEO);
        if (!ads) return;
        let adsIndex = ads.adsIndex;
        let videoId = wxCfg.VideoId[adsIndex % wxCfg.VideoId.length];
        let videoAd = this._wx.createRewardedVideoAd({
            adUnitId: videoId
        });
        videoAd.onLoad(() => {
            ads.isLoaded = true;
        });
        videoAd.onError((err) => {
            ads.isLoaded = false;
        });

        videoAd.onClose((res) => {
            if (res && res.isEnded || res === undefined) {    //视频广告成功，获得奖励
                ads.close(true);
            } else {
                ads.close(false);
            }

            ads.neespreLoad && this.preloadVedioAds();
        });
        ads.adInstance = videoAd;
    }

    preloadVedioAds() {
        if (!bAdsUse) return;

        let ads = this.getAdsData(AdsType.VIDEO);
        if (!ads.adInstance) {
            this.createVideoAds();
        }
        if (!ads.isLoaded && ads.adInstance) {
            let promise = ads.adInstance.load()
            if (promise && wxCfg.usePromise) {
                promise.then(function () {
                    ads.isLoaded = true;
                }.bind(this)).catch(function (err) {
                    ads.isLoaded = false;
                }.bind(this));
            }
        }
    }

    showVideo(ads: AdsData) {
        if (!bAdsUse) {
            SdkBase.prototype.showVideo.call(this, ads);
            return;
        }

        let adsInstance = ads.adInstance;
        if (adsInstance) {
            if (wxCfg.usePromise) {
                let promise = adsInstance.show();
                promise.then(() => {
                    ads.show(true);
                }).catch(() => {
                    ads.show(false);
                    ads.neespreLoad && this.preloadVedioAds();
                });
                return;
            } else {
                if (ads.isLoaded) {   //加载成功
                    ads.show(true);
                    adsInstance.show();
                    return;
                } else {
                    ads.show(false);
                    ads.neespreLoad && this.preloadVedioAds();
                }
            }
        } else {
            ads.show(false);
            if (ads.neespreLoad) {
                this.preloadVedioAds();
            } else {
                this.createVideoAds();
            }
        }
    }

    /**
     * 插屏广告
     */

    createInstalAds() {
        if (!bAdsUse || 0 == wxCfg.InterstitialId.length) {
            console.log("createInstallAds return 0");
            return;
        }
        if (typeof (this._wx.createInterstitialAd) == "undefined") {
            console.log("createInstallAds return 1");
            return;
        }

        let ads = this.getAdsData(AdsType.INTERSTITIAL);
        if (!ads) {
            console.log("createInstallAds return 2");
            return;
        }

        let adsIndex = ads.adsIndex;
        let adId = wxCfg.InterstitialId[adsIndex % wxCfg.InterstitialId.length];
        let interstitialAd = this._wx.createInterstitialAd({
            adUnitId: adId
        });
        interstitialAd.onError((err) => {
            console.log("插屏广告出错", err);
            ads.isLoaded = false;
        });
        interstitialAd.onLoad(() => {
            console.log("插屏广告加载成功");
            ads.isLoaded = true;
        });
        interstitialAd.onClose((ret) => {
            console.log("插屏广告关闭")
            ads.close(true);
            if (wxCfg.destroyInstl) {
                ads.adInstance.destroy();
                ads.adInstance = null;
            }
            ads.neespreLoad && this.preloadInstalAds();
        });
        ads.adInstance = interstitialAd;
    }

    preloadInstalAds() {
        if (!bAdsUse || 0 == wxCfg.InterstitialId.length) {
            console.log("preloadInstalAds return 0");
            return;
        }

        let ads = this.getAdsData(AdsType.INTERSTITIAL);
        if (!ads.adInstance) {
            this.createInstalAds();
            return;
        }
        if (!ads.isLoaded && ads.adInstance) {
            let promise = ads.adInstance.load();
            if (promise && wxCfg.usePromise) {
                promise.then(() => {
                    console.log("preloadInstalAds load then");
                    ads.isLoaded = true;
                }).catch((err) => {
                    console.log("preloadInstalAds load catch");
                    ads.isLoaded = false;
                });
            }
        }
        else {
            console.log("pre loadnistalAds 走了个寂寞");
        }
    }

    showInstal(ads: AdsData) {
        console.log("showInstal Enter");
        if (!bAdsUse || !this._adsCfg.bginterstitialshow) {
            console.log("showInstal return 0");
            return;
        }

        let adsInstance = ads.adInstance;
        if (adsInstance) {
            if (wxCfg.usePromise) {
                console.log("usePormise is true");
                let promise = adsInstance.show();
                promise.then(() => {
                    console.log("show then");
                    ads.show(true);
                }).catch(() => {
                    console.log("show catch");
                    ads.show(false);
                    ads.neespreLoad && this.preloadInstalAds();
                });
            } else {
                console.log("usePormise is false");
                if (ads.isLoaded) {
                    console.log("show");
                    ads.adInstance.show();
                    ads.show(true);
                }
                else {
                    console.log("preloadInstalAds");
                    ads.show(false);
                    ads.neespreLoad && this.preloadInstalAds();
                }
            }
        }
        else {
            console.log("adsInstance is null");
            ads.show(false);
            if (ads.neespreLoad) {
                console.log("preloadInstalAds");
                this.preloadInstalAds();
            } else {
                console.log("createInstalAds");
                this.createInstalAds();
            }
        }
    }

    /**
     * banner广告
     */

    beginMistake() {
        this.mistakeTouch = true;
    }

    endMistake() {
        this.mistakeTouch = false;
    }

    getBannerScene(bannerType: AdsType): WxBannerScreen {
        return bannerType - (AdsType.BANNER_WX_START - WxBannerScreen.SCREEN_START);
    }

    getBannerType(bannerScene: WxBannerScreen): AdsType {
        return bannerScene + (AdsType.BANNER_WX_START - WxBannerScreen.SCREEN_START);
    }

    showBannerEx(bannerIns: AdsData) {
        if (wxCfg.usePromise) {
            bannerIns.adInstance.show()
                .then(() => {
                    bannerIns.isShow = true;
                })
                .catch((err) => {
                    console.log("时钟显示banner的时候出现错误:", err);
                    bannerIns.isShow = false;
                });
        }
        else {
            bannerIns.adInstance.show();
            bannerIns.isShow = true;
        }
    }

    detectBannerPos() {
        let self = this;
        if (self.creatingbanner != WxBannerScreen.SCREEN_NONE) {//有banner正在创建,返回
            self.creatingCount++;
            if (self.creatingCount < 4) {//连续出现三次则忽略一次
                return;
            }
        }

        let enoughAds = null;       //显示时长足够的banner
        let needShowAds = null;     //显示时长不够,需要轮替显示的banner
        let needShowScene = WxBannerScreen.SCREEN_NONE;

        for (let bannerIndex = AdsType.BANNER_WX_START; bannerIndex < AdsType.BANNER_WX_TAP; bannerIndex++) {
            let tmpAds = self.getAdsData(bannerIndex);
            let tmpScene = self.getBannerScene(bannerIndex);
            if (tmpAds && tmpAds.isLoaded && tmpAds.adInstance) {
                if (tmpAds.isShow) {
                    if (tmpAds.isbannerSee) {
                        tmpAds.bannerExposureTime += 1000;
                        if (tmpAds.bannerExposureTime > self._adsCfg.bgexposureduration) {
                            enoughAds = tmpAds; //找到了一个显示时长够了的banner
                        }
                    }
                    else {
                        if (tmpAds.bannerExposureTime < self._adsCfg.bgexposureduration) {
                            needShowAds = tmpAds;
                            needShowScene = tmpScene;
                        }
                    }
                }
                else {
                    self.movebanner(tmpAds, false);
                    self.showBannerEx(tmpAds);
                }
            }
            else {
                self.createBannerCount++;
                if (self.createBannerCount < 4) {//连续出现三次则忽略一次
                    console.log("发现有问题的banner,需要重新创建索引值为:" + tmpScene + "的banner");
                    if (tmpAds) self.clearBannerInfo(tmpAds);
                    self.createBanner(tmpScene);
                    return;
                }
            }
        }

        if (self.mistakeTouch) {
            return;
        }

        self.createBannerCount = 0;
        self.creatingCount = 0;

        if (needShowAds && enoughAds) {//交换两个banner
            self.movebanner(enoughAds, false);
            self.movebanner(needShowAds, true);
            //self.showBannerEx(needShowAds);   //暂时注释,感觉业务不需要执行这句,要实际测试
            self.showingbanner = needShowScene;
        }
    }

    clearBannerInfo(bannerIns: AdsData) {
        if (bannerIns.adInstance) {
            bannerIns.adInstance.destroy();
            bannerIns.adInstance = null;
        }

        bannerIns.isShow = false;
        bannerIns.isbannerSee = false;
        bannerIns.bannerShowCount = 0;
        bannerIns.bannerExposureTime = 0;
        bannerIns.isLoaded = false;
        bannerIns.adsId = "";
        bannerIns.adsIndex = WxBannerScreen.SCREEN_NONE;
        bannerIns.bannerMistake = false;
    }

    createBanner(scene: number, style?: any) {
        if (!bAdsUse) return;

        let self = this;

        if (scene < WxBannerScreen.SCREEN_START || scene > WxBannerScreen.SCREEN_TAP) {
            //理论不会走入此分支,进来了就是相关业务代码出问题了
            console.log("XWX-LOG:create Banner() return 1, 进入了一个理论上不会走入的返回");
            return;
        }

        if (self.creatingbanner == scene) {
            console.log("XWX-LOG:正在创建相同的banner,返回");
            return;
        }

        self.creatingbanner = scene;

        let ads = self.getAdsData(self.getBannerType(scene));

        if (!ads) {
            //理论上不会走入此分支
            console.log("XWX-LOG:create Banner() return 2, 进入了一个理论上不会走入的返回");
            return;
        }

        if (ads.adInstance) {
            //理论上不会走进这个判断,程序启动会创建,其他都在相关业务销毁才会来这里创建
            console.log("XWX-LOG:create Banner() 进入了一个理论上不会走入的清理逻辑");
            self.clearBannerInfo(ads);
        }

        let adId = wxCfg.bannerIds[scene];
        ads.adsIndex = scene;   //记录当前使用banner的索引值
        ads.adsId = adId;       //记录当前使用的bannerid

        style = style || {};
        for (let key in wxCfg.style) {
            style[key] = style[key] || wxCfg.style[key];
        }

        let bannerWidth = style.width;
        if (bannerWidth <= 1) {    //按屏幕宽度百分比计算宽度
            bannerWidth = self.screenWidth * bannerWidth;
        }

        let bannerHeight = style.height;

        let mp = style.mp || 1;
        let top = mp == 1 ? (self.screenHeight - bannerHeight) : (mp == 2 ? 0 : style.top);
        let left = style.isCenter ? self.screenWidth / 2 - bannerWidth / 2 : style.left;

        let bannerAds = self._wx.createBannerAd({
            adUnitId: adId,
            style: {
                left: left,
                top: top,
                width: bannerWidth
            }
        });

        ads.adInstance = bannerAds;

        bannerAds.onResize((res) => {
            let bannerAdHeight = res.height;
            let bannerAdWidth = res.width;
            //记录banner实际宽高,需要移动到屏幕内时计算位置用
            ads.style.realWidth = bannerAdWidth;
            ads.style.realHeight = bannerAdHeight;

            if (!ads.isbannerSee) {//当前banner刚拉取到,或者是自动刷新产生的onResize,此时banner正在屏幕外显示
                ads.adInstance.style.top = this.screenHeight - 1;
                ads.adInstance.style.left = this.screenWidth - 1;
            }
            else {//当前banner在屏幕内,重新设置一下位置为底部居中
                self.movebanner(ads, true);  //没有自动刷新,理论不会走入此分支
            }

            //刚拉取到广告时,会触发一次onResize
            if (!ads.isShow) {//说明banner是刚拉取到,要显示在屏幕外
                self.showBannerEx(ads)
            }
        });

        bannerAds.onLoad(() => {
            ads.isLoaded = true;
            self.creatingbanner = WxBannerScreen.SCREEN_NONE;
        });

        bannerAds.onError((err) => {
            console.log("WX-LOG:banner onError", err, scene);
            ads.isLoaded = false;
            self.creatingbanner = WxBannerScreen.SCREEN_NONE;
            self.clearBannerInfo(ads);
        });
    }

    isEmpty(dic: Object): Boolean {
        if (dic == null) return true;
        // @ts-ignore
        for (let key in dic) {
            return false;
        }
        return true;
    }

    randombannerWithType(dic: Object): AdsType {
        let a: any[] = [];
        for (let key in dic) {
            a.push(key);
        }
        let randomValue: number = Math.floor(Math.random() * a.length);
        console.log("随机得到需要显示的banner是:", this.getBannerScene(a[randomValue]));
        return a[randomValue];
    }

    showBannerzs(style?: any) {
        if (!bAdsUse || !this._adsCfg.bgbannershow) return;

        if (!this.detectTimerStart) { //第一次显示banner的时候开启时钟
            this.detectTimerStart = true;

            if (this._adsCfg.bgbannershow && this._adsCfg.bgexposureduration > 0) {
                Laya.timer.loop(1000, this, this.detectBannerPos);
            }
            else {//指色默认进入这里
                console.log("不用启动banner轮替时钟");
            }
        }

        if (this.showingbanner != WxBannerScreen.SCREEN_NONE) return;   //当前有个正在显示的banner,那就继续显示

        let self = this;
        let retType;
        let duringTime: number = 0;
        let sceneIndex: number = WxBannerScreen.SCREEN_NONE;
        let ads = null;
        self.bannerObj = {};
        for (let bannerIndex = AdsType.BANNER_WX_START; bannerIndex < AdsType.BANNER_WX_TAP; bannerIndex++) {
            let tmpAds = self.getAdsData(bannerIndex);
            if (tmpAds && tmpAds.isLoaded && tmpAds.adInstance) {
                self.bannerObj[bannerIndex] = bannerIndex;
                if (!this._isUseZS) {
                    if (self.mistakeTouch) {//误触要找时间最长的且没有显示过的
                        if (tmpAds.bannerExposureTime > duringTime && !tmpAds.bannerMistake) {
                            retType = bannerIndex;
                            ads = tmpAds;
                            duringTime = tmpAds.bannerExposureTime;
                        }
                    }
                    else {//正常显示找时长最短的显示
                        if (duringTime == 0) {
                            retType = bannerIndex;
                            ads = tmpAds;
                            duringTime = tmpAds.bannerExposureTime;
                        }
                        else {
                            if (tmpAds.bannerExposureTime < duringTime) {
                                retType = bannerIndex;
                                ads = tmpAds;
                                break;
                            }
                        }
                    }
                }
            }
            else {  //该banner有问题
                if (this._isUseZS) { //指色重新拉取banner
                    this.createBanner(this.getBannerScene(bannerIndex));
                }
            }
        }

        console.log("banner合集:", self.bannerObj);

        if (ads) {//有可用的banner
        }
        else {
            if (self.isEmpty(self.bannerObj)) {
                return;
            }
            else {//要在bannerObj中随机一个
                retType = self.randombannerWithType(self.bannerObj);
                ads = self.getAdsData(retType);
            }
        }

        sceneIndex = self.getBannerScene(retType);
        this.movebanner(ads, true);
        if (self.mistakeTouch) ads.bannerMistake = true;

        if (wxCfg.usePromise) {
            let promis = ads.adInstance.show();
            if (promis) {
                promis.then(() => {
                    self.showingbanner = sceneIndex;
                    ads.isShow = true;
                }).catch((err) => { //之前已经判断过banner加载成功才调用的show,理论上不会出现catch
                    console.log("XWX-LOG:bnner显示失败，错误原因：", err);
                    ads.isShow = false;
                });
            }
        } else {
            self.showingbanner = sceneIndex;
            ads.adInstance.show();
            ads.isShow = true;
        }
    }

    showSpecialBanner() {
        let ads = this.getAdsData(AdsType.BANNER_WX_TAP);
        if (ads && ads.adInstance && ads.isLoaded) { }
        else {
            if (ads) {
                this.clearBannerInfo(ads);
            }
            this.createBanner(WxBannerScreen.SCREEN_TAP);
        }
    }

    /**
     * 将banner隐藏到屏幕右下角
     * location(true:移进屏幕　false:移出屏幕)
     * realShow(true:真正显示  false:时钟移动的位置)
     */
    movebanner(bannerins: AdsData, location: boolean) {
        if (!bAdsUse) return;

        if (location) {
            console.log("WX-LOG:将banner移入屏幕:", bannerins.adsIndex);
            bannerins.adInstance.style.top = this.screenHeight - bannerins.style.realHeight;
            bannerins.adInstance.style.left = (this.screenWidth - bannerins.style.realWidth) / 2;

            if (!bannerins.isbannerSee) {//真正的从屏幕外移入屏幕内,更改状态,展示次数加1
                bannerins.isbannerSee = true;
            }
        }
        else {
            console.log("WX-LOG:将banner移出屏幕:", bannerins.adsIndex);
            bannerins.adInstance.style.top = this.screenHeight - 5;
            bannerins.adInstance.style.left = this.screenWidth - 5;
            bannerins.isbannerSee = false;
        }
    }
    //查找可用banner数量
    FindBannerLoadState(): boolean {
        let bannerCount: number = 0;
        for (let bannerIndex = AdsType.BANNER_WX_START; bannerIndex < AdsType.BANNER_WX_TAP; bannerIndex++) {
            let tmpAds = this.getAdsData(bannerIndex);
            if (tmpAds && tmpAds.adInstance && tmpAds.isLoaded) bannerCount++;
        }
        if (bannerCount > 1)
            return true;
        else
            return false;
    }
    //先将所有banner都隐藏一遍
    hideAllbanner() {
        if (!bAdsUse) return;

        let ads1 = this.getAdsData(AdsType.BANNER_WX_START);
        let ads2 = this.getAdsData(AdsType.BANNER_WX_RESULT);
        let ads3 = this.getAdsData(AdsType.BANNER_WX_FULL1);
        let ads4 = this.getAdsData(AdsType.BANNER_WX_FULL2);
        if (ads1 && ads1.adInstance) {
            this.movebanner(ads1, false);
            //ads1.adInstance.hide();
            //ads1.isShow = false;
        }
        if (ads2 && ads2.adInstance) {
            this.movebanner(ads2, false);
            //ads2.adInstance.hide();
            //ads2.isShow = false;
        }
        if (ads3 && ads3.adInstance) {
            this.movebanner(ads3, false);
            //ads3.adInstance.hide();
            //ads3.isShow = false;
        }
        if (ads4 && ads4.adInstance) {
            this.movebanner(ads4, false);
            //ads4.adInstance.hide();
            //ads4.isShow = false;
        }
    }

    hideBannerzs(destroy: boolean = false) {
        if (!bAdsUse) return;

        let tmpshowid = this.showingbanner;
        this.showingbanner = WxBannerScreen.SCREEN_NONE;

        this.hideAllbanner();

        if (tmpshowid == WxBannerScreen.SCREEN_NONE) return;

        let ads;
        switch (tmpshowid) {
            case WxBannerScreen.SCREEN_START:
                ads = this.getAdsData(AdsType.BANNER_WX_START);
                break;
            case WxBannerScreen.SCREEN_RESULT:
                ads = this.getAdsData(AdsType.BANNER_WX_RESULT);
                break;
            case WxBannerScreen.SCREEN_FULL1:
                ads = this.getAdsData(AdsType.BANNER_WX_FULL1);
                break;
            case WxBannerScreen.SCREEN_FULL2:
                ads = this.getAdsData(AdsType.BANNER_WX_FULL2);
                break;
        }

        if (ads && ads.adInstance) {
            ads.bannerShowCount++;
            if (this._adsCfg.bgexposureduration > 0) {  //指色不会进这里
                let exposureTime = ads.bannerExposureTime;   //实际曝光时间
                if (exposureTime < this._adsCfg.bgexposureduration) {
                    ads.bannerShowCount--;
                }
            }
            let showcount = ads.bannerShowCount;
            if (showcount >= this._adsCfg.bgbannershowcount || destroy) {
                //销毁之前要先判断下,是否还有其他可用的banner,如果没有,当前的不能销毁,要留着用
                //console.log("XWX-LOG:显示次数够了,需要销毁banner:", tmpshowid, showcount);
                if (this.FindBannerLoadState()) {
                    this.clearBannerInfo(ads);
                }
                else {
                    console.log("WX-LOG:只有当前这个banner可用,不能销毁");
                }
            }
        }
        else {
            console.log("WX-LOG:隐藏banner时候还出现问题了????");
        }
    }

    bannerMoveBy(top: number = 80, left: number = 0) {
        let ads;
        switch (this.showingbanner) {
            case WxBannerScreen.SCREEN_START:
                ads = this.getAdsData(AdsType.BANNER_WX_START);
                break;
            case WxBannerScreen.SCREEN_RESULT:
                ads = this.getAdsData(AdsType.BANNER_WX_RESULT);
                break;
            case WxBannerScreen.SCREEN_FULL1:
                ads = this.getAdsData(AdsType.BANNER_WX_FULL1);
                break;
            case WxBannerScreen.SCREEN_FULL2:
                ads = this.getAdsData(AdsType.BANNER_WX_FULL2);
                break;
        }
        if (ads && ads.adInstance) {
            ads.adInstance.style.top += top;
            ads.adInstance.style.left += left;
        }
    }

    bannerMoveTo(top: number = 0, left: number = 0) {   //目前该函数专门针对狂点banner使用
        console.log("banner误触,位置移动到:", top, left);
        let ads = this.getAdsData(this.getBannerType(this.showingbanner));
        if (ads && ads.adInstance && ads.isLoaded) {
            if (top == 0 && left == 0) {
                //做个特殊业务,此数据组合表示最下方居中显示
                this.movebanner(ads, true);
            }
            else {
                if (left == 0)
                    ads.adInstance.style.left = (this.screenWidth - ads.style.realWidth) / 2;
                else
                    ads.adInstance.style.left = left;
                if (top != 0) {
                    ads.adInstance.style.top = top;
                }
            }
        }
    }

    /**
     * 格子广告
     */

    reLoadGrid() {
        if (!bAdsUse || 0 == wxCfg.GridId.length) return;

        let ads = this.getAdsData(AdsType.GRID);
        if (ads.loadCount < wxCfg.error_count) {
            ads.adsIndex++;
            if (ads.adInstance) {
                ads.adInstance.hide();
                ads.adInstance.destroy();
                ads.adInstance = null;
            }
            this.createGridAds();
        }
        ads.loadCount++;
    }

    createGridAds(style?: any) {
        if (!bAdsUse || 0 == wxCfg.GridId.length) return;

        if (typeof this._wx.createGridAd == 'undefined') {
            return;
        }
        //初始化格子 广告
        let sysInfo = this.getSystemInfoSync();
        let screenWidth = sysInfo.screenWidth;
        let screenHeight = sysInfo.screenHeight;

        let ads = this.getAdsData(AdsType.GRID);

        style = style || {}
        for (let key in wxCfg.gridStyle) {
            style[key] = wxCfg.gridStyle[key];
        }
        let index = ads.adsIndex;
        let adid = wxCfg.GridId[index % wxCfg.GridId.length];

        let gridWidth = style.width;
        if (gridWidth <= 1) {    //按屏幕宽度百分比计算宽度
            gridWidth = screenWidth * gridWidth;
        }

        let gridHeight = style.height;
        let mp = style.mp || 1;
        let top = mp == 1 ? (screenHeight - gridHeight) : (mp == 2 ? 0 : style.top);
        let left = screenWidth / 2 - gridWidth / 2
        let gridAd = this._wx.createGridAd({
            adUnitId: adid,
            adIntervals: wxCfg.adIntervals,
            adTheme: 'white',
            gridCount: 5,
            style: {
                left: left,
                top: top,
                width: gridWidth
            }
        });

        ads.style.height = style.height;
        ads.style.width = gridWidth;
        ads.style.top = top;
        ads.style.left = left;
        ads.style.realWidth = style.gridWidth;
        ads.style.realHeight = style.height;

        gridAd.onResize((res) => {
            var bannerAdHeight = res.height;
            var bannerAdWidth = res.width;
            gridAd.style.top = mp == 1 ? (screenHeight - bannerAdHeight) : (mp == 2 ? 0 : style.top);
            gridAd.style.left = style.isCenter ? screenWidth / 2 - bannerAdWidth / 2 : style.left;
            ads.style.realWidth = bannerAdWidth;
            ads.style.realHeight = bannerAdHeight;
            ads.style.top = gridAd.style.top;
            ads.style.left = gridAd.style.left;
        });

        gridAd.onLoad(() => {
            ads.isLoaded = true;
            ads.loadCount = 0;
        });

        gridAd.onError((err) => {
            ads.isLoaded = false;
            this.reLoadGrid();
        })

        ads.adInstance = gridAd;
        gridAd && gridAd.hide();
    }

    showGridAd(style?: any) {
        if (!bAdsUse || !this._adsCfg.bggridshow) return;

        let ads = this.getAdsData(AdsType.GRID);
        if (!ads.adInstance) {
            this.createGridAds(style);
        }
        if (wxCfg.usePromise) {
            let promis = ads.adInstance.show();
            if (promis) {
                promis.then(() => {
                    ads.loadCount = 0;
                }).catch(() => {
                    ads.loadCount = 0;
                });
            }
        } else {
            ads.adInstance.show();
        }
    }

    hideGridAd(need_pre_create: boolean = true, style?: any) {
        if (!bAdsUse) return;

        let ads = this.getAdsData(AdsType.GRID);
        if (ads.adInstance) {
            ads.adInstance.hide();
            ads.adInstance.destroy();
            ads.adInstance = null;
            ads.loadCount = 0;
            if (ads.neespreLoad) {
                this.createGridAds(style);
            }
        }
    }

    hide_gridAd() {
        if (!bAdsUse) return;

        let ads = this.getAdsData(AdsType.GRID);
        if (ads.adInstance) {
            ads.adInstance.hide();
        }
    }

    gridMoveBy(top: number = 80, left: number = 0) {
        if (!bAdsUse || !this._adsCfg.bggridshow) return;

        let ads = this.getAdsData(AdsType.GRID);
        if (ads.adInstance) {
            ads.adInstance.style.top += top;
            ads.adInstance.style.left += left;
        }
    }

    gridMoveTo(top: number = 0, left: number = 0) {
        if (!bAdsUse || !this._adsCfg.bggridshow) return;

        let ads = this.getAdsData(AdsType.GRID);
        if (ads.adInstance) {
            ads.adInstance.style.top = top;
            ads.adInstance.style.left = left;
        }
    }

    /**
     * 原生格子广告
     */

    preloadCustomAds() {
        if (!bAdsUse || 0 == wxCfg.CustomAdId.length) return;
        //默认创建2个原生模板广告
        let len = wxCfg.customAdStyle.pos.length;
        for (let index = 0; index < len; index++) {
            this.createAdsData(AdsType.CUSTOMAD);
            this.createCustomAds(index);
        }
    }

    createCustomAds(index: number = 0, style?: any) {
        if (!bAdsUse || 0 == wxCfg.CustomAdId.length) return;
        if (typeof (this._wx.createCustomAd) == "undefined") {
            return;
        }
        let ads = this.getAdsData(AdsType.CUSTOMAD, index);
        if (!ads) return;
        let adsIndex = index;//ads.adsIndex;
        let adId = wxCfg.CustomAdId[adsIndex % wxCfg.CustomAdId.length];

        let top = 0;
        let left = 0;

        if (style) {
            top = style.top || wxCfg.customAdStyle.pos[index].top;
            left = style.left || wxCfg.customAdStyle.pos[index].left;
        } else {
            top = wxCfg.customAdStyle.pos[index].top;
            left = wxCfg.customAdStyle.pos[index].left;
        }

        //坐标点转换
        top = this.screenHeight * top / Laya.stage.height;
        left = this.screenWidth * left / Laya.stage.width;

        let customAd = this._wx.createCustomAd({
            adUnitId: adId,
            style: { top: top, left: left, fixed: true }
            //adIntervals:30,//广告自动刷新的间隔时间，单位为秒，参数值必须大于等于30（仅对支持自动刷新的模板生效）
        });
        customAd.onError((err) => {
            ads.isLoaded = false;
        });
        customAd.onLoad(() => {
            ads.isLoaded = true;
        });
        customAd.onClose((ret) => {
            ads.close(true);

            if (ads.adInstance) {
                ads.adInstance.destroy();
                ads.adInstance = null;
            }
            // if (wxCfg.destroyInstl) {
            //     ads.adInstance.destroy();
            //     ads.adInstance = null;
            // }
            // ads.neespreLoad && this.preloadInstalAds();
        });
        ads.adInstance = customAd;
    }

    showCustomAd(index: number = 0, style?: any) {
        if (!bAdsUse || !this._adsCfg.bggridshow) return;
        if (wxCfg.CustomAdId.length == 0) return;
        let ads = this.getAdsData(AdsType.CUSTOMAD, index);
        if (!ads.adInstance) {
            this.createCustomAds(index, style);
        }
        if (wxCfg.usePromise) {
            let promis = ads.adInstance.show();
            if (promis) {
                promis.then(() => {
                    ads.loadCount = 0;
                }).catch(() => {
                    ads.loadCount = 0;
                });
            }
        } else {
            ads.adInstance.show();
        }
    }

    hideCustomAd(index: number = 0, need_pre_create: boolean = true, style?: any) {
        if (!bAdsUse) return;
        let ads = this.getAdsData(AdsType.CUSTOMAD, index);
        if (ads.adInstance) {
            ads.adInstance.hide();
            ads.adInstance.destroy();
            ads.adInstance = null;
            ads.loadCount = 0;
            // if (ads.neespreLoad) {
            //     this.createCustomAds(index,style);
            // }
        }
    }

    hide_CustomAd(index: number = 0) {
        if (!bAdsUse) return;
        let ads = this.getAdsData(AdsType.CUSTOMAD, index);
        if (ads.adInstance) {
            ads.adInstance.hide();
        }
    }
}