import gSdk from "../../common/platformsdk/platform";
import adSdk from "../../common/adDataSdk/adDataSdkFactory";
import EventManager from "../../common/EventManager";
import { EventCfg } from "../../scripts/defines/ResDef";

export default class NativeLayer extends Laya.Script {

    private nativeData;

    private imgNativeLayer: Laya.Image;

    private adBg: Laya.Image;
    private icon: Laya.Image;
    private nativeCloseBtn: Laya.Button;
    private subnativeCloseBtn: Laya.Button;
    private titleTxt: Laya.Label;

    private goBtn: Laya.Button;
    private nextBtn: Laya.Button;

    //调用者:tryNativeLayer,imgNativeLayer

    constructor() { super(); }


    onAwake() {
    }

    onEnable(): void {
        this.imgNativeLayer = this.owner as Laya.Image;

        this.adBg = this.owner.getChildByName("adBg") as Laya.Image;
        this.icon = this.adBg.getChildByName("icon") as Laya.Image;
        this.nativeCloseBtn = this.adBg.getChildByName("nativeCloseBtn") as Laya.Button;
        this.subnativeCloseBtn = this.adBg.getChildByName("subnativeCloseBtn") as Laya.Button;
        this.titleTxt = this.adBg.getChildByName("titleTxt") as Laya.Label;

        this.goBtn = this.owner.getChildByName("goBtn") as Laya.Button;
        this.nextBtn = this.owner.getChildByName("nextBtn") as Laya.Button;

        if (!this.imgNativeLayer) console.log("MyOPPOAD:imgNativeLayer is null");
        if (!this.goBtn) console.log("MyOPPOAD:goBtn is null");
        if (!this.nextBtn) console.log("MyOPPOAD:nextBtn is null");

        if (gSdk.isOP) {
            this.imgNativeLayer.skin = "";
            if (this.owner.name == "imgNativeLayer") {
                this.imgNativeLayer.height = 720;
                this.imgNativeLayer.y = Laya.stage.height - this.imgNativeLayer.height - 150;
            }
        }
        else if (gSdk.isVV) {
            this.imgNativeLayer.skin = "common/tips-01.png";
            this.imgNativeLayer.height = Laya.stage.height;
            this.adBg.y = Laya.stage.height - 150 - this.adBg.height;
            this.goBtn.y = this.adBg.y - 280;

            if (adSdk.sdk.adConfig.electControl("zs_native_next_limit")) {
                this.nextBtn.visible = true;
                this.nextBtn.on(Laya.Event.CLICK, this, this.CloseNative);
                this.nextBtn.y = this.adBg.y - 80;
            }
        }

        if (adSdk.sdk.adConfig.electControl("zs_native_touch_switch")) {
            if (gSdk.isOP) {
                let delayshowclose = adSdk.sdk.adConfig.electControl("zs_jump_time");
                if (delayshowclose != 0 && adSdk.sdk.adConfig.isSameVersion()) {
                    this.nativeCloseBtn.visible = false;
                    Laya.timer.once(delayshowclose, this, () => {
                        this.nativeCloseBtn.visible = true;
                        this.subnativeCloseBtn.visible = true;
                    });
                }
                else {
                    this.subnativeCloseBtn.visible = true;
                }
            }
            else if (gSdk.isVV) {
                this.subnativeCloseBtn.visible = true;
            }
            this.subnativeCloseBtn && this.subnativeCloseBtn.on(Laya.Event.CLICK, this, this.CloseNative);
            this.nativeCloseBtn && this.nativeCloseBtn.on(Laya.Event.CLICK, this, this.ViewNative);
        }
        else {
            if (gSdk.isOP) {
                let delayshowclose = adSdk.sdk.adConfig.electControl("zs_jump_time");
                if (delayshowclose != 0 && adSdk.sdk.adConfig.isSameVersion()) {
                    this.nativeCloseBtn.visible = false;
                    Laya.timer.once(delayshowclose, this, () => { this.nativeCloseBtn.visible = true; });
                }
            }
            this.nativeCloseBtn && this.nativeCloseBtn.on(Laya.Event.CLICK, this, this.CloseNative);
        }

        this.icon && this.icon.on(Laya.Event.CLICK, this, this.ViewNative);
        this.goBtn && this.goBtn.on(Laya.Event.CLICK, this, this.ViewOrClosenative);

        this.goBtn && (this.goBtn.skin = adSdk.sdk.adConfig.electControl("zs_native_click_switch") ? (gSdk.isOP ? "zsRes/oppo_04.png" : "zsRes/vivo_04.png") : "zsRes/vivo_05.png");

        this.onEnableADControl();
    }

    onStart() {
    }


    private onEnableADControl() {
        this.nativeData = gSdk.sdk.getNativeData();
        if (this.nativeData) {
            if (gSdk.isVV) {
                if (adSdk.sdk.adConfig.electControl("zs_native_delay")) {
                    Laya.timer.once(800, this, () => {
                        this.imgNativeLayer.visible = true;
                        this.ShowNative();
                    });
                }
                else {
                    this.imgNativeLayer.visible = true;
                    this.ShowNative();
                }

            }
            else if (gSdk.isOP) {
                if (adSdk.sdk.adConfig.isSameVersion()) {
                    this.imgNativeLayer.visible = true;
                    this.ShowNative();
                }
            }
        }
    }

    private ShowNative() {
        let url = "";
        if (this.nativeData.imgUrlList && this.nativeData.imgUrlList.length > 0) {
            url = this.nativeData.imgUrlList[0];
        } else if (this.nativeData.iconUrlList && this.nativeData.iconUrlList.length > 0) {
            url = this.nativeData.iconUrlList[0];
        } else if (this.nativeData.icon) {
            url = this.nativeData.icon;
        }
        this.icon && (this.icon.skin = url);
        this.titleTxt && (this.titleTxt.text = this.nativeData.desc);
        gSdk.sdk.reportAdShow(this.nativeData);
    }

    private CloseNative() {
        this.imgNativeLayer.visible = false;
        if (gSdk.isOP && this.owner.name == "imgNativeLayer") {
            EventManager.Ins.event(EventCfg.EVENT_NATIVE_PREFAB_CLOSE);
        }
    }

    private ViewNative() {
        gSdk.sdk.reportAdClick(this.nativeData);

        this.CloseNative();
    }

    private ViewOrClosenative() {
        if (adSdk.sdk.adConfig.electControl("zs_native_click_switch"))
            gSdk.sdk.reportAdClick(this.nativeData);

        this.CloseNative();
    }

    onDisable(): void {
        if (gSdk.isOP) gSdk.sdk.createNative_OPPO();
    }
}