import { ui } from "../ui/layaMaxUI";
import EventManager from "../common/EventManager";
import gSdk from "../common/platformsdk/platform";
import adSdk from "../common/adDataSdk/adDataSdkFactory";
import { UICfg } from "../scripts/defines/ResDef";
import ControlData from "../common/data/ControlData";
import AdTool from "../common/AdTool";
import display from "../common/Display";
import GameScene from "../script/GameScene";
import Utils3D from "../scripts/mgr/Utils3D";
import NativeLayer from "./zs/NativeLayer";
export default class TryUseDialog extends ui.commonUI.TryUseDialogUI {
    private _gameScene: GameScene = GameScene.getInstance();
    private isGateSelect: boolean = true;
    private unlockcontrol: boolean;
    private gate: number = 0;
    constructor() {
        super();
        this.height = Laya.stage.height;
        this.videoBtn.on(Laya.Event.CLICK, this, this.videoClick);
        this.selectImg.on(Laya.Event.CLICK, this, this.selectClick);
        this.passBtn.on(Laya.Event.CLICK, this, this.closeClick);
        this.Btn_OPClose.on(Laya.Event.CLICK, this, this.closeClick);
        this.btnOPPOPass.on(Laya.Event.CLICK, this, this.closeClick);
        if (!gSdk.isOP) {
            if (display.largeScreen()) {
                this.spLayertop.y += 240;
            }
            else {
                this.spLayertop.y += 120;
            }
            this.spLayertop.height = Laya.stage.height - this.spLayertop.y;
        }
    }
    onEnable() {
        if (gSdk.isQQ)
            gSdk.sdk.hideBlockAd();
        /*
        if(DataModel.getIns().zs_unlock_skin_btn()){
            this.passBtn.alpha=0;
            Laya.Tween.to(this.passBtn,{alpha:1},200,null,null,2000);
        }
        */
        //this.gate=Math.floor(Math.random()*MyShapes.getIns().getGateCount());
        this.gate = Math.floor(Math.random() * 40);	//需开发者根据需求修改

        this.showItem();

        this.onEnableUIInit();
        this.onEnableADControl();
    }

    onEnableUIInit() {

        if (gSdk.isQQ) {

            gSdk.sdk.showBanner();
            this.selectImg.visible = false;
            this.btnOPPOPass.visible = false;
            this.Btn_OPClose.visible = false;

            if (adSdk.sdk.adConfig.electControl("zs_banner_vertical_enable")) {
                this.passBtn.visible = false;
                Laya.timer.once(2000, this, () => {
                    this.passBtn.visible = true;
                });
            }

            this.passBtn.bottom = 240;
            this.videoBtn.visible = true;
            this.videoBtn.bottom = 280;
        }
    }

    onEnableADControl() {
        if (gSdk.isTT) {
            gSdk.sdk.hideBanner();
            this.videoBtn.skin = "platform_pics/pic_001.png";
            if (adSdk.sdk.adConfig.checkControl('zs_nulock_gate', true)) {
                this.Btn_OPClose.visible = true;
                this.selectImg.visible = true;
                this.selectImg.y = this.spLayertop.height - 130;
                this.videoBtn.x = 218;
                this.passBtn.visible = false;
                this.unlockcontrol = ControlData.inst.getvideounlockon();
                this.changeSelectGateSt();
            }
            else {
                this.Btn_OPClose.visible = false;
                this.selectImg.visible = false;
                this.unlockcontrol = false;
                this.videoBtn.x = 50;
                //this.videoBtn.y = 0;

                this.passBtn.skin = "platform_pics/pic_002.png"
                this.passBtn.x = 386;
                this.passBtn.y = this.videoBtn.y;
            }
        }
        else if (gSdk.isQQ) {
            if (adSdk.sdk.adConfig.electControl('zs_banner_vertical_enable')) {
                this.passBtn.visible = false;
                this.timer.once(2000, this, () => {
                    this.passBtn.visible = true;
                });
            }
        }
        else if (gSdk.isOP) {
            gSdk.sdk.hide_banner();
            this.passBtn.visible = false;
            let delayshowclose = adSdk.sdk.adConfig.electControl("zs_jump_time");
            if (delayshowclose != 0 && adSdk.sdk.adConfig.isSameVersion()) {
                this.btnOPPOPass.visible = false;

                Laya.timer.once(delayshowclose, this, () => {
                    this.btnOPPOPass.visible = true;
                });
            }
            else {
                this.btnOPPOPass.visible = true;
            }
            let halfHeight = Laya.stage.height / 2;
            if (halfHeight > this.spLayertop.height)
                this.spLayertop.y = (halfHeight - this.spLayertop.height) / 2;
            this.InitNativeLayer();
        }
    }
    InitNativeLayer(): boolean {
        let nativelayer: Laya.Image = this.getChildByName("tryNativeLayer") as Laya.Image;
        if (nativelayer) {
            let halfHeight = Laya.stage.height / 2;
            if (halfHeight - 105 - 535 > 0)
                nativelayer.y = (halfHeight - 105 - 535) / 2 + halfHeight;
            if (!nativelayer.getComponent(NativeLayer)) {
                nativelayer.addComponent(NativeLayer);
            }
        }
        return true;
    }

    private videoClick(e: Laya.Event) {
        AdTool.Ins.playVideo("观看完整视频才能解锁", () => {
            EventManager.Ins.event("cut", this.gate);
            Utils3D.bTryUse = true;
            //this._gameScene.tryUseCallF();
            this._gameScene.loadPlayer();
            this.close();
        }, this);
        if (e) e.stopPropagation();
    }

    private selectClick(e: Laya.Event) {
        this.isGateSelect = !this.isGateSelect;
        this.changeSelectGateSt();
        e.stopPropagation();
    }

    private changeSelectGateSt() {
        if (this.unlockcontrol) {
            this.selectImg.skin = this.isGateSelect ? "common/tips-37.png" : "common/tips-36.png";
        }
        else {
            this.selectImg.skin = this.isGateSelect ? "common/tips-35.png" : "common/tips-34.png";
        }
    }

    private closeClick(e: Laya.Event) {
        //if(this.isGateSelect){
        if (this.selectImg.skin == "common/tips-34.png" || this.selectImg.skin == "common/tips-37.png") {
            this.videoClick(null);
            return;
        }
        this.close();
        Utils3D.bTryUse = false;
        this._gameScene.tryUseCallF();
        //this._gameScene.loadPlayer();
        e.stopPropagation();
    }

    onDisable() {
        let self = this;
        if (gSdk.isOP) {
            gSdk.sdk.showBanner();
            gSdk.sdk.createNative_OPPO();
        }
        Laya.stage.event("TRYUSE_CLOSE");
    }

    showItem() {
        Utils3D.nLookSelect = Utils3D.nCurScene % 3;
        if (Utils3D.nLookSelect == 1) {
            Utils3D.nLookSelect = 3;
        } else if (Utils3D.nLookSelect == 0) {
            Utils3D.nLookSelect = 1;
        }
        //console.log("showItem  Utils3D.nLookSelect = ",Utils3D.nLookSelect);
        this.icon.skin = "image/shop/1_" + Utils3D.nLookSelect + ".png";
    }
}