import { ui } from "../ui/layaMaxUI";
import gSdk from "../common/platformsdk/platform";
import adSdk from "../common/adDataSdk/adDataSdkFactory";
import { DialogManager } from "../common/DialogManager";
import { AdsType } from "../common/platformsdk/plat-def";
import { SCENEINDEX } from "../scripts/defines/ResDef";
export default class NativeAdDialog extends ui.commonUI.NativeAdDialogUI {
    private nativeData;
    private closeShowBanner: boolean = true;//关闭时是否打开banner
    constructor() {
        super();
        this.popupEffect = null;
        this.height = Laya.stage.height;
        this.icon.on(Laya.Event.CLICK, this, this.nativeAdClick);
        this.goBtn.on(Laya.Event.CLICK, this, this.goClick);
        this.isPopupCenter = false;
        this.zOrder = 128;

        this.goBtn.skin = adSdk.sdk.adConfig.electControl('zs_native_click_switch') ? (gSdk.isOP ? "zsRes/oppo_04.png" : "zsRes/vivo_04.png") : "zsRes/vivo_05.png";
        if (adSdk.sdk.adConfig.electControl("zs_native_touch_switch")) {
            if (gSdk.isOP) {
                let delayshowclose = adSdk.sdk.adConfig.electControl("zs_jump_time");
                if (delayshowclose != 0 && adSdk.sdk.adConfig.isSameVersion()) {
                    this.closeBtn.visible = false;
                    Laya.timer.once(delayshowclose, this, () => {
                        this.closeBtn.visible = true;
                        this.subcloseBtn.visible = true;
                    });
                }
                else {
                    this.subcloseBtn.visible = true;
                }
            }
            else if (gSdk.isVV) {
                this.subcloseBtn.visible = true;
            }
            this.subcloseBtn && this.subcloseBtn.on(Laya.Event.CLICK, this, this.closeClick);
            this.closeBtn && this.closeBtn.on(Laya.Event.CLICK, this, this.nativeAdClick);
        }
        else {
            if (gSdk.isOP) {
                let delayshowclose = adSdk.sdk.adConfig.electControl("zs_jump_time");
                if (delayshowclose != 0 && adSdk.sdk.adConfig.isSameVersion()) {
                    this.closeBtn.visible = false;
                    Laya.timer.once(delayshowclose, this, () => { this.closeBtn.visible = true; });
                }
            }
            this.closeBtn && this.closeBtn.on(Laya.Event.CLICK, this, this.closeClick);
        }

        if (gSdk.isVV) {
            if (adSdk.sdk.adConfig.electControl("zs_native_next_limit")) {
                this.nextBtn.visible = true;
                this.nextBtn.on(Laya.Event.CLICK, this, this.closeClick);
            }
        }
    }

    onEnable() {
        if (gSdk.isOP)
            gSdk.sdk.hide_banner();
    }

    onOpened(_data) {
        if (_data && _data["fromscene"] && _data["fromscene"] == SCENEINDEX.SHOP_SCENE)
            this.closeShowBanner = false;

        this.nativeData = gSdk.sdk.getNativeData();
        if (this.nativeData) {
            let url = "";
            if (this.nativeData.imgUrlList && this.nativeData.imgUrlList.length > 0) {
                url = this.nativeData.imgUrlList[0];
            } else if (this.nativeData.iconUrlList && this.nativeData.iconUrlList.length > 0) {
                url = this.nativeData.iconUrlList[0];
            } else if (this.nativeData.icon) {
                url = this.nativeData.icon;
            }
            this.icon.skin = url;
            this.titleTxt.text = this.nativeData.desc;
            gSdk.sdk.reportAdShow(this.nativeData);
        }
    }

    private nativeAdClick() {
        gSdk.sdk.reportAdClick(this.nativeData);

        this.closeClick();
    }

    private closeClick() {
        if (this.closeShowBanner) {
            gSdk.sdk.showBanner();
        }
        DialogManager.Ins.sceneClose(this.url);
    }

    private goClick() {
        if (adSdk.sdk.adConfig.electControl("zs_native_click_switch")) {
            this.nativeAdClick();
        } else {
            this.closeClick();
        }
    }
    onDisable() {
        Laya.stage.event("NATIVE_AD_CLOSE");
    }
}