import { navigateSceneType } from "../../common/adDataSdk/adDataSdkDef";
import adSdk from "../../common/adDataSdk/adDataSdkFactory";
import AudioManager from "../../common/AudioManager";
import { SoundCfg, WXPlayerName } from "../../scripts/defines/ResDef"

export default class FakeFriendMsg extends Laya.Script {
    private games = [];
    private showGame;
    constructor() {
        super();
    }

    onAwake() {
        if (!adSdk.sdk.adConfig.electControl("zs_false_news_switch")) return;
        this.games = adSdk.sdk.getAdList(adSdk.sdk.getAdType(navigateSceneType.fakeMsg));
        if (!this.games || this.games.length == 0) return;

        let gameIndex = Math.floor(Math.random() * this.games.length);
        this.showGame = this.games[gameIndex];
        this.owner.getChildByName("playerName_lbl")["text"] = WXPlayerName.namelist[Math.floor(Math.random() * WXPlayerName.namelist.length)];
        this.owner.getChildByName("gameName_lbl")["text"] = this.showGame["link_name"] || this.showGame["app_title"];

        (this.owner as Laya.Button).mouseEnabled = true;
        (this.owner as Laya.Button).visible = true;
        Laya.Tween.to(this.owner, { y: 50 }, 1000, null, Laya.Handler.create(this, () => { }));
        AudioManager.inst.playSound(SoundCfg.msg, 1);
        console.log("FakeFriendMsg OnAwake ======= "+SoundCfg.msg);
    }

    onClick() {
        (this.owner as Laya.Button).mouseEnabled = false;
        adSdk.sdk.navigateToMiniProgram(this.showGame, navigateSceneType.fakeMsg);
        (this.owner as Laya.Button).mouseEnabled = true;
    }
}