
import Utils3D from "../scripts/mgr/Utils3D";
import GameScene from "../script/GameScene";
import { UICfg, SoundCfg } from "../scripts/defines/ResDef";
import UtilTool from "../common/UtilTools";
import Constant from "../scripts/mgr/Constant";
import AudioManager from "../common/AudioManager";

export default class GameEnd{
    private _gameScene: GameScene = GameScene.getInstance();
    _scene = null;
    Level_lh = null;
    lihua = null;
    baozha = null;
    cameraNode = null;
    camera = null;
    pObject = null;
    targets = [];
    xuli = null;
    yetiLh = null;
    fei = null;
    dafei = null;
    constructor(scene){
        this._scene = scene;  
        this.cameraNode = this._scene.getChildByName("CameraObject");
        var cameraAni = this.cameraNode.getChildByName("Bip_jintou");
        this.camera = cameraAni.getChildByName("Main Camera");
    }

    lvFinish = null;
    endParticles = [];
    boss = null;
    bossNode = null;
    finishPosZ = 0;
    pingdiguo = null; //背上平底锅
    onLevelLoaded(node){
        if (this.lvFinish) {
            this.lvFinish.destroy();
            this.lvFinish = null;
        }
        this.Level_lh = node;  
        var finish = this._scene.getChildByName("end");
        var endLine = node.getChildByName("end");
        endLine.active = false;
        this.lvFinish = (finish as Laya.Sprite3D).clone();
        this.finishPosZ = endLine.transform.localPositionZ;
        this.lvFinish.transform.localPositionZ = this.finishPosZ;
        node.addChild(this.lvFinish);
        this.lvFinish.active = true;
        this.bossNode = this.lvFinish.getChildByName("boss");
        this.boss = this.bossNode.getChildByName("boss") as Laya.Sprite3D;
        this.fei = this.boss.getChildByName("fei");
        this.fei.active = false;
        
        this.dafei = this.lvFinish.getChildByName("dafei");
        Utils3D.playParticle(this.dafei,false,0);
        Utils3D.playParticle(this.dafei,false,1);
        Utils3D.playAnimations(this.boss,"daiji");
        //Utils3D.AniCrossFade(this.boss,"tiaowu",0.5,0);
        //Utils3D.AniCrossFade(this.boss,"tiaowu",0.5,1);
        var tNode = this.lvFinish.getChildByName("biaoba");
        tNode.active = false;
        for(var i=0;i<tNode.numChildren;i++){
            this.targets[i] = tNode.getChildAt(i);
        }

        this.yetiLh = this._scene.getChildByName("yeti");
        this.yetiLh.active = false;
        var wastNode = Utils3D.findNode(Utils3D.curPlayer,Constant.BodyResPath[0]) as Laya.Sprite3D;
        this.pingdiguo = wastNode.getChildByName("pingdiguo1");
        var road = this.lvFinish.getChildByName("Box006") as Laya.MeshSprite3D;
        road.meshRenderer.receiveShadow = true;
        // Laya.stage.once("GameEnd",this,this.onGameEnd);
        // Laya.stage.once("EndJump",this,this.endJump);
    }

    bShowEffect = false;
    jumpCount = -1;
    EndJumping(){ //boss被打飞
        if (this.jumpCount > this.pointArray.length - 1){
            Utils3D.bJumping = false;
            this.jumpCount = -1;
            Laya.timer.clear(this,this.EndJumping);
            this.jumpOver();
        }else{
            this.bossNode.transform.position = new Laya.Vector3(this.pointArray[this.jumpCount].x,this.pointArray[this.jumpCount].y,this.pointArray[this.jumpCount].z);
            this.cameraNode.transform.position = new Laya.Vector3(this.cameraNode.transform.position.x,this.pointArray[this.jumpCount].y,this.pointArray[this.jumpCount].z);
            //console.log(" pppos = ",this.cameraNode.transform.position);
            this.jumpCount++;
        }
    }

    jumpOver(){
        AudioManager.inst.playSound(SoundCfg.hit1, 1);
        var yeti = this.yetiLh.clone();
        yeti.transform.localPosition = new Laya.Vector3(0,0,-0.5); 
        yeti.transform.localRotationEulerX = 0;
        yeti.active = true;
        Utils3D.playAnimations(yeti,"zha");
        Utils3D.playParticle(this.targets[this.targetIndex],true,1);
        this.targets[this.targetIndex].addChild(yeti);
        this.bossNode.active = false;
        Utils3D.onShocking(60,false);

        Laya.timer.once(1000,this,function(){
            this.targets[this.targetIndex].active = false;
            Laya.Scene.open(UICfg.UnlockView, false);//解锁打开ui
            Utils3D.bGameWin = true;
            this._gameScene.GameFinish(3000);
        });
    }

    setGuoScale(num){
        //console.log(" Utils3D.attackXuli.transform.localScaleX  == ",Utils3D.attackXuli.transform.localScaleX)
        if(num<0 && Utils3D.attackXuli.transform.localScaleX<=1) return;
        Utils3D.attackXuli.transform.localScaleX += num;
        Utils3D.attackXuli.transform.localScaleY += num;
        Utils3D.attackXuli.transform.localScaleZ += num;
    }

    runToBoss(){
        Utils3D.AniCrossFade(Utils3D.curPlayer,"pao",0.5,1);
        Utils3D.AniCrossFade(this.boss,"jiqi",0.5,0);
        Laya.Tween.to(this.bossNode.transform,{localPositionZ:22},500);
  
        var ry = this.camera.transform.localRotationEulerY;
        Laya.Tween.to(this,{PlayerPx:0,PlayerPz:this.finishPosZ+19,CameraPx:-2.54,CameraPz:this.finishPosZ+17+5.5,CameraRy:ry+8,CameraRx:-26},1500
                        ,null,Laya.Handler.create(this,function(){
            Utils3D.AniCrossFade(Utils3D.curPlayer,"jiqi",1,1);
            this.targetIndex = Utils3D.random(5,10);
            this.calBezier(120);
            var tNode = this.lvFinish.getChildByName("biaoba");
            tNode.active = true;
            Utils3D.attackXuli.getChildAt(0).active = true;
            
            //this.fei.active = true;
            // this.dafei.active = true;
            // Utils3D.playParticle(this.dafei,true,0);
            // Utils3D.playParticle(this.dafei,true,1);
            // Utils3D.playParticle(this.dafei,true,2);
            
            //Laya.Tween.to(Utils3D.attackXuli.transform,{localScaleY:1.5,localScaleX:1.5,localScaleZ:1.5},1000,null,
                //Laya.Handler.create(this,function(){
                     Utils3D.onShocking(60,false);
                     Utils3D.playAnimations(Utils3D.curPlayer,"dafei",0.5,null,1);
                     Utils3D.playAnimations(Utils3D.curPlayer,"dafei",0.5);   
            //}));
        }));

        Laya.timer.once(1600,this,function(){
            this.fei.active = true;
            Utils3D.AniCrossFade(this.boss,"dafei",0.2,0);
            this.jumpCount = 0;
            Utils3D.attackXuli.active = false;
            Laya.timer.frameLoop(1,this,this.EndJumping);
        })
    }

    startCpos = null;
    onGameEnd(){
        if(Utils3D.nDirection == 3){
            Utils3D.AniCrossFade(Utils3D.curPlayer,"qian",0.5,1);
            Utils3D.playParticle(Utils3D.curPlayer,false,13);
        }
        Utils3D.AniCrossFade(Utils3D.curPlayer,"jiqi",0.5,0);
        Utils3D.curWeapon.active = false;
        Utils3D.attackXuli.active = true;
        Utils3D.attackXuli.getChildAt(0).active = false;
        this.pingdiguo.active = false;
    }

    startY = 4;
    startZ = -4;
    pointArray = [];
    targetIndex = 0;
    //创建贝塞尔曲线(两点为抛物线)
    calBezier(pNum=60,bEnd = false){ 
        this.pointArray = [];
        //var playerPos = Utils3D.curPlayer.transform.position;
        let points = []
        let point1 = this.boss.transform.position; // 起点
        let endPos = this.targets[this.targetIndex].transform.position;
        //向前x坐标不变
        let point2 = new Laya.Vector3(point1.x, endPos.y,endPos.z/2)//控制点
        let point44 = new Laya.Vector3(endPos.x, endPos.y+(this.targetIndex+1)*10,endPos.z-this.targetIndex*3); //终点
        let point3 = new Laya.Vector3(endPos.x, endPos.y ,endPos.z); //终点
        points.push(point1);
        points.push(point2);
        points.push(point44);
        points.push(point3);
        
        let array = this.CreateBezierPoints(points, pNum);
        this.pointArray = array;
    }

    CreateBezierPoints(anchorpoints, pointsAmount):Array<any> {
        var points = [];
        for (var i = 0; i <= pointsAmount; i++) {
            var point = this.MultiPointBezier(anchorpoints, i / pointsAmount);
            points.push(point);
        }
        return points;
    }
 
    //二次贝塞尔曲线(抛物线)
    MultiPointBezier(points, t):any {
        let len:number = points.length;
        let x:number = 0, y:number = 0,z = 0;
        let point:any = null;
        for (let i:number = 0; i < len; i++) {
            point = points[i]//new Laya.Vector3(0,0,0) ;
            x += point.x * Math.pow((1 - t), (len - 1 - i)) * Math.pow(t, i) * (this.erxiangshi(len - 1, i));
            y += point.y * Math.pow((1 - t), (len - 1 - i)) * Math.pow(t, i) * (this.erxiangshi(len - 1, i));
            z += point.z * Math.pow((1 - t), (len - 1 - i)) * Math.pow(t, i) * (this.erxiangshi(len - 1, i));
        }
        return { x: x, y: y, z: z };
    }

    erxiangshi(start:number, end:number):number {
        let cs:number = 1, bcs:number = 1;
        while (end > 0) {
            cs *= start;
            bcs *= end;
            start--;
            end--;
        }
        return (cs / bcs);
    };

    createEndPar(){ //人物身后粒子特效
        var mat = null;
        for(var i=0;i<6-Utils3D.nMissBody.length;i++){
            this.endParticles[i].active = true;
            if(i == 0){
                this.endParticles[i].particleSystem.startColorConstant = Constant.BodyColor[Utils3D.nWaistColor];
            }else{
                mat = Utils3D.BodySkin[i-1].skinnedMeshRenderer.material as Laya.BlinnPhongMaterial;
                this.endParticles[i].particleSystem.startColorConstant = mat.albedoColor;
            }
        }
    }

    count = 0;
    nextGame(){
        this.jumpCount = -1;
        this.bShowEffect = false;
        Utils3D.bFalling = false;
        this.count = 0;
        Utils3D.nEndCoins = 0;
        Laya.Tween.clearAll(this);
        this.camera.transform.localPosition = new Laya.Vector3(0, 3.5, -11.5);
        //this.camera.transform.localPosition = new Laya.Vector3(-1.7, 4.5, -5.5);
        this.cameraNode.transform.position = new Laya.Vector3(0, 0, 0);
        this.camera.transform.localRotationEulerX = -30;
        this.camera.transform.localRotationEulerY = 180;
        Laya.timer.clearAll(this);
        this.targetIndex = 0;
    }

    set PlayerPx(value){
        Utils3D.curPlayer.transform.localPositionX = value;
    }
    get PlayerPx(){
        return Utils3D.curPlayer.transform.localPositionX;
    }

    set PlayerPy(value){
        Utils3D.curPlayer.transform.localPositionY = value;
    }
    get PlayerPy(){
        return Utils3D.curPlayer.transform.localPositionY;
    }

    set PlayerPz(value){
        Utils3D.curPlayer.transform.localPositionZ = value;
    }
    get PlayerPz(){
        return Utils3D.curPlayer.transform.localPositionZ;
    }

    set CameraPx(value){
        this.camera.transform.localPositionX = value;
    }
    get CameraPx(){
        return this.camera.transform.localPositionX;
    }

    set CameraPy(value){
        this.cameraNode.transform.localPositionY = value;
    }
    get CameraPy(){
        return this.cameraNode.transform.localPositionY;
    }

    set CameraPz(value){
        this.cameraNode.transform.localPositionZ = value;
    }
    get CameraPz(){
        return this.cameraNode.transform.localPositionZ;
    }

    set CameraRy(value){
        this.camera.transform.localRotationEulerY = value;
    }
    get CameraRy(){
        return this.camera.transform.localRotationEulerY;
    }

    set CameraRx(value){
        this.camera.transform.localRotationEulerX = value;
    }
    get CameraRx(){
        return this.camera.transform.localRotationEulerX;
    }
}