import GameScene from "./GameScene";
import Utils3D from "../scripts/mgr/Utils3D";
import Constant from "../scripts/mgr/Constant";
import GameView from "./GameView";

export default class Bullet{ //玩家子弹
    private static _ins:Bullet;
    private _gameScene: GameScene = GameScene.getInstance();
    public _scene = null;
    public levelLh = null;
    playerPos = new Laya.Vector3();
    blh = null;
    camera = null;
    bulletNode = null;
    bulletsAni = [];
    endNode = null;
    /**射线**/
    public ray: Laya.Ray = new Laya.Ray(new Laya.Vector3(), new Laya.Vector3());
    /**鼠标坐标**/
    public mousePos: Laya.Vector2 = new Laya.Vector2();
    /**碰撞信息**/
    public hitResult:Laya.HitResult = new Laya.HitResult();
    constructor(scene,lvNode){
        this._scene = scene
        this.levelLh = lvNode;
        this.camera = this._gameScene._camera;
        this.blh = this._scene.getChildByName("zidan");
        this.blh.active = false;
        //this.camera.viewportPointToRay(new Laya.Vector2(0,0),this.ray);
        this.bulletNode = Utils3D.curWeapon.getChildAt(1) as Laya.Sprite3D;
        this.bulletsAni = [];
        for(var i = 0;i<Utils3D.weaponAni.numChildren-1;i++){
            this.bulletsAni[i] = Utils3D.weaponAni.getChildAt(i);
        }
        this.endNode = lvNode.getChildByName("end");
    }

    createBullet(){
        var bullet = this.blh.clone() ;
        bullet.active = true;
        bullet.transform.position = this.from;
        var trailLh = bullet.getChildAt(0) as Laya.TrailSprite3D;
        var tFilter = trailLh.trailFilter as Laya.TrailFilter;
        tFilter.alignment = 1;
        return bullet; 
    }

    gameBegin(){
        Utils3D.bulletNum = 20;
    }

    count = 0;
    //bullets = [];
    aniLoopC = 0;
    updating(){
        if(!Utils3D.bStart) return;
        this.count++;
        this.aniLoopC++;
        this.showBullet();
        // if(this.bullet){
        //     this.ray.origin = Utils3D.curPlayer.transform.position;
        //     var out = new Laya.Vector3();
        //     Laya.Vector3.subtract(Utils3D.curPlayer.transform.position,this.bullet.transform.position,out)
        //     this.ray.direction = out;
        //     console.log(this.hitResult.succeeded,this.ray.direction);
        //     if(this.hitResult.succeeded){
        //         this.bullet.active = false;
        //     }
        // }
    }

    bullets = [];
    aniCount = 0;
    showBullet(){ //玩家发射子弹
        if(Utils3D.bulletNum > 0){
            if(this.count%10 == 0){
                if(Utils3D.nDirection == 3){
                    Utils3D.AniCrossFade(Utils3D.curPlayer,"hua",0.2,0);
                }else{
                    Utils3D.AniCrossFade(Utils3D.curPlayer,"kaiqiang",0.2,0);
                }
                if(this.count%20 == 0){
                    this.playerPos = Utils3D.curPlayer.transform.position;
                    this.count = 0;
                    Utils3D.bulletNum--;
                    this.from = this.bulletNode.transform.position;
                    var bullet = this.createBullet();
                    bullet.addComponent(GameView);
                    this.levelLh.addChild(bullet);
                    this.bullets.push(bullet);
                    this.to = new Laya.Vector3(this.from.x,this.from.y,this.from.z+20);
                    Utils3D.tweenTo(bullet,this.to,1000,Laya.Handler.create(this,function(){
                        if(bullet){
                            bullet.destroy();
                            if(this.bullets.length > 0){
                                this.bullets.splice(0,1);
                            }
                        }
                    }));
                    Utils3D.playParticle(Utils3D.curWeapon,true,0);
                    this._gameScene.updateBnum(Utils3D.bulletNum); 
                }
            }
            //this.onShoot(this.bullet);
            this.updateBulletAni();
        }
    }

    seriesC = 0;
    //根据子弹数量调整动画，整发动画计数max=10
    updateBulletAni(){ 
        if(this.aniLoopC == 50){
            this.seriesC++;
            this.aniLoopC = 0;
            if(this.aniCount<10){
                this.bulletsAni[this.aniCount].active = false;
                this.aniCount++;
            }
            //console.log("updateBulletAni bulletNum = "+Utils3D.bulletNum+" aniLoopC = "+this.aniLoopC);
            if(this.seriesC == 8){ //20发子弹打完接着动画
                this.seriesC = 0;
                var num = 10;
                if(Utils3D.bulletNum < 20){
                    num = Math.floor(Utils3D.bulletNum/2);
                }
                this.showAtNumBullet(num);
            }

            if(Utils3D.bulletNum <= 3){
                this.showAtNumBullet(0);
            }
        }
    }

    showAtNumBullet(num){
        Utils3D.playAnimations(Utils3D.weaponAni,"Take 001");
        this.aniCount = 0;
        this.aniLoopC = 0;
        for(var i = 0;i < 10;i++){
            if(i<num){
                this.bulletsAni[i].active = true;
            }else{
                this.bulletsAni[i].active = false;
            }
        }
    }

    onHitSupply(){
        this.seriesC = 0;
        this.showAtNumBullet(10);
    }

    onGameEnd(){
        Utils3D.bulletNum = 0;
        this.showAtNumBullet(0);
        for(var i = 0;i<this.bullets.length;i++){
            if(this.bullets) this.bullets[i].active = false;
        }
    }

    from = null;
    to = null;
    //hitResults = [];
    onShoot(bullet){
        //this.camera.viewportPointToRay()
        //console.log(this.bulletNum,this.from,this.to);
        //var bPos = bullet.transform.position;
        //this._scene.physicsSimulation.raycastFromTo(bPos, new Laya.Vector3(bPos.x,bPos.y,bPos.z-1), this.hitResult);
        //this._scene.physicsSimulation.rayCast(this.ray, this.hitResult);
        if(this.hitResult.succeeded){
            var hitSp = this.hitResult.collider.owner as Laya.MeshSprite3D;
        }
    }

    onTouchDown(){

    }

    nextGame(){
        Utils3D.bulletNum = 0;
        this.count = 0;
        this.aniCount = 0;
        this.aniLoopC = 0;
        this.seriesC = 0;
        this.bullets = [];
    }
    // public static getIns(){
    //     if(!this._ins)this._ins=new Bullet();
    //     return this._ins;
    // }
}