export default class UtilAld {

    static getSdk() {
        if (typeof Laya.Browser.window.wx != 'undefined') {
            return Laya.Browser.window.wx;
        }
        return null;
    }

    /** 游戏关卡开始 */
    static startLevel(level: number) {
        let wx = this.getSdk();
        if (wx && typeof wx.aldStage != 'undefined') {
            //console.log('阿拉丁 统计游戏关卡开始');
            wx.aldStage.onStart({
                stageId: `${level}`, //关卡ID， 必须是1 || 2 || 1.1 || 12.2 格式  该字段必传
                stageName: `第 ${level} 关`,//关卡名称，该字段必传
            });
        }
    }

    /** 游戏关卡中 */
    static runingLevel(level) {
        let wx = this.getSdk();
        if (wx && typeof wx.aldStage != 'undefined') {
            // 在关卡中使用道具
            //console.log('阿拉丁 统计关卡游戏中');
            wx.aldStage.onRunning({
                stageId: `${level}`,
                stageName: `第 ${level} 关`,
                event: "game"
            })
        }
    }

    /** 游戏关卡结束 event=complete:关卡完成,fail:关卡失败*/
    static endLevel(level, event = 'complete') {
        let wx = this.getSdk();
        if (wx && typeof wx.aldStage != 'undefined') {
            // 在关卡中使用道具
            //console.log('阿拉丁 统计关卡游戏结束');
            wx.aldStage.onEnd({
                stageId: `${level}`,
                stageName: `第 ${level} 关`,
                event: event
            })
        }
    }
}