import adSdk from "../../common/adDataSdk/adDataSdkFactory";
import gSdk from "../../common/platformsdk/platform";
import { navigateSceneType } from "../../common/adDataSdk/adDataSdkDef";
import ZsPlayInfo from "./ZsPlayInfo";

export default class AdList extends Laya.Script {
    private adType = null;
    private navScene = navigateSceneType.default
    private autoScroll = false;
    private scrollDir = ZsPlayInfo.SCROLL_NONE;

    private dragSleep = 5000;
    private scrollSpeed = 1;//2800;
    private waitTime = 1000;

    private passedTime = 0;
    private inAutoScroll = false;

    private adData = [];    //后台实际配置的游戏列表
    private iosFilterAppIds = [];

    private otherGames = [];    //保存字段值is_inside=0的游戏
    private otherCount: number = 10;//商业导出游戏数量最少为10

    private moreGameIconObj = {};//多图标游戏

    private list: Laya.List = null;
    private hotIds = [];

    private maxNum = null;
    private isDataUpdate = false;

    private touchIndex = -1;

    private isRandomSelect = false;

    private changeValue = 0;
    private unitValue = 0;
    private isEnd = false;

    private isClockPendulum = false;

    constructor() {
        super();
    }

    /**导出位类型，是否自动滚动，滚动方向，ios是否屏蔽id，列表显示的最大个数，是否随机选择,是否移动一个暂停一下 */
    requestAdData(navScene: navigateSceneType, autoScroll, scrollDir, iosFilterAppIds, maxNum, randomSelect, isClockPendulum) {
        this.navScene = navScene;
        this.adType = adSdk.sdk.getAdType(navScene);
        this.autoScroll = autoScroll;
        this.scrollDir = scrollDir;
        this.iosFilterAppIds = iosFilterAppIds || [];
        this.maxNum = maxNum;
        this.isRandomSelect = randomSelect;
        this.isClockPendulum = isClockPendulum;
        if (this.scrollDir == ZsPlayInfo.SCROLL_VERTICAL) {
            this.list.vScrollBarSkin = "";
        }
        else if (this.scrollDir == ZsPlayInfo.SCROLL_HORIZONTAL) {
            this.list.hScrollBarSkin = "";
        }
        var self = this;
        // let dd = [{ "app_id": 25168, "appid": "wx3e81330a34259e31", "app_title": "天天做蛋糕", "app_desc": "1", "app_icon": "https:\/\/txcdn.wqop2018.com\/guanggao\/a4db6e99e1257acddbc8f762612cf992.png", "app_icon_str": "", "app_qrcode": "", "link_text": "1", "link_path": "pages\/index\/index", "position_type": 2, "type": 2, "app_type": 5, "open_ad": 1 }, { "app_id": 19554, "appid": "wx2f1165f22849e4c6", "app_title": "奶油高手", "app_desc": "奶油高手", "app_icon": "https:\/\/txcdn.wqop2018.com\/guanggao\/2abb8480311738ccf01e474f0fff718e.png", "app_icon_str": "", "app_qrcode": "", "link_text": "1", "link_path": "pages\/index\/index", "position_type": 2, "type": 2, "app_type": 5, "open_ad": 1 }, { "app_id": 24092, "appid": "wxc485846ed154753f", "app_title": "全民切片", "app_desc": "1", "app_icon": "https:\/\/txcdn.wqop2018.com\/guanggao\/6e59e012f070ef36028e47366752d964.jpg", "app_icon_str": "", "app_qrcode": "", "link_text": "1", "link_path": "pages\/index\/index", "position_type": 2, "type": 2, "app_type": 5, "open_ad": 1 }, { "app_id": 19495, "appid": "wx5ee97961d71463df", "app_title": "完美挤奶油", "app_desc": "完美挤奶油", "app_icon": "https:\/\/txcdn.wqop2018.com\/guanggao\/97eff17dd619f66dbf0789490df40097.png", "app_icon_str": "", "app_qrcode": "", "link_text": "1", "link_path": "pages\/index\/index", "position_type": 2, "type": 2, "app_type": 5, "open_ad": 1 }, { "app_id": 28072, "appid": "wxfc76fd1eeedde43f", "app_title": "开心奶茶店", "app_desc": "1", "app_icon": "https:\/\/txcdn.wqop2018.com\/guanggao\/838e191a715d25dae8521ded24222368.png", "app_icon_str": "", "app_qrcode": "", "link_text": "1", "link_path": "?wxgamecid=CCBgAAoXkpQANPoKFhyVWhLw&channel_id=wxcc2f29f56c2ed31d", "position_type": 2, "type": 2, "app_type": 3, "open_ad": 1 }, { "app_id": 12206, "appid": "wx4d0b920de54187b9", "app_title": "农场割草大作战", "app_desc": "1", "app_icon": "https:\/\/txcdn.wqop2018.com\/guanggao\/3571c1b2bff8f43b00dba12f4cdc2ab7.jpg", "app_icon_str": "", "app_qrcode": "", "link_text": "1", "link_path": "pages\/index\/index", "position_type": 2, "type": 2, "app_type": 5, "open_ad": 1 }, { "app_id": 22703, "appid": "wx5263d71247074360", "app_title": "小恐龙快冲", "app_desc": "1", "app_icon": "https:\/\/txcdn.wqop2018.com\/guanggao\/a05a22fcc5af74ca3b66465d121fdc8c.png", "app_icon_str": "", "app_qrcode": "", "link_text": "1", "link_path": "pages\/index\/index", "position_type": 2, "type": 2, "app_type": 5, "open_ad": 1 }, { "app_id": 28210, "appid": "wxb2d7fcaedf161b3f", "app_title": "消灭地盘", "app_desc": "1", "app_icon": "https:\/\/txcdn.wqop2018.com\/guanggao\/4c3b4dd674336010ba363db836ef2a13.png", "app_icon_str": "", "app_qrcode": "", "link_text": "1", "link_path": "?channel=zs0713xcgs&aldad=3b82a767", "position_type": 2, "type": 2, "app_type": 3, "open_ad": 1 }, { "app_id": 28236, "appid": "wx145f35fcb2cfbfbd", "app_title": "小哥哥救救我", "app_desc": "1", "app_icon": "https:\/\/txcdn.wqop2018.com\/guanggao\/d411e43d51d146a3870cd3d23f35ef14.jpg", "app_icon_str": "", "app_qrcode": "", "link_text": "1", "link_path": "?wxgamecid=CCBgAAoXkpQY9458wj7TbI&channel_id=xKt6vcHPcTFJQ2gWauwK2PFv2", "position_type": 2, "type": 2, "app_type": 3, "open_ad": 1 }, { "app_id": 26467, "appid": "wx714f5990c1916bd7", "app_title": "逃出丛林", "app_desc": "1", "app_icon": "https:\/\/txcdn.wqop2018.com\/guanggao\/9c7453a5302fd909c4955a033f98cac0.png", "app_icon_str": "", "app_qrcode": "", "link_text": "1", "link_path": "pages\/index\/index", "position_type": 2, "type": 2, "app_type": 1, "open_ad": 1 }, { "app_id": 28205, "appid": "wx037309b92888e0bf", "app_title": "水管大师", "app_desc": "1", "app_icon": "https:\/\/txcdn.wqop2018.com\/guanggao\/641311022f98f44637c1e980dd1dbdd7.png", "app_icon_str": "", "app_qrcode": "", "link_text": "1", "link_path": "?channel=zs0713xcgs&ald_media_id=41010&ald_link_key=23a457fdaaf4db88&ald_position_id=0", "position_type": 2, "type": 2, "app_type": 3, "open_ad": 1 }, { "app_id": 27674, "appid": "wx9b9370927fdef3ae", "app_title": "翻转吧摩托", "app_desc": "1", "app_icon": "https:\/\/txcdn.wqop2018.com\/guanggao\/246ea93de3f5fea4f4b568c529b97722.jpg", "app_icon_str": "", "app_qrcode": "", "link_text": "1", "link_path": "pages\/index\/index", "position_type": 2, "type": 2, "app_type": 2, "open_ad": 1 }, { "app_id": 27508, "appid": "wx52712075418fb7d4", "app_title": "彩绘服装师", "app_desc": "彩绘服装师", "app_icon": "https:\/\/txcdn.wqop2018.com\/guanggao\/cfb91fb5a2b4cbe866b08eebfa6a6974.png", "app_icon_str": "", "app_qrcode": "", "link_text": "1", "link_path": "pages\/index\/index", "position_type": 2, "type": 2, "app_type": 1, "open_ad": 1 }, { "app_id": 23968, "appid": "wx28293c23454a3c14", "app_title": "贪吃虫仔", "app_desc": "贪吃虫仔", "app_icon": "https:\/\/txcdn.wqop2018.com\/guanggao\/f9f08f4d65c206554affec65f0e5619a.jpg", "app_icon_str": "", "app_qrcode": "", "link_text": "1", "link_path": "pages\/index\/index", "position_type": 2, "type": 2, "app_type": 2, "open_ad": 1 }, { "app_id": 17620, "appid": "wx529203a9f43b2f2a", "app_title": "最强矿工", "app_desc": "1", "app_icon": "https:\/\/txcdn.wqop2018.com\/guanggao\/165a001c8edbd8d332e7b531da3bd2c4.png", "app_icon_str": "", "app_qrcode": "", "link_text": "1", "link_path": "pages\/index\/index", "position_type": 2, "type": 2, "app_type": 2, "open_ad": 1 }];
        // if (self.list) {
        //     self.adData = dd;//data[self.adType.toString()];
        //     self.initHotIds();
        //     self.freshAdList();
        // }
        if (adSdk.isZS) {
            var zsSdk = window["zs"]["sdk"];
            zsSdk.loadAd(function (data) {
                self.adData = data[self.adType.toString()];
                self.freshAdList();
                self.initHotIds();
                if (self.scrollDir == ZsPlayInfo.SCROLL_VERTICAL && !self.isClockPendulum) {
                    self.freshFinger();
                    Laya.timer.loop(3000, self, self.freshFinger);
                }
            });
        }
        else {
            if (self.list) {
                self.adData = adSdk.sdk.getAdList(self.adType);
                self.freshAdList();
                self.initHotIds();
                if (self.scrollDir == ZsPlayInfo.SCROLL_VERTICAL && !self.isClockPendulum) {
                    self.freshFinger();
                    Laya.timer.loop(3000, self, self.freshFinger);
                }
            }
        }
    }
    freshFinger() {
        let cells = this.list.cells;
        let selectCells = [];
        let scrollBar = this.list.scrollBar as Laya.ScrollBar;
        let value = scrollBar.value;
        let min = scrollBar.min;
        let max = scrollBar.max;
        let start = value;
        let end = start + this.list.height;
        for (let i = 0; i < cells.length; i++) {
            let cellItem = cells[i];
            if (cellItem.y >= start && cellItem.y <= end - cellItem.height) {
                selectCells.push(cellItem);
            }
            let finger = cellItem.getChildByName("fingertap") as Laya.Sprite;
            if (finger)
                finger.visible = false;
        }
        if (selectCells.length > 0) {
            let fingerindex = Math.floor(Math.random() * selectCells.length);
            let finger = selectCells[fingerindex].getChildByName("fingertap");
            if (finger)
                finger.visible = true;
            //this.list.refresh();
        }
    }

    /**
     * 获得排除重复后数据
     */
    getExcludeRepeatData() {
        let resultData = [];
        let statusObj = {};
        for (let i = 0; i < this.adData.length; i++) {
            let appid = this.adData[i]["appid"] || this.adData[i]["id"];
            if (!statusObj[appid]) {
                statusObj[appid] = 1;//记录状态
                resultData.push(this.adData[i]);
            } else {
                //存储重复数据到moreGameIconObj中
                if (this.moreGameIconObj[appid]) {
                    let iconList = this.moreGameIconObj[appid] as [{}];
                    iconList.push(this.adData[i]);
                } else {
                    this.moreGameIconObj[appid] = [this.adData[i]];
                }
            }
        }
        return resultData;
    }

    freshAdList() {
        var self = this;
        this.adData = this.adData.filter(function (elment) {
            return Laya.Browser.onAndroid || self.iosFilterAppIds.indexOf(elment.appid) == -1;
        });

        if (adSdk.isZS) {
            this.adData = this.getExcludeRepeatData();//排重
        }

        if (this.maxNum != null) {
            if (this.adData.length < this.maxNum) {
                while (this.adData.length < this.maxNum) {
                    this.adData.push(this.adData[Math.floor(Math.random() * this.adData.length)]);
                }
            }
            else if (this.adData.length > this.maxNum) {
                while (this.adData.length > this.maxNum) {
                    this.adData.splice(Math.floor(Math.random() * this.adData.length), 1);
                }
            }
        }

        if (adSdk.isQT) {
            for (let i = 0; i < self.adData.length; i++) {
                if (self.adData[i]["is_inside"] == 0) {
                    self.otherGames.push(self.adData[i]);
                }
            }
        }

        this.list.array = this.adData;

        var unitNum = 0;
        var ceil = this.list.getCell(0);
        if (!ceil) return;//计算单元格对应滑动值
        if (this.scrollDir == ZsPlayInfo.SCROLL_VERTICAL) {
            unitNum = Math.ceil(this.list.array.length / this.list.repeatX);
            this.unitValue = (ceil.height + this.list.spaceY) / (unitNum * ceil.height + this.list.spaceY * (unitNum - 1) - this.list.height) * this.list.scrollBar.max;
        } else if (this.scrollDir == ZsPlayInfo.SCROLL_HORIZONTAL) {
            unitNum = Math.ceil(this.list.array.length / this.list.repeatY);
            this.unitValue = (ceil.width + this.list.spaceX) / (unitNum * ceil.width + this.list.spaceX * (unitNum - 1) - this.list.width) * this.list.scrollBar.max;
        }

        if (this.autoScroll) {
            Laya.stage.frameOnce(1, this, this.startAutoScrollAd);
        }
    }

    autoclick() {
        let self = this;
        if (this.list.array.length > 0) {
            if (adSdk.isQT) {
                let slist = this.otherGames.length >= this.otherCount ? this.otherGames : this.adData;
                let randomIndex = Math.floor(Math.random() * slist.length);
                //跳转
                let showgame = this.processGamePlayed(slist[randomIndex], true);
                adSdk.sdk.navigateToMiniProgram(showgame, self.navScene, () => { }, () => { }, () => { });
            }
            else {
                let randomIndex = Math.floor(Math.random() * this.list.array.length);
                this.onSelectAd(randomIndex, true);
                console.log("调用了自动跳转游戏", randomIndex);
            }
        }
    }

    initHotIds() {
        var hotNum = Math.random() < 0.5 ? 3 : 4;
        var interval = Math.floor(this.adData.length / hotNum);
        for (var index = 0; index < hotNum; index++) {
            this.hotIds.push(Math.floor(interval * Math.random()) + index * interval);
        }
    }

    startAutoScrollAd() {
        if (!this.list) {
            return;
        }
        this.inAutoScroll = true;
    }

    onItemRender(item, index) {
        var data = this.list.array[index];
        if (!data) {
            item.visible = false;
            return;
        }
        var icon = item.getChildByName("icon");
        if (icon) {
            icon.loadImage(data.app_icon || data.link_img, null);
        } else {
            var iconBox = item.getChildByName("iconBox");
            if (iconBox) {
                var icon = iconBox.getChildByName("icon");
                if (icon) {
                    icon.skin = data.app_icon || data.link_img;
                }
            }
        }
        var name = item.getChildByName("name");
        if (name) {
            name.text = data.app_title || data.link_name;
        }
        var desc = item.getChildByName("desc");
        if (desc) {
            desc.text = data.app_desc || data.link_des;
        }
        if (this.isDataUpdate == true) {
            return
        }
        var titleBg = item.getChildByName("titleBg");
        if (titleBg) {
            titleBg.index = Math.floor(titleBg.clipY * Math.random());
        }
        var tag = item.getChildByName("tag");
        if (tag) {
            if (this.hotIds.indexOf(index) > 0) {
                tag.visible = true;
                tag.index = Math.floor(tag.clipY * Math.random());
            }
            else {
                tag.visible = false;
            }
        }
        else {
            var hotTag = item.getChildByName("hot");
            var newTag = item.getChildByName("new");
            hotTag && (hotTag.visible = false);
            newTag && (newTag.visible = false);
            if (this.hotIds.indexOf(index) > 0) {
                if (hotTag && newTag) {
                    if (Math.random() < 0.5) {
                        hotTag.visible = true;
                    }
                    else {
                        newTag.visible = true;
                    }
                }
                else if (hotTag && !newTag) {
                    hotTag.visible = true;
                }
                else if (newTag && !hotTag) {
                    newTag.visible = true;
                }
            }
        }
    }

    onTouchEnd(e) {
        if (!this.list) {
            return;
        }
        if (!this.list.array) {
            return;
        }
        if (adSdk.sdk.adConfig.electControl('zs_slide_jump_switch') && this.isRandomSelect && this.touchIndex == -1) {
            this.touchIndex = Math.floor(Math.random() * this.list.array.length);
        }
        this.onSelectAd(this.touchIndex, false);
        this.touchIndex = -1;
    }

    onMouseAd(e, index) {
        if (e.type == Laya.Event.MOUSE_DOWN) {
            this.touchIndex = index;
        }
    }

    /**
     * 处理已经跳转过游戏
     * @param data 
     * @returns 
     */
    processGamePlayed(data, isAutoClick = false) {
        if (adSdk.isQT && !adSdk.sdk.adConfig.electControl("zs_play_other_game"))
            return data;

        var navAppObj = adSdk.sdk.getNavigateSuccessObj();
        let appid = data["appid"] || data["id"];
        if (navAppObj && navAppObj[appid]) {
            let slist;
            if (adSdk.isZS) {
                slist = this.adData;
            }
            else {
                slist = isAutoClick ? (this.otherGames.length >= this.otherCount ? this.otherGames : this.adData) : this.adData
            }
            let showdata;
            for (let i = 0; i < 3; i++) {//最多查找3次
                showdata = slist[Math.floor(Math.random() * slist.length)];
                if (showdata) {
                    let newAppid = showdata["appid"] || showdata["id"];
                    if (!navAppObj[newAppid]) {
                        console.log('已跳转成功过', appid, newAppid);
                        break;
                    }
                }
            }
            return showdata;
        }
        return data;
    }

    /**
     * 替换图标
     * @param data 
     * @returns 
     */
    replaceGameIcon(index, data) {
        let appid = data["appid"] || data["id"];
        let iconList = this.moreGameIconObj[appid];
        if (iconList) {
            let newData = iconList[Math.floor(Math.random() * iconList.length)];
            console.log('替换图标', appid)
            this.list.array[index] = newData;
            this.list.refresh();
        }
    }

    /**
     * 替换随机游戏
     * @param index 
     */
    private replaceRandGame(index, data) {
        let newgameindex = Math.floor(Math.random() * this.list.length);
        while (newgameindex == index) {
            newgameindex = Math.floor(Math.random() * this.list.length);
        }
        console.log("跳转成功,新游戏索引", newgameindex, this.list.array[newgameindex]);
        this.list.array[index] = this.list.array[newgameindex];
        this.list.array[newgameindex] = data;
        this.list.refresh();
    }

    onSelectAd(index, isAutoClick = false) {
        if (index == null || index == -1) {
            return;
        }
        if (!this.list) {
            return;
        }
        if (!this.list.array) {
            return;
        }
        var data = this.list.array[index];
        var self = this;
        self.isDataUpdate = true;

        //跳转
        let showgame = this.processGamePlayed(data, isAutoClick);
        adSdk.sdk.navigateToMiniProgram(showgame, self.navScene,
            () => {
                self.replaceRandGame(index, data);//跳转成功,调换游戏
            },
            () => {
                if (adSdk.isQT)
                    self.replaceRandGame(index, data);//跳转失败也调换游戏
                else
                    self.replaceGameIcon(index, data);//跳转失败,先换游戏图标
            },
            () => {
                self.list.selectedIndex = -1;//完成
            }
        );
    }

    params2String(args) {
        var params = args[0] + "=" + args[1];
        for (var index = 2; index < args.length; index += 2) {
            params += "&" + args[index] + "=" + args[index + 1];
        }
        return params;
    }

    onDragStateChanged(newState) {
        this.inAutoScroll = false;
        if (this.autoScroll && newState == 0) {
            this.passedTime = 0;
        }
    }

    onAwake() {
        this.list = this.owner as Laya.List;
        this.list.selectEnable = true;
        this.list.renderHandler = Laya.Handler.create(this, this.onItemRender, null, false);
        this.list.mouseHandler = Laya.Handler.create(this, this.onMouseAd, null, false);
    }

    onEnable() {
        this.owner.on(Laya.Event.MOUSE_UP, this, this.onTouchEnd);

        this.list.on(Laya.Event.MOUSE_UP, this, this.onDragStateChanged, [0]);
        this.list.on(Laya.Event.MOUSE_OUT, this, this.onDragStateChanged, [0]);
        this.list.on(Laya.Event.MOUSE_DOWN, this, this.onDragStateChanged, [1]);
    }

    onDisable() {
        this.owner.off(Laya.Event.MOUSE_UP, this, this.onTouchEnd);

        this.list.off(Laya.Event.MOUSE_UP, this, this.onDragStateChanged);
        this.list.off(Laya.Event.MOUSE_OUT, this, this.onDragStateChanged);
        this.list.off(Laya.Event.MOUSE_DOWN, this, this.onDragStateChanged);
    }

    onUpdate() {

        if (this.autoScroll && this.inAutoScroll == true && this.list && this.list.scrollBar && this.list.scrollBar.max) {
            if (this.list.scrollBar.value >= this.list.scrollBar.max) {
                this.list.scrollBar.value = this.list.scrollBar.max;
                this.scrollSpeed = 0 - this.scrollSpeed;
                this.isEnd = true;
            }
            else if (this.list.scrollBar.value <= 0) {
                this.list.scrollBar.value = 0;
                this.scrollSpeed = 0 - this.scrollSpeed;
                this.isEnd = true;
            }
            this.list.scrollBar.value += this.scrollSpeed;
            if (!this.unitValue || !this.isClockPendulum) return;
            this.isEnd = this.isEnd && this.changeValue != 0;
            this.changeValue += Math.abs(this.scrollSpeed);
            if (this.changeValue >= this.unitValue || this.isEnd) {
                this.autoScroll = false;
                this.isEnd = false;
                this.changeValue = 0;
                Laya.timer.once(this.waitTime, this, function () {
                    this.autoScroll = true;
                })
            }
        }
        if (this.autoScroll && this.inAutoScroll == false) {
            this.passedTime += Laya.timer.delta;
            if (this.passedTime > this.dragSleep) {
                this.startAutoScrollAd();
            }
        }

    }
}
