import EventManager from "../../common/EventManager";
import UserData from "../../common/data/UserData";

export default class GoldSp extends Laya.Script {


    constructor() {
        super();
        EventManager.Ins.on("changeGold", this, this.updateUI);
    }

    onEnable(): void {
        this.updateUI();
    }

    public updateUI() {
        this.owner.getChildByName("txt")["text"] = UserData.inst.gold;//根据开发者具体业务修改
    }

    onDisable(): void {
    }

    onDestroy() {
        EventManager.Ins.off("changeGold", this, this.updateUI);
    }
}