    /**显示原生广告 */
    reportAdShow(data: any, adstype: AdsType = AdsType.NATIVE_INSTIL) { }
    /**添加桌面图标 */
    installShortcut(success: Function, fail: Function, compelete: Function) { }

    /**九宫格广告 */
