    //*********************************************/
    //***********    广告接口   *******************/
    //********************************************/
    /**初始化广告配置 */
    initAdsCfg(params: AdsCfgBase = null) { }

    getAdsData(adType: AdsType, index: number = 0): AdsData {
        let adsList = this._adsList[adType];
        if (!adsList) {
            let ads = new AdsData(adType);
            this._adsList[adType] = [];
            this._adsList[adType].push(ads);
        }
        return this._adsList[adType][index];
    }
    createAdsData(adType: AdsType) {
        let ads = new AdsData(adType);
        !this._adsList[adType] && (this._adsList[adType] = []);
        this._adsList[adType].push(ads);
        return ads;
    }

    /**视频广告 */
    createVideoAds() { }
    preloadVedioAds() { }
    showVideo(ads: AdsData) { ads && ads.close(false); }
    /**插屏广告 */
    createInstalAds() { }
    preloadInstalAds() { }
    showInstal(ads: AdsData) { ads && ads.close(false); }
    /**banner 广告 */
    showBanner(style?: any) { }
