    gridMoveTo(top: number = 0, left: number = 0) { }
    /**qq 应用盒子广告 */
    showAppBoxAd(params?: any) { }
    /**qq 积木广告 */
    showBlockAd(bShow: boolean = true, movetop: number = 0): boolean { return false; }
    hideBlockAd() { }
