import { navigateSceneType } from "../../common/adDataSdk/adDataSdkDef";
import adSdk from "../../common/adDataSdk/adDataSdkFactory";
import gSdk from "../../common/platformsdk/platform";

export default class FloatGameImg extends Laya.Script {
    /** @prop {name:intType, tips:"整数类型示例", type:Int, default:1000}*/
    public intType: number = 1000;
    /** @prop {name:numType, tips:"数字类型示例", type:Number, default:1000}*/
    public numType: number = 1000;
    /** @prop {name:strType, tips:"字符串类型示例", type:String, default:"hello laya"}*/
    public strType: string = "hello laya";
    /** @prop {name:boolType, tips:"布尔类型示例", type:Bool, default:true}*/
    public boolType: boolean = true;
    // 更多参数说明请访问: https://ldc2.layabox.com/doc/?nav=zh-as-2-4-0


    private elect_list: any = null;
    private _curIdx: number = 0;
    private canshow: boolean = false;
    constructor() { super(); }

    onEnable(): void {
        if (gSdk.isWX && !adSdk.sdk.adConfig.electControl("zs_jump_switch"))
            return;

        let self = this;

        self.InitData();

        (self.owner as Laya.Sprite).on(Laya.Event.CLICK, self, (e: Laya.Event) => {
            adSdk.sdk.navigateToMiniProgram(self.getData(), navigateSceneType.float);
        });
    }

    private InitData() {
        let self = this;
        if (!self.elect_list || self.elect_list.length == 0) {
            if (gSdk.sdk.isUseZS) {
                var zsSdk = window["zs"]["sdk"];
                zsSdk.loadAd(function (data) {
                    self.elect_list = data["endPage"];

                    if (!self.elect_list || self.elect_list.length == 0) {
                        (self.owner as Laya.Sprite).visible = false;
                    }
                    else {
                        self.UpdateGame();
                        (self.owner as Laya.Sprite).visible = true;
                        self.canshow = true;
                        Laya.timer.loop(3000, self, self.itemShake);
                    }
                });
            }
            else {
                self.elect_list = adSdk.sdk.getAdList(adSdk.sdk.getAdType(navigateSceneType.float));

                if (!self.elect_list || self.elect_list.length == 0) {
                    (self.owner as Laya.Sprite).visible = false;
                }
                else {
                    self.UpdateGame();
                    (self.owner as Laya.Sprite).visible = true;
                    self.canshow = true;
                    Laya.timer.loop(3000, self, self.itemShake);
                }
            }
        }
    }

    private getData() {
        if (!this.elect_list || this.elect_list.length == 0)
            return null;

        return this.elect_list[this._curIdx % this.elect_list.length];
    }


    private UpdateGame() {
        this._curIdx = Math.floor(Math.random() * this.elect_list.length);
        let data = this.getData();
        if (!data)
            return;

        (this.owner.getChildByName("icon") as Laya.Image).skin = data.link_img || data.app_icon;
        (this.owner.getChildByName("nameTxt") as Laya.Label).text = data.link_name || data.app_title;
    }

    private itemShake() {
        Laya.Tween.to(this.owner, { rotation: 10 }, 300, null, Laya.Handler.create(this, () => {
            Laya.Tween.to(this.owner, { rotation: 0 }, 300, null, Laya.Handler.create(this, () => {
                Laya.Tween.to(this.owner, { rotation: -10 }, 300, null, Laya.Handler.create(this, () => {
                    Laya.Tween.to(this.owner, { rotation: 0 }, 300, null, Laya.Handler.create(this, () => {
                        this.UpdateGame();
                    }))
                }))
            }))
        }))
    }

    public setcanshow() {
        if (this.canshow)
            (this.owner as Laya.Sprite).visible = true;
    }

    onDisable(): void {
    }
}