import { ui } from "../ui/layaMaxUI";
import gSdk from "../common/platformsdk/platform";
import { AdsType, WxBannerScreen } from "../common/platformsdk/plat-def";
import List = Laya.List;
import Box = Laya.Box;
import Image = Laya.Image;
import Sprite = Laya.Sprite;
import Label = Laya.Label;
import TipManager from "../common/TipManager";
import { UICfg, EventCfg, SCENEINDEX } from "../scripts/defines/ResDef";
import adSdk from "../common/adDataSdk/adDataSdkFactory";
import AdList from "../commonUI/zs/AdList";
import FakeExitBtn from "../commonUI/zs/FakeExitBtn";
import { DialogManager } from "../common/DialogManager";
import ADConfigZS from "../common/adDataSdk/ADConfigZS";
import GameScene from "../script/GameScene";
import Utils3D from "../scripts/mgr/Utils3D";
import EventManager from "../common/EventManager";
import ExportMoreGame from "../commonUI/zs/ExportMoreGame";
import UserData from "../common/data/UserData";
import Canstant from "../scripts/mgr/Constant";
import ZsPlayInfo from "../commonUI/zs/ZsPlayInfo";
import FloatGameImg from "../commonUI/zs/FloatGameImg";
import AdTool from "../common/AdTool";
import { navigateSceneType } from "../common/adDataSdk/adDataSdkDef";
import { TapStyle } from "../common/adDataSdk/ADConfigBase";
export default class GameStartView extends ui.gameUI.GameStartViewUI {
    private _gameScene: GameScene = GameScene.getInstance();
    static showinitfullad: boolean = false;
    constructor() {
        super();
        this.height = Laya.stage.height;
        this.touch.height = Laya.stage.height;
    }

    onAwake(): void {
    }

    onMouseDown(): void {

    }

    onEnable(): void {

        //开始游戏
        this.btnPlayer.on(Laya.Event.MOUSE_DOWN, this, this.onClickPlayer);
        // this.touch.on(Laya.Event.MOUSE_DOWN, this, function () {
        //     this.onClickPlayer();
        // });
        this.btnSetting.on(Laya.Event.CLICK, this, this.onClickSetting);
        this.btnShop.on(Laya.Event.CLICK, this, this.onClickShop);
        this.btnShop.visible = true;
        this.btnMoreGame.on(Laya.Event.CLICK, this, this.onClickMoreGame);
        this.btnSign.on(Laya.Event.CLICK, this, this.onClickSign);
        this.btnDesk.on(Laya.Event.CLICK, this, this.onDesk);

        this.btnPopGame.on(Laya.Event.CLICK, this, this.onPopGameClick)
        this.btnPopGame.on(Laya.Event.MOUSE_DOWN, this, this.onPopMouseDown)
        this.btnPopGame.on(Laya.Event.MOUSE_UP, this, this.onPopMouseUp)
        this.textLevel.text = "第" + Utils3D.nShowScene.toString() + "关";
        EventManager.Ins.on("Shopping", this, this.shopping);
        this.PlatformUIInit();
        this.onEnableADControl();
    }

    onDisable() {
        console.log("GameStartView onDisable 退出程序");
        this._gameScene.onDestroy();
    }

    /**添加桌面图标 */
    onDesk(e: Laya.Event) {
        if (e.type == Laya.Event.CLICK) {
            gSdk.sdk.installShortcut((state) => {
                if (!state) TipManager.Ins.showMsgTip("桌面图标已添加");
            }, (err) => { TipManager.Ins.showMsgTip("桌面图标添加失败"); }, () => { });
        }
        e.stopPropagation();
    }

    playVideo() {
        let self = this;
        if (adSdk.sdk.adConfig.electControl("zs_start_video_switch")) {
            AdTool.Ins.playVideo("", () => { self.tapEnergy(); }, self, () => { self.tapEnergy(); });
        }
        else {
            self.tapEnergy();
        }
    }
    tapEnergy() {

        let self = this;
        let tapResult: boolean = false;
        if (gSdk.isQQ) {
            tapResult = adSdk.sdk.adConfig.isCrazyTap(UserData.inst.level, TapStyle.TAP_ENERGY);
        }
        else if (gSdk.isWX) {
            if (adSdk.isQT) {
                tapResult = adSdk.sdk.adConfig.energyCanShow();
            }
            else {
                tapResult = adSdk.sdk.adConfig.isCrazyTap(UserData.inst.level, TapStyle.TAP_CHEST);
            }
        }
        if (tapResult) {
            Laya.stage.once("QQ_CLICK_ENERGY", self, function () {
                self._gameScene.initMainLayer();
            });
            Laya.Scene.open(UICfg.QQEnergy, false);
        }
        else {
            self._gameScene.initMainLayer();
        }

        // if (adSdk.sdk.adConfig.energyCanShow()) {
        //     Laya.stage.once("QQ_CLICK_ENERGY", self, function () {
        //         self._gameScene.initMainLayer();
        //     });
        //     Laya.Scene.open(UICfg.QQEnergy, false);
        // }
        // else {
        //     self._gameScene.initMainLayer();
        // }
    }

    //开始游戏
    onClickPlayer(e: Laya.Event): void {
        ZsPlayInfo.IS_START_GAME = true;
        //if(!Utils3D.bLoadedComplete) return;
        //头条平台,还有微信指色开了导出位广告这两种情况下,点击开始游戏时就关闭bannner
        let self = this;
        if (gSdk.isTT)
            gSdk.sdk.hide_banner();


        if (gSdk.isWX) {
            gSdk.sdk.hideBannerzs();
            if (!gSdk.sdk.isUseZS) {
                self.playVideo();
            }
            else {
                self._gameScene.initMainLayer();
            }
        }
        else if (gSdk.isOP) {
            if (adSdk.sdk.adConfig.electControl("zs_native_limit") && gSdk.sdk.getNativeData()) {
                Laya.stage.once("TRYUSE_CLOSE", self, function () {
                    self._gameScene.initMainLayer();
                });
                Laya.Scene.open(UICfg.UseDialog, false);
            }
            else
                self._gameScene.initMainLayer();
        }
        else if (gSdk.isQQ) {
            //顺序1、试用，2、宝箱
            if (adSdk.sdk.adConfig.checkControl("zs_tryuse_switch", true)) {
                Laya.stage.once("TRYUSE_CLOSE", self, function () {
                    self.tapEnergy();
                });
                DialogManager.Ins.sceneOpen(UICfg.UseDialog, false);
            } else {
                this.tapEnergy();
            }
        }
        else {
            Laya.stage.once("TRYUSE_CLOSE", self, function () {
                self._gameScene.initMainLayer();
            });
            Laya.Scene.open(UICfg.UseDialog, false);
        }
        e.stopPropagation();
        this.close();
    }

    onClickShop(e: Laya.Event): void {
        if (e.type == Laya.Event.CLICK && !Utils3D.bBtnClicked) {
            //先隐藏掉界面上游戏导出,等关闭商店再显示出来
            this.SetRollGameVisible(false);
            this.HideFloatGame();
            Utils3D.bBtnClicked = true;
            //Laya.Scene.open("commonUI/ShopDialog.scene");
            this._gameScene.onShoping();
            Laya.Scene.open("gameUI/ShopView.scene", false);
        }
        e.stopPropagation();
    }

    onClickMoreGame(e: Laya.Event): void {
        let self = this;
        if (e.type == Laya.Event.CLICK) {
            if (gSdk.isTT) {
                let listdata = adSdk.sdk.getAdList('defaultData');
                gSdk.sdk.showMoreGamesModal((listdata && listdata.length > 0) ? listdata[0] : [], null, null, null, (code) => {
                    if (code == 1) TipManager.Ins.showMsgTip("打开更多游戏失败", this);
                })
            }
            else if (gSdk.isQQ) {
                gSdk.sdk.showAppBoxAd();
            }
            else if (gSdk.isOP) {
                gSdk.sdk.showAds(AdsType.GAME_PORTAL,
                    (ret) => {
                        console.log("MyOPPOAD:show call", ret);
                        if (ret) {
                            gSdk.sdk.hideBanner();
                        }
                        else {
                            TipManager.Ins.showMsgTip("正在获取更多游戏内容...", self);
                        }
                    },
                    () => {
                        console.log("MyOPPOAD:close call");
                    },
                    this);
            }
            else if (gSdk.isWX) {
                Laya.Scene.open(UICfg.floatAd, false, { isCloseBanner: false });
            }
        }
        e.stopPropagation();
    }

    onClickSetting(e: Laya.Event): void {
        if (e.type == Laya.Event.CLICK) {
            Laya.Scene.open("commonUI/SettingDialog.scene", false);
        }
        e.stopPropagation();
    }

    onClickSign(e: Laya.Event): void {
        if (e.type == Laya.Event.CLICK && !Utils3D.bBtnClicked) {
            Utils3D.bBtnClicked = true;
            Laya.Scene.open("commonUI/UISign.scene", false);
        }
        e.stopPropagation();
    }

    onPopMouseDown() {
        this.btnPopGame.scaleX = 0.9;
        this.btnPopGame.scaleY = 0.9;
    }
    onPopMouseUp() {
        this.btnPopGame.scaleX = 1;
        this.btnPopGame.scaleY = 1;
    }

    onPopGameClick(e: Laya.Event = null) {
        this.btnPopGame.visible = false;

        let exportgame = this.getChildByName('exportList2') as Laya.Sprite;
        exportgame.visible = true;

        if (!exportgame.getComponent(ExportMoreGame)) {
            exportgame.addComponent(ExportMoreGame);
        }
        let expcom = exportgame.getComponent(ExportMoreGame);
        expcom.tweenshow(this.btnPopGame.y);

        if (!exportgame.getChildByName('adList').getComponent(AdList)) {
            let adList: AdList = exportgame.getChildByName('adList').addComponent(AdList);
            adList.requestAdData(navigateSceneType.sidebar, true, ZsPlayInfo.SCROLL_VERTICAL, null, null, false, true);
        }

        e && e.stopPropagation();
    }
    showExportGame() {
        this.btnPopGame.visible = true;
    }


    OnShow(): void {
    }

    onDestroy() {
    }

    onEnableADControl() {
        if (gSdk.isQQ) {
            Laya.timer.once(1000, this, () => {
                gSdk.sdk.showBlockAd();
            });
            gSdk.sdk.showBanner();
        }
        else if (gSdk.isWX) {
        }
        else if (gSdk.isOP) {
            gSdk.sdk.hideBanner();
        }
        else
            gSdk.sdk.showBanner();
    }

    static cblcanshow = false;
    PlatformUIInit() {
        let self = this;
        if (gSdk.isOP) {
            this.btnDesk.visible = true;
            if (gSdk.sdk.supportGamePortal())
                this.btnMoreGame.visible = true;
            else
                this.btnMoreGame.visible = false;
        }
        else if (gSdk.isTT) {
            this.btnPopGame.visible = false;
            this.btnDesk.visible = false;
            this.btnMoreGame.visible = true;
            this.btnShop.visible = true;
            this.btnSetting.x = 20;
            this.btnSetting.y = 90;
        }
        else if (gSdk.isQQ) {
            this.btnDesk.visible = false;
            this.btnMoreGame.visible = true;
            this.btnShop.visible = true;
            this.btnSetting.y = 0;
            this.btnSetting.bottom = 463;
        }
        else if (gSdk.isWX) {
            console.log("GameStartview  enterBanner")
            Laya.stage.on(ZsPlayInfo.EVENT_NAVIGATE_FAILED, this, this.onShowFullListAd);
            //EventManager.Ins.on(EventCfg.EVENT_SHOP_CLOSED, this, this.OnShopClose);

            this.btnDesk.visible = false;
            this.getChildByName("GoldSp")["y"] = 10;
            this.getChildByName("GoldSp")["x"] = Laya.stage.width - 200;

            if (adSdk.sdk.adConfig.electControl("zs_auto_witch")) {
                if (adSdk.sdk.adConfig.electControl("zs_horizontal_export")) {
                    let electList: Laya.Image = this.getChildByName("exportList1") as Laya.Image;
                    electList.visible = true;
                    if (!electList.getChildAt(0).getComponent(AdList)) {
                        let adList: AdList = electList.getChildAt(0).addComponent(AdList);
                        adList.requestAdData(navigateSceneType.scroll, true, ZsPlayInfo.SCROLL_HORIZONTAL, null, null, false, true);
                    }
                }

                let exitBtn: Laya.Button = this.getChildByName("exitBtn") as Laya.Button;
                if (!exitBtn.getComponent(FakeExitBtn)) {
                    exitBtn.active = false;
                    let exitscript = exitBtn.addComponent(FakeExitBtn);
                    exitBtn.active = true;
                }

                //侧边
                EventManager.Ins.on(EventCfg.EVENT_SHOW_EXPORTGAME, this, this.showExportGame);
                this.btnPopGame.visible = true;
                let loginCount = UserData.inst.get('login', {})['loginCount'] || 0;
                console.log("登录次数", loginCount);

                if (loginCount > 1)
                    GameStartView.cblcanshow = true;
                if (adSdk.sdk.adConfig.electControl("zs_auto_pop_ups_switch") && GameStartView.cblcanshow) {
                    Laya.timer.once(100, this, () => {
                        this.onPopGameClick();
                    });
                }
                GameStartView.cblcanshow = true;
                this.InitFloatGame();
                this.btnMoreGame.visible = true;
            }
            this.processWxBanner();
        }
    }

    OnShopClose() {
        this.SetRollGameVisible(true);
        this.InitFloatGame();
    }

    SetRollGameVisible(bshow: boolean) {
        if (adSdk.sdk.adConfig.electControl("zs_auto_witch") && adSdk.sdk.adConfig.electControl("zs_horizontal_export")) {
            let electList: Laya.Image = this.getChildByName("exportList1") as Laya.Image;
            if (electList)
                electList.visible = bshow;
        }
    }

    InitFloatGame() {
        for (let i = 1; i < 5; i++) {
            let fgame: Laya.Sprite = this.getChildByName("fgame" + i) as Laya.Sprite;
            if (fgame) {
                let fgamescript = fgame.getComponent(FloatGameImg);
                if (!fgamescript) {
                    fgame.active = false;
                    let fscript = fgame.addComponent(FloatGameImg);
                    fgame.active = true;
                }
                else {
                    fgamescript.setcanshow();
                }
            }
        }
    }

    HideFloatGame() {
        for (let i = 1; i < 5; i++) {
            let fgame: Laya.Sprite = this.getChildByName("fgame" + i) as Laya.Sprite;
            if (fgame) {
                fgame.visible = false;
            }
        }
    }

    static entergame = true;    //标记是否刚刚进入游戏,全屏导出只在true时弹一次
    processWxBanner() {
        let self = this;
        if (adSdk.isQT) {
            if (GameStartView.entergame) {
                if (adSdk.sdk.adConfig.electControl("zs_gdt_full_screen_1")) {
                    Laya.stage.once("FULL_SCREEN_AD1_CLOSE", self, function () {
                        if (adSdk.sdk.adConfig.electControl("zs_gdt_full_screen_2")) {
                            Laya.stage.once("FULL_SCREEN_AD2_CLOSE", self, function () {
                                self.showWxBanner();
                            });
                            DialogManager.Ins.sceneOpen(UICfg.fullscreenad2, false);
                        }
                        else {
                            self.showWxBanner();
                        }
                    });
                    DialogManager.Ins.sceneOpen(UICfg.fullscreenad1, false);
                }
                else if (adSdk.sdk.adConfig.electControl("zs_gdt_full_screen_2")) {
                    Laya.stage.once("FULL_SCREEN_AD2_CLOSE", self, function () {
                        self.showWxBanner();
                    });
                    DialogManager.Ins.sceneOpen(UICfg.fullscreenad2, false);
                }
                else {
                    self.showWxBanner();
                }
            }
            else {
                self.showWxBanner();
            }
        }
        else {
            if (GameStartView.entergame) {
                if (adSdk.sdk.adConfig.electControl("zs_Fakerjump")) {
                    Laya.stage.once("FULL_SCREEN_AD1_CLOSE", self, function () {
                        gSdk.sdk.showAds(AdsType.INTERSTITIAL, () => { }, () => { }, self);
                        self.showWxBanner();
                    });
                    DialogManager.Ins.sceneOpen(UICfg.fullscreenad1, false);
                }
                else {
                    gSdk.sdk.showAds(AdsType.INTERSTITIAL, () => { }, () => { }, self);
                    self.showWxBanner();
                }
            }
            else {
                gSdk.sdk.showAds(AdsType.INTERSTITIAL, () => { }, () => { }, self);
                self.showWxBanner();
            }
        }
    }
    showWxBanner() {
        GameStartView.entergame = false;
        gSdk.sdk.showBannerzs();
    }

    onShowFullListAd(data) {
        let self = this;

        let showFullScreen = true;
        console.log("gamestartvie监听游戏跳转失败打开全屏一");
        if (adSdk.isQT) {
            if (data) {
                console.log("游戏跳转失败打开全屏一的参数:", data);
                if (data.msgFrom == navigateSceneType.full2Page || data.msgFrom == navigateSceneType.full1Page) { showFullScreen = false; }//全屏二的游戏跳转取消就不调用全屏一了
                else {
                    if (data.msgFrom == navigateSceneType.endPage || data.msgFrom == navigateSceneType.fakeMsg)
                        ZsPlayInfo.IS_AUTO_CLICK = true;
                    DialogManager.Ins.sceneOpen(UICfg.fullscreenad1, false, null);
                }
            }
            else {
                DialogManager.Ins.sceneOpen(UICfg.fullscreenad1, false, null);
            }
        }
        else {
            showFullScreen = false;
            DialogManager.Ins.sceneOpen(UICfg.fullscreenad1, false, null);
        }
        if (showFullScreen) {
            Laya.stage.once("FULL_SCREEN_AD1_CLOSE", self, function () {
                gSdk.sdk.showBannerzs();
            });
        }
    }

    onOpened(_data) {
        if (_data && _data["fromscene"]) {
            if (_data["fromscene"] == SCENEINDEX.LOAD_SCENE) { }
            else if (_data["fromscene"] == SCENEINDEX.RESULT_SCENE) {
                if (gSdk.isWX) {
                    if (adSdk.isQT) {
                        gSdk.sdk.showAds(AdsType.INTERSTITIAL, () => { }, () => { }, this);
                    }
                }
            }
        }
    }

    shopping() {
        this.spLogo.visible = !Utils3D.bOpenShop;
        this.level.visible = !Utils3D.bOpenShop;
    }
}