import { AudioData, AudioType } from "./data/AudioData";
import gSdk from "./platformsdk/platform";

export default class AudioManager {
    private _audioData: AudioData = null;
    /**背景音乐回调 */
    private _musicChannel: Laya.SoundChannel = null;
    private _musicUrl: string = null;
    private static _instance: AudioManager = null;
    constructor() {
        this._audioData = new AudioData();
    }

    public static get inst(): AudioManager {
        if (null == this._instance) {
            this._instance = new AudioManager();
        }
        return this._instance;
    }

    /**
     * @method 更新当前状态信息并保存到本地(点击音乐音效按钮)
     * @param type 
     * @param value 
     */
    update(type: AudioType, value: number = 1) {
        if (type == AudioType.Sound_Volume) {
            this._audioData.soundVolume = value;
            Laya.SoundManager.musicVolume = value;
        } else if (type == AudioType.Music_Volume) {
            this._audioData.soundVolume = value;
            Laya.SoundManager.soundVolume = value;
        } else if (type == AudioType.Music_Mute) {
            this._audioData.musicMute = this._audioData.musicMute == 0 ? 1 : 0;
            Laya.SoundManager.musicMuted = this._audioData.musicMute == 1 ? true : false;
        } else if (type == AudioType.Sound_Mute) {
            this._audioData.soundMute = this._audioData.soundMute == 0 ? 1 : 0;
            Laya.SoundManager.soundMuted = this._audioData.soundMute == 1 ? true : false;
        } else if (type == AudioType.Mute) {
            this._audioData.mute = this._audioData.mute == 0 ? 1 : 0;
            //Laya.SoundManager.muted = this._audioData.mute == 1 ? true : false;
            if (this._audioData.mute == 0) {
                Laya.SoundManager.stopMusic();
            } else {
                this.playMusic(this._musicUrl);
            }
        } else if (type == AudioType.Vibrator) {
            this._audioData.vibrator = this._audioData.vibrator == 0 ? 1 : 0;
        }
    }

    /**
     * @method 获取当前音乐音效状态
     * @param type 
     */
    getState(type: AudioType) {
        if (type == AudioType.Sound_Volume) {
            return this._audioData.soundVolume;
        } else if (type == AudioType.Music_Volume) {
            return this._audioData.musicVolume
        } else if (type == AudioType.Music_Mute) {
            return this._audioData.musicMute;
        } else if (type == AudioType.Sound_Mute) {
            return this._audioData.soundMute;
        } else if (type == AudioType.Mute) {
            return this._audioData.mute;
        } else if (type == AudioType.Vibrator) {
            return this._audioData.vibrator;
        }
    }

    /** 播放音效 */
    playSound(url: string, loops?: number, complete?: Laya.Handler, startTime?: number) {
        if (!this._audioData.mute || !this._audioData.soundMute) {
            complete && complete.runWith(null);
            return;
        }
        Laya.SoundManager.playSound(url, loops, complete, null, startTime);
    }

    /**播放音乐 */
    playMusic(url: string, loops?: number, complete?: Laya.Handler, startTime?: number) {
        this._musicUrl = url;
        if (!this._audioData.mute || !this._audioData.soundMute) {
            return;
        }
        this._musicUrl = url;
        this._musicChannel = Laya.SoundManager.playMusic(url, 0);
    }
    /**停止背景音乐 */
    stopMusic() {

        if (!this._audioData.mute || !this._audioData.soundMute) {
            return;
        }

        if (this._musicChannel) {
            this._musicChannel.pause();
        } else {
            Laya.SoundManager.stopMusic();
        }
    }

    resumeMusic() {
        if (!this._audioData.mute || !this._audioData.soundMute) {
            return;
        }
        if (this._musicChannel) {
            this._musicChannel.resume();
        } else {
            this._musicUrl && Laya.SoundManager.playMusic(this._musicUrl);
        }
    }
    /**播放震动
     * 默认true 短震动，  false是长震动
     */
    playVibrator(isShort = true) {
        if (this._audioData.vibrator) {
            gSdk.sdk.playVibrative(isShort);
        }
    }
}