import { SdkPlat } from "../platformsdk/plat-def";

/**
* 运营渠道类型
*/
export enum cpType {
    None,           //没有sdk
    QT,            //奇兔
    ZS,          //ZS
    HL          //嗨鹿
}

/**
 * 平台对应运营渠道类型
 */
export const PlatformCpType = {
    [SdkPlat.WX]:{
        cp: cpType.QT
    },
    [SdkPlat.OP]:{
        cp: cpType.ZS
    },
    [SdkPlat.QQ]:{
        cp: cpType.ZS
    },
    [SdkPlat.TT]:{
        cp: cpType.QT
    },
    [SdkPlat.VV]:{
        cp: cpType.ZS
    },
    [SdkPlat.BD]:{
        cp: cpType.QT
    },
    [SdkPlat.None]:{
        cp: cpType.QT
    }
}

/**
 * 奇兔点击导出位类型
 */
export enum navigateSceneType {
    default = 1,//首页默认
    endPage = 2,//结束页
    float = 3,//浮动
    full1Page = 4,//全屏1
    full2Page = 5,//全屏2
    scroll = 6,//横屏滚动
    more = 7,//更多游戏
    sidebar = 8,//侧边栏
    fakeMsg = 9,//结算页假好友消息
}


