import SdkBase from "./sdk-base";
import { ShareCfg, AdsType, bAdsUse } from "./plat-def";
import { ttCfg } from "./plat-def";
import { AdsData } from "./Ads-data";
import { AdsCfgBase } from "./Adscfg-def";

export default class SdkTT extends SdkBase {
    private _tt: any = null;
    private _adsCfg:AdsCfgBase=null;
    constructor() {
        super();
        this._tt = Laya.Browser.window.tt;
        this.version = ttCfg.version;
        this.cfg = ttCfg;
    }

    init(params?: any) {
        /*
        this._tt.onShow((res) => {  //从后台返回到游戏
            //EventManager.dispatchGlobalByCmd(GameEvent.ONSHOW,{});
        });

        this._tt.onHide(() => {  //从游戏到后台
            //EventManager.dispatchGlobalByCmd(GameEvent.ONHIDE,{});
        });
        this._tt.exitMiniProgram({
            success:() => {},
            fail:() =>{},
            complete:()=>{}
        })
        */
        this._tt.onShareAppMessage(function () {
            return {
                title: ShareCfg.name,
                desc: ShareCfg.desc,
                path: "",
                imageUrl: ShareCfg.imageUrl,
                templateId: ShareCfg.templateId,
                success: () => {
                    console.log('转发发布器已调起，并不意味着用户转发成功，微头条不提供这个时机的回调');
                },
                fail: () => {
                    console.log('转发发布器调起失败');
                }
            }
        });
        this.initAds();
    }

    /**获取右上角菜单栏信息 */
    getRightMenuRectInfo(): any {
        if (typeof this._tt.getMenuButtonLayout == 'undefined') {
            return SdkBase.prototype.getRightMenuRectInfo.call(this);
        }
        return this._tt.getMenuButtonLayout();
    }

    getLaunchOptionsSync(): any {
        return this._tt.getLaunchOptionsSync()
    }

    getSystemInfoSync(): any {
        return this._tt.getSystemInfoSync()
    }

    share(msg?: string, cb?: Function) {
        msg = msg || ShareCfg.name;
        this._tt.onShareAppMessage(function (res) {
            return {
                title: ShareCfg.name,
                desc: ShareCfg.desc,
                templateId: ShareCfg.templateId,
                imageUrl: ShareCfg.imageUrl,
                query: "k1=v1&k2=v2",
                success() {
                    cb && cb(true);
                },
                fail(e) {
                    console.log("分享失败", e);
                    cb && cb(false);
                }
            };
        });
    }

    playVibrative(isShort = true) {
        isShort ? this._tt.vibrateShort() : this._tt.vibrateLong();
    }

    getappId() {
        return this.cfg.appid;
    }

    getopenId(callback) {
        let self = this;
        this.login((state, ret) => {
            if (state && ret.code) {
                this._tt.request({
                    method: 'POST',
                    url: self.cfg.serviceUrl,
                    data: {
                        code: ret.code,
                        appid: self.cfg.appid
                    },
                    success: (res) => {
                        self.openId = res.data.data.openid;
                        callback && callback(true, self.openId);
                    },
                    fail: (err) => {
                        console.log('头条获取 openid 失败', err);
                        callback && callback(false);
                    }
                });
            }
            else {
                callback && callback(false);
            }
        });
    }

    login(cb: Function = null) {
        this._tt.login({
            force: false,
            success: (ret) => {
                cb && cb(true, ret);
            },
            fail: (err) => {
                console.log("头条 登录失败 ", err);
                cb && cb(false);
            },
            complete: (res) => { }
        })
    }

    /**头条跳转(需要在game.json 配置"ttNavigateToMiniGameAppIdList": [
        "tt264750fab96d525e",
        "ttc753b1ee5879d1dc",
        "tt18d66e6b87fc744a",
        "ttda69acd44543f5a1",
        "tt31d54768aeff5928"
    ]) */
    public showMoreGamesModal(datalist, success = null, fail = null, complete = null, showCall = null, closeCall = null) {
        if (typeof this._tt.showMoreGamesModal == 'undefined') {
            return;
        }
        if (!datalist) datalist = [];
        const systemInfo = this._tt.getSystemInfoSync();

        // iOS 不支持，建议先检测再使用
        if (systemInfo.platform.indexOf("ios") >= 0) {
            this._tt.showToast({ title: "ios 暂不支持" });
            if (showCall) showCall(2);
            return;
        }

        // 监听弹窗关闭
        if (typeof this._tt.onMoreGamesModalClose != 'undefined') {
            this._tt.onMoreGamesModalClose(function (res) {
                closeCall && closeCall();
            });
        }
        // 监听小游戏跳转
        if (typeof this._tt.onNavigateToMiniProgram != 'undefined') {
            this._tt.onNavigateToMiniProgram(function (res) {
                if (res) {
                    if (res.errCode == 0) {
                        if (success)
                            success(res.errCode, res.errMsg, res.from);
                    } else {
                        if (fail)
                            fail();
                    }
                }
            });
        }
        
        if(typeof this._tt.onNavigateToMiniGameBox != 'undefined'){
            this._tt.onNavigateToMiniGameBox((res) => {
                // console.log('++++++++++  onNavigateToMiniGameBox  +++++++++++');
                // console.log(res);
            })
        }

        // var targetMini = datalist;
        // var navigateData = targetMini.extraData || {};
        // navigateData.appId = ttCfg.appid;

        let appLaunchOptions = [];
        for (let i = 0; i < datalist.length; i++) {
            appLaunchOptions.push({
                appId: datalist[i].app_id,
                query: datalist[i].path,
                extraData: {}
            })
        }

        this._tt.showMoreGamesModal({
            appLaunchOptions: appLaunchOptions,
            success(res) {
                if (showCall) showCall(0)
            },
            fail(res) {
                console.log("showMoreGamesModal fail", res.errMsg);
                if (showCall) showCall(1)
            }
        });

    }


    //*********************************************/
    //***********    广告接口   *******************/
    //********************************************/
    initAdsCfg(params: AdsCfgBase = null) {
        this._adsCfg = params;
    }

    initAds() {
        this._adsCfg = {ver: "0.0", switch: 0};
        this.createBanner();
        this.preloadInstalAds();
        this.preloadVedioAds();
    }

    createVideoAds() {
        if (typeof (this._tt.createRewardedVideoAd) == "undefined") {
            console.log('头条 不存在激励视频广告 api');
            return;
        }

        let ads = this.getAdsData(AdsType.VIDEO);
        if (ads.adInstance) return;

        let adsIndex = ads.adsIndex;
        let videoId = ttCfg.VideoId[adsIndex % ttCfg.VideoId.length];
        let videoAd = this._tt.createRewardedVideoAd({
            adUnitId: videoId
        });

        videoAd.onLoad(() => {
            ads.isLoaded = true;
        });
        videoAd.onError((err) => {
            console.log('tt  视频广告错误=>', err);
            ads.isLoaded = false;
        });

        videoAd.onClose(function (res) {
            if (res && res.isEnded || res === undefined) {    //视频广告成功，获得奖励
                ads.close(true);
            } else {
                ads.close(false);
            }
            ads.neespreLoad && this.preloadVedioAds();
        }.bind(this));
        ads.adInstance = videoAd;
    }

    preloadVedioAds() {
        let ads = this.getAdsData(AdsType.VIDEO);
        if (!ads.adInstance) {
            this.createVideoAds();
        }
        if (!ads.isLoaded && ads.adInstance) {
            let promise = ads.adInstance.load()
            if (promise && ttCfg.usePromise) {
                promise.then(function () {
                    ads.isLoaded = true;
                }.bind(this)).catch(function (err) {
                    console.log("tt video fail=>", err)
                    ads.isLoaded = false;
                }.bind(this));
            }
        }
    }

    showVideo(ads: AdsData) {
        if (!bAdsUse) {
            SdkBase.prototype.showVideo.call(this, ads);
            return;
        }
        let adsInstance = ads.adInstance;
        if (adsInstance) {
            if (ttCfg.usePromise) {
                let promise = adsInstance.show();
                promise.then(() => {
                    ads.show(true);
                }).catch(() => {
                    console.log('头条  视屏广告加载失败');
                    ads.show(false);
                    ads.neespreLoad && this.preloadVedioAds();
                });
                return;
            } else {
                if (ads.isLoaded) {   //加载成功
                    ads.show(true);
                    adsInstance.show();
                    return;
                } else {
                    ads.show(false);
                    ads.neespreLoad && this.preloadVedioAds();
                }
            }
        } else {
            ads.show(false);
            if (ads.neespreLoad) {
                this.preloadVedioAds();
            } else {
                this.createVideoAds();
            }
        }
    }

    createInstalAds() {
        if (typeof (this._tt.createInterstitialAd) == "undefined") {
            console.log('tt 不存在插屏广告 api');
            return;
        }
        let ads = this.getAdsData(AdsType.INTERSTITIAL);
        if (!ads) return;
        let adsIndex = ads.adsIndex;
        let adId = ttCfg.InterstitialId[adsIndex % ttCfg.InterstitialId.length];
        let interstitialAd = this._tt.createInterstitialAd({
            adUnitId: adId
        });
        interstitialAd.onError((err) => {
            console.log('tt 插屏广告 错误', err);
            ads.isLoaded = false;
        });
        interstitialAd.onLoad(() => {
            ads.isLoaded = true;
        });
        interstitialAd.onClose((ret) => {
            console.log('关闭插屏广告=>', ret);
            ads.close(true);
            if (ttCfg.destroyInstl) {
                ads.adInstance.destroy();
                ads.adInstance = null;
            }
            ads.neespreLoad && this.preloadInstalAds();
        });
        ads.adInstance = interstitialAd;
    }

    preloadInstalAds() {
        let ads = this.getAdsData(AdsType.INTERSTITIAL);
        if (!ads.adInstance) {
            this.createInstalAds();
            return;
        }
        if (!ads.isLoaded && ads.adInstance) {
            let promise = ads.adInstance.load();
            if (promise && ttCfg.usePromise) {
                promise.then(() => {
                    ads.isLoaded = true;
                }).catch((err) => {
                    console.log('tt 插屏广告加载失败', err);
                    ads.isLoaded = false;
                });
            }
        }
    }

    showInstal(ads: AdsData) {
        let adsInstance = ads.adInstance;
        if (adsInstance) {
            if (ttCfg.usePromise) {
                let promise = adsInstance.show();
                promise.then(() => {
                    ads.show(true);
                }).catch(() => {
                    console.log('tt  插屏广告显示失败');
                    ads.show(false);
                    ads.neespreLoad && this.preloadInstalAds();
                });
                return;
            } else {
                if (ads.isLoaded) {
                    ads.adInstance.show();
                    ads.show(true);
                } else {
                    ads.show(false);
                    ads.neespreLoad && this.preloadInstalAds();
                }
            }
        } else {
            ads.show(false);
            if (ads.neespreLoad) {
                this.preloadInstalAds();
            } else {
                this.createInstalAds();
            }
        }
    }

    createBanner(style?: any) {
        if (typeof (this._tt.createBannerAd) == "undefined") {
            console.log("头条 不存在 createBanner 函数");
            return;
        }
        //初始化banner 广告
        let sysInfo = this.getSystemInfoSync();
        let screenWidth = sysInfo.screenWidth;
        let screenHeight = sysInfo.screenHeight;
        let ads = this.getAdsData(AdsType.BANNER);

        if (ads.adInstance) {
            ads.adInstance.destroy();
            ads.adInstance = null;
        }

        style = style || {};
        for (let key in ttCfg.style) {
            style[key] = ttCfg.style[key];
        }
        let bannerWidth = style.width;
        if (bannerWidth <= 1) {    //按屏幕宽度百分比计算宽度
            bannerWidth = screenWidth * bannerWidth;
        }

        let bannerHeight = style.height;
        let mp = style.mp || 1;

        let adsIndex = ads.adsIndex;
        let adId = ttCfg.bannerIds[adsIndex % ttCfg.bannerIds.length];

        let top = mp == 1 ? (screenHeight - bannerHeight) : (mp == 2 ? 0 : style.top);
        let left = style.isCenter ? screenWidth / 2 - bannerWidth / 2 : style.left;
        let bannerAds = this._tt.createBannerAd({
            adUnitId: adId,
            adIntervals: ttCfg.adIntervals,
            style: {
                left: left,
                top: top,
                width: bannerWidth
            }
        });

        ads.style.height = bannerHeight;
        ads.style.width = bannerWidth;
        ads.style.left = left;
        ads.style.top = top;
        ads.style.realWidth = bannerWidth;
        ads.style.realHeight = bannerHeight;

        bannerAds.onResize((res) => {
            var bannerAdHeight = res.height;
            var bannerAdWidth = res.width;
            bannerAds.style.top = mp == 1 ? (screenHeight - bannerAdHeight) : (mp == 2 ? 0 : style.top);
            bannerAds.style.left = style.isCenter ? screenWidth / 2 - bannerAdWidth / 2 : style.left;
            ads.style.realWidth = bannerAdWidth;
            ads.style.realHeight = bannerAdHeight;
            ads.style.left = bannerAds.left;
            ads.style.top = bannerAds.top;
        });
        let self = this;
        bannerAds.onLoad(() => {
            ads.isLoaded = true;
        });

        bannerAds.onError((err) => {
            console.log('头条 banner 加载错误=>', err);
            ads.isLoaded = false;
        })

        ads.adInstance = bannerAds;
        bannerAds && bannerAds.hide();
    }

    reLoadBanner() {
        let ads = this.getAdsData(AdsType.BANNER);
        if (ads.loadCount < ttCfg.error_count) {
            ads.adsIndex++;
            if (ads.adInstance) {
                ads.adInstance.hide();
                ads.adInstance.destroy();
                ads.adInstance = null;
            }
            this.createBanner();
        }
    }

    showBanner(style?: any) {
        if (!bAdsUse) return;
        if (!this._adsCfg.switch || this._adsCfg.ver == this.version) {
            return;  //版本相同,过审,不显示banner
        }

        this.bannerhided = false;
        let ads = this.getAdsData(AdsType.BANNER);

        if (!ads.adInstance) {
            this.createBanner(style);
        }
        else {
            if (this.bannerhided)
                return;

            ads.adInstance.show();
        }
    }

    hideBanner(need_pre_create: boolean = true, style?: any) {
        this.bannerhided = true;
        let ads = this.getAdsData(AdsType.BANNER);
        if (ads.adInstance) {
            ads.adInstance.hide();
            ads.adInstance.destroy();
            ads.adInstance = null;
        }
    }

    hide_banner() {
        this.bannerhided = true;
        let ads = this.getAdsData(AdsType.BANNER);
        if (ads.adInstance) {
            ads.adInstance.hide();
            //ads.adInstance.destroy();
            //ads.adInstance = null;
        }
    }

    bannerMoveBy(top: number = 80, left: number = 0) {
        let ads = this.getAdsData(AdsType.BANNER);
        if (ads.adInstance) {
            ads.adInstance.style.top += top;
            ads.adInstance.style.left += left;
        }
    }

    bannerMoveTo(top: number = 0, left: number = 0) {
        let ads = this.getAdsData(AdsType.BANNER);
        if (ads.adInstance) {
            let sysInfo = this.getSystemInfoSync();
            let screenWidth = sysInfo.screenWidth;
            let screenHeight = sysInfo.screenHeight;
            if(top == 0)
            {
                ads.adInstance.style.top = screenHeight - ads.style.realHeight;
            }
            else {
                top = screenHeight * top / Laya.stage.height;
            ads.adInstance.style.top = top;
            //ads.adInstance.style.left = left;
            }
        }
    }

    /*************************************  录屏功能   *********************/
    private _isVideoAlreadyShared: boolean = false;
    private _recordRes: any = null;
    startRecord() {
        if (typeof (this._tt.getGameRecorderManager) == "undefined") {
            console.log('头条 startRecord 不存在录屏 api');
            return;
        }
        console.log('recording~~~')
        this._isVideoAlreadyShared = false;
        this._recordRes = null;
        const recorder = this._tt.getGameRecorderManager();
        recorder.start({
            duration: 60,
        })

        recorder.onStart(res => {
            console.log("头条 录屏开始")
        })

        // 记录时间
        var startTime = new Date().getTime();

        recorder.onStop((res) => {
            console.log("recording onstop!!")
            var endTime = new Date().getTime();
            if ((endTime - startTime) / 1000 < 3) {
                //Utils.tips("录屏时间过短!");
                console.log("头条 录屏时间过短!");
            }
            else {
                this._recordRes = res;
            }
        })
    }
    shareRecordVideo(call) {
        if (!this._recordRes) {
            this._tt.showToast({ title: '录屏时间过短 无法分享!!!' });
            return;
        }
        let self = this;
        this._tt.shareAppMessage({
            channel: 'video',
            title: ShareCfg.name,
            desc: ShareCfg.desc,
            imageUrl: ShareCfg.imageUrl,
            query: '',
            extra: {
                videoPath: this._recordRes.videoPath, // 可用录屏得到的视频地址
                videoTopics: [ShareCfg.desc]
            },
            success() {
                call && call(true);
                self._recordRes = null;
                self._isVideoAlreadyShared = true;
            },
            fail(e) {
                call && call(false);
            }
        })
    }

    stopRecording() {
        if (typeof (this._tt.getGameRecorderManager) == "undefined") {
            console.log('头条 stopRecording 不存在录屏 api');
            return;
        }
        const recorder = this._tt.getGameRecorderManager();
        recorder.stop();
    }


}