import EventManager from "../../common/EventManager";
import { EventCfg } from "../../scripts/defines/ResDef";

export default class ExportMoreGame extends Laya.Script {
    private isClick: boolean = false;
    private showData: any[] = [];
    private unShowData: any[] = [];
    private showNum: number = 0;
    constructor() {
        super();
    }

    onAwake() {
        (this.owner as Laya.Sprite).height = Laya.stage.height;
        (this.owner as Laya.Sprite).x = -720;
        let Imgbg = this.owner.getChildByName("ImgListBg") as Laya.Image;
        let adList = this.owner.getChildByName("adList") as Laya.List;
        let closebtn = this.owner.getChildByName("btnclose") as Laya.Button;

        // closebtn.bottom = 334;
        // Imgbg.bottom = 204;
        // adList.y = Imgbg.y + 10;
    }

    onEnable() {
        let closebtn = this.owner.getChildByName("btnclose") as Laya.Button;
        closebtn.on(Laya.Event.CLICK, this, this.onClose);
    }
    tweenshow(yloc: number) {
        // let adList = this.owner.getChildByName("adList") as Laya.List;
        // adList.x = -adList.width;
        // Laya.Tween.to(adList, {x: 0, y: adList.y},3000,null,Laya.Handler.create(this,() => {}),1000);

        let Imgbg = this.owner.getChildByName("ImgListBg") as Laya.Image;
        Imgbg.y = yloc - 520 - 52;
        let adList = this.owner.getChildByName("adList") as Laya.List;
        adList.y = Imgbg.y + 10;
        let closebtn = this.owner.getChildByName("btnclose") as Laya.Clip;
        closebtn.index = 0;
        closebtn.y = yloc - 52;
        Laya.Tween.to((this.owner as Laya.Sprite), { x: 0 }, 500, null, Laya.Handler.create(this, () => {
            closebtn.index = 1;
        }), 0);
    }
    onClose() {
        Laya.Tween.to((this.owner as Laya.Sprite), { x: -620 }, 500, null, Laya.Handler.create(this, () => {
            (this.owner as Laya.Sprite).visible = false;
            EventManager.Ins.event(EventCfg.EVENT_SHOW_EXPORTGAME);
        }), 0);
    }
}