import { ui } from "../ui/layaMaxUI";
import UITool from "../common/UITool";
import UserData from "../common/data/UserData";
import GoldSp from "./prefab/GoldSp";
import gSdk from "../common/platformsdk/platform";
import UtilTool from "../common/UtilTools";
import display from "../common/Display";
import adSdk from "../common/adDataSdk/adDataSdkFactory";
import AdTool from "../common/AdTool";
import AdList from "./zs/AdList";
import ZsPlayInfo from "./zs/ZsPlayInfo";
import { WxBannerScreen } from "../common/platformsdk/plat-def";
import { navigateSceneType } from "../common/adDataSdk/adDataSdkDef";
import EventManager from "../common/EventManager";
import { EventCfg } from "../scripts/defines/ResDef";

export default class UIAdwardBox extends ui.commonUI.UIAdwardBoxUI {
    private _isRunning: boolean = false;
    /**每次点击 增加的长度 */
    private _up_step: number = 0.1;
    /**进度回退的步长 */
    private _down_step: number = 0.02;
    /**进度 */
    private _progress: number = 0;
    /**结束百分比 */
    private _endpercent: number = 0.85;
    /**点击时间 */
    private _click_time: number = 0;
    /**手指帧动画索引 */
    private _index: number = 0;

    private _callback: Function = null;

    private _isHide: boolean = false;
    /**点击进度条完成 */
    private _isClickComplete: boolean = false;
    private _isAppShow: boolean = false;
    private _isClosed: boolean = false;

    constructor() {
        super();
        this.height = Laya.stage.height;
        this.closeBtn.on(Laya.Event.MOUSE_DOWN, this, this.closeDown);
        this.closeBtn.on(Laya.Event.MOUSE_UP, this, this.closeUp);

        if (gSdk.isQQ) {
            this.boxIcon.on(Laya.Event.MOUSE_DOWN, this, this.boxDown);
            this.boxIcon.on(Laya.Event.MOUSE_UP, this, this.boxUp);
            this.boxIcon.y = Laya.stage.height / 2;
            this.fingerImg.y = this.boxIcon.y + 20;
            this.tipImg.y = this.boxIcon.y + 200;
            this.boxBg.y = this.boxIcon.y;
            this.Imgbgpro.y = this.boxIcon.y - 300;
            this.ImgTitle.y = this.boxIcon.y - 400;
            this.closeBtn.visible = true;
            //this.clickBtn.visible = true;

            this.clickBtn.on(Laya.Event.MOUSE_DOWN, this, this.boxDown);
            this.clickBtn.on(Laya.Event.MOUSE_UP, this, this.boxUp);
            this._endpercent = adSdk.sdk.adConfig.GetCrazyPercent();
            this._up_step = adSdk.sdk.adConfig.electControl("zs_click_award_add");
            this._down_step = adSdk.sdk.adConfig.electControl("zs_click_award_back");
        }
        else if (gSdk.isTT) {
            this.closeBtn.visible = false;
            this.ImgTitle.skin = "common/tt-08.png";
            this.ImgTitle.x = 65;
            this.ImgTitle.y = 190;
            this.Imgbgpro.y = 755;
            this.boxIcon.y = 402 + 125;
            this.boxBg.y = this.boxIcon.y;
            this.fingerImg.y = 880;
            this.clickBtn.visible = false;
            this.Btn_tt_tap.visible = true;
            this.Chk_tt_tap.visible = true;
            this.Btn_tt_tap.on(Laya.Event.CLICK, this, this.OnCrazyTap);
            this.Chk_tt_tap.on(Laya.Event.CLICK, this, this.OnChkTapClick);
        }
        else if (gSdk.isWX) {
            this.taptip.visible = true;
            this.boxBg.visible = false;

            this.ImgTitle.skin = "common/QQ-46.png";
            this.ImgTitle.x = 54;
            this.ImgTitle.y = 96;

            this.boxIcon.skin = "common/QQ-47.png";
            this.boxIcon.x = 206 + 155;
            this.boxIcon.y = 328 + 137;

            this.Imgbgpro.skin = "common/QQ-48.png";
            this.Imgbgpro.x = 207;
            this.Imgbgpro.y = 894;

            this.prossImg.skin = "common/QQ-49.png";
            this.prossImg.x = 0;
            this.prossImg.y = 0;

            this.Btn_tt_tap.visible = true;
            this.Btn_tt_tap.skin = "common/QQ-50.png";
            this.Btn_tt_tap.x = 186;
            this._endpercent = adSdk.sdk.adConfig.GetCrazyPercent();
            this._up_step = adSdk.sdk.adConfig.electControl("zs_click_award_add");
            this._down_step = adSdk.sdk.adConfig.electControl("zs_click_award_back");
            this.Btn_tt_tap.y = Laya.stage.height - 150;
            this.fingerImg.y = this.Btn_tt_tap.y + 65;

            this.Btn_tt_tap.on(Laya.Event.MOUSE_DOWN, this, this.boxDown);
            this.Btn_tt_tap.on(Laya.Event.MOUSE_UP, this, this.boxUp);
        }

        if (gSdk.isWX) {
            EventManager.Ins.on(EventCfg.EVENT_BG_TO_GAME, this, this.Bgtogame);
            EventManager.Ins.on(EventCfg.EVENT_GAME_TO_BG, this, this.gametoBg);
        }

        //this.closeBtn.y = Laya.stage.height - 245;

        //EventManager.addEvent(GameEvent.GAME_SHOW,this.onGameShow,this);
        //EventManager.addEvent(GameEvent.GAME_HIDE,this.onGameHide,this);
    }
    Bgtogame() {
        console.log("能量界面从后台切换回游戏");
        this.complete();
    }
    gametoBg() {
        console.log("能量界面切换到后台");
        this.timer.clearAll(this);
    }

    onGameShow() {
        if (this._isAppShow) return;
        if (this._isClickComplete) {
            this.timer.once(500, this, () => {
                this.complete();
            })
        }
    }

    onGameHide() {
        this._isHide = true;
        if (this._isClickComplete) {
            this.timer.clearAll(this);
        }
    }

    private startRun() {
        this.timerLoop(50, this, this.update);
        this._isRunning = true;
    }

    private stop() {
        this.clearTimer(this, this.update);
        this._isRunning = false;
        Laya.Tween.clearTween(this.boxIcon);
    }

    private complete() {
        if (gSdk.isWX) {
            gSdk.sdk.hideBannerzs();
            gSdk.sdk.endMistake();
        }
        UserData.inst.setGold(100);

        this.getChildByName("GoldSp").getComponent(GoldSp).updateUI();
        //UserData.useGold(100);
        let x = this.boxIcon.x;
        let y = this.boxIcon.y;
        // this.goldEffect(new Laya.Vector2(x,y),10,() => {
        //     this.gold.text = `${UserData.getGold()}`;
        //     this.closeView();
        // });
        UITool.Ins.goldEffect(new Laya.Vector2(x, y), 10, this, () => { this.closeView(); });
    }

    onOpened(params) {
        //this._callback = params.call;
    }

    closeView() {
        if (this._isClosed) return;
        this._isClosed = true;
        this.timer.clearAll(this);
        //gSdk.sdk.hideBanner();
        //EventManager.removeEvent(GameEvent.GAME_HIDE,this,this.onGameHide);
        //EventManager.removeEvent(GameEvent.GAME_SHOW,this,this.onGameShow);
        //this._callback && this._callback();
        this.close();
    }

    goldEffect(pt: Laya.Vector2, goldCount: number = 10, callback: Function = null) {
        let count = goldCount;
        for (let i = 0; i < goldCount; i++) {
            let sprite = new Laya.Sprite();
            sprite.loadImage("adwardbox/1-03.png");
            let ptc = UtilTool.Math.randomCircle(10);
            sprite.x = pt.x + ptc.x;
            sprite.y = pt.y + ptc.y;
            sprite.scale(0.8, 0.8);
            this.addChild(sprite);
            let pos;
            if (gSdk.isWX) {
                pos = { x: Laya.stage.width - 200, y: 10 };
            }
            else {
                pos = { x: 20, y: 90 };
            }
            Laya.Tween.to(sprite, pos, 500, Laya.Ease.bounceIn, Laya.Handler.create(this, () => {
                sprite.removeSelf();
                count--;
                if (count <= 0) {
                    callback && callback();
                }
            }), 50 * i);
        }
    }

    closeDown(e: Laya.Event) { e.stopPropagation(); this.closeView(); }
    closeUp(e: Laya.Event) { e.stopPropagation(); }

    boxDown(e: Laya.Event) {
        e.stopPropagation();
        this._progress += this._up_step;
        if (this._progress > 1) this._progress = 1;
        if (!this._isRunning) this.fresh();
        this._click_time = new Date().getTime();
    }
    boxUp(e: Laya.Event) { e.stopPropagation(); }

    private update() {
        this.fresh();
        let ttime = new Date().getTime();
        let interval_t = ttime - this._click_time;
        if (interval_t >= 50) {
            this._progress -= this._down_step;
            if (this._progress < 0 || isNaN(this._progress)) this._progress = 0;
        }

        if (this._index % 2 == 0) this.fingerImg.skin = this.fingerImg.skin == "common/fingerup.png" ? "common/fingerdown.png" : "common/fingerup.png"

        if (this._progress >= this._endpercent) {
            this.stop();
            if (gSdk.isWX) {
                gSdk.sdk.showBannerzs();
            } else if (gSdk.isQQ) {
                if (gSdk.sdk.isUseZS) {
                    // if (adSdk.sdk.adConfig.CrazyTapVideo()) {
                    //     //看视频
                    //     AdTool.Ins.playVideo("", () => { }, this, () => { });
                    // }
                    // else {
                    //     gSdk.sdk.showAppBoxAd();
                    // }
                    gSdk.sdk.showAppBoxAd();
                }
                else {
                    if (adSdk.sdk.adConfig.checkControl('zs_over_cell_ad', true)) {
                        // gSdk.sdk.showAds(AdsList.APPBOX,(state) => {
                        //     state && (this._isAppShow = true);
                        // },(state) => {
                        //     this._isAppShow = false;
                        //     //this.closeView();
                        // },null);
                        gSdk.sdk.showAppBoxAd();
                    }
                    else if (adSdk.sdk.adConfig.checkControl('zs_over_banner_ad', true)) {
                        gSdk.sdk.showBanner({
                            isCenter: true,      //是否居中显示
                            left: 100,           //离左边的距离
                            mp: 2,               //是否是底部   mp 1=> 底部 ,2=> 自由高度   3=> 顶部
                            width: 0.8,          //banner 宽度  当小于1 时 适配屏幕宽度的比例     
                            top: display.designToScreen(0, this.boxIcon.y).y,              //广告顶部距离
                            bh: 70,              //广告默认高度    
                        });
                    }
                }
            }
            this._isClickComplete = true;
            if (gSdk.isQQ)
                this.boxIcon.skin = "common/QQ-03.png";
            Laya.timer.once(1500, this, () => {
                //if(this._isAppShow) return;
                this.complete();
            })
        }
        this._index++;
    }

    private fresh() {
        this.prossImg.scaleX = this._progress;
    }

    private shake() {
        Laya.Tween.to(this.boxIcon, { rotation: 30 }, 50, null, new Laya.Handler(this, () => {
            Laya.Tween.to(this.boxIcon, { rotation: -30 }, 100, null, new Laya.Handler(this, () => {
                Laya.Tween.to(this.boxIcon, { rotation: 0 }, 50, null, new Laya.Handler(this, () => {
                    this.shake();
                }))
            }))
        }), 1000);
    }

    private onShow() {
        if (this._isAppShow) {

        }
    }

    private onHide() {

    }

    InitHorGames() {
        let electList: Laya.Image = this.getChildByName("exportList1") as Laya.Image;
        if (electList) {
            electList.visible = true;
            if (!electList.getChildAt(0).getComponent(AdList)) {
                let adList: AdList = electList.getChildAt(0).addComponent(AdList);
                adList.requestAdData(navigateSceneType.scroll, true, ZsPlayInfo.SCROLL_HORIZONTAL, null, null, false, true);
            }
        }
    }
    onEnable() {
        //UserData.addBoxCount();
        //EventManager.addEvent(GameEvent.BOX_SHOW,this.onShow,this);
        //EventManager.addEvent(GameEvent.BOX_HIDE,this.onHide,this)
        // Platform.hideBanner(false,true,{
        //     isCenter: true,      //是否居中显示
        //     left: 100,           //离左边的距离
        //     mp: 2,               //是否是底部   mp 1=> 底部 ,2=> 自由高度   3=> 顶部
        //     width: 0.8,          //banner 宽度  当小于1 时 适配屏幕宽度的比例     
        //     top:display.designToScreen(0,this.boxIcon.y).y,              //广告顶部距离
        //     bh: 100,              //广告默认高度  
        // });
        //this.gold.text = `${UserData.getGold()}`;

        if (gSdk.isQQ) {
            gSdk.sdk.hide_banner();
            this.startRun();
        }
        else if (gSdk.isTT) {
            this._down_step = 0;
        }
        else if (gSdk.isWX) {
            gSdk.sdk.hideBannerzs();
            gSdk.sdk.beginMistake();
            this.getChildByName("GoldSp")["y"] = 10;
            this.getChildByName("GoldSp")["x"] = Laya.stage.width - 200;
            this.InitHorGames();
            this.startRun();
        }
        //this.bg.height = Laya.stage.height; 
        this._progress = 0;
        this._click_time = 0;
        //this.isShowedBanner=false;
        let isqq: boolean = gSdk.isQQ;//Sdk.getIns().platform=="qq";
        if (gSdk.isQQ)
            this.closeBtn.visible = adSdk.sdk.adConfig.checkControl('zs_qq_close_show', true);
        else
            this.closeBtn.visible = isqq;
        this.fingerImg.visible = isqq;
        this.tipImg.visible = isqq;
        this.shake();
        this._isClickComplete = false;
        this._isHide = false;
        this._isAppShow = false;
        this._isClosed = false;
        //PlayerData.getIns().awardBoxShowAdd();
    }

    onDisable() {
        if (gSdk.isWX) {
            EventManager.Ins.off(EventCfg.EVENT_BG_TO_GAME, this, this.Bgtogame);
            EventManager.Ins.off(EventCfg.EVENT_GAME_TO_BG, this, this.gametoBg);
        }
        //EventManager.removeEvent(GameEvent.BOX_SHOW,this,this.onShow);
        //EventManager.removeEvent(GameEvent.BOX_HIDE,this,this.onHide)
        this.stop();
        Laya.stage.event("QQ_CLICK_BOX");
        //Laya.Scene.open(UICfg.ResultView, false, true)
    }

    private bShowVideo = false;
    OnCrazyTap() {
        let self = this;
        if (self.Btn_tt_tap.skin == "common/tt-09.png") {
            self._progress += self._up_step;
            if (self._progress >= 0.9) {
                self.Btn_tt_tap.skin = "common/tt-10.png";
                self.boxIcon.skin = "common/QQ-03.png";
                self.Chk_tt_tap.visible = true;
                self._progress = 1;
            }
            self.prossImg.scaleX = self._progress;
        }
        else {
            if (this.Chk_tt_tap.skin == "common/tt-12.png") {
                if (!self.bShowVideo) {
                    self.bShowVideo = true;
                    let x = this.boxIcon.x;
                    let y = this.boxIcon.y;
                    AdTool.Ins.playVideo("观看完整视频才能获得金币", () => {
                        UserData.inst.setGold(100);

                        this.getChildByName("GoldSp").getComponent(GoldSp).updateUI();
                        UITool.Ins.goldEffect(new Laya.Vector2(x, y), 10, this, () => { this.close(); });
                        //UITool.Ins.goldEffect(new Laya.Vector2(x, y), 10, self);
                        //this.close();
                    }, self, () => { self.bShowVideo = false; this.close() });
                }
            }
            else this.close();
        }
    }

    OnChkTapClick() {
        if (this.Chk_tt_tap.skin == "common/tt-12.png")
            this.Chk_tt_tap.skin = "common/tt-11.png";
        else
            this.Chk_tt_tap.skin = "common/tt-12.png";
    }

}