import GameScene from "./GameScene";
import Utils3D from "../scripts/mgr/Utils3D";
import  tEnemy  from "../scripts/mgr/Utils3D";
import Constant from "../scripts/mgr/Constant";
import GameView from "./GameView";

export default class Road{
    private _gameScene: GameScene = GameScene.getInstance();
    public _scene = null;
    public levelLh = null;
    playerPos = new Laya.Vector3();
    obstLh1 = null;
    obstLh2 = null;
    clouds = null;
    enemyLh = null;
    enemies = [];//[{"sprite":null,"bullet":0,"tag":0}]; //敌人数据
    bulletNode = null; 
    blh = null;
    enemyNum = 1;
    dyLh = null;
    effectLh = null;
    constructor(scene,lvNode){
        this._scene = scene
        this.levelLh = lvNode;
        this.obstLh1 = this._scene.getChildByName("zhangai1_sui") as Laya.Sprite3D;
        this.obstLh1.active = false;
        this.obstLh2 = this._scene.getChildByName("zhangai2_sui") as Laya.Sprite3D;
        this.obstLh2.active = false;
        var bgs = this._scene.getChildByName("bgs");
        this.clouds = bgs.getChildByName("yun");
        //敌人子弹
        this.blh = this._scene.getChildByName("zidan2");
        this.blh.active = false;
        this.dyLh = this._scene.getChildByName("danyao");
        var lhStr = "LayaScene_PlayerMode/Conventional/renwu2.lh";
        Laya.loader.create(lhStr, Laya.Handler.create(this, function (node) {
            //this.enemyLh = node.cloe() as Laya.Sprite3D;
            this.onLevelLoaded(node);
        }));
        //打中障碍物特效
        this.effectLh = this._scene.getChildByName("tx_daqiang");
        var road = this.levelLh.getChildByName("road");
        for(var i=0;i<road.numChildren;i++){
            var cNode = road.getChildAt(i);
            if(cNode.name != "di4" && cNode.name != "di5" && cNode.name != "edge"){
                cNode.meshRenderer.receiveShadow = true;
            }
        }
        //var di3 = road.getChildByName("di3");
        //di3.addComponent(GameView);
    }

    //初始化敌人及敌人子弹数量
    onLevelLoaded(node){ 
        this.enemyNum = Utils3D._curData['enemy'].length;
        this.curEnemyNum = this.enemyNum;
        var enemy = null;
        this.enemies = [];
        var eBody = null;
        for (var i = 0; i < this.enemyNum; i++) {
            enemy = (node as Laya.Sprite3D).clone();
            enemy.active = true;
            enemy.name = i;
            enemy.bullet = 100;
            enemy.bShooting = false;
            enemy.bInDistance = false; //是否在范围内敌人开枪
            let cPos = Utils3D._curData['enemy'][i].pos;
            enemy.transform.position = new Laya.Vector3(cPos.x, cPos.y, cPos.z);
            this.levelLh.addChild(enemy);
            this.enemies.push(enemy);
            Utils3D.AniCrossFade(enemy,"daiji",0.2);
            eBody = enemy.getChildAt(0) as Laya.SkinnedMeshSprite3D;
            eBody.skinnedMeshRenderer.castShadow = true;
        }

        var dyNum = Utils3D._curData['danyao'].length;
        var dy = null; //子弹补给
        for(var j = 0;j < dyNum;j++){
            dy = this.dyLh.clone();
            dy.active = true;
            let cPos = Utils3D._curData['danyao'][j].pos;
            dy.transform.position = new Laya.Vector3(cPos.x, cPos.y, cPos.z);
            this.levelLh.addChild(dy);
            dy.getChildAt(0).meshRenderer.castShadow = true;
        }

         //var bgs = this._scene.getChildByName("bgs");
        // bgs.active = false;
        //var jz = bgs.getChildByName("jz");
        //jz.active = false;
        //var ps1 = this._scene.getChildAt(1);
        //ps1.active = false;
        // var road = this.levelLh.getChildByName("road");
        // road.active = false;
        // var z1 = this.levelLh.getChildByName("zhangai1");
        // z1.active = false;
        // var z2 = this.levelLh.getChildByName("zhangai2");
        // z2.active = false;
        var z3 = this.levelLh.getChildByName("zhangai3");
        //z3.active = false;
        if(z3){
            var numNode = z3.getChildByName("yuan") as Laya.Sprite3D;
            for(var i=2;i<5;i++){
                numNode.getChildAt(i).active = false;
            }
        }
    }

    bObst3Fall = false;
    obstacle3 = null;
    curObstNum = 5;
    effectObs = null;
    onHitObstacles(node,hitBody){
        if(this.effectObs){
            this.effectObs.destroy();
            this.effectObs = null;
        }
        this.effectObs = this.effectLh.clone();
        this.effectObs.transform.position = hitBody.transform.position;
        this.levelLh.addChild(this.effectObs);
        this.effectObs.active = true;

        if(node.name == "zhangai3"){
            if(this.curObstNum > 1){
                this.curObstNum--;
                var numNode = node.getChildByName("yuan") as Laya.Sprite3D;
                for(var i=1;i<6;i++){
                    numNode.getChildAt(i).active = false;
                }
                var curNumSp = numNode.getChildAt(this.curObstNum) as Laya.Sprite3D;
                curNumSp.active = true;
                curNumSp.transform.localPositionY = 1;
                node.getChildAt(6-this.curObstNum).active = false;
                Laya.timer.once(500,this,function(){
                    node["bHit"] = false;
                });
            }else{
                //node.active = false;
                this.obstacle3 = node as Laya.Sprite3D;
                this.bObst3Fall = true;
            }
        }else{
            node.active = false;
            var obst = node.name == "zhangai1"?this.obstLh1.clone():this.obstLh2.clone();
            obst.transform.position = node.transform.position;
            this.levelLh.addChild(obst);
            obst.active = true;
            Laya.timer.once(1000,this,function(){
                obst.active = false;
            })
        }
    }

    curEnemyNum = 1;
    updating(){
        if(this.clouds.transform.localPositionX <= 135){
            this.clouds.transform.localPositionX += 0.02;
        }
        this.count++;
        this.showBullet();
        if(this.bObst3Fall){
            if(this.obstacle3.transform.localPositionY > -4){
                this.obstacle3.transform.localPositionY -= 0.1;
            }else{
                this.bObst3Fall = false;
                this.obstacle3.active = false;
            } 
        }
    }

    createBullet(){
        var bullet = this.blh.clone() ;
        bullet.active = true;
        bullet.transform.position = this.from;//new Laya.Vector3(this.playerPos.x,this.playerPos.y+2,this.playerPos.z);
        bullet.addComponent(GameView);
        var trailLh = bullet.getChildAt(0) as Laya.TrailSprite3D;
        var tFilter = trailLh.trailFilter as Laya.TrailFilter;
        tFilter.alignment = 1;
        return bullet; 
    }

    ePos = null;
    count=0;
    from = null;
    to = null;
    bbbs = [];
    curShootNum = 0;//正在射击的敌人数量
    showBullet(){ //创建敌人子弹
        var playerZ = Utils3D.curPlayer.transform.position.z;
        if(this.curEnemyNum > 0 && playerZ > 0){
            if(this.count%20 == 0){ //检测距离
                for(var i = 0;i<this.enemyNum;i++){
                    if(this.enemies[i].transform.position.z < playerZ+20 && !this.enemies[i].bInDistance){
                        this.enemies[i].bInDistance = true;
                        //console.log("bInDistance == true "+this.enemies[i].bInDistance +" : "+i);
                    }

                    if(playerZ >= this.enemies[i].transform.position.z && this.enemies[i].bShooting){
                        this.enemies[i].bShooting = false;
                        this.enemies[i].bullet = 0;
                        this.curEnemyNum--;
                        this.curShootNum--;
                        //console.log("超过敌人 this.curEnemyNum  == "+this.curEnemyNum+" curShootNum = "+this.curShootNum+" index  == "+i);
                    }
                }
            }
            if(this.count%100==0){
                for(var i = 0;i<this.enemyNum;i++){
                    if(this.enemies[i].bullet > 0 && this.enemies[i].bInDistance
                        && !this.enemies[i].bShooting){ //开枪动画
                        Utils3D.AniCrossFade(this.enemies[i],"d_kaiqiang1",0.2);
                        //console.log("bShooting == true "+this.enemies[i].bShooting +" : "+i);
                        this.curShootNum++;
                        this.enemies[i].bShooting = true;
                    }
                }
            }else if(this.count%120==0){
                var pos = null;
                for(var i = 0;i<this.enemyNum;i++){
                    if(this.enemies[i].bullet > 0 && this.enemies[i].bInDistance && this.enemies[i].bShooting){
                        //发射子弹
                        this.ePos = this.enemies[i].transform.position;
                        this.count = 0;
                        this.enemies[i].bullet--;
                        this.from = new Laya.Vector3(this.ePos.x,this.ePos.y+1.6,this.ePos.z-1);
                        var bullet = this.createBullet();
                        this.bbbs.push(bullet);
                        this.levelLh.addChild(bullet);
                        this.to = new Laya.Vector3(this.from.x,this.from.y,this.from.z-10);
                        Utils3D.tweenTo(bullet,this.to,2000,Laya.Handler.create(this,function(){
                            var len = this.bbbs.length;
                            var rLen = len==this.curShootNum?this.curShootNum:len-this.curShootNum; 
                            for(var i = 0;i<rLen;i++){
                                if(this.bbbs[i]){
                                    this.bbbs[i].destroy();
                                    this.bbbs.splice(i,1);
                                } 
                            }
                        }));
                        
                    }
                } 
            }
        }
    }

    onHitEnemy(node){ //击中敌人
        if(node.parent){
            //node.parent.active = false;
            if(this.effectObs){
                this.effectObs.destroy();
                this.effectObs = null;
            }
            this.effectObs = this.effectLh.clone();
            this.effectObs.transform.position = node.parent.transform.position;
            this.levelLh.addChild(this.effectObs);
            this.effectObs.active = true;

            this.curEnemyNum--;
            this.curShootNum--;
            var str = "si"+Utils3D.random(1,4);
            Utils3D.AniCrossFade(node.parent,str,0.2,0);
            Utils3D.playParticle(node.parent,true,3);
            var index = node.parent.name;
            this.enemies[index].bullet = 0;
            this.enemies[index].bShooting = false;
            Laya.timer.once(1500,this,function(){
                if(node.parent) node.parent.active = false;
            });
        }
    }

    nextGame(){
        this.clouds.transform.localPositionX = 0;
        this.bbbs = [];
        this.curShootNum = 0;
        //Utils3D.tEnemy = {"sprite":Laya.MeshSprite3D,"bullet":0};
    }
}