import { adDataSdkBase } from "./adDataSdkBase";
import ADConfigQT from "./ADConfigQT";
import gSdk from "../platformsdk/platform";
import { navigateSceneType } from "./adDataSdkDef";
import ZsPlayInfo from "../../commonUI/zs/ZsPlayInfo";
import UtilTool from "../UtilTools";
import { AdsCfgBase, AdsCfgOppo, AdsCfgWx, AdsCfgQQ } from "../platformsdk/Adscfg-def";

export class adDataSdkQT extends adDataSdkBase {
    private config: ADConfigQT = null;
    private adData = [];
    private isGameFinish: boolean = false;
    private isCtrlFinish: boolean = false;

    private loadAdDataUrl = "https://cp.game.7to.cn/api/export/list";
    private loadAdDataBackupUrl = "https://cp2.game.7to.cn/api/export/list";
    private loadCtrCfgUrl = "https://cp.game.7to.cn/api/cfg/getcfg";
    private loadCtrCfgBackupUrl = "https://cp2.game.7to.cn/api/cfg/getcfg";
    private clickGameReqQiTuUrl = "https://cp.game.7to.cn/api/export/report";
    private clickGameReqQiTuBackupUrl = "https://cp2.game.7to.cn/api/export/report";
    private loadCtrErrorCount = 0;//后台开关加载错误次数
    private loadDataErrorCount = 0;//游戏导出加载错误次数

    private referrerAppId: string = "";
    private sceneValue: number = 0;
    private loginDT: number = 0;

    private navigateSuccessObj = {};//跳转成功app数据对象

    constructor() {
        super();

        this.config = new ADConfigQT();
        this.loginDT = new Date().getTime();
    }

    init(params?: any) {
        let self = this;
        this.navigateSuccessObj = {};

        //获得来源场景
        if (gSdk.isWX || gSdk.isTT || gSdk.isQQ) {
            //获取来源游戏，来源场景
            let lauchOpton = gSdk.sdk.getLaunchOptionsSync();
            if (lauchOpton) {
                let referrerInfo = lauchOpton["referrerInfo"];
                if (referrerInfo) {
                    this.referrerAppId = referrerInfo["appId"] ? referrerInfo["appId"] : "";//来源APPID
                }
                this.sceneValue = lauchOpton["scene"] || 0;
            }
        }

        //加载配置
        self.loadCtrErrorCount = 0;
        self.loadCtrCfg(function (data) {
            self.config.initAdSetting(gSdk.sdk.ver, data);
            self.initAdsCfg();
            self.isCtrlFinish = true;
        });

        //加载游戏
        self.loadDataErrorCount = 0;
        self.loadAdData(function (data) {
            self.adData = data;
            self.isGameFinish = true;
        });

        //登录获得userId/openid
        gSdk.sdk.getopenId((state, openid) => {
            // if (state) {
            // }
        });

    }

    /**初始化广告配置 */
    private initAdsCfg() {
        let params: AdsCfgBase = null;
        let ver = this.adConfig.electControl("zs_version");
        let switchVal = this.adConfig.electControl("zs_switch");
        if (gSdk.isWX) {
            let cfgObj = new AdsCfgWx(ver, switchVal);
            cfgObj.bgbannershow = this.adConfig.electControl("zs_banner_show");
            cfgObj.bgbannershowcount = this.adConfig.electControl("zs_banner_show_number");
            cfgObj.bginterstitialshow = this.adConfig.electControl("zs_interstitial_show");
            cfgObj.bggridshow = this.adConfig.electControl("zs_grid_show");
            cfgObj.bgexposureduration = this.adConfig.electControl("zs_banner_exposure_duration");
            params = cfgObj;
        }
        else if (gSdk.isOP) {
            let cfgObj = new AdsCfgOppo(ver, switchVal);
            cfgObj.bgshowad_inonemin = this.adConfig.electControl("zs_onemin_show_ad_switch");
            cfgObj.bgnative_exposure_num = this.adConfig.electControl("zs_start_exposure_num");
            params = cfgObj;
        }
        else if (gSdk.isQQ) {
            let cfgObj = new AdsCfgQQ(ver, switchVal);
            cfgObj.bgbannerrefreshtime = 8000;
            params = cfgObj;
        }
        else {
            params = new AdsCfgBase(ver, switchVal);
        }

        gSdk.sdk.initAdsCfg(params);
    }


    /**
    * 游戏数据是否加载完成
    */
    public get isLoadGameFinish(): boolean {
        return this.isGameFinish;
    }


    /**
    * 开关配置是否加载完成
    */
    public get isLoadCtrlFinish(): boolean {
        return this.isCtrlFinish;
    }

    public get adConfig() {
        return this.config;
    }

    private AdTypeObj: any = {
        [navigateSceneType.default]: "defaultData",
        [navigateSceneType.endPage]: "endPage",
        [navigateSceneType.float]: "floatData",
        [navigateSceneType.full1Page]: "full1Page",
        [navigateSceneType.full2Page]: "full2Page",
        [navigateSceneType.scroll]: "scrollData",
        [navigateSceneType.more]: "defaultData",
        [navigateSceneType.sidebar]: "defaultData",
        [navigateSceneType.fakeMsg]: "defaultData"
    }

    /**
     * 获得广告位类型
     * @param navScene 
     */
    public getAdType(navScene: navigateSceneType = navigateSceneType.default) {
        let adType = this.AdTypeObj[navScene];
        if (!adType) {
            adType = 'defaultData';
        }
        return adType;
    }

    public getAdList(adType: String = 'defaultData', maxNum?) {
        if (typeof this.adData == 'undefined') {
            console.log("getAdList adData is undefined, return");
            return [];
        }
        let data = this.adData[adType.toString()];
        if (!data) {
            if (adType.toString() != "defaultData") {
                data = this.adData["defaultData"];//未获取到数据时，再去获取一次默认数据
                if (!data) { return []; }
            } else {
                return [];
            }
        }

        //换天重置isplay
        let curTime = new Date().getTime();
        let day = UtilTool.Time.dayDiff(this.loginDT, curTime);
        if (day > 0) {
            this.loginDT = curTime;
            this.navigateSuccessObj = {};
        }

        if (maxNum != null) {
            if (data.length < maxNum) {
                let tlen = maxNum - data.length;
                for (let h: number = 0; h < tlen; h++) {
                    data.push(data[h]);
                }
            }
            else if (data.length > maxNum) {
                while (data.length > maxNum) {
                    data.splice(Math.floor(Math.random() * data.length), 1);
                }
            }
        }
        else {
            //随机调换8组游戏
            for (let i = 0; i < 8; i++) {
                let random1 = Math.floor(Math.random() * data.length);
                let random2 = Math.floor(Math.random() * data.length);
                if (random1 != random2) {
                    let tmpdata = data[random1];
                    data[random1] = data[random2];
                    data[random2] = tmpdata;
                }
            }
        }
        return data;
    }

    private setGamePlayed(data) {
        if (data && data.id)
            this.navigateSuccessObj[data.id] = 1;
    }

    /**
     * 小游戏跳转
     * @param data 
     * @param scene 
     * @param success 
     * @param fail 
     * @param complete 
     */
    navigateToMiniProgram(data, scene: navigateSceneType, success = null, fail = null, complete = null) {
        let self = this;
        gSdk.sdk.navigateToMiniProgram(data,
            (e) => {
                self.setGamePlayed(data);
                self.clickGameReq(data, scene, 3);
                success && success(e);
            },
            () => {
                fail && fail();
                if (gSdk.isWX)
                    Laya.stage.event(ZsPlayInfo.EVENT_NAVIGATE_FAILED, { msgFrom: scene });
            }, complete);
    }

    /**
     * 获得跳转成功数据
     */
    public getNavigateSuccessObj() {
        return this.navigateSuccessObj;
    }


    //加载导出游戏
    private loadAdData(func: Function = null, retryCount: number = 3, url: string = null, isTryBackupUrl: boolean = true) {
        let self = this;
        var requestUrl = url || self.loadAdDataUrl;
        let tappid = gSdk.sdk.getappId();
        let timestamp = Math.round(new Date().getTime() / 1000).toString();
        let sign = "";//MathTool.getIns().md5(tappid+"&"+timestamp);
        let sendData = { appid: tappid, scene: this.sceneValue, referr_appid: this.referrerAppId, timestamp: timestamp, sign: sign, game_ver: gSdk.sdk.ver }
        let xhr = new Laya.HttpRequest();
        xhr.http.timeout = 10000;//设置超时时间；
        xhr.once(Laya.Event.COMPLETE, self, (e) => {
            func && func(xhr.data.data);
        });
        xhr.once(Laya.Event.ERROR, self, (e) => {
            self.loadDataErrorCount++;
            if (self.loadDataErrorCount < retryCount) {
                console.log('loadAdData 重试' + self.loadDataErrorCount);
                self.loadAdData(func, retryCount, url, isTryBackupUrl);
                return;
            }
            if (isTryBackupUrl) {//是否尝试备用地址
                console.log('loadAdData 尝试备用');
                self.loadCtrCfg(func, retryCount, self.loadAdDataBackupUrl, false);
                return;
            }
            console.log('request error', e);
            func && func();
        });
        xhr.send(requestUrl, JSON.stringify(sendData), "post", "json", ["content-type", "application/json"]);
    }


    //加载奇兔游戏配置
    private loadCtrCfg(func: Function = null, retryCount: number = 3, url: string = null, isTryBackupUrl: boolean = true) {
        let self = this;
        var requestUrl = url || self.loadCtrCfgUrl;
        let tappid = gSdk.sdk.getappId();
        let timestamp = Math.round(new Date().getTime() / 1000).toString();
        let sign = ""//MathTool.getIns().md5(tappid+"&"+timestamp);
        let sendData = { appid: tappid, scene: this.sceneValue, referr_appid: this.referrerAppId, timestamp: timestamp, sign: sign, game_ver: gSdk.sdk.ver }
        let xhr = new Laya.HttpRequest();
        xhr.http.timeout = 10000;//设置超时时间；
        xhr.once(Laya.Event.COMPLETE, self, (e) => {
            //this._loadFinish = true;
            func && func(xhr.data.data);
        });
        xhr.once(Laya.Event.ERROR, self, (e) => {
            self.loadCtrErrorCount++;
            if (self.loadCtrErrorCount < retryCount) {
                console.log('loadCtrCfg 重试' + self.loadCtrErrorCount);
                self.loadCtrCfg(func, retryCount, url, isTryBackupUrl);
                return;
            }
            if (isTryBackupUrl) {//是否尝试备用地址
                console.log('loadCtrCfg 尝试备用');
                self.loadCtrCfg(func, retryCount, self.loadCtrCfgBackupUrl, false);
                return;
            }
            console.log('request error', e);
            //this._loadFinish = true;
            func && func();
        });
        xhr.send(requestUrl, JSON.stringify(sendData), "post", "json", ["content-type", "application/json"]);
    }

    /**
     * 
     * @param data 
     * @param scene 场景值
     * @param type 'type': 1-进入游戏上报，2-看完视频上报，3-跳出成功上报	
     */
    public clickGameReq(data, scene: number, type: number) {
        this.clickGameReqQiTu(data, scene);
    }

    /**奇兔点击导出位的图标上报 scene 1=界面底部导出=2全屏导出3=界面中间导出4=轮播图标导出*/
    private clickGameReqQiTu(data, scene: number, url: string = null, isTryBackupUrl: boolean = true) {
        if (Laya.Browser.window.conch) {//LayaNative
            return;
        }
        let self = this;
        var requestUrl = url || self.clickGameReqQiTuUrl;
        let openid = gSdk.sdk.userId;
        let appid = gSdk.sdk.getappId();
        if (openid) {
            let xhr = new Laya.HttpRequest();
            xhr.http.timeout = 10000;//设置超时时间；
            xhr.once(Laya.Event.COMPLETE, self, (e) => {
            });
            if (isTryBackupUrl) {//是否需要使用备用地址重试
                xhr.once(Laya.Event.ERROR, self, (e) => {
                    self.clickGameReqQiTu(data, scene, self.clickGameReqQiTuBackupUrl, false);
                });
            }
            let sendData = {
                appid: appid,
                user_id: openid,
                link_id: data.id,
                link_adv_appid: data.link_adv_appid || 0,
                nav_scene: scene,
                scene: this.sceneValue,
                referr_appid: this.referrerAppId,
                game_ver: gSdk.sdk.ver
            }
            xhr.send(requestUrl, JSON.stringify(sendData), "post", "json", ["content-type", "application/json"]);
        }
    }

}
