export default class HttpHelp {
    private static _instance: HttpHelp = null;
    static get Ins() {
        if (null == this._instance) {
            this._instance = new HttpHelp();
        }
        return this._instance;
    }

    public sendHttpPost(url, params, callback) {
        let xhr = new Laya.HttpRequest();
        //设置超时时间
        xhr.http.timeout = 10000;
        xhr.once(Laya.Event.COMPLETE, this, (e) => {
            let data = xhr.data;
            callback && callback(true, data);
        });
        xhr.once(Laya.Event.ERROR, this, (e) => {
            callback && callback(false);
        });
        xhr.send(url, JSON.stringify(params), "post", "txt", ["content-type", "application/json"]);
    }

    public sendHttpGet(url, params, callback) {
        let xhr = new Laya.HttpRequest();
        //设置超时
        xhr.http.timeout = 10000;
        xhr.once(Laya.Event.COMPLETE, this, (e) => {
            let data = xhr.data;
            callback && callback(true, data);
        });
        xhr.once(Laya.Event.ERROR, this, (e) => {
            console.log('http error:',e);
            callback && callback(false);
        });
        xhr.send(url, JSON.stringify(params), 'get', 'txt', ["content-type", "application/json"]);
    }

    /**
     * 
     * @param url 
     * @param data 数据格式:{ file: "games/sword.json"}
     * @param callback 
     */
    readHttpData(url, data, callback) {
        this.sendHttpPost(url || this.edit_url_read, data, (state, data) => {
            if (state) {
                let jdata = JSON.parse(data);
                if (jdata['state']) {
                    callback && callback(true, jdata['data']);
                } else {
                    callback && callback(false, null);
                }
            } else {
                callback && callback(false, data);
            }
        });
    }

    /**
     * 
     * @param url 
     * @param data 数据格式:{ file: "games/sword.json", data: EditData.inst.allPts }
     * @param callback 
     */
    edit_url_write = "http://192.168.1.118/LevelEdits/write.php"
    edit_url_read = "http://192.168.1.118/LevelEdits/read.php"
    writeHttpData(url, data, callback) {
        this.sendHttpPost(url || this.edit_url_write, data, (state, data) => {
            if (state) {
                callback && callback(data);
            }else {
                console.log('error:',data);
                callback && callback(null);
            }
        });
    }
}