import DataStorage from "./DataStorage";

/** 签到 */
export default class SignData extends DataStorage {
    /**当天是否签到完成 */
    private _signed: boolean = false;
    private static _instance: SignData = null;

    private constructor() {
        super();
    }

    getData() {
        this.init({ name: 'game-singed' });
        this._data['time'] = this._data['time'] || 0;
        this._data['day'] = this._data['day'] || 0;

        //判断签到时间是否是同一天
        let day = this.dayDiff(new Date(this._data['time']), new Date());
        if (day > 0) {        //隔天，可以签到
            if (this._data['day'] >= 7) {
                this._data['day'] = 0;
            }
            this._signed = false;
        } else {  //当天已签到
            this._signed = true;
        }
        this.save();
    }

    public static get inst(): SignData {
        if (this._instance == null) {
            this._instance = new SignData();
        }
        return this._instance
    }

    /**是否已经签到 */
    public get isSigned() {
        return this._signed;
    }

    /**签到到第几天 */
    public get signDay() {
        return this._data['day'];
    }

    /**签到时间 */
    public get signTime() {
        return this._data['time'];
    }

    /**签到 */
    sign() {
        if (this._signed) {
            return;
        }
        this._data['day']++;
        this._data['time'] = new Date().getTime();
        this._signed = true;
        this.save();
    }
}