import { ui } from "../ui/layaMaxUI";
import gSdk from "../common/platformsdk/platform";
import { AdsType } from "../common/platformsdk/plat-def";
import List = Laya.List;
import Box = Laya.Box;
import Image = Laya.Image;
import Sprite = Laya.Sprite;
import Label = Laya.Label;
import TipManager from "../common/TipManager";
import { UICfg } from "../scripts/defines/ResDef";
import adSdk from "../common/adDataSdk/adDataSdkFactory";
import { DialogManager } from "../common/DialogManager";
import ZsPlayInfo from "../commonUI/zs/ZsPlayInfo";
import UserData from "../common/data/UserData";
export default class GameMain extends ui.gameUI.GameMainUI {

    constructor() {
        super();
        this.height = Laya.stage.height;
    }

    onAwake(): void {
    }

    onMouseDown(): void {

    }

    onEnable(): void {
        this.btnMoreGame.on(Laya.Event.CLICK, this, this.onClickMoreGame);
        this.btnResetGame.on(Laya.Event.CLICK, this, this.onResetGame);
        this.btnWin.on(Laya.Event.CLICK, this, this.onWin);
        this.btnFail.on(Laya.Event.CLICK, this, this.onFail);

        this.onEnableADControl();
        this.PlatformUIInit();
    }
    onOpened() {
        /*
        if (gSdk.isTT)
            gSdk.sdk.startRecord();
        */
    }

    onDisable() {

    }

    onFail() {
        if (gSdk.isTT)
            gSdk.sdk.stopRecording();

        if (gSdk.sdk.isUseZS) {
            console.log('zs导出2');
            if (adSdk.sdk.adConfig.isBeforeGameAccount()) {
                Laya.stage.once("FULL_SCREEN_AD1_CLOSE", this, this.onCloseFullAd, [false]);
                ZsPlayInfo.IS_AUTO_CLICK = true;
                DialogManager.Ins.sceneOpen(UICfg.fullscreenad1, false)
                return;
            }
        }
        this.Next(false);
    }

    onWin() {
        if (gSdk.isTT)
            gSdk.sdk.stopRecording();

        if (gSdk.sdk.isUseZS) {
            console.log('zs导出2');
            if (adSdk.sdk.adConfig.isBeforeGameAccount()) {
                Laya.stage.once("FULL_SCREEN_AD1_CLOSE", this, this.onCloseFullAd, [true]);
                ZsPlayInfo.IS_AUTO_CLICK = true;
                DialogManager.Ins.sceneOpen(UICfg.fullscreenad1, false)
                return;
            }
        }

        this.Next(true);
    }

    onCloseFullAd(result) {
        this.Next(result);
    }

    Next(result) {
        this.close();
        //DialogManager.Ins.sceneOpen(UICfg.ResultView, false, { gameresult: result })
    }

    onResetGame() {
        if (gSdk.isTT)
            gSdk.sdk.stopRecording();
        this.close();
        Laya.Scene.open(UICfg.GameStartView);
    }

    onClickMoreGame(e: Laya.Event): void {
        if (e.type == Laya.Event.CLICK) {
            if (gSdk.isTT) {
                let listdata = adSdk.sdk.getAdList('defaultData');//DataModule.Ins.getGameList(true);
                gSdk.sdk.showMoreGamesModal((listdata && listdata.length > 0) ? listdata[0] : [], null, null, null, (code) => {
                    if (code == 1) TipManager.Ins.showMsgTip("打开更多游戏失败", this);
                })
            }
            else if (gSdk.isQQ) {
                gSdk.sdk.showAppBoxAd();
            }
            else if (gSdk.isWX && gSdk.sdk.isUseZS) {
                Laya.Scene.open(UICfg.floatAd, false, { isCloseBanner: false });
            }
            else
                Laya.Scene.open("commonUI/MoreGameDialog.scene", false, { isCloseBanner: false });
        }
        e.stopPropagation();
    }

    OnShow(): void {
    }

    onDestroy() {
    }

    onEnableADControl() {

        if (gSdk.isTT) {
            console.log("tt游戏中-隐藏banner");
            gSdk.sdk.hide_banner();
        }

    }

    PlatformUIInit() {
    }

}