import AdList from "./AdList";
import gSdk from "../../common/platformsdk/platform";
import adSdk from "../../common/adDataSdk/adDataSdkFactory";
import { DialogManager } from "../../common/DialogManager";
import ZsPlayInfo from "./ZsPlayInfo";
import { WxBannerScreen } from "../../common/platformsdk/plat-def";
import { navigateSceneType } from "../../common/adDataSdk/adDataSdkDef";

export default class HomeFloatAdView extends Laya.Script {

    private adList: AdList;
    private closeBtn: Laya.Button;
    private middleUI: Laya.Box;
    constructor() {
        super();
    }

    onAwake() {
        super.onAwake();
        (this.owner as Laya.View).height = Laya.stage.height;
        var topUI = this.owner.getChildByName("topUI");
        var adListUI;
        if (topUI) {
            adListUI = topUI.getChildByName("adList");
            this.closeBtn = topUI.getChildByName("closeBtn") as Laya.Button;
        }
        this.middleUI = this.owner.getChildByName("middleUI") as Laya.Box;
        if (this.middleUI) {
            adListUI = adListUI || this.middleUI.getChildByName("adList");
            this.closeBtn = this.closeBtn || this.middleUI.getChildByName("closeBtn") as Laya.Button;
        }
        // var bottomUI = this.owner.getChildByName("bottomUI");
        // if(bottomUI){
        //     this.closeBtn = this.closeBtn || bottomUI.getChildByName("closeBtn") as Laya.Button;
        // }
        this.closeBtn = this.closeBtn || this.owner.getChildByName("closeBtn") as Laya.Button;
        this.adList = adListUI.addComponent(AdList);
        this.closeBtn.on(Laya.Event.CLICK, this, this.closeView);
    }

    onDestroy() {
        this.closeBtn.off(Laya.Event.CLICK, this, this.closeView);
    }

    onStart() {
        this.adList.requestAdData(navigateSceneType.more, false, ZsPlayInfo.SCROLL_NONE, null, 9, false, false);
    }

    onEnable() {
        //this.closeBtn.y = this.middleUI.y + this.middleUI.height + 40
        if (adSdk.sdk.adConfig.electControl('zs_banner_vertical_enable')) {
            gSdk.sdk.beginMistake();
            gSdk.sdk.hideBannerzs();
            this.closeBtn.y = Laya.stage.height - 100;

            let tbanner_show_delay: number = adSdk.sdk.adConfig.electControl("zs_banner_banner_time");
            let ttext_move_delay: number = adSdk.sdk.adConfig.electControl("zs_banner_text_time");
            let ttext_move_time: number = adSdk.sdk.adConfig.electControl("zs_banner_move_time");

            Laya.timer.once(tbanner_show_delay, this, () => { gSdk.sdk.showBannerzs(); });

            Laya.Tween.to(this.closeBtn, { x: this.closeBtn.x, y: 1000 }, ttext_move_time, null, Laya.Handler.create(this, () => {
            }), ttext_move_delay);
        }
    }

    closeView() {
        if (gSdk.sdk) gSdk.sdk.endMistake();
        DialogManager.Ins.sceneClose(this.owner["url"]);
    }
}