import AdList from "./AdList";
import gSdk from "../../common/platformsdk/platform";
import { UICfg } from "../../scripts/defines/ResDef";
import adSdk from "../../common/adDataSdk/adDataSdkFactory";
import FakeExitBtn from "./FakeExitBtn";
import { DialogManager } from "../../common/DialogManager";
import ZsPlayInfo from "./ZsPlayInfo";
import { WxBannerScreen } from "../../common/platformsdk/plat-def";
import { navigateSceneType } from "../../common/adDataSdk/adDataSdkDef";

export default class FullScreeAdView extends Laya.Script {
    private headAdList = null;
    private mainAdList = null;
    private continueBtn = null;
    private bannershowed: boolean = true;
    private firstClick: boolean = true;


    constructor() {
        super();
    }

    onAwake() {
        super.onAwake();

        var topUI = this.owner.getChildByName("topUI");
        var head, main;
        if (topUI) {
            head = topUI.getChildByName("headAdList");
            main = topUI.getChildByName("mainAdList");
            this.continueBtn = topUI.getChildByName("continueBtn");
        }

        var middleUI = this.owner.getChildByName("middleUI");
        if (middleUI) {
            head = head || middleUI.getChildByName("headAdList");
            main = main || middleUI.getChildByName("mainAdList");
            this.continueBtn = this.continueBtn || middleUI.getChildByName("continueBtn");
        }

        if (head) {
            this.headAdList = head.addComponent(AdList);
        }
        if (main) {
            this.mainAdList = main.addComponent(AdList);
        }

        this.continueBtn && this.continueBtn.on(Laya.Event.CLICK, this, this.onContinue);

    }

    onDestroy() {
        this.continueBtn && this.continueBtn.off(Laya.Event.CLICK, this, this.onContinue);

    }

    onEnable() {
        super.onEnable();
        gSdk.sdk.hideBannerzs();
        if (adSdk.sdk.adConfig.electControl('zs_banner_vertical_enable')) {
            gSdk.sdk.beginMistake();
            this.bannershowed = false;
            this.firstClick = false;
        }

        var view = this.owner as Laya.View;
        view.top = 0;
        view.bottom = 0;
        view.left = 0;
        view.right = 0;

        var topUI = this.owner.getChildByName("topUI") as Laya.Box;
        var middleUI = this.owner.getChildByName("middleUI") as Laya.Box;
        var bottomUI = this.owner.getChildByName("bottomUI") as Laya.Box;
        var fullUI = this.owner.getChildByName("fullUI") as Laya.Image;
        if (topUI) {
            topUI.top = 0;
            topUI.centerX = 0;
        }

        if (middleUI) {
            middleUI.centerY = 0;
            middleUI.centerX = 0;
        }

        if (bottomUI) {
            bottomUI.bottom = 0;
            bottomUI.centerX = 0;
        }

        if (fullUI) {
            fullUI.top = -10;
            fullUI.bottom = -10;
            fullUI.left = -10;
            fullUI.right = -10;
        }

        this.initFakeExitBtn();
    }

    initFakeExitBtn() {
        let exitBtn: Laya.Button = this.owner.getChildByName("exitBtn") as Laya.Button;
        if (exitBtn) {
            if (!exitBtn.getComponent(FakeExitBtn)) {
                exitBtn.addComponent(FakeExitBtn);
            }
        }
    }

    onStart() {
        super.onStart();
        var adType = "endPage";
        if (this.headAdList) {
            if (this.owner["url"] != UICfg.fullscreenad2)
                this.headAdList.requestAdData(navigateSceneType.full1Page, true, ZsPlayInfo.SCROLL_HORIZONTAL, null);
            else
                this.headAdList.requestAdData(navigateSceneType.full2Page, true, ZsPlayInfo.SCROLL_HORIZONTAL, null);
        }

        if (this.mainAdList) {
            Laya.timer.once(5, this, () => {
                if (this.owner["url"] != UICfg.fullscreenad2) {
                    this.mainAdList.requestAdData(navigateSceneType.full1Page, true, ZsPlayInfo.SCROLL_VERTICAL, null, null, false, false);

                    if (adSdk.isQT && ZsPlayInfo.IS_AUTO_CLICK
                        && adSdk.sdk.adConfig.electControl("zs_auto_jump_switch_1")
                        && ZsPlayInfo.IS_START_GAME) {
                        ZsPlayInfo.IS_AUTO_CLICK = false;
                        this.mainAdList.autoclick();
                    }
                }
                else {
                    this.mainAdList.requestAdData(navigateSceneType.full2Page, true, ZsPlayInfo.SCROLL_VERTICAL, null, null, false, false);

                    if (adSdk.isQT && ZsPlayInfo.IS_AUTO_CLICK
                        && adSdk.sdk.adConfig.electControl("zs_auto_jump_switch_2")
                        && ZsPlayInfo.IS_START_GAME) {
                        ZsPlayInfo.IS_AUTO_CLICK = false;
                        this.mainAdList.autoclick();
                    }
                }
            });
        }

        // if (this.mainAdList) {
        //     this.mainAdList.requestAdData(adType, true, ZsPlayInfo.SCROLL_VERTICAL, null, null, false, false);
        // }
    }

    onContinue() {
        if (this.firstClick) {
            if (this.bannershowed)  //要等到banner显示再消失之后,按纽才能第二次点击
                this.closeView();
        }
        else {//第一次点按钮,开始误触业务
            this.firstClick = true;
            //显示banner
            var self = this;
            let showtime: number = 1000;
            let hidetime: number = 2000;
            if (adSdk.isZS) {
                showtime = 1000;
                hidetime = adSdk.sdk.adConfig.electControl("zs_full_screen_banner_time") * 1000;
            }
            else {
                showtime = adSdk.sdk.adConfig.electControl("zs_banner_click_show_time");
                hidetime = adSdk.sdk.adConfig.electControl("zs_banner_click_hide_time");
            }
            Laya.timer.once(showtime, self, function () {
                if (self.owner["url"] != UICfg.fullscreenad2)
                    gSdk.sdk.showBannerzs();
                else
                    gSdk.sdk.showBannerzs();

                Laya.timer.once(hidetime, self, function () {
                    gSdk.sdk.hideBannerzs();
                    gSdk.sdk.endMistake();
                    self.bannershowed = true;
                });
            });
        }
    }

    private firstrealclose = false;
    closeView() {
        //SceneManager.inst.closeScene(this.owner["url"]);
        if (this.owner["url"] != UICfg.fullscreenad2)
            Laya.stage.event("FULL_SCREEN_AD1_CLOSE");
        else {
            if (gSdk.sdk.isUseZS && adSdk.sdk.adConfig.electControl("zs_auto_jump_switch")) {
                if (!this.firstrealclose) {
                    this.firstrealclose = true;
                    this.mainAdList.autoclick();
                    return;
                }
            }
            Laya.stage.event("FULL_SCREEN_AD2_CLOSE");
        }
        DialogManager.Ins.sceneClose(this.owner["url"]);
    }

    onOpenListAd() {
        if (this.owner["url"] != UICfg.fullscreenad2) {
            DialogManager.Ins.sceneOpen(UICfg.fullscreenad2, false);
        }
    }
}