import SdkBase from "./sdk-base";
import { vivoCfg, AdsType, bAdsUse } from "./plat-def";
import { AdsData } from "./Ads-data";
import { AdsCfgBase } from "./Adscfg-def";

/**
 * 记录应该创建哪个原生广告,显示时应该取哪个原生广告
 */
enum NativeNeedCreate {
    INITCREATE,       //初始值,表示还未拉取原生广告,或者拉取原生广告一直失败
    NATIVEONE,  //原生广告一拉取成功
    NATIVETWO   //原生广告二拉取成功
}
/**
 * 记录当前显示的原生广告是哪个
 */
enum NativeShowed {
    INITSHOW,
    NATIVEONE,
    NATIVETWO
}
export default class SdkVivo extends SdkBase {
    private _vivo: any = null;
    private _nativeCreateIndex: number; //当前应该创建的原生广告,初始值为0.在getnativedata成功获取到数据后,在1和2之间切换
    private _nativeShowIndex: number;    //当前显示和上报的原生广告,初始值为0,在1和2之间切换
    private _nativedelay: number;   //计数,两个native创建间隔要超过15秒
    private _nativeloadresult1: boolean; //记录原生一拉取结果
    private _nativeloadresult2: boolean; //记录原生二拉取结果

    private _adsCfg: AdsCfgBase = null;

    constructor() {
        super();
        this._vivo = Laya.Browser.window.qg;
        this.version = vivoCfg.version;
        this.cfg = vivoCfg;
        this._nativeCreateIndex = NativeNeedCreate.INITCREATE;
        this._nativeShowIndex = NativeShowed.INITSHOW;
        this._nativedelay = 15; //初值为15,保证第一次能正常拉取
        this._nativeloadresult1 = false;
        this._nativeloadresult2 = false;
    }

    init(params?: any) {
        this.initAds();
        this._vivo['onShow'] && this._vivo.onShow((res) => {  //从后台返回到游戏
            //EventManager.dispatchGlobalByCmd(GameEvent.ONSHOW, {});
        });

        this._vivo['onHide'] && this._vivo.onHide(() => {  //从游戏到后台
            //EventManager.dispatchGlobalByCmd(GameEvent.ONHIDE, {});
        });
    }

    getLaunchOptionsSync(): any {
        return this._vivo.getLaunchOptionsSync()
    }

    getSystemInfoSync(): any {
        return this._vivo.getSystemInfoSync()
    }

    playVibrative(isShort = true) {
        isShort ? this._vivo.vibrateShort() : this._vivo.vibrateLong();
    }

    /**获取右上角菜单栏信息 */
    getRightMenuRectInfo(): any {
        if (typeof this._vivo.getMenuButtonBoundingClientRect == 'undefined') {
            return SdkBase.prototype.getRightMenuRectInfo.call(this);
        }
        return this._vivo.getMenuButtonBoundingClientRect();
    }

    getappId() {
        return this.cfg.appid;
    }

    getopenId(callback) {
        let self = this;
        this.login((state, ret) => {
            if (state) {
                if (ret.data.token) {
                    let xhr = new Laya.HttpRequest();
                    xhr.http.timeout = 10000;
                    xhr.once(Laya.Event.COMPLETE, this, (res) => {
                        self.openId = res.data.user_id;
                        callback && callback(true, self.openId);
                    });
                    xhr.once(Laya.Event.ERROR, this, (err) => {
                        callback && callback(false);
                    })
                    let sendData = { appid: self.cfg.appid, code: ret.data.token };
                    xhr.send(self.cfg.serviceUrl, JSON.stringify(sendData), "post", "json", ["content-type", "application/json"]);
                }
            } else {
                callback && callback(false);
            }
        });
    }

    login(cb: Function = null) {
        if (this._vivo.login) { console.log("login is not null"); }

        this._vivo.login().then((res) => {
            if (res.data.token) {
                cb && cb(true, res);
            }
        }, (err) => {
            console.log('登录失败' + JSON.stringify(err));
            cb && cb(false, err);
        })
    }

    /**
     * 分包加载
     * @param resList 
     * @param callback 
     * @param progressCall 
     */
    loadSubpackage(resList: Array<string>, callback, progressCall) {
        let count = resList.length;
        let loadPackageCount = count;
        let load = function () {
            loadPackageCount--;
            if (loadPackageCount <= 0) {
                callback && callback();
            }
        }
        for (let i = 0; i < count; i++) {
            var loadTask = this._vivo.loadSubpackage({
                name: resList[i], // name 可以填 name 或者 root
                success: function (res) {
                    progressCall && progressCall(i);
                    load();
                },
                fail: function (res) {
                    // 分包加载失败通过 fail 回调
                }
            });

            loadTask.onProgressUpdate(res => {

            })
        }
    }

    navigateToMiniProgram(data, success = null, fail = null, complete = null) {
        this._vivo.navigateToMiniGame(
            {
                pkgName: data.link_pkgname,
                path: data.link_path,
                extraData: data.extraData,
                success: function (e) {
                    success && success(e)
                },
                fail: function (e) {
                    fail && fail(e);
                },
                complete: function (e) {
                    complete && complete(e);
                }
            }
        )
    }

    /**添加桌面 */
    installShortcut(success: Function, fail: Function, compelete: Function) {
        if (typeof this._vivo.hasShortcutInstalled == 'undefined' || typeof this._vivo.installShortcut == 'undefined') {
            return;
        }
        let self = this;
        this._vivo.hasShortcutInstalled({
            success: function (res) {
                // 判断图标未存在时，创建图标
                if (res == false) {
                    self._vivo.installShortcut({
                        success: function () {
                            // 执行用户创建图标奖励
                            success && success(true);
                        },
                        fail: function (err) {
                            fail && fail();
                        },
                        complete: function () {
                            compelete && compelete();
                        }
                    })
                } else {
                    success && success(false);
                }
            },
            fail: function (err) {
                fail && fail()
            },
            complete: function () {
            }
        })
    }


    //*********************************************/
    //***********    广告接口   *******************/
    //********************************************/
    initAdsCfg(params: AdsCfgBase = null) {
        this._adsCfg = params;
    }

    initAds() {
        this._adsCfg = {ver: "0.0", switch: 0};
        if (!bAdsUse) return;
        //this.createBanner();
        this.preloadVedioAds();
        //this.preloadInstalAds();
        this.createNative();
        Laya.timer.loop(1000, this, () => { this._nativedelay++ });
    }

    createVideoAds() {
        if (typeof this._vivo.createRewardedVideoAd == 'undefined') {
            return;
        }
        let ads = this.getAdsData(AdsType.VIDEO);
        if (!ads) return;

        let adsIndex = ads.adsIndex;
        let videoId = vivoCfg.VideoId[adsIndex % vivoCfg.VideoId.length];

        let videoAd = this._vivo.createRewardedVideoAd({
            adUnitId: videoId
        });

        videoAd.onLoad(() => {
            ads.isLoaded = true;
        });

        videoAd.onError((err) => {
            console.log('MyOPPOAD:video error=>', JSON.stringify(err));
            ads.isLoaded = false;
        });

        videoAd.onClose((res) => {
            if (res && res.isEnded || res === undefined) {    //视频广告成功，获得奖励
                ads.close(true);
            } else {
                ads.close(false);
            }
            ads.neespreLoad && this.preloadVedioAds();
        });
        ads.adInstance = videoAd;
    }

    preloadVedioAds() {
        let ads = this.getAdsData(AdsType.VIDEO);
        if (!ads.adInstance) {
            this.createVideoAds();
        }
        if (!ads.isLoaded && ads.adInstance) {
            let promise = ads.adInstance.load()
            if (promise && vivoCfg.usePromise) {
                promise.then(function () {
                    ads.isLoaded = true;
                }.bind(this)).catch(function (err) {
                    console.log("MyOPPOAD:video fail=>", JSON.stringify(err))
                    ads.isLoaded = false;
                }.bind(this));
            }
        }
    }

    showVideo(ads: AdsData) {
        if (!bAdsUse) {
            SdkBase.prototype.showVideo.call(this, ads);
            return;
        }
        let adsInstance = ads.adInstance;
        if (adsInstance) {
            if (vivoCfg.usePromise) {
                let promise = adsInstance.show();
                promise.then(() => {
                    ads.show(true);
                }).catch(() => {
                    console.log('MyOPPOAD:video show fail');
                    ads.show(false);
                    ads.neespreLoad && this.preloadVedioAds();
                });
                return;
            } else {
                if (ads.isLoaded) {   //加载成功
                    ads.show(true);
                    adsInstance.show();
                    return;
                } else {
                    ads.show(false);
                    ads.neespreLoad && this.preloadVedioAds();
                }
            }
        } else {
            ads.show(false);
            if (ads.neespreLoad) {
                this.preloadVedioAds();
            } else {
                this.createVideoAds();
            }
        }
    }

    createInstalAds() {
        if (typeof (this._vivo.createInterstitialAd) == "undefined") {
            console.log('MyOPPOAD:createInterstitialAd api not exists');
            return;
        }
        let ads = this.getAdsData(AdsType.INTERSTITIAL);
        if (!ads) return;
        let adsIndex = ads.adsIndex;
        let adId = this.cfg.InterstitialId[adsIndex % this.cfg.InterstitialId.length];
        let interstitialAd = this._vivo.createInterstitialAd({
            adUnitId: adId
        });
        interstitialAd.onError((err) => {
            console.log('MyOPPOAD: InterstitialAd err', JSON.stringify(err));
            ads.isLoaded = false;
        });
        interstitialAd.onLoad(() => {
            ads.isLoaded = true;
        });
        interstitialAd.onClose((ret) => {
            ads.close(true);
            if (this.cfg.destroyInstl) {
                ads.adInstance.destroy();
                ads.adInstance = null;
            }
            ads.neespreLoad && this.preloadInstalAds();
        });
        ads.adInstance = interstitialAd;
    }

    preloadInstalAds() {
        let ads = this.getAdsData(AdsType.INTERSTITIAL);
        if (!ads.adInstance) {
            this.createInstalAds();
            return;
        }
        if (!ads.isLoaded && ads.adInstance) {
            let promise = ads.adInstance.load();
            if (promise && this.cfg.usePromise) {
                promise.then(() => {
                    ads.isLoaded = true;
                }).catch((err) => {
                    console.log('MyOPPOAD:InterstitialAd load fail', err);
                    ads.isLoaded = false;
                });
            }
        }
    }

    showInstal(ads: AdsData) {
        let adsInstance = ads.adInstance;
        if (adsInstance) {
            if (this.cfg.usePromise) {
                let promise = adsInstance.show();
                promise.then(() => {
                    ads.show(true);
                }).catch(() => {
                    console.log('MyOPPOAD:showInstal fail');
                    ads.show(false);
                    ads.neespreLoad && this.preloadInstalAds();
                });
                return;
            } else {
                if (ads.isLoaded) {
                    ads.adInstance.show();
                    ads.show(true);
                } else {
                    ads.show(false);
                    ads.neespreLoad && this.preloadInstalAds();
                }
            }
        } else {
            ads.show(false);
            if (ads.neespreLoad) {
                this.preloadInstalAds();
            } else {
                this.createInstalAds();
            }
        }
    }

    createBanner(style?: any) {
        //初始化banner 广告
        let sysInfo = this.getSystemInfoSync();
        let screenWidth = sysInfo.screenWidth;
        let screenHeight = sysInfo.screenHeight;
        let ads = this.getAdsData(AdsType.BANNER);

        if (ads.adInstance) {
            ads.adInstance.destroy();
            ads.adInstance = null;
        }

        style = style || {};
        for (let key in vivoCfg.style) {
            style[key] = vivoCfg.style[key];
        }
        let bannerWidth = style.width;
        if (bannerWidth <= 1) {    //按屏幕宽度百分比计算宽度
            bannerWidth = screenWidth * bannerWidth;
        }

        let bannerHeight = style.height;
        let mp = style.mp || 1;

        let adsIndex = ads.adsIndex;
        let adId = vivoCfg.bannerIds[adsIndex % vivoCfg.bannerIds.length];

        let top = mp == 1 ? (screenHeight - bannerHeight) : (mp == 2 ? 0 : style.top);
        let left = style.isCenter ? screenWidth / 2 - bannerWidth / 2 : style.left;
        let bannerAds = this._vivo.createBannerAd({
            posId: adId,
            style: {}
        });

        ads.style.height = bannerHeight;
        ads.style.width = bannerWidth;
        ads.style.left = left;
        ads.style.top = top;
        ads.style.realHeight = bannerHeight;
        ads.style.realWidth = bannerWidth;

        bannerAds.onSize((res) => {
            // var bannerAdHeight = res.height;
            // var bannerAdWidth = res.width;
            // bannerAds.style.top = mp == 1 ? (screenHeight - bannerAdHeight) : (mp == 2 ? 0 : style.top);
            // bannerAds.style.left = style.isCenter ? screenWidth / 2 - bannerAdWidth / 2 : style.left;
            // ads.style.realWidth = bannerAdWidth;
            // ads.style.realHeight = bannerAdHeight;
            // ads.style.left = bannerAds.style.left;
            // ads.style.top = bannerAds.style.top;
        });

        bannerAds.onLoad(() => {
            ads.isLoaded = true;
            ads.loadCount = 0;
        });

        bannerAds.onError((err) => {
            console.log('MyOPPOAD:banner error=>', JSON.stringify(err));
            ads.isLoaded = false;
            //this.reLoadBanner();
        })

        ads.adInstance = bannerAds;
        //bannerAds && bannerAds.hide();
        bannerAds.show().then(() => {
        }).catch((err) => {
            console.log('banner广告展示失败', JSON.stringify(err));
        })
    }

    reLoadBanner() {
        let ads = this.getAdsData(AdsType.BANNER);
        if (ads.loadCount < vivoCfg.error_count) {
            ads.adsIndex++;
            if (ads.adInstance) {
                ads.adInstance.hide();
                ads.adInstance.destroy();
                ads.adInstance = null;
            }
            this.createBanner();
            this.showBanner();
        }
        ads.loadCount++;
    }

    showBanner(style?: any) {
        if (!bAdsUse) return;
        let ads = this.getAdsData(AdsType.BANNER);

        if (!ads.adInstance) {
            this.createBanner(style);
        }
        if (vivoCfg.usePromise) {
            let promis = ads.adInstance.show();
            if (promis) {
                promis.then(() => {
                    ads.loadCount = 0;
                }).catch(() => {
                    ads.loadCount = 0;
                });
            }
        } else {
            ads.adInstance.show();
        }
        ads.adsIndex++;
    }

    hideBanner(need_pre_create: boolean = true, style?: any) {
        let ads = this.getAdsData(AdsType.BANNER);
        if (ads.adInstance) {
            ads.adInstance.hide();
            ads.adInstance.destroy();
            ads.adInstance = null;
            ads.loadCount = 0;
            if (ads.neespreLoad) {
                this.createBanner(style);
            }
        }
    }

    hide_banner() {
        let ads = this.getAdsData(AdsType.BANNER);
        if (ads.adInstance) {
            ads.adInstance.hide();
        }
    }

    bannerMoveBy(top: number = 80, left: number = 0) {
        let ads = this.getAdsData(AdsType.BANNER);
        if (ads.adInstance) {
            ads.adInstance.style.top += top;
            ads.adInstance.style.left += left;
        }
    }

    bannerMoveTo(top: number = 0, left: number = 0) {
        let ads = this.getAdsData(AdsType.BANNER);
        if (ads.adInstance) {
            ads.adInstance.style.top = top;
            ads.adInstance.style.left = left;
        }
    }

    loadNativeData(adsData: AdsData, tid, func) {
        if (!adsData) return;
        let self = this;
        if (adsData.adInstance) {
            if (adsData.adInstance.destroy) adsData.adInstance.destroy();
            adsData.adInstance = null;
            adsData.adData = null;
        }
        let nativeAd = this._vivo.createNativeAd({
            adUnitId: tid
        });

        adsData.adInstance = nativeAd;

        nativeAd.onLoad((res) => {
            console.log("MyOPPOAD:loadNativeData() onLoad()", tid, JSON.stringify(res));
            if (self._nativeCreateIndex == NativeNeedCreate.INITCREATE) {//第一次拉取成功,把索引值改成原生一
                self._nativeCreateIndex = NativeNeedCreate.NATIVEONE;
                self._nativeloadresult1 = true;
            }
            else if (self._nativeCreateIndex == NativeNeedCreate.NATIVEONE) {
                self._nativeloadresult1 = true;
            }
            else if (self._nativeCreateIndex == NativeNeedCreate.NATIVETWO) {
                self._nativeloadresult2 = true;
            }
            adsData.adData = res.adList;
            func(res.adList);
        })
        nativeAd.onError((err) => {
            console.log("MyOPPOAD:loadNativeData() onError()", tid, JSON.stringify(err));
            if (self._nativeCreateIndex == NativeNeedCreate.INITCREATE) { }
            else if (self._nativeCreateIndex == NativeNeedCreate.NATIVEONE) {
                self._nativeloadresult1 = false;
            }
            else if (self._nativeCreateIndex == NativeNeedCreate.NATIVETWO) {
                self._nativeloadresult2 = false;
            }
            if (adsData && adsData.adInstance && adsData.adInstance.destroy) adsData.adInstance.destroy();
            if (adsData && adsData.adInstance) adsData.adInstance = null;
            if (adsData && adsData.adData) adsData.adData = null;
            func(null);
        })

        nativeAd.load();
    }

    getNativeAdId(_index: number = 0) {
        return this.cfg.nativeIds[_index];
    }
    getNativeAd(func: Function, _index: number = 1) {
        if (typeof this._vivo['createNativeAd'] == "undefined") {
            return;
        }
        /*
        if (this._nativeAd) {
            if (this._nativeAd.destroy) this._nativeAd.destroy();
            this._nativeAd = null;
        }
        */

        let tid = this.getNativeAdId(_index);
        if (!tid) {
            func(null);
            return;
        }
        let nativeAd = this._vivo.createNativeAd({
            posId: tid
        })
        nativeAd.load();
        nativeAd.onLoad((res) => {
            func(res.adList);
            //WeiXinMgr.logTest('getNativeAd6', res.adList[0]);
        })
        nativeAd.onError((err) => {
            console.log("MyOPPOAD:getNativeAd->onError", JSON.stringify(err));
            func(null);
        })
    }

    /**创建原生插屏 */
    createNative(loadCount = 0, adtype: AdsType = AdsType.NATIVE_INSTIL) {  //创建vivo_native
        if (this._nativedelay < 15) {
            console.log("MyOPPOAD:createNative time less then 15s");
            return;
        }
        this._nativedelay = 0;  //重新记时间

        let nativeid;
        if (this._nativeCreateIndex == NativeNeedCreate.INITCREATE) {   //第一次拉取或之前从未成功拉取过
            adtype = AdsType.NATIVE_INSTIL;
            nativeid = vivoCfg.nativeIds[0];
        }
        else if (this._nativeCreateIndex == NativeNeedCreate.NATIVEONE) {
            if (this._nativeloadresult1) {//原生一已经拉取成功,换原生二拉取
                this._nativeCreateIndex = NativeNeedCreate.NATIVETWO;
                adtype = AdsType.NATIVE_INSTIL_VIVO;
                nativeid = vivoCfg.nativeIds[1];
            }
            else {  //重新拉取原生一
                adtype = AdsType.NATIVE_INSTIL;
                nativeid = vivoCfg.nativeIds[0];
            }
        }
        else if (this._nativeCreateIndex == NativeNeedCreate.NATIVETWO) {
            if (this._nativeloadresult2) {//原生二已经拉取成功,换原生一拉取
                this._nativeCreateIndex = NativeNeedCreate.NATIVEONE;
                adtype = AdsType.NATIVE_INSTIL;
                nativeid = vivoCfg.nativeIds[0];
            }
            else {  //重新拉取原生二
                adtype = AdsType.NATIVE_INSTIL_VIVO;
                nativeid = vivoCfg.nativeIds[1];
            }
        }

        let ads = this.getAdsData(adtype);
        // let id_len = this.cfg.nativeIds.length;
        // if (loadCount >= id_len)
        //     return;

        //this.loadNativeData(ads, vivoCfg.nativeIds[ads.adsIndex % id_len], (ret) => {
        this.loadNativeData(ads, nativeid, (ret) => {
            if (null == ret) {
                //this.createNative(loadCount++, adtype);
            }
        })

    }

    getNativeData() {
        let nativeid;
        let adtype;
        if (this._nativeCreateIndex == NativeNeedCreate.INITCREATE) {
            console.log("MyOPPOAD:getNativeData()---never success");
            this.createNative();
            return null;
        }

        if (this._nativeCreateIndex == NativeNeedCreate.NATIVEONE) {
            if (this._nativeloadresult1) {
                nativeid = vivoCfg.nativeIds[0];
                let ad1 = this.getAdsData(AdsType.NATIVE_INSTIL);
                if (ad1 && ad1.adInstance && ad1.adData) {
                    this._nativeShowIndex = NativeShowed.NATIVEONE;
                    console.log("MyOPPOAD:getNativeData()---return 1", nativeid);
                    return ad1.adData[0];
                }
                else {
                    console.log("MyOPPOAD:getNativeData()---return 2, wrong way");
                    return null;
                }
            }
            else {
                nativeid = vivoCfg.nativeIds[1];
                let ad11 = this.getAdsData(AdsType.NATIVE_INSTIL_VIVO);
                if (ad11 && ad11.adInstance && ad11.adData) {
                    this._nativeShowIndex = NativeShowed.NATIVETWO;
                    console.log("MyOPPOAD:getNativeData()---return 3", nativeid);
                    return ad11.adData[0];
                }
                else {
                    console.log("MyOPPOAD:getNativeData()---return 4, wrong way");
                    return null;
                }
            }
        }

        if (this._nativeCreateIndex == NativeNeedCreate.NATIVETWO) {
            if (this._nativeloadresult2) {
                nativeid = vivoCfg.nativeIds[1];
                let ad2 = this.getAdsData(AdsType.NATIVE_INSTIL_VIVO);
                if (ad2 && ad2.adInstance && ad2.adData) {
                    this._nativeShowIndex = NativeShowed.NATIVETWO;
                    console.log("MyOPPOAD:getNativeData()---return 5", nativeid);
                    return ad2.adData[0];
                }
                else {
                    console.log("MyOPPOAD:getNativeData()---return 6, wrong way");
                    return null;
                }
            }
            else {
                nativeid = vivoCfg.nativeIds[0];
                let ad22 = this.getAdsData(AdsType.NATIVE_INSTIL);
                if (ad22 && ad22.adInstance && ad22.adData) {
                    this._nativeShowIndex = NativeShowed.NATIVEONE;
                    console.log("MyOPPOAD:getNativeData()---return 7", nativeid);
                    return ad22.adData[0];
                }
                else {
                    console.log("MyOPPOAD:getNativeData()---return 8, wrong way");
                    return null;
                }
            }
        }
    }

    reportAdClick(data: any, adtype: AdsType = AdsType.NATIVE_INSTIL) { //点击vivo_native
        if (this._nativeShowIndex == NativeShowed.NATIVETWO) {
            adtype = AdsType.NATIVE_INSTIL_VIVO;
        }
        else if (this._nativeShowIndex == NativeShowed.NATIVEONE) {
            adtype = AdsType.NATIVE_INSTIL;
        }
        let ads = this.getAdsData(adtype);
        if (ads.adInstance) {
            ads.adInstance.reportAdClick({
                adId: data.adId.toString()
            })
            //ads.adData = null;

            //this.createNative();
        }
    }
    /**显示原生广告 */
    reportAdShow(data: any, adstype: AdsType = AdsType.NATIVE_INSTIL) { //展示vivo_native
        let nativeid;
        if (this._nativeShowIndex == NativeShowed.NATIVEONE) {
            adstype = AdsType.NATIVE_INSTIL;
            this._nativeloadresult2 = false;    //要预加载原生二
            nativeid = vivoCfg.nativeIds[0];
        }
        else if (this._nativeShowIndex == NativeShowed.NATIVETWO) {
            adstype = AdsType.NATIVE_INSTIL_VIVO;
            this._nativeloadresult1 = false;    //要预加载原生一
            nativeid = vivoCfg.nativeIds[1];
        }
        let ads = this.getAdsData(adstype);
        console.log("MyOPPOAD:reportAdShow()", data.adId, nativeid);
        if (ads.adInstance) {
            ads.adInstance.reportAdShow({
                adId: data.adId.toString()
            })
        }
        //预加载下一个原生广告
        this.createNative();
    }

    setKeepScreenOn() {
        this._vivo.setKeepScreenOn({
            keepScreenOn: true,
            success: function () {
            },
            fail: function (data, code) {
                console.log(`handling fail, code = ${code}`)
            }
        })
    }
}