
import GameView from "./GameView"
import MainLayer from "../gameUI/MainLayer";
import Constant from "../scripts/mgr/Constant"
import Utils3D from "../scripts/mgr/Utils3D";
import gSdk from "../common/platformsdk/platform";
import { DialogManager } from "../common/DialogManager";
import { UICfg, SoundCfg, SceneCfg, SCENEINDEX } from "../scripts/defines/ResDef";
import AudioManager from "../common/AudioManager";
import adSdk from "../common/adDataSdk/adDataSdkFactory";
import EventManager from "../common/EventManager";
import Player from "./Player";
import UserData from "../common/data/UserData";
import GameEnd from "./GameEnd";
import HttpHelp from "../scripts/mgr/HttpHelp";
import TipManager from "../common/TipManager";
import Bullet from "./Bullet";
import Road from "./Road";
import ZsPlayInfo from "../commonUI/zs/ZsPlayInfo";
import { TapStyle } from "../common/adDataSdk/ADConfigBase";
//游戏主场景
export default class GameScene {
    /**单例*/
    private static _instance: GameScene = null;
    public _scene = null;
    public _camera: Laya.Camera;
    private JsonPath: string = "res/level/leveldata.json";
    public _JsonData = null;

    Level_lh = null;
    public mainLayer = null;
    bRestart = false;
    public _Player = null;
    public _GameView = null;
    public _BgLayer = null;
    public _End = null;
    public _Bullet = null;
    public _Road = null;
    public _Enemy = null;
    shopNode = null;

    /* 构造函数*/
    constructor() {
    }
    /**
   * 获取单例
   */
    static getInstance() {
        return this._instance || (this._instance = new GameScene())
    }
    static get instance(): GameScene {
        //GameScene.create();
        if (GameScene._instance == null) {
            GameScene._instance = new GameScene();
        }
        return GameScene._instance;
    }

    /* 创建*/
    public static create(): void {
        if (GameScene._instance == null) {
            GameScene._instance = new GameScene();
        }
    }

    //初始化场景
    public initScene(bNext = false): void {
        if (!bNext) {
            Utils3D.GetCacheData();
            Laya.Scene.open("gameUI/LoadSceneView.scene");
        } else {
            //Laya.loader.create(SceneCfg.game,Laya.Handler.create(this,this.onLoadedComplete));
            this.reLoadData();
            this.loadLevel();
        }
    }

    private onLoadedJosn(): void {
        var jsonData: JSON = null;
        jsonData = Laya.Loader.getRes(this.JsonPath);
        this._JsonData = jsonData;
        Utils3D._curData = jsonData[Utils3D.nCurScene.toString()];
    }


    public onLoadedComplete(scene): void {
        //Laya.Stat.show();
        this._scene = scene;
        this.onLoadedJosn();
        // if (this._BgLayer == null) {
        //     this._BgLayer = new BgLayer();
        //     Laya.stage.addChildAt(this._BgLayer, 0);
        // }

        Laya.stage.addChildAt(scene, 0);
        Laya.Scene.open(UICfg.GameStartView, false, { fromscene: SCENEINDEX.LOAD_SCENE });//打开ui

        var cameraNode = this._scene.getChildByName("CameraObject");
        var cameraAni = cameraNode.getChildByName("Bip_jintou")
        this._camera = cameraAni.getChildByName("Main Camera");
        this._camera.farPlane = 150;
        //部分手机不支持hdr导致黑屏 一定要设置为false
        this._camera.enableHDR = false;
        ///this._camera.clearFlag = Laya.BaseCamera.CLEARFLAG_DEPTHONLY;
        this.resetCamera();
        this._GameView = new GameView();
        this._End = new GameEnd(this._scene);

        this.loadLevel();
        Laya.MouseManager.multiTouchEnabled = false;
        this.shopNode = this._scene.getChildByName("shop");
        this.shopNode.transform.localPositionZ = -6;
        this.shopNode.active = false;
        this.receiveShadows();
    }

    loadPlayer(bShopSelect = false, index = 0) {
        var fIndex = bShopSelect == false ? Utils3D.shopSelect[1] + 1 : index;
        var lhStr = "LayaScene_PlayerMode/Conventional/renwu1.lh";
        Laya.loader.create(lhStr, Laya.Handler.create(this, function (node) {
            if (Utils3D.curPlayer) {
                Utils3D.curPlayer.destroy();
                Utils3D.curPlayer = null;
            }
            Utils3D.curPlayer = (node as Laya.Sprite3D).clone();
            Utils3D.curPlayer.active = true;
            this._scene.addChild(Utils3D.curPlayer);
            this._Player = new Player(this._scene, this.Level_lh);
            this._Player.findPlayerBody();
            Utils3D.curPlayer.transform.localRotationEulerY = 180;
            this._Bullet = new Bullet(this._scene, this.Level_lh);
            this._End.onLevelLoaded(this.Level_lh);
        }));
    }

    loadLevel() { //加载关卡
        console.log("loadLevel  nCurScene === ", Utils3D.nCurScene);
        var lhStr = "LayaScene_LevelMode/Conventional/" + Utils3D.nCurScene.toString() + ".lh";
        Laya.loader.create(lhStr, Laya.Handler.create(this, this.onLevelLoaded));
        //var lhStr = "LayaScene_GameInfo/Conventional/GameInfo.lh";
        //Laya.loader.create(lhStr, Laya.Handler.create(this, this.onLevelInfo));
    }

    onLevelInfo(node: Laya.Sprite3D) {
        let levleDatas = {};
        let nums = node.numChildren;
        var chipNode = null;
        var dyNode = null;
        var level = null;
        for (let i = 0; i < nums; i++) {
            let curLev = {}
            let levelNode = node.getChildAt(i) as Laya.Sprite3D;
            chipNode = levelNode.getChildByName("Enemy") as Laya.Sprite3D;
            dyNode = levelNode.getChildByName("danyao") as Laya.Sprite3D;
            curLev['enemy'] = [];
            let elen = chipNode.numChildren;
            for (let c = 0; c < elen; c++) {
                let eObj = {};
                var e = chipNode.getChildAt(c) as Laya.MeshSprite3D;
                eObj['name'] = e.name;
                eObj['pos'] = e.transform.localPosition;
                curLev['enemy'].push(eObj);
            }

            curLev['danyao'] = [];
            for (let n = 0; n < dyNode.numChildren; n++) {
                let dyObj = {};
                var dy = dyNode.getChildAt(n) as Laya.MeshSprite3D;
                dyObj['name'] = dy.name;
                dyObj['pos'] = dy.transform.localPosition;
                curLev['danyao'].push(dyObj);
            }

            levleDatas[levelNode.name] = curLev;
        }

        console.log("data ==========  ", levleDatas)
        HttpHelp.Ins.writeHttpData(null, { file: "games/leveldata.json", data: levleDatas }, (ret) => {
            if (ret) {
                TipManager.Ins.showMsgTip('上传关卡数据成功');
                console.log(ret);
            } else {
                TipManager.Ins.showMsgTip('上传关卡数据失败');
            }
        });
    }

    finishPosZ = 0;
    chips = [];
    onLevelLoaded(node) {
        if (this.Level_lh) {
            this.Level_lh.destroy();
            this.Level_lh = null;
        }

        this.Level_lh = (node as Laya.Sprite3D).clone();
        this.Level_lh.active = true;
        this._scene.addChild(this.Level_lh);
        this._Road = new Road(this._scene, this.Level_lh);

        this.loadPlayer();
        //碎片收集动画
        Utils3D.bLoadedComplete = true;
        if (Utils3D.bTryUse) {
            this.initMainLayer();
        }
    }

    receiveShadows() {
        var drecLight = this._scene.getChildByName("Directional Light") as Laya.DirectionLight;
        // Use soft shadow.
        drecLight.shadow = true;
        drecLight.shadowDistance = 30;
        drecLight.shadowResolution = 2048;
        //模糊等级,越大越高,效果更好，更耗性能
        drecLight.shadowPCFType = 1;
        //生成阴影贴图数量
        drecLight.shadowPSSMCount = 1;

        //var start = this._scene.getChildByName("Start") as Laya.MeshSprite3D;
        //start.meshRenderer.receiveShadow = true;
    }

    onTouchDown() {
        //this.qiliu.active = true;
        //Utils3D.AniCrossFade(Utils3D.curPlayer,"Run");
        this._Bullet.onTouchDown();
    }

    onMouseUp() {
        this._Player.onMouseUp();
    }

    reLoadData() {
        Utils3D._curData = null;
        Utils3D._curData = this._JsonData[Utils3D.nCurScene.toString()];
        //Utils3D.nCurColor = Utils3D.shopSelect[1];
        if (this._BgLayer) {
            this._BgLayer.changeBg();
        }
        Laya.Scene.open(UICfg.GameStartView, true, { fromscene: SCENEINDEX.RESULT_SCENE });//打开ui  
    }

    resetCamera(bShop = false, bFirst = true) {
        var pos = new Laya.Vector3(0, 3.5, -11.5);//new Laya.Vector3(-1.7, 4.5, -5.5);
        if (bShop) {
            pos = new Laya.Vector3(0, 1.48, -12.5);
            this._camera.transform.localRotationEulerY = 180;
            this._camera.fieldOfView = 80;
            if (Utils3D.curPlayer) {
                //Utils3D.curPlayer.transform.localRotationEulerY = 180;
                Utils3D.AniCrossFade(Utils3D.curPlayer, "kaiqiang", 0.5, 0);
                Utils3D.AniCrossFade(Utils3D.curPlayer, "daiji", 0.5, 1);
            }
        } else {
            this._camera.transform.localRotationEulerY = 180;
            this._camera.fieldOfView = 60;
            if (Utils3D.curPlayer) {
                Utils3D.AniCrossFade(Utils3D.curPlayer, "daiji", 0.5, 1);
            }
        }
        this._camera.transform.localPosition = pos;
        this._camera.transform.localRotationEulerX = -30;
        //this._camera.transform.localRotationEulerY = -90;
    }

    onDestroy() {
        console.log("GameScene 退出程序");
        //Laya.timer.clearAll(this);
    }

    initMainLayer() { //开始游戏
        if (this.mainLayer) {
            this.mainLayer.destroy();
            this.mainLayer = null;
        }
        if (this.mainLayer == null) {
            Laya.Scene.open('gameUI/MainLayer.scene', false, null, Laya.Handler.create(this, (scene: Laya.Scene) => {
                this.mainLayer = scene as MainLayer;
                //试用界面
                //Laya.Scene.open(UICfg.UseDialog,false);
                this.gameBegin();
            }));
            //this.mainLayer = new view.MainLayer();
        }

    }

    tryUseCallF() {
        if (!Utils3D.bTryUse) { //暂不试用
            this.initMainLayer();
        } else {
            //this.loadPlayer(true, Utils3D.nLookSelect);
            this._Player.changeWeapon(true, Utils3D.nLookSelect - 1);
        }
    }

    gameBegin() { //开始游戏
        this.bRestart = false;
        if (!Utils3D.bTryUse) {
            gSdk.sdk.startLevel(Utils3D.nShowScene);
        }

        this._Bullet.gameBegin();
        this._Player.gameBegin();
        Laya.Tween.to(this._camera.transform, {
            localRotationEulerX: -12, localPositionX: -1.7, localPositionY: 3.5,
            localPositionZ: -11.5, localRotationEulerY: 180 + 8
        }, 1000, null, Laya.Handler.create(this, function () {
            Utils3D.bStart = true;
            Utils3D.weaponAni.active = true;
            this.mainLayer.gameBegin();
            Laya.timer.frameLoop(1, this, this.updating);
        }));
    }

    updating() {
        if (Utils3D.bStart) {
            this._Player.updating();
            this._Bullet.updating();
            this._Road.updating();
        }
    }

    get2DPoint(y = 2, node = null) {
        var point = new Laya.Vector3(0, 0, 0);
        var fPos = Utils3D.curPlayer.transform.position;
        var fx = 0;
        if (y != 2) {
            fx = -0.5;
        }
        let pos = new Laya.Vector3(fPos.x + fx, fPos.y + y, fPos.z);
        this._camera.viewport.project(pos, this._camera.projectionViewMatrix, point);
        return point;
    }

    updateLvUI(coin: number = 0, node = null) {
        if (this.mainLayer) {
            if (coin == 1) {
                this.addGold(1);
                var point = this.get2DPoint();
                this.mainLayer.showImgTips(point.x, point.y, 0);
                var nodeP = this.get2DPoint(0, node);
                this.mainLayer.showCoinAciton(nodeP.x - 20, nodeP.y - 20);
            }
            this.mainLayer.updateLvUI(Math.abs(29 / this.finishPosZ), coin);
        }
    }

    updateBnum(num) {
        this.mainLayer.updateBnum(num);
    }

    onHitSupply() {
        Utils3D.bulletNum += 20;
        if (Utils3D.bulletNum > 40) {
            Utils3D.bulletNum = 40;
        }
        this.updateBnum(Utils3D.bulletNum)
        this._Bullet.onHitSupply();
        this.showImgTips(0, 0);
        Laya.timer.once(1000, this, function () {
            this.showImgTips(1, 5);
        })
    }

    onHitObstacles(node, hitBody) {
        this._Road.onHitObstacles(node, hitBody);
    }

    onHitBullet() { //被敌人子弹击中
        this._Player.onHitBullet();
        this.mainLayer.onHitBullet();
        this.showImgTips(5, 9);
    }

    playerDeath() {
        Utils3D.bulletNum = 0;
        this._Bullet.showAtNumBullet(0);
    }

    addGold(_value: number, _per: number = 1) {
        let tgold: number = Math.floor(_value * _per);
        UserData.inst.setGold(tgold);
        EventManager.Ins.event("changeGold");
    }

    onGameEnd(bEnd = true) { //到达终点线
        if (bEnd) {
            Utils3D.bStart = false;
            this._End.onGameEnd();
            this.mainLayer.showEndClick();
            this._Bullet.onGameEnd();
            this.mainLayer.onGameEnd();
        } else {
            //this._Bullet.onGameEnd();
            //this.mainLayer.onGameEnd();
        }
    }

    onShoping() {
        Utils3D.bOpenShop = true;
        this.shopNode.active = true;
        this.resetCamera(true);
        EventManager.Ins.event("Shopping");
    }

    closeShop(bTryUse = false) {
        this.shopNode.active = false;
        this.resetCamera(false, false);
        this.changeWeapon();
        this.changeShipin();
        EventManager.Ins.event("Shopping");
        Utils3D.AniCrossFade(Utils3D.curPlayer, "tiaowu", 0.5, 0);
        Utils3D.AniCrossFade(Utils3D.curPlayer, "tiaowu", 0.5, 1);
    }

    changeShipin(bShopSelect = false, index = 0) {
        this._Player.changeShipin(bShopSelect, index);
    }

    changeWeapon(bShopSelect = false, index = 0) {
        this._Player.changeWeapon(bShopSelect, index);
    }

    showImgTips(min, max) {
        //if (!Utils3D.bStart) return;
        var point = this.get2DPoint();
        if (min == 0) {
            point = this.get2DPoint(1.5);
        }
        var randNum = Utils3D.random(min, max);
        if (this.mainLayer) {
            this.mainLayer.showImgTips(point.x, point.y, randNum);
        }
    }

    //游戏完成
    GameFinish(t = 1000) {
        if (gSdk.isTT) {
            gSdk.sdk.stopRecording();
        }
        else if (gSdk.isOP) {
            gSdk.sdk.exitGameScene();
        }
        let self = this;
        Utils3D.bStart = false;
        Utils3D.bMouseMoving = false;

        if (Utils3D.bGameWin == false) {
            Utils3D.curPlayer.active = false;
            this.showImgTips(9, 13);
        }

        if ((gSdk.isTT && adSdk.sdk.adConfig.boxCanShow())) {
            Laya.stage.once("QQ_CLICK_BOX", self, function () {
                self.onCloseFullAd(Utils3D.bGameWin);
            });
            Laya.timer.once(400, self, function () {
                Laya.Scene.open(UICfg.QQBox, false);
            })
            return;
        }

        Laya.timer.once(t, this, function (): void {
            Laya.timer.clearAll(this);
            if (this.mainLayer) {
                this.mainLayer.destroy();
                this.mainLayer = null;
            }
            if (gSdk.isWX) {
                self.tapBox();
                return;
            }
            //qq
            else if (gSdk.isQQ && adSdk.sdk.adConfig.isCrazyTap(UserData.inst.level,TapStyle.TAP_CHEST)) {
                Laya.stage.once("QQ_CLICK_BOX", self, function () {
                    self.onCloseFullAd(Utils3D.bGameWin);
                });
                Laya.Scene.open(UICfg.QQBox, false);
                return;
            }
            //Laya.Scene.open("gameUI/ResultView.scene");   
            DialogManager.Ins.sceneOpen(UICfg.ResultView, false, { gameresult: Utils3D.bGameWin })
        })
    }

    tapBox() {
        let self = this;
        if (adSdk.sdk.adConfig.boxCanShow()) {
            Laya.stage.once("QQ_CLICK_BOX", self, function () {
                self.showFull1();
            });
            Laya.Scene.open(UICfg.QQBox, false);
        }
        else
            self.showFull1();
    }

    showFull1() {
        let self = this;
        if (adSdk.sdk.adConfig.isBeforeGameAccount()) {
            Laya.stage.once("FULL_SCREEN_AD1_CLOSE", self, self.onCloseFullAd, [Utils3D.bGameWin]);
            ZsPlayInfo.IS_AUTO_CLICK = true;
            DialogManager.Ins.sceneOpen(UICfg.fullscreenad1, false);
        }
        else
            DialogManager.Ins.sceneOpen(UICfg.ResultView, false, { gameresult: Utils3D.bGameWin });
    }

    onCloseFullAd(result) {
        DialogManager.Ins.sceneOpen(UICfg.ResultView, false, { gameresult: Utils3D.bGameWin })
    }

    nextGame(bNext) {
        if (bNext) {
            if (!Utils3D.bTryUse) {
                gSdk.sdk.endLevel(Utils3D.nShowScene, "complete");
            }

            if (Utils3D.nPassGame == 0) {
                Utils3D.nCurScene++;
                Utils3D.nShowScene = Utils3D.nCurScene;
                if (Utils3D.nCurScene > Utils3D.nTotalScene) {
                    Utils3D.nCurScene = Utils3D.random(10, Utils3D.nTotalScene);
                    Utils3D.nPassGame = 1;
                    Laya.LocalStorage.setItem("PassGame", "1");
                }
                Laya.LocalStorage.setItem("CurScene", Utils3D.nCurScene.toString());
            } else {
                Utils3D.nShowScene++;
                //30关后随机10-30随机
                Utils3D.nCurScene = Utils3D.random(10, Utils3D.nTotalScene);
            }
            Laya.LocalStorage.setItem("ShowScene", Utils3D.nShowScene.toString());
        } else {
            if (!Utils3D.bTryUse) {
                gSdk.sdk.endLevel(Utils3D.nShowScene, "fail");
            }
        }

        this._camera.active = true;
        Utils3D.bLoadedComplete = false;
        Laya.timer.clearAll(this);
        this._GameView.nextGame();
        this._Player.nextGame();
        this._End.nextGame();
        this._Bullet.nextGame();
        Utils3D.bParticling = false;
        Utils3D.bStart = false;
        Utils3D.bGameWin = false;
        Utils3D.bMouseMoving = false;
        Utils3D.bPlayingAni = false;
        this.bRestart = true;
        Utils3D.taskScore = 0;

        Utils3D._Intervals = 0;
        Utils3D.bTryUse = false;
        this.initScene(this.bRestart);
    }

}