    // 每天更新从第1次开始
    static getUseLevel(){
        if(Utils3D.nTryUseCount < 6){
            var level = 20 - Utils3D.nTryUseCount + 1;
        }else{
            var level = Math.floor(Math.random() * 10) + 10;
        }
        if(level == Utils3D.nCurScene){
            level = level -1;
        }
        return level;
    }

    //获取3D节点
    static findNode(pNode:Laya.Node,path:string){
        if(!pNode || path == ""){
            return null;
        }
        
        let paths = path.split('/');
        let count = 0;
        let node = null;
        while(count < paths.length){
            if(node == null){
                node = pNode.getChildByName(paths[count]);
            }else{
                node = node.getChildByName(paths[count]);
            }
            count++;
        }
        return node;
    }

