    static onShocking(time=30,isShort = true){ //震动
        let t1 = Laya.timer.currTimer;
        if(t1-Utils3D._Intervals >= time){
            AudioManager.inst.playVibrator(isShort);
            Utils3D._Intervals = Laya.timer.currTimer;
            if(time == 60){
                AudioManager.inst.playSound(SoundCfg.hit, 1);
            }
        }
    }

    static tweenMove(target:Laya.Sprite3D,purSp,duration:number,complete:Laya.Handler = null,delay?:number){
        if(target == null) return null;
        let pos = target.transform.position;
        let tween = Laya.Tween.to(pos,{
                x:pos.x,
                y:purSp.transform.position.y,
                z:pos.z,
                update:new Laya.Handler(target,function(){
                    //this.transform.position = pos;
                    //console.log("tweenMove update  positionZ === ",purSp.transform.position.z);
                    if(purSp && Utils3D.bStart){
                        target.transform.localPositionX = purSp.transform.position.x;
                        target.transform.localPositionY = pos.y+0.4;
                        target.transform.localPositionZ = purSp.transform.position.z;
                    }
                    
                })
            },duration,null,complete,delay);
       return tween;
    }

    static tweenTo(target:Laya.Sprite3D,position,duration:number,complete:Laya.Handler = null,delay?:number){
        if(target == null || target.transform.position == null) return null;
        let pos = target.transform.position;
        if(target.transform){
            let tween = Laya.Tween.to(pos,{
                x:position.x,
                y:position.y,
                z:position.z,
                update:new Laya.Handler(target,function(){
                    if(Utils3D.bStart && target.transform){
                        this.transform.position = pos;
                    }
                    //console.log("tweenMove update  positionZ === ",this.transform.localPositionZ);
                })
            },duration,null,complete,delay);
        return tween;
        }
        
    }

    static tweenToY(target:Laya.Sprite3D,position,duration:number,ease, complete:Laya.Handler = null,delay?:number){
        if(target == null) return null;
        let pos = target.transform.position;
        var strongO = Number(Laya.Ease.strongOut(0,0,200,1000));
        var aaa = Laya.Ease.strongOut
        let tween = Laya.Tween.to(pos,{
                z:position.z,
                y:position.y,
                update:new Laya.Handler(target,function(){
                    this.transform.position = pos;
                    //console.log("tweenMove update  positionZ === ",this.transform.localPositionZ);
                })
            },duration,ease,complete,delay);
       return tween;
    }

    /** 获取范围内的随机数 [min,max) */
    static random(min, max) {
        return Math.random() * (max - min) + min << 0;
    }

