
import GameScene from "./GameScene";
import Utils3D from "../scripts/mgr/Utils3D"
import Constant from "../scripts/mgr/Constant"
import AudioManager from "../common/AudioManager";
import { SoundCfg } from "../scripts/defines/ResDef";


export default class GameView extends Laya.Script3D {
    private _gameScene:GameScene = GameScene.getInstance();
    /**场景 */
    public scene: Laya.Scene3D = null;
    constructor() { super(); }

    public onEnable(){
   
    }

    //进入触发(必须满足有一模型是刚体且 isTrigger = true)
    public onTriggerEnter(other: Laya.PhysicsComponent): void {
        if(!Utils3D.bStart) return;
        var name = other.owner.name;
        var node = other.owner as Laya.Sprite3D;
        if((name == "zhangai2"||name == "zhangai3"||name == "zhangai1") && !Utils3D.bHurt){
            //玩家碰到障碍物一直反弹后退
            node["bHit"] = false;
        }
        if(name == "0"){ //玩家子弹射中敌人
            Laya.timer.once(1500,this,function(){
                if(node) node["bHit"] = false;
            })
        }
        
        if(!node["bHit"] && node){
            node["bHit"] = true;
            var hitBody = this.owner as Laya.Sprite3D;
            if(name == "zhangai1" || name == "zhangai2" || name == "zhangai3"){
                if(node["hitCount"] == 1 && name != "zhangai3"){
                    node["hitCount"] = 2;
                }
                this.onHitObstacles(node,hitBody);
            }else if(name == "danyao"){
                this.onHitSupply(node,hitBody);
            }else if(name == "0"){ //玩家子弹射中敌人
                if(hitBody.name == "zidan"){
                    Utils3D.onShocking();
                    AudioManager.inst.playSound(SoundCfg.hit, 1);
                    this._gameScene._Road.onHitEnemy(node);
                }
            }else if(name == "zidan2" && hitBody.name == "00" && Utils3D.nDirection!=3){
                //被敌人子弹击中(滑行不能被击中)
                node.active = false;
                this._gameScene.onHitBullet();
            }else if(name == "GameEnd"){
                if(hitBody.name == "00"){
                    this._gameScene.onGameEnd();
                }
                node["bHit"] = false;
            }else if(name == "edge"){
                Utils3D.bHurt = true;
                node["bHit"] = false;
            }else if(name == "changeX1" || name == "changeX2"){
                Utils3D.nMaxLeftx = name=="changeX1"?0.5:3.2;
                Utils3D.nMaxRightx = name=="changeX1"?-0.5:-3.2;
                node.active = false;
            }else if(name == "di3"){ //滑行带
                Utils3D.nMaxLeftx = 3.2;
                Utils3D.nMaxRightx = -3.2;  
                Utils3D.nDirection = 3;
                Utils3D.AniCrossFade(Utils3D.curPlayer,"hua",0.5,0);
                Utils3D.AniCrossFade(Utils3D.curPlayer,"hua",0.5,1);
                Utils3D.playParticle(Utils3D.curPlayer,true,13);
            }else if(name == "EndAttack"){
                //this._gameScene.onGameEnd(false);
            }else if(name == "di6"){
                Utils3D.nMaxLeftx = 3.2;
                Utils3D.nMaxRightx = -2.2;  
            }else if(name == "di7"){
                Utils3D.nMaxLeftx = 2;
                Utils3D.nMaxRightx = -3.2;  
            }else if(name == "di1"){
                Utils3D.nMaxLeftx = 3.2;
                Utils3D.nMaxRightx = -3.2;  
            }else if(name == "di9"){
                Utils3D.nMaxLeftx = 1.7;
                Utils3D.nMaxRightx = -1.7; 
            }else if(name == "di8"){
                Utils3D.nMaxLeftx = 3.2;
                Utils3D.nMaxRightx = -0.5; 
            }else if(name == "di10"){
                Utils3D.nMaxLeftx = 0.5;
                Utils3D.nMaxRightx = -3.2; 
            }
        }
    }

    onHitCoins(other){
        var node = other.owner as Laya.Sprite3D;
        if(node){
            Utils3D.onShocking();
            other.owner["bHit"] = true;
            node.active = false;
            AudioManager.inst.playSound(SoundCfg.coin, 1);
            this._gameScene.updateLvUI(1,node);
        }
    }

    onHitSupply(node,hitBody){ //子弹补给
        if(hitBody.name == "zidan" || hitBody.name == "zidan2"){
            node["bHit"] = false;
        }else{ //必须人物碰到
            node.active = false;
            this._gameScene.onHitSupply();
            Utils3D.onShocking();
        }
    }

    onHitObstacles(node,hitBody){ //碰到障碍物
        if(node && hitBody){
            if(hitBody.name == "00"){ //人碰到障碍物
                Utils3D.bHurt = true;
                Utils3D.onShocking(60,false);
                if(node["hitCount"]==2){
                    node.active = false;
                    AudioManager.inst.playSound(SoundCfg.hit, 1);
                    this._gameScene.onHitObstacles(node,node);
                }
                node["hitCount"] = 1;
            }else{ //子弹打到障碍物
                //node.active = false;
                hitBody.active = false;
                AudioManager.inst.playSound(SoundCfg.hit, 1);
                this._gameScene.onHitObstacles(node,hitBody);
            }
        }
    }
 
    public onTriggerExit(other: Laya.PhysicsComponent): void {   
        var name = other.owner.name;
        if(name == "di3"){
            //console.log("出滑行带 ==========");
            Utils3D.nDirection = 0;
            Utils3D.bRunning = false;
            Utils3D.playParticle(Utils3D.curPlayer,false,13);
        }else if(name == "di4" || name == "di5"){
            //console.log("出 减速带 ======== ==========");
            Utils3D._speedZ = -0.08;
        }
    }

    public onTriggerStay(other: Laya.PhysicsComponent): void {
        //console.log("进入触发器中" + other.owner.name);
        var name = other.owner.name;
        if(name == "di4" || name == "di5"){
            if(this.owner.name == "00"){
                Utils3D._speedZ = name =="di4"?-0.02:-0.16;
                Utils3D.onShocking();
                return;
            }
        }
    }

        /* 开始碰撞时执行*/
    public onCollisionEnter(collision:laya.d3.physics.Collision):void{
        //进入触发(必须满足有一模型是刚体且 isTrigger = false)情况
        var name = collision.other.owner.name;
        var node = collision.other.owner as Laya.Sprite3D;
        console.log("开始碰撞 onCollisionEnter  name = "+name);
        if(node && !collision.other.owner["bHit"]){
            collision.other.owner["bHit"] = true;
            Utils3D.curPlayer.transform.position.y = node.transform.position.y+3;
        }
    }

    nextGame(){
        Laya.timer.clearAll(this);
        Utils3D.nAniPriority = 0;
        Utils3D.sAniState = "Run";
        Utils3D.nMissBody = [];
    }
}


