    constructor() {
        super();
        this.height = Laya.stage.height;
        this.bg.graphics.drawRect(0, 0, Laya.stage.width, Laya.stage.height, "#23a8ea");

        this.PlatformUIInit();
    }

    onAwake(): void {
    }

    onEnable(): void {
        this.passBtn.on(Laya.Event.CLICK, this, this.onPassClick);
        this.closeBtn.on(Laya.Event.CLICK, this, this.onResetGame, [1]);
        this.shareBtn.on(Laya.Event.CLICK, this, this.sharevideoClick);
        this.moreGameBtn.on(Laya.Event.CLICK, this, this.moreGameClick);
        //Utils3D.nTotalCoins += 100;
        this.Btn_tt_next1.on(Laya.Event.CLICK, this, this.onPassClick);
        this.Btn_tt_next2.on(Laya.Event.CLICK, this, this.OnPassClick_hole);
        this.Btn_closehole.on(Laya.Event.CLICK, this, this.OnClosehole);

        //Laya.LocalStorage.setItem("TotalCoins",Utils3D.nTotalCoins.toString());
        //this.PlatformUIInit();
        this.onEnableADControl();

        AudioManager.inst.playSound(SoundCfg.success, 1);
    }

    onGameEnd() {
        this.titleImg.skin = Utils3D.bGameWin ? "common/end-01.png" : "common/end-02.png";
        if (Utils3D.bGameWin) {
            this.titleImg.visible = true;
            //this.faiedImg.visible = false;
            this.passBtn.visible = true;
            //this.restartBtn.visible = false;
            this.closeBtn.visible = true;
        } else {
            this.scoreTxt.text = "x" + Utils3D.nCurScene.toString();
            Utils3D.nTotalCoins += Utils3D.nCurScene;
            this.titleImg.visible = false;
            //this.faiedImg.visible = true;
            this.passBtn.visible = false;
            //this.restartBtn.visible = true;
            this.closeBtn.visible = false;
        }
    }

    nextGame() {
        //this.close();
        let self = this;
        DialogManager.Ins.sceneClose(this.url)
        Laya.timer.once(50, this, function () {
            self._gameScene.nextGame(Utils3D.bGameWin);
        })
    }

    onOpened(_data) {
        if (_data) {
            let score = Utils3D.taskScore > 0 ? Utils3D.taskScore : this.failcoins;
            this.GetCoins = _data.gameresult ? 100 : this.failcoins;
            this.UpdateUI(_data.gameresult);
            // this.GetCoins = Utils3D.bGameWin ? score : this.failcoins;
            // this.UpdateUI(Utils3D.bGameWin);
            UserData.inst.setLevel();
        }
    }

    /**
      * 判定两个时间是否是同一天
      */
    public isSameDate(timestamp1: number, timestamp2: number): boolean {
        let date1 = new Date(timestamp1);
        let date2 = new Date(timestamp2);
        return date1.getFullYear() == date2.getFullYear()
            && date1.getMonth() == date2.getMonth()
            && date1.getDate() == date2.getDate();
    }

