import { ui } from "../ui/layaMaxUI";
import TipManager from "../common/TipManager";
import EventManager from "../common/EventManager";
import gSdk from "../common/platformsdk/platform";
import { AdsType } from "../common/platformsdk/plat-def";
import Utils3D from "../scripts/mgr/Utils3D";
import value from "*.glsl";

export default class UnlockNewDialog extends ui.commonUI.UnlockNewDialogUI {
    constructor() {
        super();
        this.height = Laya.stage.height;
        //this.bg.height = this.height;
    }
    onEnable() {
        //this.bg.on(Laya.Event.CLICK, this, this.onCloseClick)
        let maskSprite = new Laya.Sprite();
        this.imgIcon.mask = maskSprite;
        this._mask = maskSprite;
       
        this.updateLockData();
        Laya.timer.frameLoop(1,this,this.updateProgress)
        Laya.timer.once(3000,this,function(){
            var self = this;
            if(self){
                this.close();
            }
        });
    }       

    private rareGateId = 0;
    private type = 0;
    onOpened(data) {
        console.log('UnlockRareNewDialog', data);
        if (data) {
            this.type = data.type;
            //this.lockImg.skin = GameMgr.Instance.getImgByGateId(data.id);
            this.rareGateId = data.id;
        }
    }

    onCloseClick(e) {
        this.close();
        e.stopPropagation();
    }

    onUnLock(e) {
        console.log('onUnLock', this.rareGateId)
        this.close();
        if (this.type == 2) {//立即运行
            //DialogManager.Ins.sceneClose("Views/SkinDialog.scene");
            EventManager.Ins.event("cut", this.rareGateId);
        } else if (this.type == 3) {//立即运行
            //DialogManager.Ins.sceneClose("Views/RareUnlockDialog.scene");
            EventManager.Ins.event("cut", this.rareGateId);
        } else {
            //GameMgr.Instance.setIsRareGate(true);
        }
        e.stopPropagation();
    }

    _mask = null;
    tempCount = 0;
    textCount = 0;
    cc = 0;
    updateProgress(){
        var value = 3;
        if(Utils3D.nCurScene<=2){
            Utils3D.proCount+=2;
            value = 2;
        }else{
            Utils3D.proCount++;
        }
   
        if(Utils3D.proCount <= (this.tempCount+this.iconH*this.lvPercent*0.01)){
            this._mask.graphics.clear();
            this._mask.graphics.drawRect(0, 0, this.imgIcon.width, Utils3D.proCount, "#fff000");
            if(Utils3D.proCount%value==0 && this.cc < this.lvPercent){
                this.textCount++;
                this.cc++;
                if(this.textCount >= 95){
                    this.textCount = 100;
                }
                this.txtUnlock.text = "解锁："+this.textCount+"%";
            }
        }else{
            this.cc = 0;
            Laya.timer.clear(this,this.updateProgress);
        }
    }

    lvPercent = 0;
    lockLv = 2;
    iconH = 370;
    updateLockData(){ //解锁数据
        var value1 = Utils3D.nCurScene%2;
        var value2 = Math.floor(Utils3D.nCurScene/5);
        var w = this.imgIcon.width;
        var h = this.imgIcon.height;
        
        if(Utils3D.nCurScene <= 2){
            this.imgIcon.skin = "image/shop/lock2_6.png";
            this.imgGray.skin = "image/shop/m2_6.png";
            h = 408;
            this.lvPercent = 50;
            if(Utils3D.nCurScene == 2){
                this.saveData(2,5);
            }
            this.lockLv = 2;
        }else if(Utils3D.nCurScene>2 && Utils3D.nCurScene <= 6){
            this.imgGray.skin = "image/shop/m1_4.png";
            this.imgIcon.skin = "image/shop/lock1_4.png";
            h = 300
            value1 = (Utils3D.nCurScene-2)%4;
            this.lvPercent = 25;
            this.lockLv = 3;
            if(Utils3D.nCurScene == 6){
                this.saveData(1,3)
            }
        }else if(Utils3D.nCurScene>6 && Utils3D.nCurScene <= 10){
            this.imgGray.skin = "image/shop/m2_2.png";
            this.imgIcon.skin = "image/shop/lock2_2.png";
            h = 394
            value1 = (Utils3D.nCurScene-6)%4;
            this.lvPercent = 25;
            this.lockLv = 3;
            if(Utils3D.nCurScene == 10){
                this.saveData(2,1)
            }
        }else if(Utils3D.nCurScene>10 && Utils3D.nCurScene <= 15){
            this.imgGray.skin = "image/shop/m1_5.png";
            this.imgIcon.skin = "image/shop/lock1_5.png";
            h = 300
            value1 = (Utils3D.nCurScene-10)%5;
            this.lvPercent = 20;
            this.lockLv = 3;
            if(Utils3D.nCurScene == 15){
                this.saveData(1,4)
            }
        }else if(Utils3D.nCurScene>15 && Utils3D.nCurScene <= 20){
            this.imgGray.skin = "image/shop/m1_2.png";
            this.imgIcon.skin = "image/shop/lock1_2.png";
            h = 300
            this.lvPercent = 20;
            this.lockLv = 3;
            value1 = (Utils3D.nCurScene-15)%5;
            if(Utils3D.nCurScene == 15){
                this.saveData(1,1)
            }
        }
        this.iconH = h;
        var num1 = Math.floor(h*(100-this.lvPercent)/100);
        var num2 = Math.floor(h*this.lvPercent/100);
        Utils3D.proCount = value1==0?num1:(value1-1)*num2;
        this.textCount = value1==0?100-this.lvPercent:(value1-1)*this.lvPercent;
        this.tempCount = Utils3D.proCount;
        this._mask.graphics.clear();
        this._mask.graphics.drawRect(0, 0, w, Utils3D.proCount, "#fff000");
        
    }

    saveData(tab,index){
        for(var i = 0;i<6;i++){
            if(Utils3D.shopTools[tab][i] == 1){
                Utils3D.shopTools[tab][i] = 2;
            } 
        }

        Utils3D.shopSelect[tab] = index;
        Utils3D.shopTools[tab][index] = 1;
        Laya.LocalStorage.setJSON("ShopItems", Utils3D.shopTools);
        Laya.LocalStorage.setJSON("ShopItemSelect", Utils3D.shopSelect);
    }

    onDestroy() {
    }
}