import { ui } from "../ui/layaMaxUI";
import TipManager from "../common/TipManager";
//import PlayerData from "../../data/PlayerData";
import EventManager from "../common/EventManager";
import Display from "../common/Display";
import UtilTool from "../common/UtilTools";
import gSdk from "../common/platformsdk/platform";
import { AdsType } from "../common/platformsdk/plat-def";
import UserData from "../common/data/UserData";
import SignData from "../common/data/SignData";
import adSdk from "../common/adDataSdk/adDataSdkFactory";
import AudioManager from "../common/AudioManager";
import { AudioType } from "../common/data/AudioData";
import UITool from "../common/UITool";
import AdTool from "../common/AdTool";
import { DialogManager } from "../common/DialogManager";

export default class UISelectBox extends ui.commonUI.UISelectBoxUI {
    private box_golds = [25, 50, 75, 100, 125, 150, 175, 200, 300];
    private openedBoxs = [0, 0, 0, 0, 0, 0, 0, 0, 0];
    /**已经打开的宝箱的个数 */
    private box_open_count = 0;
    /**当前打开盒子的次数 */
    private cur_open_count: number = 0;
    private _callBack: Function = null;
    private _sumGold: number = 0;

    constructor() {
        super();
        this.height = Laya.stage.height;
        this.bg.graphics.drawRect(0, 0, Laya.stage.width, Laya.stage.height, "#23a8ea");

        this.PlatformUIInit();
    }


    onOpened(params) {
        if (params) {
            this._callBack = params.callback;
        }
    }

    onAwake() {
    }

    onEnable() {
        UtilTool.Array.randomArray(this.box_golds);
        this.threeBtn.clickHandler = new Laya.Handler(this, this.onThree);
        this.closeBtn.clickHandler = Laya.Handler.create(this, this.closeView);
        this.backBtn.clickHandler = Laya.Handler.create(this, this.closeView);
        let boxs = this.main.getChildByName('boxs');
        for (let i = 1; i <= 9; i++) {
            let item = boxs.getChildByName('item' + i) as Laya.Button;
            item.clickHandler = new Laya.Handler(this, this.onItem, [i]);
        }
        this.box_open_count = 0;
        this.cur_open_count = 0;
        this.threeBtn.visible = false;
        this.closeBtn.visible = false
    }


    onItem(itemIndex) {
        if (this.cur_open_count >= 3 || this.openedBoxs[itemIndex - 1] == 1) return;
        let item = this.boxs.getChildByName(`item${itemIndex}`) as Laya.Button;
        let box = item.getChildByName('box') as Laya.Sprite;
        box.visible = false;
        let getGoldAdward = () => {
            let goldIcon = new Laya.Image();
            goldIcon.skin = "prefab/home-12.png";
            goldIcon.x = item.width / 2 - goldIcon.width / 2;
            goldIcon.y = 25;
            let label = new Laya.Label(`+ ${this.box_golds[this.box_open_count]}`);
            this._sumGold += this.box_golds[this.box_open_count];
            label.color = "#ffffff";
            label.fontSize = 35;
            label.x = item.width / 2 - label.width / 2;
            label.y = 102.5;
            item.addChild(goldIcon);
            item.addChild(label);
        }

        if (this.box_open_count == 8) {
            let rares = this.getRare();
            if (rares.length > 0) {
                //获得稀有道具
                item.skin = "box/tips-21.png";
                // var index = Math.floor(Math.random() * rares.length);
                // let rare = rares[index];
                // let img = new Laya.Image();
                // img.skin = `skin/cake_${rare.id}.png`;
                // img.anchorX = 0.5;
                // img.anchorY = 0.5;
                // img.x = item.width / 2;
                // img.y = item.height / 2 + 10;
                // img.scale(0.6, 0.6);
                // item.addChild(img);
                // UserData.unLockCreams(rare.type, rare.id);
                // Laya.Scene.open(ViewCfg.UIUnlock, false, {
                //     type: rare.type, id: rare.id, callback: () => {
                //         this.closeView();
                //     }
                // });
            } else {
                getGoldAdward();
                this.timer.once(2000, this, () => {
                    this.closeView();
                });
            }
        } else {
            getGoldAdward();
        }

        this.openedBoxs[itemIndex - 1] = 1;
        this.box_open_count++;
        this.cur_open_count++;
        if (this.cur_open_count == 3) {
            this.threeBtn.visible = true;
            this.closeBtn.visible = true;
        }
    }

    getRare() {
        let rares = [];

        return rares;
    }

    onThree() {
        AdTool.Ins.playVideo("请观看完整视频后才能获得奖励", () => {
            this._sumGold = this._sumGold * 3;
            this.closeView();
            // this.cur_open_count = 0;
            // this.threeBtn.visible = false;
            // this.closeBtn.visible = false;
        }, this);
    }

    closeView() {
        //获取金币奖励
        UserData.inst.setGold(this._sumGold);
        //UITool.Ins.goldEffect(new Laya.Vector2(this.threeBtn.x, this.threeBtn.y), 10, this);
        EventManager.Ins.event("changeGold");
        this._callBack && this._callBack();
        DialogManager.Ins.sceneClose(this.url);
    }

    PlatformUIInit() {
        let uis: Array<Laya.Sprite> = [];
        if (Display.largeScreen()) { //如果是大屏
            this.main.y += 120;
        }

        if (gSdk.isWX) {
            this.getChildByName("GoldSp")["x"] = Laya.stage.width - 200;
            this.getChildByName("GoldSp")["y"] = 10;
        }
    }
}