import gSdk from "../../common/platformsdk/platform";
import TipManager from "../../common/TipManager";
import adSdk from "../../common/adDataSdk/adDataSdkFactory";
import { navigateSceneType } from "../../common/adDataSdk/adDataSdkDef";

export default class MoreGame1 extends Laya.Script {
    private data: any;
    /** @prop {name:isH,tips:"ssss",type:boolean,default:false}*/
    /**是否水平滚动*/
    public isH: boolean = false;
    /** @prop {name:downH,tips:"ssss",type:int,default:54}*/
    public downH: number = 54;
    /** @prop {name:downW,tips:"ssss",type:int,default:200}*/
    public downW: number = 200;
    private timestmp: number = 0;
    private time: number = 0;
    private adData = [];
    private fingerindex: number = -1;
    constructor() {
        super();
    }

    onAwake(): void {
    }

    onEnable(): void {
        let self = this;

        if (gSdk.isOP || gSdk.isQQ || gSdk.isVV) {
            (this.owner as Laya.Box).visible = false;
            return;
        }
        else if (gSdk.isWX) {
            if (!adSdk.sdk.adConfig.electControl('zs_jump_switch')) {//WX二级开关
                (this.owner as Laya.Box).visible = false;
                return;
            }
        }

        (this.owner.getChildByName("list") as Laya.List).mouseHandler = new Laya.Handler(this, this.click);
        (this.owner.getChildByName("list") as Laya.List).renderHandler = new Laya.Handler(this, this.rederer);

        if (gSdk.isTT) {
            let gameList = adSdk.sdk.getAdList(adSdk.sdk.getAdType(navigateSceneType.endPage), 30);
            if (gSdk.isTT)
                this.setData(gameList, true);
            else
                this.setData(gameList, this.isH);
        }
        else if (gSdk.isWX) {

            (this.owner.getChildByName("list") as Laya.List).hScrollBarSkin = "";
            (this.owner.getChildByName("list") as Laya.List).vScrollBarSkin = "";
            if (gSdk.sdk.isUseZS) {
                var zsSdk = window["zs"]["sdk"];
                zsSdk.loadAd(function (data) {
                    self.adData = data["endPage"];
                    self.LoadShowGame();
                    Laya.timer.loop(3000, self, self.LoadShowGame);
                });
            }
            else {
                this.adData = adSdk.sdk.getAdList(adSdk.sdk.getAdType(navigateSceneType.endPage));
                this.LoadShowGame();
                Laya.timer.loop(3000, this, this.LoadShowGame);
            }
        }
        //this.owner["visible"]=false;
    }

    LoadShowGame() {
        let gameList = [];
        gameList.push(this.adData[Math.floor(Math.random() * this.adData.length)]);
        let maxCount = 6;
        if (this.adData.length < maxCount)
            maxCount = this.adData.length;
        while (gameList.length < maxCount) {
            let tmpdata = this.adData[Math.floor(Math.random() * this.adData.length)];
            let bfind = false;
            for (let i = 0; i < gameList.length; i++) {
                let tmpappid = tmpdata.appid || tmpdata.id;
                let gameid = gameList[i].appid || gameList[i].id;
                if (tmpappid == gameid) {
                    bfind = true;
                    //console.log("随机到了一个相同的游戏:", tmpdata.app_title);
                    break;
                }
            }
            if (!bfind)
                gameList.push(tmpdata);
        }

        let tmpfinger = Math.floor(Math.random() * gameList.length);
        while (tmpfinger == this.fingerindex) {
            tmpfinger = Math.floor(Math.random() * gameList.length);
        }

        this.fingerindex = tmpfinger;
        let posx, posy;
        switch (this.fingerindex) {
            case 0:
                posx = 120;
                posy = 160;
                break;
            case 1:
                posx = 360;
                posy = 160;
                break;
            case 2:
                posx = 600;
                posy = 160;
                break;
            case 3:
                posx = 120;
                posy = 450;
                break;
            case 4:
                posx = 360;
                posy = 450;
                break;
            case 5:
                posx = 600;
                posy = 450;
                break;
        }
        let finger = this.owner.getChildByName("fingertap") as Laya.Image;
        if (finger) {
            finger.x = posx;
            finger.y = posy;
            finger.visible = true;
        }

        //console.log("选中的6个游戏列表", gameList);
        (this.owner.getChildByName("list") as Laya.List).array = gameList;
    }


    public setData(_data, _isH: boolean = true) {
        this.data = _data;
        this.isH = _isH;
        (this.owner.getChildByName("list") as Laya.List).array = this.data;
        if (_isH) {
            (this.owner.getChildByName("list") as Laya.List).hScrollBarSkin = "";
        }
        else (this.owner.getChildByName("list") as Laya.List).vScrollBarSkin = "";

        Laya.timer.once(1000, this, this.twMove);
    }

    public OnShow(): void {
        /*
        if (this.isStopMove) {
            this.isStopMove = false;
            this.twMove();
        }*/

        if (!gSdk.isWX) {
            this.stopMove();
            let tlist = this.owner.getChildByName("list") as Laya.List;
            tlist.tweenTo(0, 500, Laya.Handler.create(this, () => {
                this.tweenList(tlist, false);
            }))
        }
    }

    private rederer(sp: Laya.Sprite, index: number) {
        let tdata = sp["dataSource"];
        if (tdata) {
            let timg: Laya.Sprite = sp.getChildByName("icon") as Laya.Sprite
            timg.texture = tdata.link_img || tdata.app_icon;//tdata.link_img;
            sp.getChildByName("nameTxt")["text"] = tdata.link_name || tdata.app_title;//tdata.link_name;
            let tsp = sp.getChildByName("downBg") as Laya.Sprite;
            tsp.graphics.drawRect(0, 0, tsp.width, tsp.height, this.getClor(index));
            sp.getChildByName("hot")["visible"] = index < 2;
            if (index < 2) {
                sp.getChildByName("hot")["texture"] = index == 0 ? "prefab/icon-hot.png" : "prefab/icon-new.png";
            }
        }
    }

    /**
     * 处理已经跳转过游戏
     * @param data 
     * @returns 
     */
    processGamePlayed(data) {
        var navAppObj = adSdk.sdk.getNavigateSuccessObj();
        let appid = data["appid"] || data["id"];
        if (navAppObj && navAppObj[appid]) {
            let showdata;
            for (let i = 0; i < 3; i++) {//最多查找3次
                showdata = this.adData[Math.floor(Math.random() * this.adData.length)];
                if (showdata) {
                    let newAppid = showdata["appid"] || showdata["id"];
                    if (!navAppObj[newAppid]) {
                        console.log('未跳转成功过', appid, newAppid);
                        break;
                    }
                }
            }
            return showdata;
        }
        return data;
    }

    private click(e: Laya.Event, index) {
        let tdata = e.target["dataSource"];
        let tscene = navigateSceneType.endPage;
        if (this.isH) tscene = navigateSceneType.endPage;
        let tcan = false;
        if (e.type == Laya.Event.CLICK) {
            if (!gSdk.isWX) {
                if (this.isH) {
                    if (gSdk.isTT) {
                        gSdk.sdk.showMoreGamesModal(tdata, null, null, null, (code) => {
                            if (code == 1) TipManager.Ins.showMsgTip("打开更多游戏失败", this);
                        })
                    } else adSdk.sdk.navigateToMiniProgram(tdata, tscene);
                }
                else {
                    adSdk.sdk.navigateToMiniProgram(tdata, tscene);
                }
            }
        }
        if (e.type == Laya.Event.MOUSE_DOWN) {
            if (gSdk.isWX) {
                let showgame = this.processGamePlayed(tdata);
                adSdk.sdk.navigateToMiniProgram(showgame, tscene, () => { }, () => { });
                //this.stopMove();
            }
        }
        //if(Sdk.getIns().platform=="toutiao")e.stopPropagation();
    }

    private getClor(_index: number): string {
        let ttt = ["0xb852b6", "0x4294a2", "0x2f67c4", "0xe09031", "0x35bd75", "0xc5645e"];
        return ttt[_index % 6];
    }


    public twMove() {
        let time = new Date().getTime();
        if (time - this.timestmp >= 20000 || this.timestmp > 0) {
            this.timestmp = time;
            let tlist = this.owner.getChildByName("list") as Laya.List;
            //let tparam:any={y:100};
            //if(this.isH)tparam={x:100};
            let tnum = 30;
            let ttime = 50000;
            if (gSdk.isTT) {
                //tnum=7;
                ttime = 70000;
            }
            //tlist.scrollTo(0);
            //tlist.tweenTo(tnum, ttime);
            this.tweenList(tlist, false, 0);
        };
        //Laya.Tween.to(tlist.,tparam,9000);
    }

    private isStopMove = false;
    private stopMove() {
        this.isStopMove = true;
        let tlist = this.owner.getChildByName("list") as Laya.List;
        Laya.Tween.clearTween(tlist);
    }

    private createGameInfo(appid: string, name: string, icon: string) {
        return { app_icon: icon, appid: appid, app_title: name, app_desc: name };
    }

    onDisable(): void {

    }

    /**
     * 列表自动滚动
     * @param list 滚动列表
     * @param scrollX 是否水平滚动 否则 垂直滚动
     * @param curIndex 开始滚动索引(默认 0)
     */
    tweenList(list: Laya.List, scrollX: boolean, curIndex = 0) {
        let tlist = list;
        let count = tlist.array.length;
        let repeatX = tlist.repeatX;
        let repeatY = tlist.repeatY;
        let oneCount = repeatX * repeatY;
        if (count <= oneCount) return;
        let l = oneCount;
        if (scrollX && count % repeatY != 0) {   //水平移动
            let n = repeatY - count % repeatY;
            l = oneCount - n;
        } else if (!scrollX && count % repeatX != 0) { //垂直移动
            let n = repeatX - count % repeatX;
            l = oneCount - n;
        }
        let cc = count - l;
        //tlist.tweenTo(cc - curIndex, (cc - curIndex) * 1500, null);

        tlist.tweenTo(cc - curIndex, (cc - curIndex) * 1500, Laya.Handler.create(this, () => {
            tlist.tweenTo(0, cc * 1500, Laya.Handler.create(this, () => {
                this.tweenList(list, false, 0);
            }));
        }));

    }
}