        this.zs_auto_pop_ups_switch = webResponse.zs_auto_pop_ups_switch == 1 && this.zs_auto_witch;
        this.zs_jump_switch = webResponse.zs_jump_switch == 1 && this.isSameVersion();  //首页浮窗是四级开关,其他浮窗,结算页前后的全屏,结算页的游戏导出是二级开关 控制右上角游戏导出是否显示
