import { ui } from "../ui/layaMaxUI";
import Utils3D from "../scripts/mgr/Utils3D"
import GameScene from "../script/GameScene";
import Constant from "../scripts/mgr/Constant"
import gSdk from "../common/platformsdk/platform";
import adSdk from "../common/adDataSdk/adDataSdkFactory";
import TipManager from "../common/TipManager";
import { UICfg, SoundCfg, EventCfg } from "../scripts/defines/ResDef";
import AudioManager from "../common/AudioManager";
import EventManager from "../common/EventManager";
import AdList from "../commonUI/zs/AdList";
import FakeExitBtn from "../commonUI/zs/FakeExitBtn";
import UserData from "../common/data/UserData";
import { DialogManager } from "../common/DialogManager";
import ExportMoreGame from "../commonUI/zs/ExportMoreGame";
import AdTool from "../common/AdTool";
import ZsPlayInfo from "../commonUI/zs/ZsPlayInfo";
import FloatGameImg from "../commonUI/zs/FloatGameImg";
import UITool from "../common/UITool";
import { navigateSceneType } from "../common/adDataSdk/adDataSdkDef";
import { adDataSdkBase } from "../common/adDataSdk/adDataSdkBase";
import display from "../common/Display";
import { TapStyle } from "../common/adDataSdk/ADConfigBase";
//export  module view {
/**
 * 主界面
 */
export default class MainLayer extends ui.gameUI.MainLayerUI {
    private _gameScene: GameScene = GameScene.getInstance();
    private curTouchId: number = -1;
    private originPiont = new Laya.Vector2(0, 0);
    public deltaX = 0;
    public deltaY = 0;

    constructor() {//closeCallback: Function = null
        super();
        //Laya.stage.addChild(this);
        this.height = Laya.stage.height;
        this.imgBg.height = Laya.stage.height;
        this.shakeBg.height = Laya.stage.height;
        this.imgBg.on(Laya.Event.MOUSE_DOWN, this, this.onTouchDown);
        this.imgBg.on(Laya.Event.MOUSE_MOVE, this, this.onMoveEvent);
        this.imgBg.on(Laya.Event.MOUSE_UP, this, this.onMouseUp);
        this.imgBg.on(Laya.Event.MOUSE_OUT, this, this.onMouseUp);

        this.btnPopGame.on(Laya.Event.CLICK, this, this.onPopGameClick)
        this.btnPopGame.on(Laya.Event.MOUSE_DOWN, this, this.onPopMouseDown)
        this.btnPopGame.on(Laya.Event.MOUSE_UP, this, this.onPopMouseUp)

        //重新开始
        this.btnReset.on(Laya.Event.CLICK, this, this.nextGame);
        this.btnMoregame.on(Laya.Event.CLICK, this, this.onClickMoreGame);

        this.updateBnum(20);
        this.imgh.skin = "image/h3.png";
        this.bulletFclip.value = "20";
        this.shakeBg.visible = false;
        this.imgClick1.visible = false;
        this.imgClick2.visible = false;
        this.imgClick1.on(Laya.Event.CLICK, this, this.updateClickPro, [this.nClickAdd]);
        this.imgClick2.on(Laya.Event.CLICK, this, this.updateClickPro, [this.nClickAdd]);

        if (gSdk.isWX) {
            EventManager.Ins.on(EventCfg.EVENT_BG_TO_GAME, this, this.Bgtogame);
            EventManager.Ins.on(EventCfg.EVENT_GAME_TO_BG, this, this.gametoBg);
        }
    }

    updateBnum(num) { //更新子弹数量
        //this.bulletText.text = num.toString();
        this.bulletFclip.value = num.toString();
    }

    nextGame() { //重新开始
        Laya.timer.clearAll(this);
        Utils3D.nLookSelect = -1;
        this.hpCount = 3;
        this.clickCount = 0;
        this.timeCount = 0;
        Utils3D.bEndClick = false;
        Utils3D.bBtnClicked = false;
        clearInterval(this.pressTime);
        if (this._gameScene.mainLayer) {
            this._gameScene.mainLayer.destroy();
            this._gameScene.mainLayer = null;
        }
        this._gameScene.nextGame(false);

        if (gSdk.isTT || gSdk.isOP || gSdk.isQQ) {
            console.log("show TryUseView------")
            //Laya.Scene.open(UICfg.UseDialog,false);
        }
    }

    showEndClick() { //疯狂点击UI
        this.clickBg.visible = true;
        this.clickBar.height = 0;
        this.imgClick2.visible = false;
        Laya.timer.loop(50, this, this.updateClickPro, [-this.nTimeReduce]);
    }

    nClickFinish = 1; //完成进度(1为100%)
    nClickAdd = 0.2; //每点一次增加
    nTimeReduce = 0.02; //每50毫秒减少
    clickCount = 0;
    secondTime = 6;
    timeCount = 0;
    //更新疯狂点击进度条及手指动作
    updateClickPro(addnum) {
        if (addnum > 0) {
            this.clickBar.height += 310 * addnum;
            this._gameScene._End.setGuoScale(addnum / 2);
            if (this.clickBar.height >= 310 * this.nClickFinish) { //进度条满
                if (Utils3D.bOpenClick) {
                    display.init();
                    let xy = display.designToScreen(0, this.imgClick1.y);
                    gSdk.sdk.bannerMoveTo(xy.y, 0);
                }
                Utils3D.bEndClick = true;
                Laya.timer.clear(this, this.updateClickPro);
                this.clickBg.visible = false;
                this.imgClick1.visible = false;
                this.imgClick2.visible = false;
                //Laya.timer.once(1000, this, () => {this.completeClick} );
                Laya.timer.once(1000, this, this.completeClick);
            }
        } else {
            if (this.clickBar.height > 0) {
                this.clickBar.height += 310 * addnum;
                this._gameScene._End.setGuoScale(addnum / 2);
            }
        }
        this.clickCount++;
        if (this.clickCount == 5) {
            this.imgClick1.visible = false;
            this.imgClick2.visible = true;
        } else if (this.clickCount == 10) {
            this.imgClick1.visible = true;
            this.imgClick2.visible = false;
            this.clickCount = 0;
        }

        //if (!Utils3D.bOpenClick) {
        this.timeCount++;
        if (this.timeCount % 20 == 0 && this.secondTime > 0) {
            this.secondTime--;
            this.timeText.text = this.secondTime.toString();
            if (this.secondTime <= 0) {
                Laya.timer.clear(this, this.updateClickPro);
                this.clickBg.visible = false;
                this.imgClick1.visible = false;
                this.imgClick2.visible = false;
                this.completeClick();
            }
        }
        //}
    }

    completeClick() { //完成点击
        if (gSdk.isWX) gSdk.sdk.bannerMoveTo(0, 0);
        this._gameScene._End.runToBoss();
    }

    Bgtogame() {
        console.log("MainLayer界面从后台切换回游戏");
        // if (Utils3D.bEndClick) {
        //     this.completeClick();
        // }
    }
    gametoBg() {
        console.log("MainLayer界面切换到后台");
    }

    onEnable(): void {
        this.onEnableADControl();
        this.PlatformUIInit();
    }

    onPopMouseDown() {
        this.btnPopGame.scaleX = 0.9;
        this.btnPopGame.scaleY = 0.9;
    }
    onPopMouseUp() {
        this.btnPopGame.scaleX = 1;
        this.btnPopGame.scaleY = 1;
    }

    onPopGameClick(e: Laya.Event) {
        this.btnPopGame.visible = false;

        let exportgame = this.getChildByName('exportlistPen') as Laya.Sprite;
        exportgame.visible = true;

        if (!exportgame.getComponent(ExportMoreGame)) {
            exportgame.addComponent(ExportMoreGame);
        }
        let expcom = exportgame.getComponent(ExportMoreGame);
        expcom.tweenshow(this.btnPopGame.y);

        if (!exportgame.getChildByName('adList').getComponent(AdList)) {
            let adList: AdList = exportgame.getChildByName('adList').addComponent(AdList);
            adList.requestAdData(navigateSceneType.sidebar, true, ZsPlayInfo.SCROLL_VERTICAL, null, null, false, true);
        }
        e.stopPropagation();
    }
    showExportGame() {
        this.btnPopGame.visible = true;
    }

    onEnableADControl() {
        if (gSdk.isTT) {
            console.log("tt游戏中-隐藏banner");
            gSdk.sdk.hide_banner();
            gSdk.sdk.startRecord();
        }
        else if (gSdk.isWX) {
            if (adSdk.isQT) {
                if (adSdk.sdk.adConfig.electControl("zs_banner_show")) {
                    gSdk.sdk.showBannerzs();
                }
                else {
                    gSdk.sdk.hideBannerzs();
                }
            }
            else {
                gSdk.sdk.showBannerzs();
            }
        }
        else {
            console.log("非tt游戏中-显示banner");
            gSdk.sdk.showBanner();
        }

        if (gSdk.isOP) {
            gSdk.sdk.enterGameScene();
        }
    }

    PlatformUIInit() {
        if (gSdk.isWX) {
            //Laya.stage.on(ZsPlayInfo.EVENT_NAVIGATE_FAILED, this, this.onShowFullListAd);

            if (adSdk.sdk.adConfig.electControl("zs_horizontal_export")) {
                let electList: Laya.Image = this.getChildByName("exportList1") as Laya.Image;
                electList.visible = true;
                if (!electList.getChildAt(0).getComponent(AdList)) {
                    let adList: AdList = electList.getChildAt(0).addComponent(AdList);
                    adList.requestAdData(navigateSceneType.scroll, true, ZsPlayInfo.SCROLL_HORIZONTAL, null, null, false, true);
                }
            }

            let exitBtn: Laya.Button = this.getChildByName("exitBtn") as Laya.Button;
            if (!exitBtn.getComponent(FakeExitBtn)) {
                exitBtn.addComponent(FakeExitBtn);
            }

            EventManager.Ins.on(EventCfg.EVENT_SHOW_EXPORTGAME, this, this.showExportGame);
            //侧边
            this.btnPopGame.visible = false;

            this.slipBg.y = 545;
            //this.btnMoregame.y = 246;
            //this.btnReset.y = 373;

            this.InitFloatGame();
            this.getChildByName("GoldSp")['x'] = Laya.stage.width - 200;
            this.getChildByName("GoldSp")['y'] = 10;

            this.initCrazyData();
        }
    }

    initCrazyData() {
        Utils3D.bOpenClick = adSdk.sdk.adConfig.isCrazyTap(0, TapStyle.TAP_CHEST);
        if (Utils3D.bOpenClick) {
            this.nClickFinish = adSdk.sdk.adConfig.GetCrazyPercent();
            this.nClickAdd = adSdk.sdk.adConfig.electControl("zs_click_award_add");
            this.nTimeReduce = adSdk.sdk.adConfig.electControl("zs_click_award_back");
        }

        //开启狂点误触 不显示倒计时
        //this.timeBg.visible = !Utils3D.bOpenClick;
    }

    InitFloatGame() {
        for (let i = 1; i < 5; i++) {
            let fgame: Laya.Sprite = this.getChildByName("fgame" + i) as Laya.Sprite;
            if (fgame) {
                if (!fgame.getComponent(FloatGameImg)) {
                    fgame.addComponent(FloatGameImg);
                }
            }
        }
    }

    onShowFullListAd() {
        console.log("mainlayer监听游戏跳转失败打开全屏一");
        DialogManager.Ins.sceneOpen(UICfg.fullscreenad1, false, null);
    }


    onClickMoreGame(e: Laya.Event): void {
        if (e.type == Laya.Event.CLICK) {
            if (gSdk.isTT) {
                let listdata = adSdk.sdk.getAdList('defaultData');//DataModule.Ins.getGameList(true);
                gSdk.sdk.showMoreGamesModal((listdata && listdata.length > 0) ? listdata[0] : [], null, null, null, (code) => {
                    if (code == 1) TipManager.Ins.showMsgTip("打开更多游戏失败", this);
                })
            }
            else if (gSdk.isQQ) {
                gSdk.sdk.showAppBoxAd();
            }
            else if (gSdk.isWX) {
                Laya.Scene.open(UICfg.floatAd, false, { isCloseBanner: false });
            }
            else
                Laya.Scene.open("commonUI/MoreGameDialog.scene", false, { isCloseBanner: false });
        }
        e.stopPropagation();
    }


    public onTouchDown(e: Laya.Event): void {
        //if (Utils3D.bStart == false || Utils3D.bDeath) return;
        this.curTouchId = e.touchId;
        Utils3D.bMouseMoving = true;
        this.originPiont.setValue(e.stageX, e.stageY);
        //this.checkPressDown();
        if (this.slipBg.visible) {
            this.handSlip(false);
        }
    }

    pressTime = 0;
    checkPressDown() {
        let t1 = Laya.timer.currTimer;
        let self = this;
        this.pressTime = setInterval(function () {
            let t2 = Laya.timer.currTimer;//getTimeNow();//获取鼠标按下时的时间
            if (t2 - t1 >= 200) {
                //self._gameScene.checkPressDown(self.pressCount);
            }
        }, 20); //20毫秒执行一次
    }

    public onMoveEvent(e: Laya.Event): void {
        if (e.touchId != this.curTouchId || !Utils3D.bMouseMoving) return;
        var localPos: Laya.Point = this.globalToLocal(new Laya.Point(Laya.stage.mouseX, Laya.stage.mouseY), false);
        this.deltaX = e.stageX - this.originPiont.x//localPos.x - this.originPiont.x;
        this.deltaY = e.stageY - this.originPiont.y//localPos.y - this.originPiont.y;
        Utils3D.nChangeX = this.deltaX / 60;
        //console.log("Change x  ==  ",Utils3D.nChangeX);
        //let angle = 180 - Math.atan2(this.deltaX, this.deltaY) * 180 / Math.PI;
        //this._gameScene.onTouchMove(this.deltaX/100,this.deltaY);
        this.originPiont.setValue(e.stageX, e.stageY);
    }

    private onMouseUp(e: Laya.Event) {
        if (e.touchId != this.curTouchId) return;
        this.curTouchId = -1;
        Utils3D.nChangeX = 0;
        Utils3D.bRunning = false;
        Utils3D.bMouseMoving = false;
        //this._gameScene.onMouseUp();
    }


    showImgTips(x, y, index) {
        this.imgTips.x = x / Laya.stage.clientScaleX;
        this.imgTips.y = y / Laya.stage.clientScaleY;
        if (index == 0) {
            this.imgTips.skin = "image/num0.png";
        } else {
            this.imgTips.skin = "image/" + index + ".png";
        }
        this.ani4.play(null, false);
    }

    hpCount = 3;
    onHitBullet() {
        this.hpCount--;
        this.imgh.skin = "image/h" + this.hpCount + ".png";
        this.ani5.play(null, false);
    }

    onGameEnd() {
        this.imgHp.visible = false;
        this.bulletBg.visible = false;
    }

    showCoinAciton(x, y) {
        var startPos = new Laya.Vector2(x / Laya.stage.clientScaleX, y / Laya.stage.clientScaleY);
        Utils3D.goldEffect(startPos, 1, this);
    }

    clearPressTime() {
        clearInterval(this.pressTime);
        this.pressTime = 0;
    }

    gameBegin() {
        this.txtLv1.text = Utils3D.nShowScene.toString();
        this.txtLv2.text = (Utils3D.nShowScene + 1).toString();
        //this.showRetButton(true);
        this.handSlip(true);
        Laya.timer.once(3000, this, function () {
            this.handSlip(false);
        })
    }

    //左右滑动UI动画
    handSlip(bShow) {
        if (bShow) {
            this.slipBg.visible = true;
            this.ani1.play();
        } else {
            this.ani1.stop();
            this.slipBg.visible = false;
        }
    }

    coins = 0;
    endCount = 0;
    updateLvUI(num, coin) {
        this.progressLv.width += num;
        if (this.progressLv.width >= 287) {
            this.progressLv.width = 287;
        }
        if (coin == 1) {
            this.coins++;
            this.txtLvCoin.text = this.coins.toString();
        }
    }

    showRetButton(bShow = true) {
        this.btnReset.visible = bShow;
        this.btnMoregame.visible = bShow;
    }

    showLvBg(bShow = true) {
        this.lvbg.visible = bShow;
    }

    setZSADVisible(bVisible = false) {
        if (adSdk.sdk.adConfig.electControl("zs_horizontal_export"))
            this.getChildByName("exportList1")["visible"] = bVisible;
        this.getChildByName("exportlistPen")["visible"] = bVisible;
        this.getChildByName("exitBtn")["visible"] = bVisible;
    }
}

