import DataStorage from "./DataStorage";

export default class ControlData extends DataStorage {
    private static _instance: ControlData = null;

    private videounlockon: boolean = false;
    private videonoadcount: number = 0;  //前三次都是暂不试用
    private nativebtnswitch: boolean = true;

    constructor() {
        super();
    }

    public static get inst(): ControlData {
        if (this._instance == null) {
            this._instance = new ControlData();
        }
        return this._instance;
    }

    //视频解锁开关
    public getvideounlockon() {
        if (this.videonoadcount < 1) {
            this.videonoadcount++;
            return this.videounlockon;
        }
        return this.videounlockon = !this.videounlockon;
    }

    //原生广告开关
    public getnativebtnswitch() {
        return this.nativebtnswitch = !this.nativebtnswitch;
    }

    getData() {
        //清理缓存
        this.clear();
        this.init({ name: 'game-control' });
        this.initDatas();
        this.save();
    }

    initDatas() {
        this._data.zs_click_award_num = this._data.zs_click_award_num || 0;
        this._data.zs_ready_click_num = this._data.zs_ready_click_num || 0;
        this._data.zs_click_award_num1 = this._data.zs_click_award_num1 || 0;
    }

    /**获得当天字段值 */
    getValByToday(key: string, defaultValue: any = 0) {
        if (typeof this._data[key] == 'undefined') {
            this._data[key] = defaultValue;
            return this._data[key];
        }
        let timeKey = key + '_time';
        this._data[timeKey] = this._data[timeKey] || 0;
        let day = this.dayDiff(new Date(this._data[timeKey]), new Date()); //判断签到时间是否是同一天
        if (day > 0) {        //隔天
            this._data[key] = 0;
        }
        return this._data[key];
    }

    /**设置当天字段值 */
    setValByToday(key: string, value?: any) {
        if (value) {
            this._data[key] = value;
        } else {
            this._data[key]++;
        }
        this._data[key + '_time'] = new Date().getTime();
        this.save();
    }

}