import SdkBase from "./sdk-base";
import { oppoCfg, AdsType, bAdsUse } from "./plat-def";
import { AdsData } from "./Ads-data";
import { AdsCfgOppo } from "./Adscfg-def";

export default class SdkOppo extends SdkBase {
    private _oppo: any = null;
    private _adsCfg: AdsCfgOppo = null;

    private _adcanshow = false;
    private fakeNativeShow: number = 0; //记录一局游戏已经假曝光的次数
    private fakeNativeLoop: number = 4; //几秒上报一次假曝光
    private loadRealNative: boolean = false;//自动上报次数已够,是否把需要真实显示的原生广告拉取好
    constructor() {
        super();
        this._oppo = Laya.Browser.window.qg;
        this.version = oppoCfg.version;
        this.cfg = oppoCfg;
    }

    init(params?: any) {
        this.initAds();

        this._oppo['onShow'] && this._oppo.onShow((res) => {  //从后台返回到游戏
            //EventManager.dispatchGlobalByCmd(GameEvent.ONSHOW, {});
        });

        this._oppo['onHide'] && this._oppo.onHide(() => {  //从游戏到后台
            //EventManager.dispatchGlobalByCmd(GameEvent.ONHIDE, {});
        });

        Laya.timer.once(60000, this, () => { this._adcanshow = true; });
    }

    getLaunchOptionsSync(): any {
        return this._oppo.getLaunchOptionsSync()
    }

    getSystemInfoSync(): any {
        return this._oppo.getSystemInfoSync()
    }

    playVibrative(isShort = true) {
        isShort ? this._oppo.vibrateShort() : this._oppo.vibrateLong();
    }

    /**获取右上角菜单栏信息 */
    getRightMenuRectInfo(): any {
        if (typeof this._oppo.getMenuButtonBoundingClientRect == 'undefined') {
            return SdkBase.prototype.getRightMenuRectInfo.call(this);
        }
        return this._oppo.getMenuButtonBoundingClientRect();
    }

    getappId() {
        return this.cfg.appid;
    }

    getopenId(callback) {
        let self = this;
        this.login((state, ret) => {
            if (state) {
                if (ret.token) {
                    let xhr = new Laya.HttpRequest();
                    xhr.http.timeout = 10000;
                    xhr.once(Laya.Event.COMPLETE, this, (res) => {
                        self.openId = res.data.user_id;
                        callback && callback(true, self.openId);
                    });
                    xhr.once(Laya.Event.ERROR, this, (err) => {
                        console.log('MyOPPOAD:OPPO get openid fail', JSON.stringify(err));
                        callback && callback(false);
                    })
                    let sendData = { appid: self.cfg.appid, code: ret.token };
                    xhr.send(self.cfg.serviceUrl, JSON.stringify(sendData), "post", "json", ["content-type", "application/json"]);
                }
            } else {
                callback && callback(false);
            }
        });
    }

    login(cb: Function = null) {
        if (this._oppo.login) { console.log("MyOPPOAD:login is not null"); }

        this._oppo.login({
            success: (ret) => {
                cb && cb(true, ret);
            },
            fail: (err) => {
                console.log("MyOPPOAD:OPPO login fail ", JSON.stringify(err));
                cb && cb(false);
            },
            complete: (res) => { }
        });
    }

    /**
     * 分包加载
     * @param resList 
     * @param callback 
     * @param progressCall 
     */
    loadSubpackage(resList: Array<string>, callback, progressCall) {
        let count = resList.length;
        let loadPackageCount = count;
        let load = function () {
            loadPackageCount--;
            if (loadPackageCount <= 0) {
                callback && callback();
            }
        }
        for (let i = 0; i < count; i++) {
            var loadTask = this._oppo.loadSubpackage({
                name: resList[i], // name 可以填 name 或者 root
                success: function (res) {
                    progressCall && progressCall(i);
                    load();
                },
                fail: function (err) {
                    // 分包加载失败通过 fail 回调
                    console.log('MyOPPOAD:loadSubpackage fail=>', resList[i], JSON.stringify(err));
                }
            });

            loadTask.onProgressUpdate(res => {
            })
        }
    }

    navigateToMiniProgram(data, success = null, fail = null, complete = null) {
        this._oppo.navigateToMiniGame(
            {
                pkgName: data.link_pkgname,
                path: data.link_path,
                extraData: data.extraData,
                success: function (e) {
                    success && success(e)
                },
                fail: function (e) {
                    console.log('MyOPPOAD:navigateToMiniProgram fail=>', JSON.stringify(e));
                    fail && fail(e);
                },
                complete: function (e) {
                    complete && complete(e);
                }
            }
        )
    }

    /**数据上报 */
    reportMonitor() {
        this._oppo.reportMonitor('game_scene', 0)
    }

    private breportbegin: boolean = false;
    reportMonitorEx(name) {
        if (this._oppo.getSystemInfoSync().platformVersionCode < 1076) {
            return;
        }
        if (name == "begin_click_btn") {
            if (!this.breportbegin) {
                this.breportbegin = true;
            }
            else {
                return;
            }
        }
        this._oppo.reportMonitor(name);
    }

    /**添加桌面 */
    installShortcut(success: Function, fail: Function, compelete: Function) {
        if (typeof this._oppo.hasShortcutInstalled == 'undefined' || typeof this._oppo.installShortcut == 'undefined') {
            return;
        }
        let self = this;
        this._oppo.hasShortcutInstalled({
            success: function (res) {
                // 判断图标未存在时，创建图标
                if (res == false) {
                    self._oppo.installShortcut({
                        success: function () {
                            // 执行用户创建图标奖励
                            success && success(true);
                        },
                        fail: function (err) {
                            console.log("MyOPPOAD:installShortcut fail");
                            fail && fail();
                        },
                        complete: function () {
                            compelete && compelete();
                        }
                    })
                } else {
                    success && success(false);
                }
            },
            fail: function (err) {
                console.log("MyOPPOAD:hasShortcutInstalled fail");
                fail && fail()
            },
            complete: function () {
            }
        })
    }

    //*********************************************/
    //***********    广告接口   *******************/
    //********************************************/
    initAdsCfg(params: AdsCfgOppo = null) {
        this._adsCfg = params;
    }


    initAds() {
        this._adsCfg = {
            ver: "0.0",
            switch: 0,
            bgshowad_inonemin: 0,
            bgnative_exposure_num: 1
        };
        this.createNative(AdsType.NATIVE_INSTIL);
        this.preloadVedioAds();
        this.createBanner();
        //this.preloadInstalAds();
        this.preloadGamePortal();
        if (this._oppo.getSystemInfoSync().platformVersionCode < 1076)
            return;
        this._oppo.onReportMonitor(function (res) {
            //响应码为0 即对应场景上报成功
            if (res.code == 0) {
                console.log("MyOPPOAD:onReportMonitor success");
            }
            else {
                console.log("MyOPPOAD:onReportMonitor fail");
            }
        });
    }

    /**
     * 视频广告
     */

    createVideoAds() {
        if (typeof this._oppo.createRewardedVideoAd == 'undefined') {
            return;
        }
        let ads = this.getAdsData(AdsType.VIDEO);
        if (!ads) return;

        let adsIndex = ads.adsIndex;
        let videoId = oppoCfg.VideoId[adsIndex % oppoCfg.VideoId.length];

        let videoAd = this._oppo.createRewardedVideoAd({
            adUnitId: videoId
        });

        videoAd.onLoad(() => {
            ads.isLoaded = true;
        });

        videoAd.onError((err) => {
            console.log('MyOPPOAD:video error=>', JSON.stringify(err));
            ads.isLoaded = false;
        });

        videoAd.onClose((res) => {
            if (res && res.isEnded || res === undefined) {    //视频广告成功，获得奖励
                ads.close(true);
            } else {
                ads.close(false);
            }
            ads.neespreLoad && this.preloadVedioAds();
        });
        ads.adInstance = videoAd;
    }

    preloadVedioAds() {
        let ads = this.getAdsData(AdsType.VIDEO);
        if (!ads.adInstance) {
            this.createVideoAds();
        }
        if (!ads.isLoaded && ads.adInstance) {
            let promise = ads.adInstance.load()
            if (promise && oppoCfg.usePromise) {
                promise.then(function () {
                    ads.isLoaded = true;
                }.bind(this)).catch(function (err) {
                    console.log("MyOPPOAD:video fail=>", JSON.stringify(err))
                    ads.isLoaded = false;
                }.bind(this));
            }
        }
    }

    showVideo(ads: AdsData) {
        if (!bAdsUse) {
            SdkBase.prototype.showVideo.call(this, ads);
            return;
        }
        let adsInstance = ads.adInstance;
        if (adsInstance) {
            if (oppoCfg.usePromise) {
                let promise = adsInstance.show();
                promise.then(() => {
                    ads.show(true);
                }).catch(() => {
                    console.log('MyOPPOAD:video show fail');
                    ads.show(false);
                    ads.neespreLoad && this.preloadVedioAds();
                });
                return;
            } else {
                if (ads.isLoaded) {   //加载成功
                    ads.show(true);
                    adsInstance.show();
                    return;
                } else {
                    ads.show(false);
                    ads.neespreLoad && this.preloadVedioAds();
                }
            }
        } else {
            ads.show(false);
            if (ads.neespreLoad) {
                this.preloadVedioAds();
            } else {
                this.createVideoAds();
            }
        }
    }

    /**
     * 插屏广告
     */

    createInstalAds() {
        if (typeof (this._oppo.createInterstitialAd) == "undefined") {
            console.log('MyOPPOAD:createInterstitialAd api not exists');
            return;
        }
        let ads = this.getAdsData(AdsType.INTERSTITIAL);
        if (!ads) return;
        let adsIndex = ads.adsIndex;
        let adId = this.cfg.InterstitialId[adsIndex % this.cfg.InterstitialId.length];
        let interstitialAd = this._oppo.createInterstitialAd({
            adUnitId: adId
        });
        interstitialAd.onError((err) => {
            console.log('MyOPPOAD: InterstitialAd err', JSON.stringify(err));
            ads.isLoaded = false;
        });
        interstitialAd.onLoad(() => {
            ads.isLoaded = true;
        });
        interstitialAd.onClose((ret) => {
            ads.close(true);
            if (this.cfg.destroyInstl) {
                ads.adInstance.destroy();
                ads.adInstance = null;
            }
            ads.neespreLoad && this.preloadInstalAds();
        });
        ads.adInstance = interstitialAd;
    }

    preloadInstalAds() {
        let ads = this.getAdsData(AdsType.INTERSTITIAL);
        if (!ads.adInstance) {
            this.createInstalAds();
            return;
        }
        if (!ads.isLoaded && ads.adInstance) {
            let promise = ads.adInstance.load();
            if (promise && this.cfg.usePromise) {
                promise.then(() => {
                    ads.isLoaded = true;
                }).catch((err) => {
                    console.log('MyOPPOAD:InterstitialAd load fail', err);
                    ads.isLoaded = false;
                });
            }
        }
    }

    showInstal(ads: AdsData) {
        let adsInstance = ads.adInstance;
        if (adsInstance) {
            if (this.cfg.usePromise) {
                let promise = adsInstance.show();
                promise.then(() => {
                    ads.show(true);
                }).catch(() => {
                    console.log('MyOPPOAD:showInstal fail');
                    ads.show(false);
                    ads.neespreLoad && this.preloadInstalAds();
                });
                return;
            } else {
                if (ads.isLoaded) {
                    ads.adInstance.show();
                    ads.show(true);
                } else {
                    ads.show(false);
                    ads.neespreLoad && this.preloadInstalAds();
                }
            }
        } else {
            ads.show(false);
            if (ads.neespreLoad) {
                this.preloadInstalAds();
            } else {
                this.createInstalAds();
            }
        }
    }

    /**
     * banner广告
     */

    createBanner(style?: any) {
        //初始化banner 广告
        let sysInfo = this.getSystemInfoSync();
        let screenWidth = sysInfo.screenWidth;
        let screenHeight = sysInfo.screenHeight;
        let ads = this.getAdsData(AdsType.BANNER);

        if (ads.adInstance) {
            ads.adInstance.destroy();
            ads.adInstance = null;
        }

        style = style || {};
        for (let key in oppoCfg.style) {
            style[key] = oppoCfg.style[key];
        }
        let bannerWidth = style.width;
        if (bannerWidth <= 1) {    //按屏幕宽度百分比计算宽度
            bannerWidth = screenWidth * bannerWidth;
        }

        let bannerHeight = style.height;
        let mp = style.mp || 1;

        let adsIndex = ads.adsIndex;
        let adId = oppoCfg.bannerIds[adsIndex % oppoCfg.bannerIds.length];

        let top = mp == 1 ? (screenHeight - bannerHeight) : (mp == 2 ? 0 : style.top);
        let left = style.isCenter ? screenWidth / 2 - bannerWidth / 2 : style.left;
        let bannerAds = this._oppo.createBannerAd({
            adUnitId: adId,
            adIntervals: oppoCfg.adIntervals,
            style: {
                left: left,
                top: top,
                width: bannerWidth,
                height: bannerHeight
            }
        });

        ads.style.height = bannerHeight;
        ads.style.width = bannerWidth;
        ads.style.left = left;
        ads.style.top = top;
        ads.style.realHeight = bannerHeight;
        ads.style.realWidth = bannerWidth;

        bannerAds.onResize((res) => {
            var bannerAdHeight = res.height;
            var bannerAdWidth = res.width;
            bannerAds.style.top = mp == 1 ? (screenHeight - bannerAdHeight) : (mp == 2 ? 0 : style.top);
            bannerAds.style.left = style.isCenter ? screenWidth / 2 - bannerAdWidth / 2 : style.left;
            ads.style.realWidth = bannerAdWidth;
            ads.style.realHeight = bannerAdHeight;
            ads.style.left = bannerAds.style.left;
            ads.style.top = bannerAds.style.top;
        });

        bannerAds.onLoad(() => {
            ads.isLoaded = true;
            ads.loadCount = 0;
        });

        bannerAds.onError((err) => {
            console.log('MyOPPOAD:banner error=>', JSON.stringify(err));
            ads.isLoaded = false;
            this.reLoadBanner();
        })

        ads.adInstance = bannerAds;
        bannerAds && bannerAds.hide();
    }

    reLoadBanner() {
        let ads = this.getAdsData(AdsType.BANNER);
        if (ads.loadCount < oppoCfg.error_count) {
            ads.adsIndex++;
            if (ads.adInstance) {
                ads.adInstance.hide();
                ads.adInstance.destroy();
                ads.adInstance = null;
            }
            this.createBanner();
            this.showBanner();
        }
        ads.loadCount++;
    }

    showBanner(style?: any) {
        if (!bAdsUse) return;

        if (this._adsCfg.bgshowad_inonemin) {
            console.log("MyOPPOAD:sdk-oppo.ts--showBanner() zs_showad_inonemin");
            if (!this._adcanshow) {
                console.log("MyOPPOAD:sdk-oppo.ts--showBanner() in 60 return");
                return;
            }
        }
        //console.log("MyOPPOAD:sdk-oppo.ts--showBanner() 2");
        let ads = this.getAdsData(AdsType.BANNER);

        if (!ads.adInstance) {
            this.createBanner(style);
        }
        if (oppoCfg.usePromise) {
            let promis = ads.adInstance.show();
            if (promis) {
                promis.then(() => {
                    ads.loadCount = 0;
                }).catch(() => {
                    ads.loadCount = 0;
                });
            }
        } else {
            ads.adInstance.show();
        }
        ads.adsIndex++;
    }

    hideBanner(need_pre_create: boolean = true, style?: any) {
        let ads = this.getAdsData(AdsType.BANNER);
        if (ads.adInstance) {
            ads.adInstance.hide();
            ads.adInstance.destroy();
            ads.adInstance = null;
            ads.loadCount = 0;
            if (ads.neespreLoad) {
                this.createBanner(style);
            }
        }
    }

    hide_banner() {
        let ads = this.getAdsData(AdsType.BANNER);
        if (ads.adInstance) {
            ads.adInstance.hide();
        }
    }

    bannerMoveBy(top: number = 80, left: number = 0) {
        let ads = this.getAdsData(AdsType.BANNER);
        if (ads.adInstance) {
            ads.adInstance.style.top += top;
            ads.adInstance.style.left += left;
        }
    }

    bannerMoveTo(top: number = 0, left: number = 0) {
        let ads = this.getAdsData(AdsType.BANNER);
        if (ads.adInstance) {
            ads.adInstance.style.top = top;
            ads.adInstance.style.left = left;
        }
    }

    /**
     * 原生广告
     */

    enterGameScene() {
        this.fakeNativeShow = 0;
        this.loadRealNative = false;
        Laya.timer.loop(this.fakeNativeLoop * 1000, this, this.fakeNativeTimer);
    }

    exitGameScene() {
        Laya.timer.clear(this, this.fakeNativeTimer);
    }

    fakeNativeTimer() {
        if (this.cfg.version != this._adsCfg.ver) return; //调试代码

        if (this.fakeNativeShow >= this._adsCfg.bgnative_exposure_num) { //假上报次数已经足够
            if (this.loadRealNative) {  //需要真实显示的原生广告也拉取好了
                //要判断一下是否拉取成功,不成功,再拉
                let checkNativeData = this.getAdsData(AdsType.NATIVE_INSTIL);
                if (checkNativeData && checkNativeData.adInstance && checkNativeData.adData) {
                    return;
                }
                else {
                    this.createNative(AdsType.NATIVE_INSTIL);
                }
            }
            else {
                this.loadRealNative = true;
                this.createNative(AdsType.NATIVE_INSTIL);
                return;
            }
        }
        let ads = this.getAdsData(AdsType.NATIVE_INSTIL);
        if (ads && ads.adInstance && ads.adData && !ads.nativeShowed) {
            this.fakeNativeShow++;
            this.reportAdShow(ads.adData[0]);
        }
        else {
            console.log("MyOPPOAD:auto report show in timer");
            this.createNative(AdsType.NATIVE_INSTIL, true);
        }
    }

    loadNativeData(adsData: AdsData, tid, func) {
        let nativeAd = this._oppo.createNativeAd({
            adUnitId: tid
        });

        adsData.adInstance = nativeAd;

        nativeAd.load();

        nativeAd.onLoad((res) => {
            adsData.adData = res.adList;
            adsData.isLoaded = true;
            console.log("MyOPPOAD:load NativeData->onLoad", JSON.stringify(res.adList[0].adId));
            nativeAd.offLoad();
            func(res.adList);
        })
        nativeAd.onError((err) => {
            adsData.adData = null;
            adsData.isLoaded = false;
            console.log("MyOPPOAD:load NativeData->onError", JSON.stringify(err));
            nativeAd.offError();
            func(null);
        })
    }

    createNative(adtype: AdsType, reportShow: boolean = false) {
        let self = this;
        let ads = this.getAdsData(adtype);
        if (!ads) return;
        if (ads.adInstance) {
            ads.adInstance.destroy();
            ads.adInstance = null;
            ads.adData = null;
            ads.isLoaded = false;
            ads.nativeShowed = false;
        }
        let nativeid;
        if (adtype == AdsType.NATIVE_INSTIL) {
            nativeid = oppoCfg.nativeIds[0];
        }
        else {
            nativeid = oppoCfg.nativeIds[1];
        }
        this.loadNativeData(ads, nativeid, (ret) => {
            if (ret) {
                if (reportShow) {//自动上报曝光
                    self.fakeNativeShow++;
                    self.reportAdShow(ret[0]);
                }
            }
            else
                console.log("MyOPPOAD:load NativeData ret is null");
        });
    }

    createNative_OPPO() {
        this.createNative(AdsType.NATIVE_INSTIL);
    }

    getNativeData() {
        if (this._adsCfg.bgshowad_inonemin) {
            if (!this._adcanshow) {
                console.log("MyOPPOAD:getNativeData return");
                return null;
            }
        }
        let ads = this.getAdsData(AdsType.NATIVE_INSTIL);
        if (!ads) return null;
        if (ads.adInstance && ads.adData && ads.isLoaded)
            return ads.adData[0];
        else {
            this.createNative(AdsType.NATIVE_INSTIL);
            return null;
        }
    }
    /**上报点击 */
    reportAdClick(data: any, adtype: AdsType = AdsType.NATIVE_INSTIL) {
        let ads = this.getAdsData(adtype);
        if (ads.adInstance) {
            console.log("MyOPPOAD:reportAdClick:", data.adId);
            ads.adInstance.reportAdClick({
                adId: data.adId
            })
        }
    }
    /**上报曝光 */
    reportAdShow(data: any, adstype: AdsType = AdsType.NATIVE_INSTIL) {
        let ads = this.getAdsData(adstype);
        if (ads.adInstance) {
            ads.nativeShowed = true;
            console.log("MyOPPOAD:reportAdShow:", data.adId);
            ads.adInstance.reportAdShow({
                adId: data.adId
            })
        }
        else {
            console.log("MyOPPOAD:ads.adINstance is null:", JSON.stringify(ads));
        }
    }

    /**
     * 九宫格广告
     */

    supportGamePortal() {
        if (this._oppo.getSystemInfoSync().platformVersionCode >= 1076)
            return true;
        return false;
    }
    createGamePortal() {
        if (typeof this._oppo.createGamePortalAd == "undefined") {
            return;
        }
        let ads = this.getAdsData(AdsType.GAME_PORTAL);

        let id = oppoCfg.GamePortalId[0];
        let GamePortalAd = this._oppo.createGamePortalAd({
            adUnitId: id
        });

        ads.adInstance = GamePortalAd;

        GamePortalAd.onLoad(function () {
            console.log("MyOPPOAD:PortalAd onLoad");
            ads.isLoaded = true;
        });
        GamePortalAd.onError(function (err) {
            console.log("MyOPPOAD:PortalAd onError:", JSON.stringify(err));
            ads.isLoaded = false;
        });
        GamePortalAd.onClose(function () {
            ads.isLoaded = false;
            ads.close(true);
            console.log("MyOPPOAD:PortalAd onClose");
            GamePortalAd.load().then(function () {
                console.log("MyOPPOAD:PortalAd load success");
            }).catch(function (error) {
                console.log("MyOPPOAD:PortalAd load fail with:" + error.errCode + "," + error.errMsg);
            });
        });
    }

    preloadGamePortal() {
        let ads = this.getAdsData(AdsType.GAME_PORTAL);
        if (!ads.adInstance) {
            this.createGamePortal();
        }
    }
    showGamePortal(ads: AdsData) {
        if (!ads.isLoaded) {
            ads.show(false);
            ads.adInstance.load();
            return;
        }
        if (ads.adInstance) {
            ads.adInstance.show().then(function () {
                ads.show(true);
                console.log("MyOPPOAD:PortalAd show success");
            }).catch(function (error) {
                console.log("MyOPPOAD:PortalAd show fail with:" + error.errCode + "," + error.errMsg);
                ads.show(false);
                ads.adInstance.load();
            });
        }
        else {
            ads.show(false);
        }
    }
}