import { ui } from "../ui/layaMaxUI";
import gSdk from "../common/platformsdk/platform";
import { AdsType, WxBannerScreen } from "../common/platformsdk/plat-def";
import TipManager from "../common/TipManager";
import { UICfg, EventCfg } from "../scripts/defines/ResDef";
import adSdk from "../common/adDataSdk/adDataSdkFactory";
import { DialogManager } from "../common/DialogManager";
import Utils3D from "../scripts/mgr/Utils3D";
import EventManager from "../common/EventManager";
import GameScene from "../script/GameScene";
import AudioManager from "../common/AudioManager";
import { AudioType } from "../common/data/AudioData";
import UserData from "../common/data/UserData";
import UITool from "../common/UITool";
import GoldSp from "../commonUI/prefab/GoldSp";
import AdTool from "../common/AdTool";
import Constant from "../scripts/mgr/Constant"
import display from "../common/Display";

export default class ShopView extends ui.gameUI.ShopViewUI {
    private _gameScene: GameScene = GameScene.getInstance();
    tabNum = 2;
    nCurTab = 1;
    tabAarry = [];
    nPayIndex = 1;
    nCurUsing = 0;
    constructor() {
        super();
        this.height = Laya.stage.height;
        //this.bg.height = this.height;
        //this.bg.graphics.drawRect(0, 0, Laya.stage.width, Laya.stage.height, "#23a8ea");
    }

    onAwake(): void {
    }

    onMouseDown(): void {

    }

    onEnable(): void {
        if (gSdk.isQQ) {
            gSdk.sdk.hideBlockAd();
            gSdk.sdk.hide_banner();
        }
        else if(gSdk.isWX) {
            gSdk.sdk.hideBannerzs();
        }
        else {
            gSdk.sdk.hide_banner();
        }
        this.closeBtn.on(Laya.Event.MOUSE_DOWN, this, function () {
            Utils3D.bBtnClicked = false;
            Utils3D.bOpenShop = false;
            EventManager.Ins.event("Shopping");
            this._gameScene.closeShop();
            this.close();
        })

        this.videoBtn.on(Laya.Event.MOUSE_DOWN, this, this.onVideo)

        this.nCurUsing = 0;
        for (var i = 1; i <= this.tabNum; i++) {
            this.tabAarry[i] = this.getChildByName("tabBtn" + i.toString()) as Laya.Image;
            this.tabAarry[i].name = i;
            this.tabAarry[i].on(Laya.Event.CLICK, this, function (e) {
                this.nCurTab = e.target.name;
                this.updateTab();
            })
        }

        this.updateTab();
        this.selectBtn.on(Laya.Event.CLICK, this, this.onBuy);

        // this.list2.array = datas;
        this.list2.renderHandler = new Laya.Handler(this, this.onListRender);
        this.list2.selectHandler = new Laya.Handler(this, this.onSelect);
        this.list2.mouseHandler = Laya.Handler.create(this, this.onSelect, null, false);
        this.list2.vScrollBarSkin = "";
        //this.updateBtnDesc();

        this.PlatformUIInit();
        //this.adjustLargerUI();

    }

    adjustLargerUI() {
        if (display.largeScreen()) { //如果是大屏
            console.log(" 高大屏  ===== ");
            this.y += 120;
            this.bg.y -= 120;
            this.goldBg.y -= 120;
        }
    }

    RefreshList() {
        let datas = [];
        let res = "";
        let len = 7;
        let steps = this.nPayIndex.toString();
        for (let i = 1; i < len; i++) {
            res = "image/shop/" + this.nPayIndex + "_" + i + ".png";
            datas.push({
                icon: res,
                name: "item" + i,
                curIndex: Utils3D.shopSelect[steps],
                state: Utils3D.shopTools[steps][i - 1],
                type: Constant.ToolType[steps][i - 1]
            })
        }
        this.list2.array = datas;
    }

    onVideo() {
        AdTool.Ins.playVideo("观看完整视频才能获得金币", () => {
            this.addGold(500);
            UITool.Ins.goldEffect(new Laya.Vector2(this.videoBtn.x + 150, this.videoBtn.y + 50), 10, this, () => { });
        }, this);
    }

    private addGold(_value: number, _per: number = 1) {
        let tgold: number = Math.floor(_value * _per);

        UserData.inst.setGold(tgold);

        EventManager.Ins.event("changeGold");
    }

    updateData() {
        let len = 5;
        for (let i = 1; i < len; i++) {
            let state = Utils3D.shopTools[this.nPayIndex.toString()][i - 1];
            if (state == 1) {
                Utils3D.shopTools[this.nPayIndex.toString()][i - 1] = 2;
            }
        }

        Utils3D.shopTools[this.nPayIndex.toString()][this.nCurUsing] = 1;
        Laya.LocalStorage.setJSON("ShopItems", Utils3D.shopTools);
        Utils3D.shopSelect[this.nPayIndex.toString()] = this.nCurUsing;
        Laya.LocalStorage.setJSON("ShopItemSelect", Utils3D.shopSelect);
    }

    onBuy() { //购买
        let state = Utils3D.shopTools[this.nPayIndex.toString()][this.nCurUsing];
        let price = Constant.ToolType[this.nPayIndex.toString()][this.nCurUsing] * 2000;
        if (state == 0) {
            if (UserData.inst.gold >= price) {
                Utils3D.showTips("购买道具成功");
                UserData.inst.setGold(-price);
                EventManager.Ins.event("changeGold");//玩家金币发生改变
                this.updateData();
                this.RefreshList();
            } else {
                Utils3D.showTips("金币不足");
            }
        } else if (state == 1) {
            TipManager.Ins.showMsgTip("使用中!", this);
        } else {
            //购买了换道具使用
            //Utils3D.showTips("使用成功!"); 0
            TipManager.Ins.showMsgTip("使用成功!", this);
            this.updateData();
            this.RefreshList();
        }
        this.updateBtnDesc();
    }

    updateTab() {
        for (var i = 1; i <= this.tabNum; i++) {
            var resPath = "image/shop/tab" + i.toString() + ".png";
            if (i == this.nCurTab) {
                resPath = "image/shop/tab" + (this.nCurTab + 10).toString() + ".png";
            }
            this.tabAarry[i].skin = resPath;
        }

        this.nPayIndex = this.nCurTab;

        this.nCurUsing = Utils3D.shopSelect[this.nPayIndex.toString()];
        this.updateBtnDesc();
        this.RefreshList();
    }


    updateBtnDesc() {
        let price = Constant.ToolType[this.nPayIndex][this.nCurUsing] * 2000;
        let state = Utils3D.shopTools[this.nPayIndex][this.nCurUsing];
        if (state == 0) {
            this.selectBtn.label = "  " + price.toString() + " 解锁";
        } else if (state == 1) {
            this.selectBtn.label = "使用中"
        } else {
            this.selectBtn.label = "使用道具"
        }
    }


    onOpened() {
        //if (gSdk.isTT)
        //    gSdk.sdk.startRecord();
    }

    onDisable() {
        if(gSdk.isQQ) {
            gSdk.sdk.showBlockAd();
            gSdk.sdk.showBanner();
        }
        else if(gSdk.isWX){
            gSdk.sdk.showBannerzs();
            //EventManager.Ins.event(EventCfg.EVENT_SHOP_CLOSED);
        }
        else {
            gSdk.sdk.showBanner();
        }
    }


    onCloseFullAd(result) {
        this.Next(result);
    }

    Next(result) {
        this.close();
        DialogManager.Ins.sceneOpen(UICfg.ResultView, false, { gameresult: result })
    }

    onResetGame() {
        if (gSdk.isTT)
            gSdk.sdk.stopRecording();
        this.close();
        Laya.Scene.open(UICfg.GameStartView);
    }


    onListRender(cell, index: number) {
        if (index > this.list2.array.length) {
            return;
        }

        let data = this.list2.array[index];
        if (!data) {
            return;
        }

        let icon = cell.getChildByName('icon') as Laya.Image;
        icon && (icon.skin = data.icon);
        if(this.nCurTab == 1){
            icon.x = 22;
        }else{
            icon.x = 48;
        }
        let select = cell.getChildByName('selectImg') as Laya.Image;
        let imgType = cell.getChildByName("type") as Laya.Image;
        let name = cell.getChildByName('porssTxt') as Laya.Text;
        name && (name.text = data.name);

        if (index == data.curIndex) {
            select && (select.visible = true);
            //name.color = Laya.Color.GREEN;
        } else {
            select && (select.visible = false);
        }

        if (data.type == 2) {
            imgType && (imgType.visible = true);
            //name.color = Laya.Color.GREEN;
        } else {
            imgType && (imgType.visible = false);
        }

        if (data.state == 0) {
            name && (name.text = "未解锁");
            //name.color = "#fdfdfd";
            name.strokeColor = "#666666"
        } else if (data.state == 1) {
            name && (name.text = "使用中");
            //this.nCurSelct = index;
            //name.color = "#4df63e"
            name.strokeColor = "#00a008"
        } else {
            name && (name.text = "已解锁");
            //name.color = "#ff6600"
            name.strokeColor = "#be589e"
        }
    }

    onSelect(e, index) {
        if (e.type == Laya.Event.MOUSE_DOWN) {
            this.updateCell();
            this.nCurUsing = index;
            let select = e.target.getChildByName('selectImg') as Laya.Image;
            select && (select.visible = true);

            this.updateBtnDesc();
            if(this.nCurTab == 1){
                this._gameScene.changeWeapon(true,index);
            }else{
                this._gameScene.changeShipin(true,index);
            }
        }
    }

    updateCell() {
        for (var i = 0; i < this.list2.array.length; i++) {
            let node = this.list2.getCell(i) as Laya.Sprite;
            if (node) {
                let select = node.getChildByName('selectImg') as Laya.Image;
                select && (select.visible = false);
            }
        }
    }

    PlatformUIInit() {
        if (gSdk.isWX) {
            this.getChildByName("GoldSp")["x"] = Laya.stage.width - 200;
            this.getChildByName("GoldSp")["y"] = 10;
        }
        else if(gSdk.isTT) {
            this.videoBtn.skin = "platform_pics/pic_007.png";
        }
    }

}