import { adDataSdkBase } from "./adDataSdkBase";
import { adDataSdkZS } from "./adDataSdkZS";
import { adDataSdkQT } from "./adDataSdkQT";
import { cpType, PlatformCpType } from "./adDataSdkDef";
import gSdk from "../platformsdk/platform";

export class adDataSdkFactory {
    public sdk: adDataSdkBase = null;
    private _ptype: cpType = cpType.None;

    constructor() {
    }

    init(params?: any) {
        var cp = PlatformCpType[gSdk.pType]["cp"];
        if (cp == cpType.ZS) {
            this.sdk = new adDataSdkZS();
            this._ptype = cpType.ZS;
            gSdk.sdk.setUseZS();//解决代码中许多使用gSdk.sdk.isUseZS做判断问题，以后建议使用adSdk.isZS判断
        } else {
            this.sdk = new adDataSdkQT();
            this._ptype = cpType.QT;
        }

        this.sdk.init(params);
    }

    get isQT() { return this._ptype == cpType.QT }
    get isZS() { return this._ptype == cpType.ZS }
}

const adSdk = new adDataSdkFactory();
export default adSdk;